import { Component, Input, Renderer2, ViewChild, Output, EventEmitter, Optional, OnInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { dateStructToDate, dateToDateStruct, dateToString, NgbDateStruct } from '@core/utils';
import { ModelComponent } from '../model.component';

@Component({
  selector: 'sp-datepicker',
  templateUrl: './datepicker-popup.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: DatePickerPopupComponent,
      multi: true
    }
  ]
})
export class DatePickerPopupComponent implements ControlValueAccessor, OnInit {
  static nameCounter = 0;

  @Input() disabled = false;

  @Input() set group(value: string) { this._group = value; }
  private _group: string;

  @Input() label: string;

  @Input() set model(value: any) { this._model = value; }
  private _model: any;

  @Input() set modelType(value: string) { this._modelType = value; }
  private _modelType: string;

  @Input() name: string;
  @Input() type = 'text';
  @Input() placeholder = '';
  @Input() propertyName: string;

  @ViewChild('child') child: any;

  @Output() focus = new EventEmitter();
  @Output() blur = new EventEmitter();
  @Output() onChange = new EventEmitter();
  @Output() onTouched = new EventEmitter();

  private accessorOnChange = (...args: any[]) => {};
  private accessorOnTouched = (...args: any[]) => {};
  private currentValue: Date | string;
  dateStruct: NgbDateStruct;

  constructor(@Optional() public modelContext?: ModelComponent) { }

  ngOnInit() {
    this.name = (this.name || '') + '$' + DatePickerPopupComponent.nameCounter++;
  }

  /** Get the latest data-bound group. If not bound, get group from modelContext. */
  get group(): string {
    return this._group || (this.modelContext && this.modelContext.group);
  }

  /** Get the latest data-bound model. If not bound, get model from modelContext. */
  get model(): any {
    return this._model || (this.modelContext && this.modelContext.model);
  }

  /** Get the latest data-bound model type. If not bound, get model type from modelContext. */
  get modelType(): string {
    return this._modelType || (this.modelContext && this.modelContext.modelType);
  }

  writeValue(dateValue: Date | string): void {
    this.currentValue = dateValue;
    delete this.dateStruct;
  }

  registerOnChange(fn: any): void {
    this.accessorOnChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.accessorOnTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  onValueChanged() {
    const changedDate = dateStructToDate(this.getValue());
    this.accessorOnChange(changedDate);
    this.onChange.emit(changedDate);
  }

  onFocus(): void {
    this.focus.emit();
  }

  onBlur = (newValue: string) => {
    this.accessorOnTouched();
    this.onTouched.emit();
  }

  getValue(): NgbDateStruct {
    if (!this.dateStruct) {
      if (this.model && this.modelType && this.propertyName) {
        this.dateStruct = dateToDateStruct(this.model[this.propertyName]);
      } else {
        this.dateStruct = dateToDateStruct(this.currentValue);
      }
    }

    return this.dateStruct;
  }

  setValue(value: NgbDateStruct): void {
    this.dateStruct = value;
    const dateValue = dateStructToDate(value);

    if (this.model && this.modelType && this.propertyName) {
      this.model[this.propertyName] = dateValue;
    }

    this.currentValue = dateValue;
    if (this.modelContext) {
      this.modelContext.notifyOfModelChange();
    }
  }
}
