import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpErrorResponse
} from '@angular/common/http';

import { combineLatest, EMPTY, Observable, throwError } from 'rxjs';
import { catchError, concatMap, first } from 'rxjs/operators';

import { AuthService } from '@core/services/auth-services/auth.service';
import { SessionService } from '@session/session.service';

import { environment } from '@environment';
import { WellKnownRoutes } from '@core/well-known-routes';

@Injectable() // do not use providedIn because multi-provided
export class AuthHttpInterceptor implements HttpInterceptor {

  constructor(
    private authService: AuthService,
    private sessionService: SessionService,
    private router: Router
  ) { }

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    // add withCredentials to ensure that auth cookies are sent on CORS requests
    req = req.clone({
      withCredentials: true
    });
    if (req.url.startsWith(environment.apiRoot) || req.url.startsWith(environment.spApiRoot)) {
      return combineLatest(
        this.authService.accessToken$,
        this.authService.oidcUser$,
        this.sessionService.currentUserId$
      ).pipe(
        first(),
        concatMap(([accessToken, oidcUser, currentUserId]) => {
          if (accessToken) {
            this.authService.logEvent(`AuthHttpInterceptor adding access token to HTTP request "${req.url}"`);
            const headers: any = {
              'Authorization': `Bearer ${accessToken}`
            };
            if (currentUserId) {
              headers['X-Sp-Current-User-Id'] = currentUserId;
            }
            req = req.clone({ setHeaders: headers });
          } else {
            this.authService.logEvent(`AuthHttpInterceptor request to "${req.url}" untouched because no AuthService.accessToken.`);
          }
          return next.handle(req).pipe(
            catchError((error: HttpErrorResponse) => {
              if (error && error.status === 401) {
                let authFailRoute: string;
                this.authService.onAuthError(`AuthHttpInterceptor HTTP auth-related error response, ${error.status}.`);

                // Actual example: WWW-Authenticate: Bearer error="invalid_token", error_description="The token is expired"
                const authResHeader = error.headers ? error.headers.get('WWW-Authenticate') : null;
                if (/is expired/.test(authResHeader) || (oidcUser && oidcUser.expired)) {
                  authFailRoute = WellKnownRoutes.SignedOut;
                  const msg = `AuthHttpInterceptor: Token expired. WWW-Authenticate: ${authResHeader};
                  re-routing to  ${authFailRoute} and signing in.`;
                  this.authService.logEvent(msg);
                  this.authService.signin();
                } else {
                  authFailRoute = WellKnownRoutes.AuthFailed;
                  const authErrorMessage = authResHeader ? ` (WWW-Authenticate: "${authResHeader}")` : '';
                  const msg = `AuthHttpInterceptor error:${authErrorMessage}. Re-routing to ${authFailRoute}.`;
                  this.sessionService.logger.error(msg, error);
                  this.authService.logEvent(msg);
                }
                this.router.navigate([authFailRoute]);
                return EMPTY; // completes w/o calling next
              } else {
                return throwError(error);
              }
            })
          );
        })
      );
    } else {
      return next.handle(req);
    }
  }
}
