import { Injectable } from '@angular/core';

import {
  HttpEvent,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpErrorResponse
} from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { concatMap, first } from 'rxjs/operators';

import { SessionService } from '@session';

import { WellKnownRoutes } from '@core';

@Injectable() // do not use providedIn because multi-provided
export class ReadOnlyHttpInterceptor implements HttpInterceptor {

  constructor(private sessionService: SessionService) { }

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const method = req.method.toUpperCase();
    const url = req.url;
    if (couldBeReadOnly()) {
      return this.sessionService.readOnly$.pipe(
        first(),
        concatMap(readOnly => {
          if (readOnly) {
            const msg = `ReadOnlyHttpInterceptor error: cannot ${method} ${url} when read-only`;
            this.sessionService.logger.error(msg);
            throw throwError(new Error(msg));
          }
          return next.handle(req);
        })
      );
    } else {
      return next.handle(req);
    }

    /**
     * Could be subject to read-only interception
     * if not a GET or JSONP and
     * not a POST to the Tax API
     */
    function couldBeReadOnly() {
      return ('GET' !== method && 'JSONP' !== method) &&
      // POST to Tax Service API is actually a GET
      !(/\/taxapi\//.test(url) && method === 'POST');
    }
  }
}
