import { HttpResponse } from '@angular/common/http';

import { Observable } from 'rxjs';

import { ChangeSet, ChangeSetItem, ChangeSetOperation } from 'ngrx-data';
import { Address, Company, Employee, EmployeePaymentMethod } from '@model';

import {
  createBadRequestResponse$,
  createOkResponse$,
  Db,
  RequestInfo,
} from './in-mem-service-utils';

/**
 * Apply an SP Company changeSet to the in-mem db as if saving it transactionally to a real DB
 * Ensures preconditions for saving company entity changes before forwarding to saveEntities()
 * @returns A terminating cold response Observable of ChangeSet HTTP response
 */
export function saveCompanyChangeSet(actionName: string, crudCounts: CrudCount[], reqInfo: RequestInfo, db: Db)
  : Observable<HttpResponse<ChangeSet>> {
  const changeSet = (reqInfo.req as any).body as ChangeSet;
  try {
    validateCrudCounts(changeSet, crudCounts);
    saveCompanyEntities(changeSet, db);
    return createOkResponse$(reqInfo, changeSet);
  } catch (error) {
    const emsg = `${error.message} when saving ${actionName}`;
    return createBadRequestResponse$(reqInfo, emsg);
  }
}
