import { Component, Input, Output, EventEmitter, OnInit, AfterViewInit } from '@angular/core';
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { ScheduleDates } from './schedule-dates';
import { PayrollFrequency } from '@app/model';
import { CalendarState } from '@model/codes-enums/enums';
import { getFirstAllowedCheckDate, getNgbDate, ngbDateToString, getFirstDayOfMonth, getDateFromNgb, getDaysInMonth } from '@app/core';
import { formatDate, determineEndOfMonthStatus, determineEndOfQuarterStatus, getNgbDateFromString } from '@app/core';
import { DateRangePayPeriodvm } from './date-range-pay-period-vm';
import { NgModel } from '@angular/forms';

@Component({
  selector: 'sp-date-range-pay-period',
  templateUrl: './date-range-pay-period.component.html'
})

export class DateRangePayPeriodComponent implements OnInit, AfterViewInit {

  formatDate  = formatDate;
  CalendarState = CalendarState;
  private hoveredDate: NgbDate;
  private _selectedFrequency: string;
  private _holidaysTime: number[];
  /* Initial status set to from date */
  private _status = CalendarState.PeriodStart;
  private _showFirstPeriodQuestion = false;
  private _showFirstCheckQuestion = false;
  private _showSecondPeriodQuestion = false;
  private _showSecondCheckQuestion = false;
  navigationType = 'arrows';
  markDisabled: any;
  calendar: NgbCalendar;
  minimumCheckDate: NgbDate;
  currentDate = new Date();
  minimumStartDate: NgbDate = getNgbDate(new Date(
    this.currentDate.getFullYear() - 1,
    this.currentDate.getMonth(),
    this.currentDate.getDate()));
  maximumEndDate: NgbDate = getNgbDate(new Date(
    this.currentDate.getFullYear() + 1,
    this.currentDate.getMonth(),
    this.currentDate.getDate()));
  dateRangevm = new DateRangePayPeriodvm();

  // Rule for disabling the check dates on the calendar
  disabledCheckDateRule = ((date: NgbDate) =>
  (this.selectedFrequency !== PayrollFrequency.SemiMonthly
    && this.selectedFrequency !== PayrollFrequency.Monthly
    && this.selectedFrequency !== PayrollFrequency.Quarterly
    && this.calendar.getWeekday(date) >= 6 )
      || date.before(this.minimumCheckDate)
      || date.after(this.maximumEndDate));

  // Rule for disabling the period dates on the calendar
  disablePeriodDateRule = ((date: NgbDate) =>
   date.before(this.minimumStartDate)
   || date.after(this.maximumEndDate));


  constructor(calendar: NgbCalendar) {
    this.calendar = calendar;
  }

  // #region Inputs & Outputs
  @Input()
  set selectedFrequency(frequency: string) {
    this._selectedFrequency = frequency;
    this.dateRangevm.selectedFrequency = frequency;
    if (this.companyDates != null) {
      this._showFirstPeriodQuestion = false;
      this._showFirstCheckQuestion = false;
      this._showSecondPeriodQuestion = false;
      this._showSecondCheckQuestion = false;
      this.companyDates.firstPeriodEndOfMonth = null;
      this.companyDates.firstCheckEndOfMonth = null;
      this.companyDates.secondPeriodEndOfMonth = null;
      this.companyDates.secondCheckEndOfMonth = null;
      this.status = CalendarState.PeriodStart;
      this.updateScheduleDates();
    }
  }
  get selectedFrequency() {
    return this._selectedFrequency;
  }

  get holidayTimes() {
    return this._holidaysTime;
  }

  @Input()
  set holidays(dateArray: Date[]) {
    if (dateArray) {
      this._holidaysTime = dateArray.map(x => x.getTime());
    }
    this.minimumCheckDate = this.findFirstAllowedCheckDate();
  }

  @Input() companyDates: ScheduleDates;
  @Output() scheduleDates = new EventEmitter<ScheduleDates>();
  // #endregion Inputs & Outputs

  // #region Getters and Setters for EOM display flags
  get showFirstPeriodQuestion() {
    return this._showFirstPeriodQuestion;
  }
  set showFirstPeriodQuestion(val: boolean) {
    if (val !== this._showFirstPeriodQuestion && val) {
      this.companyDates.firstPeriodEndOfMonth = true;
    }
    this._showFirstPeriodQuestion = val;
  }

  get showFirstCheckQuestion() {
    return this._showFirstCheckQuestion;
  }
  set showFirstCheckQuestion(val: boolean) {
    if (val !== this._showFirstCheckQuestion && val) {
      this.companyDates.firstCheckEndOfMonth = true;
    }
    this._showFirstCheckQuestion = val;
  }

  get showSecondPeriodQuestion() {
    return this._showSecondPeriodQuestion;
  }
  set showSecondPeriodQuestion(val: boolean) {
    if (val !== this._showSecondPeriodQuestion && val) {
      this.companyDates.secondPeriodEndOfMonth = true;
    }
    this._showSecondPeriodQuestion = val;
  }

  get showSecondCheckQuestion() {
    return this._showSecondCheckQuestion;
  }
  set showSecondCheckQuestion(val: boolean) {
    if (val !== this._showSecondCheckQuestion && val) {
      this.companyDates.secondCheckEndOfMonth = true;
    }
    this._showSecondCheckQuestion = val;
  }
  // #endregion Getters and Setters for EOM display flags

  /* Detect status changes -- if check date is being selected, disable weekends */
  set status(value: CalendarState) {
    this._status = value;
    if (value === CalendarState.Check) {
      this.checkValidationRules();
    } else if (value === CalendarState.Disabled) {
      this.disableAll();
    } else {
      this.periodValidationRules();
    }
  }
  get status() {
    return this._status;
  }

  ngOnInit(): void {
    this.dateRangevm.fromDate = getNgbDateFromString(this.companyDates.periodStartDate);
    this.dateRangevm.toDate = getNgbDateFromString(this.companyDates.periodEndDate);
    this.dateRangevm.checkDate = getNgbDateFromString(this.companyDates.checkDate);
    this.dateRangevm.secondFromDate = getNgbDateFromString(this.companyDates.secondPeriodStartDate);
    this.dateRangevm.secondToDate = getNgbDateFromString(this.companyDates.secondPeriodEndDate);
    this.dateRangevm.secondCheckDate = getNgbDateFromString(this.companyDates.secondCheckDate);

    this._showFirstPeriodQuestion = determineEndOfMonthStatus(new Date(this.companyDates.periodEndDate)) === null;
    this._showFirstCheckQuestion = determineEndOfMonthStatus(new Date(this.companyDates.checkDate)) === null;
    this._showSecondPeriodQuestion = determineEndOfMonthStatus(new Date(this.companyDates.secondPeriodEndDate)) === null;
    this._showSecondCheckQuestion = determineEndOfMonthStatus(new Date(this.companyDates.secondCheckDate)) === null;

    this.minimumCheckDate = this.findFirstAllowedCheckDate();
    if (this.dateRangevm.fromDate && this.dateRangevm.toDate && this.dateRangevm.checkDate) {
      this.status = CalendarState.Disabled;
    } else {
      this.status = CalendarState.PeriodStart;
    }
  }

  ngAfterViewInit() {
    this.scheduleDates.emit(this.companyDates);
  }

  get IsFrequencySemiMonthly() {
    return this.selectedFrequency === PayrollFrequency.SemiMonthly;
  }

  get IsFrequencyMonthly() {
    return this.selectedFrequency === PayrollFrequency.Monthly;
  }

  get IsFrequencyQuarterly() {
    return this.selectedFrequency === PayrollFrequency.Quarterly;
  }

  get IsFrequencyAnnual() {
    return this.selectedFrequency === PayrollFrequency.Annual;
  }

  private checkValidationRules() {
    this.markDisabled = this.disabledCheckDateRule;
  }

  private periodValidationRules() {
    this.markDisabled = this.disablePeriodDateRule;
  }

  private disableAll() {
    this.markDisabled = (date: NgbDate) => true;
  }

  // Get the first allowed check date
  private findFirstAllowedCheckDate() {
    const standardizedDate = getFirstAllowedCheckDate(this._holidaysTime);
    return getNgbDate(standardizedDate);
  }

  // Trigger validators of a specific control
  markControlAsTouched(controlName: NgModel) {
    if (controlName.control.value) {
      controlName.control.markAsTouched();
      controlName.control.updateValueAndValidity();
    }
  }

  // Date Range picker control event handler
  onDateSelection(date: NgbDate) {
      if (this.status === CalendarState.PeriodStart) {
        this.dateRangevm.setFromDateWithSync(date);
        if (this.IsFrequencyMonthly) {
          this.updatePeriodEOM(this.dateRangevm.toDate);
        }
        this.status = CalendarState.Check;
      } else if (this.status === CalendarState.PeriodEnd) {
        this.dateRangevm.setToDateWithSync(date);
        if (this.IsFrequencyMonthly) {
          this.updatePeriodEOM(date);
        }
        this.status = CalendarState.Check;
      } else {
        this.dateRangevm.checkDate = date;
        if (this.IsFrequencyMonthly) {
          this.onCheckDateChange(date);
        }
      }
      if (this.dateRangevm.fromDate && this.dateRangevm.toDate && this.dateRangevm.checkDate) {
        this.status = CalendarState.Disabled;
      }
    this.updateScheduleDates();
  }

  getEndOfMonthStatus(date: NgbDate) {
    return date === null ? false
    : this.IsFrequencyQuarterly ? determineEndOfQuarterStatus(getDateFromNgb(date))
    : determineEndOfMonthStatus(getDateFromNgb(date));
  }

  // #region Monthly handlers
  updatePeriodEOM(date: NgbDate) {
    const endOfMonthStatus = this.getEndOfMonthStatus(date);
    if (endOfMonthStatus !== null) {
      this.companyDates.firstPeriodEndOfMonth = endOfMonthStatus;
      this.showFirstPeriodQuestion = false;
    } else {
      this.showFirstPeriodQuestion = true;
    }

    this.updatePeriodDatesBasedOnEOM(this.companyDates.firstPeriodEndOfMonth, date);
  }

  getMonthOffsetByFrequency() {
    let monthOffset = 0;
    if (this.selectedFrequency) {
      switch (this.selectedFrequency) {
        case PayrollFrequency.Monthly:
          monthOffset = 1;
          break;
        case PayrollFrequency.Quarterly:
          monthOffset = 3;
          break;
        case PayrollFrequency.Annual:
          monthOffset = 12;
          break;
      }
    }
    return monthOffset;
  }

  // Update the from date and to date based on the end of month status question
  private updatePeriodDatesBasedOnEOM(isEndOfMonth: boolean, date?: NgbDate) {
    let endDate;
    if (date) { endDate = date; } else { endDate = this.dateRangevm.toDate; }

    if (isEndOfMonth) {
      const firstDayOfMonth = getFirstDayOfMonth(endDate.year, endDate.month - this.getMonthOffsetByFrequency());
      this.dateRangevm.setFromDateWithSync(getNgbDate(firstDayOfMonth));
    } else {
      this.dateRangevm.setToDateWithSync(endDate);
    }
  }
  // #endregion Monthly handlers

  // #region date Event Handlers
  onPeriodStartChange(date: NgbDate) {
    if (this.IsFrequencyQuarterly) {
      this.dateRangevm.setFromDateWithSync(date);
      this.updatePeriodEOM(this.dateRangevm.toDate);
    }

    this.updateScheduleDates();
  }

  onPeriodEndChange(date: NgbDate) {
    this.dateRangevm.setToDateWithSync(date);
    const endOfMonthStatus = this.getEndOfMonthStatus(date);

    if (endOfMonthStatus !== null) {
      this.companyDates.firstPeriodEndOfMonth = endOfMonthStatus;
      this.showFirstPeriodQuestion = false;
    } else {
      this.showFirstPeriodQuestion = true;
    }

    if (this.IsFrequencyQuarterly) {
      this.updatePeriodEOM(this.dateRangevm.toDate);
    }

    this.updateScheduleDates();
  }

  onCheckDateChange(date: NgbDate) {
    const endOfMonthStatus = this.getEndOfMonthStatus(date);
    if (endOfMonthStatus !== null) {
      this.companyDates.firstCheckEndOfMonth = endOfMonthStatus;
      this.showFirstCheckQuestion = false;
    } else {
      this.showFirstCheckQuestion = true;
    }

    this.updateScheduleDates();
  }

  onSecondPeriodEndChange(date: NgbDate) {
    const endOfMonthStatus = this.getEndOfMonthStatus(date);
    if (endOfMonthStatus !== null) {
      this.companyDates.secondPeriodEndOfMonth = endOfMonthStatus;
      this.showSecondPeriodQuestion = false;
    } else {
      this.showSecondPeriodQuestion = true;
    }

    // Update the first Start Date from the second period end date
    this.dateRangevm.setFromDateWithSync(this.getFirstPeriodStartFromSecondEndDate(date));
    this.onSecondPeriodEndOfMonthChange(this.companyDates.secondPeriodEndOfMonth);

    this.updateScheduleDates();
  }

  getFirstPeriodStartFromSecondEndDate(date: NgbDate) {
    const tempDate = getDateFromNgb(date);
    if (tempDate) {
      const daysInPrevMonth = getDaysInMonth(tempDate.getFullYear(), tempDate.getMonth() - 1);
      if (tempDate.getDate() > daysInPrevMonth) {
        tempDate.setDate(daysInPrevMonth);
      }
      tempDate.setMonth(tempDate.getMonth() - 1);
      tempDate.setDate(tempDate.getDate() + 1);
    }
    return getNgbDate(tempDate);
  }

  onSecondCheckDateChange(date: NgbDate) {
    const endOfMonthStatus = this.getEndOfMonthStatus(date);
    if (endOfMonthStatus !== null) {
      this.companyDates.secondCheckEndOfMonth = endOfMonthStatus;
      this.showSecondCheckQuestion = false;
    } else {
      this.showSecondCheckQuestion = true;
    }

    this.updateScheduleDates();
  }

  onPeriodEndOfMonthChange(isEndOfMonth: boolean) {
    if ((this.IsFrequencyMonthly || this.IsFrequencyQuarterly) && typeof isEndOfMonth === 'boolean') {
      this.updatePeriodDatesBasedOnEOM(isEndOfMonth);
    }
    this.updateScheduleDates();
  }

  onSecondPeriodEndOfMonthChange(isEndOfMonth: boolean) {
    if (this.IsFrequencySemiMonthly && typeof isEndOfMonth === 'boolean') {
      const endDate = this.dateRangevm.secondToDate;

      if (isEndOfMonth) {
        const firstDayOfMonth = getFirstDayOfMonth(endDate.year, endDate.month - 1);
        this.dateRangevm.setFromDateWithSync(getNgbDate(firstDayOfMonth));
      } else {
        this.dateRangevm.setFromDateWithSync(this.getFirstPeriodStartFromSecondEndDate(endDate));
      }
    }
    this.updateScheduleDates();
  }

  onEndOfMonthChange(val: string) {
    this.scheduleDates.emit(this.companyDates);
  }
  // #endregion date event handlers

  // Update the company dates and emit
  updateScheduleDates() {
    this.companyDates.checkDate = ngbDateToString(this.dateRangevm.checkDate);
    this.companyDates.periodStartDate = ngbDateToString(this.dateRangevm.fromDate);
    this.companyDates.periodEndDate = ngbDateToString(this.dateRangevm.toDate);

    if (this.IsFrequencySemiMonthly) {
      this.companyDates.secondCheckDate = ngbDateToString(this.dateRangevm.secondCheckDate);
      this.companyDates.secondPeriodStartDate = ngbDateToString(this.dateRangevm.secondFromDate);
      this.companyDates.secondPeriodEndDate = ngbDateToString(this.dateRangevm.secondToDate);
    }

    this.scheduleDates.emit(this.companyDates);
  }


  // #region css styling helpers
  isCheckDate(date: NgbDate) {
    return date.equals(this.dateRangevm.checkDate);
  }

  isHovered(date: NgbDate) {
    return this.dateRangevm.fromDate && !this.dateRangevm.toDate
    && this.hoveredDate && date.after(this.dateRangevm.fromDate) && date.before(this.hoveredDate);
  }

  isInside(date: NgbDate) {
    return date.after(this.dateRangevm.fromDate) && date.before(this.dateRangevm.toDate);
  }

  isRange(date: NgbDate) {
    return date.equals(this.dateRangevm.fromDate) || date.equals(this.dateRangevm.toDate) || this.isInside(date) || this.isHovered(date);
  }

  get showCheckDateCssClass() {
    return this.status === CalendarState.Check;
  }

  get showPeriodDateCssClass() {
    return this.status === CalendarState.PeriodStart || this.status === CalendarState.PeriodEnd;
  }
  // #endregion css styling helpers
}
