import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanDeactivate, RouterStateSnapshot } from '@angular/router';

import { isObservable, of, Observable } from 'rxjs';
import { concatMap, first } from 'rxjs/operators';

import { isPromise } from '@core';
import { SessionService } from '@session';

/**
 * Routing guard the asks a component if (when) the user can leave the page.
 * Implements the router's `CanDeactivate` by asking the component if it has
 * a `canLeaveAfterSave` method and calling that method if it does.
 * Don't bother if in ReadOnly mode
 */
@Injectable({providedIn: 'root'})
export class PageLeaveGuard implements CanDeactivate<any> {
  private readOnly$: Observable<boolean>;

  constructor(sessionService: SessionService) {
    this.readOnly$ = sessionService.readOnly$;
  }
  canDeactivate (
    component: any,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState?: RouterStateSnapshot
  ): Observable<boolean> {
    return this.readOnly$.pipe(
      first(),
      concatMap(readOnly => {
        if (readOnly || component === null || !component.canLeaveAfterSave) {
          return of(true);
        }
        const res = component.canLeaveAfterSave(component, currentRoute, currentState, nextState);
        return isObservable(res) || isPromise(res) ? res : of(res) ;
      })
    );
  }
}
