import { Component, Input, OnInit } from '@angular/core';

import { WellKnownRoutes } from '@core/well-known-routes';

@Component({
  selector: 'sp-nav-bottom',
  template: `
  <div class="nav-button-container">
    <div class="nav-buttons d-flex justify-content-end">
      <a *ngIf="returnTo" [routerLink]="[returnTo]">
        <button type="button" class="btn btn-outline-secondary" type="button" [disabled]="disabled || disableReturnTo"
          id="navBottomReturnTo">
          {{returnToLabel}}
        </button>
      </a>
      <a *ngIf="back" [routerLink]="[back]">
        <button type="button" class="btn btn-outline-secondary" type="button" [disabled]="disabled || disableBack" id="navBottomBack">
          {{backLabel}}
        </button>
      </a>
      <a *ngIf="next" [routerLink]="[next]">
        <button type="button" class="btn btn-navigation" type="button" [disabled]="disabled || disableNext" id="navBottomNext">
          {{nextLabel}}
        </button>
      </a>
    </div>
  </div>
  `
})
export class NavBottomComponent implements OnInit {
  dashboardRoute = WellKnownRoutes.Dashboard;
  defaultRoute = WellKnownRoutes.Dashboard;

  /** Set true and ALL navigation buttons are disabled. */
  @Input() disabled = false;
  @Input() disableBack = false;
  @Input() disableNext = false;
  @Input() disableReturnTo = false;

  // Bind to any of the following route values to change them from dashboard

  // Probably don't change these two values

  /** Where "returnTo" button goes(bind to null to hide button) */
  @Input() returnTo = this.dashboardRoute;
  /** "returnTo" button label */
  @Input() returnToLabel = 'Return to Dashboard';

  /** Where back button goes (bind to null to hide button) */
  @Input() back: string = this.defaultRoute;
  /** "back" button label */
  @Input() backLabel = 'Back';

  /** Where next button goes (bind to null to hide button) */
  @Input() next: string = this.defaultRoute;
  /** "next" button label */
  @Input() nextLabel = 'Next Step';

  ngOnInit() {
    this.back = this.back == null ? null : this.back;
    this.next = this.next == null ? null : this.next;
    this.returnTo = this.returnTo == null ? null : this.returnTo;
  }
}
