import { Injectable } from '@angular/core';
import { createOkResponse$, Db, RequestInfo, } from './in-mem-service-utils';
import { Earning } from '@model';
import { loadMockEarnings } from './master-data-earnings-loader';

@Injectable({ providedIn: 'root' })
export class InMemEarningService {

  /** In-mem version of all deductions metadata */
  private earningMetadata: Earning[] = this.getAdditionalEarnings();

  getRequest(req: RequestInfo, db: Db) {
    if (req.url && req.url.endsWith('/earnings')) {
      return createOkResponse$(req, this.earningMetadata);
    }
  }
  // Post requests handled in InMemSaveEntitiesService (in-mem-save-entities.service.ts)

  private getAdditionalEarnings(): Earning[] {
    return loadMockEarnings();
  }
}
