import { DOCUMENT } from '@angular/common';
import { Inject, Injectable } from '@angular/core';
import { interval, Observable } from 'rxjs';
import { map, skipWhile, take } from 'rxjs/operators';

import { LoggerService } from '@core';

import { AnalyticsEvent, AnalyticsEventType } from '@core/analytics/analytics-event';
import { AnalyticsEventReceiver } from '@core/analytics/analytics-event-receiver';

import { environment } from '@environment';

declare global {
    interface Window { dataLayer: any[]; }
}
let tagInstalled = false;

@Injectable()
export class GoogleTagManagerReceiver implements AnalyticsEventReceiver {
    private TagManagerKey = environment.googleTagManagerKey;
    static create: any;
    constructor(
        @Inject(DOCUMENT) private document: Document,
        private logger: LoggerService
    ) { }

    async initialize(events$: Observable<AnalyticsEvent>): Promise<void> {

        const dataLayer = await this.loadLibrary().toPromise();

        if (!dataLayer) {
            return;
        }

        events$.subscribe((ev) => {
            if (ev.type === AnalyticsEventType.Navigate) {
                dataLayer.push({
                    'event': ev.type,
                    'route': ev.payload
                });
            } // Tag manager already listens to navigate events for angular so this custom event could be disabled

            if (ev.type === AnalyticsEventType.BeginAction ||
                ev.type === AnalyticsEventType.EndAction) {
                dataLayer.push({
                    'event': ev.type,
                    'action': ev.payload
                });
            }

            if (ev.type === AnalyticsEventType.Exception) {
                dataLayer.push({
                    'event': ev.type,
                    'exception': ev.payload
                });
            }  // Tag manager already listens error triggers, however SP as non exception errors which might be usefull to send
        });
    }

    loadLibrary() {
        if (!tagInstalled) {
            tagInstalled = true; // install once

            const ref = this.document.getElementsByTagName('script')[0];
            const script = this.document.createElement('script');
            script.text = `
            (function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
              new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
              j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
              'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
              })(window,document,'script','dataLayer','${this.TagManagerKey}');`;
            ref.parentNode.insertBefore(script, ref);
        }
        return interval(1000).pipe(
            skipWhile(tick => window.dataLayer == null && tick < 15),
            take(1),
            map(_ => {
                if (!window.dataLayer) {
                    const msg = 'Google Tag Manager load timed out; look in console for script load error(s).';
                    this.logger.error(msg);
                }
                return window.dataLayer;
            })
        );
    }
}
