import { Component } from '@angular/core';
import { Router } from '@angular/router';

import { combineLatest, concat, Observable } from 'rxjs';
import { first, map, shareReplay, startWith } from 'rxjs/operators';

import { AuthService } from '@core/services/auth-services/auth.service';
import { ENTITY_CACHE_DUMP_MESSAGE } from '@core/error-handling/sentry.functions';
import { EntitySelectors } from '@entity/entity-selectors';
import { environment } from '@environment';
import { getFullName, LoggerService, WellKnownRoutes } from '@core';
import { SessionService } from '@session';
import { UserRole } from '@model';
import { DocumentPackageService } from '@entity';

export class ToolbarVm {
  accountDetailsRoute = WellKnownRoutes.AccountView;
  adminConsoleRoute = WellKnownRoutes.Admin;
  isAdmin = false;
  isLoaded = false;
  isReadOnly = false;
  name = '';
  showAccountDetailsLink = false;
  showDashboard = false;
  showEditToggle = false;
  version = environment.version;
}

@Component({
  selector: 'sp-toolbar',
  templateUrl: './toolbar.component.html'
})
export class ToolbarComponent {
  vm: ToolbarVm;

  constructor(
    private authService: AuthService,
    private documentPackageService: DocumentPackageService,
    private entitySelectors: EntitySelectors,
    private logger: LoggerService,
    private router: Router,
    private sessionService: SessionService,
    ) {}

  vm$ = combineLatest(
    this.sessionService.authUser$,
    this.sessionService.currentUser$,
    this.sessionService.companyPackageLoaded$,
    this.sessionService.readOnly$
  ).pipe(
    map(([authUser, currUser, isLoaded, readOnly]) => {
      const vm = new ToolbarVm();
      if (authUser) {
        const isAdmin = vm.isAdmin = authUser.userRole === UserRole.Administrator;
        vm.isLoaded = isLoaded;
        vm.showEditToggle = isAdmin && isLoaded;
        vm.isReadOnly = isAdmin && isLoaded && readOnly;
        vm.showDashboard = isLoaded;
        if (currUser) {
          vm.showAccountDetailsLink = true;
          vm.accountDetailsRoute = isAdmin ? WellKnownRoutes.AdminAccountView : WellKnownRoutes.AccountView;
          if (currUser.id === authUser.id) {
            vm.name = getFullName(currUser);
          } else {
            vm.name = `${getFullName(authUser)} (for ${getFullName(currUser)})`;
          }
        } else {
          vm.name = getFullName(authUser);
        }
      }
      return this.vm = vm;
    }),
    startWith(new ToolbarVm()),
    shareReplay(1)
  );

  /**
   * Reload the company package after first
   * navigating to a safe place (discarding pending changes!)
   */
  async refresh() {
    if (!this.vm.isLoaded) {
      return; // don't refresh/reload when there is no current company
    }

    // When done will restore current read-only mode
    const currentReadOnly = this.vm.isReadOnly;
    const successCallback = () => this.sessionService.setReadOnly(currentReadOnly);

    const refreshUrl = this.vm.isLoaded
      ? WellKnownRoutes.Dashboard
      : this.vm.isAdmin ? WellKnownRoutes.Admin : '' ;

    if (refreshUrl) {
      // Go readOny temporarily to prevent save
      this.sessionService.setReadOnly(true);
      await this.router.navigate([refreshUrl]);
    }
    this.reload(successCallback);
  }

  private reload(successCallback?: Function) {
    successCallback = successCallback || (() => {});
    concat(
      this.sessionService.reloadCurrentCompanyPackage(),
      this.documentPackageService.loadDocumentPackage()
    ).subscribe(() => successCallback());
  }

  signout() {
    this.authService.signout();
  }

  toggleEdit() {
    if (this.vm.isReadOnly) {
      // switching to edit mode; reload first.
      this.reload(() => this.sessionService.setReadOnly(false));
    } else {
      this.sessionService.setReadOnly(true);
    }
  }

  /** Dump the entity cache if version number is clicked while holding shift key */
  versionClicked(evt: MouseEvent) {
    if (evt.shiftKey) {
      this.entitySelectors.entityCache$.pipe(first()).subscribe(
        cache => this.logger.warn(ENTITY_CACHE_DUMP_MESSAGE, cache)
      );
    }
  }
}
