import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { Document } from '@model';
import { DocumentsViewService } from './documents-view.service';

@Component({
  selector: 'sp-documents-list',
  templateUrl: './documents-list.component.html'
})
export class DocumentsListComponent {
  @Input('docs') docs: Document[];
  @Input() isEsign: boolean;
  @ViewChild('downloadLink') private downloadLink: ElementRef;

  constructor(
    private documentsViewService: DocumentsViewService,
  ) {}

  download(doc: Document) {
    const link = this.downloadLink.nativeElement as HTMLAnchorElement;
    const filename = doc.title + '.pdf';

    this.documentsViewService.getDocPdf(doc).subscribe(file => {
      if (window.navigator.msSaveOrOpenBlob) {
        // IE 11
        const fn = function () {
          window.navigator.msSaveOrOpenBlob(file, filename);
        };
        link.addEventListener('click', fn);
        link.click();
        link.removeEventListener('click', fn);
      } else {
        const blobUrl = window.URL.createObjectURL(file);
        link.href = blobUrl;
        link.download = filename;
        link.click();
        window.URL.revokeObjectURL(blobUrl);
      }
    });
  }


}

