import { Injectable } from '@angular/core';
import { Actions } from '@ngrx/effects';
import { EntityAction, ofEntityOp, OP_ERROR, OP_SUCCESS } from 'ngrx-data';

import { filter } from 'rxjs/operators';

import { environment } from '@environment';
import { LoggerService } from '@core/services/logger.service';
import { ToastService } from '@core/services/toast.service';

const showToast = window.location.search.includes('toast');

/** Report ngrx-data success/error actions as toast messages **/
@Injectable() // provided in module and injected there to start it.
export class NgrxDataToastService {
  constructor(actions$: Actions, logger: LoggerService, toast: ToastService) {
    // this service never dies so no need to unsubscribe
    actions$
      .pipe(
        ofEntityOp(),
        filter((ea: EntityAction) => ea.payload.entityOp.endsWith(OP_SUCCESS) || ea.payload.entityOp.endsWith(OP_ERROR))
      )
      .subscribe(action => {
        const message = `${action.payload.entityName} action`;
        if (showToast) {
          toast.openSnackBar(message, action.payload.entityOp);
        }
        if (!environment.production) {
          logger.log(message, action);
        }
      });
  }
}
