import { CompanyEntityBase } from './company-entity-base';
import { HouseholdFilersStatus } from '@model/codes-enums';
import { getFullName, hasName } from '@utils/ee-utils';

// NB: Do not leave CLASS fields uninitialized (i.e, `undefined`)
//    The update/upsert operations will not clear previous fields that are undefined.
//    Set these fields to null, which will be propagated on save!
//    Example: canContact?: boolean = null;
//    This advice applies to CLASSes not interfaces

/** Household filers */
export class HouseholdFilers extends CompanyEntityBase {
  id: string;
  companyId: string;
  filingStatus: HouseholdFilersStatus = null;
  primaryFirstName: string = null;
  primaryLastName: string = null;
  primarySsn: string = null;
  spouseFirstName: string = null;
  spouseLastName: string = null;
  spouseSsn: string = null;

  constructor(id?: string, companyId?: string) {
    super(id, companyId);
  }

  get primaryFullName() {
    return getFullName({
      firstName: this.primaryFirstName,
      lastName: this.primaryLastName,
    });
  }
  get spouseFullName() {
    return getFullName({
      firstName: this.spouseFirstName,
      lastName: this.spouseLastName,
    });
  }

  get hasNames(): boolean {
    if (this.filingStatus === HouseholdFilersStatus.Single) {
      return this.primaryHasName;
    }
    if (this.filingStatus === HouseholdFilersStatus.Joint) {
      return this.primaryHasName && this.spouseHasName;
    }
    return false;
  }
  get primaryHasName(): boolean {
    return hasName({
      firstName: this.primaryFirstName,
      lastName: this.primaryLastName,
    });
  }
  get spouseHasName(): boolean {
    return hasName({
      firstName: this.spouseFirstName,
      lastName: this.spouseLastName,
    });
  }
}
