import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, forkJoin, timer } from 'rxjs';
import { finalize, filter, first, switchMap, mapTo } from 'rxjs/operators';
import { EntityCollectionServiceBase } from 'ngrx-data';

@Injectable({ providedIn: 'root' })
export class BusyService {

  private busyCounter = 0;
  private busySubject = new BehaviorSubject<boolean>(false);
  private _message: string;
  get message() {
    return this._message;
  }

  get isBusy(): Observable<any> {
    return this.busySubject.asObservable();
  }

  get isDelayBusy(): Observable<any> {
    return this.busySubject.asObservable().pipe(switchMap(b => timer(1000).pipe(mapTo(b))));
  }

  busy<T>(op: Promise<T>): Promise<T> {
    this.increment();
    op.then(result => {
      this.decrement();
    }).catch((reason: any) => {
      this.decrement();
      throw reason;    // Rethrow error
    });

    return op;
  }

  /**
   * Indicate busy until the observable completes
   * @param obs Observable that the busy service should watch
   * @return piped continuation of the source observable.
   * Warning: busy and live forever if observable fails to terminate
   */
  busy$<T>(message: string, obs: Observable<T>): Observable<T> {
    this.increment(message);
    return obs.pipe(
      finalize(() => this.decrement())
    );
  }

  /**
   * Set busy flag and clear it when all loading$ observables return false
   * @param message to show while busy
   * @param loadings$ Observables of multiple loading flags
   * @returns Observable that emits and terminates when all loading flags become false.
   * Warning: busy and live forever if any loading flag observable fails to emit false
   */
  busyWhileTrue$(message: string, ...loadings$: Observable<boolean>[]): Observable<boolean[]> {
    this.increment(message);
    const mapped = loadings$.map(o => o.pipe(filter(loading => !loading), first()));
    return forkJoin(mapped).pipe(
      finalize(() => this.decrement())
    );
  }

  /**
   * Set busy flag and clear it when every service's loading$ observables return false
   * @param message to show while busy
   * @param services Services with observable `loading$` flags.
   * @returns Observable that emits and terminates when all loading flags become false.
   * Warning: busy and live forever if any loading flag observable fails to emit false
   */
  busyLoading$(message: string, ...services: EntityCollectionServiceBase<any>[]): Observable<boolean[]> {
    const loadings = services.map(s => s.loading$);
    return this.busyWhileTrue$(message, ...loadings);
  }

  /**
   * Set busy flag and clear it when all services.loading$ are false.
   * Delegates to `busyLoading$` and subscribes for you.
   */
  busyLoading(message: string, ...services: EntityCollectionServiceBase<any>[]) {
    return this.busyLoading$(message, ...services).subscribe();
  }

  increment(msg?: string) {
    // only outermost message is shown
    if (msg && !this.message) {
      this._message = msg;
    }
    this.busyCounter++;
    this.busySubject.next(this.isBusyCore);
  }

  decrement() {
    if (this.busyCounter > 0) {
      this.busyCounter--;
    }
    if (this.busyCounter <= 0) {
      this._message = '';
    }
    this.busySubject.next(this.isBusyCore);
  }

  private get isBusyCore(): boolean {
    return this.busyCounter > 0;
  }
}
