import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, CanActivateChild, Router, RouterStateSnapshot } from '@angular/router';

import { combineLatest, Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';

import { AuthService } from '@core/services/auth-services/auth.service';
import { WellKnownRoutes } from '@core';
/**
 * Guard a page that must be authenticated but is not tied to the Current Account
 */
@Injectable({ providedIn: 'root' })
export class AdminConsoleGuard implements CanActivate, CanActivateChild {

  constructor(private authService: AuthService, private router: Router) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.canActivate$('canActivate', route, state);
  }

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.canActivate$('canActivateChild', route, state);
  }

  private canActivate$(method: string, route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return combineLatest(
      this.authService.isAdmin$,
      this.authService.isSignedIn$
    ).pipe(
      map(([isAdmin, isSignedIn]) => {
        const caller = 'AdminConsoleGuard.' + method;

        if (isSignedIn) {
          this.authService.logEvent(`${caller} sees user is signed in`);
          if (isAdmin) {
            return true; // signed-in Admins can see the admin console
          } else {
            this.authService.logEvent(`${caller} re-routing non-Admin user from admin console to dashboard`);
            this.router.navigate([WellKnownRoutes.Dashboard], { replaceUrl: true });
            return false; // regular user can't see the admin console
          }
        } else {
          this.authService.logEvent(`${caller} sees user is not signed in`);
          this.authService.setReroute(state.url, '${caller}');
          this.authService.logEvent(`${caller} called AuthService.signin()`);
          this.authService.signin(); // re-navigates to signin page
          return false; // not signed in.
        }
      }),
      first()
    );
  }
}
