import { Component } from '@angular/core';
import { AuthService } from '@core/services/auth-services/auth.service';

@Component({
  selector: 'sp-app-unauthorized',
  template: `
  <div class="standard static-nav d-flex flex-column unauthorized" style="max-width: 30rem;">
    <h1>Unauthorized</h1>
    <p>I'm sorry but you have tried to access data or perform an action that you are not authorized for.</p>
     <p>Signout out and sign back in with another account.</p>
    <button  class="btn btn-primary btn-cta" (click)="authService.signout()">Sign out</button>
  </div>
  `
})
export class UnauthorizedComponent {
  constructor(public authService: AuthService) {
    this.authService.logEvent('UnauthorizedComponent created');

  }
}
