import { Pipe, PipeTransform } from '@angular/core';
import { Company, companyKind } from '@model';

/** Returns the displayable "kind" of customer - 'Household' or 'Company' */
@Pipe({ name: 'companyKind', pure: true })
export class CompanyKindPipe implements PipeTransform {
  transform(company: Company): string {
    return companyKind(company);
  }
}
