import { Injectable } from '@angular/core';
import { Company, User } from '@model';
import { createOkResponse$, Db, RequestInfo } from './in-mem-service-utils';

@Injectable({providedIn: 'root'})
export class InMemAdminService {

  getRequest(req: RequestInfo, db: Db) {
    if (req.url && req.url.includes('/admin/accountquery')) {
      return this.getAccountList(req, db);
    }
  }

  private getAccountList(req: RequestInfo, db: Db) {
    let rows = db.User.map(u => {
      const c = db.Company.find(co => co.id === u.companyId);
      return {
        bcl: c.bcl,
        companyId: c.id,
        dbaName: c.name,
        email: u.email,
        firstName: u.firstName,
        lastName: u.lastName,
        legalName: c.legalName,
        phone: u.phone,
        startDate: c.enrollmentStartDate,
        status: c.status,
        userId: u.id,
        username: u.username
      };
    });

    // filter rows based on query params
    if (req.query.has('status')) {
      const status = req.query.get('status')[0];
      rows = rows.filter(r => status.includes(r.status));
    }
    if (req.query.has('text')) {
      const text = req.query.get('text')[0].toLowerCase();
      rows = rows.filter(r => r.legalName && r.legalName.toLowerCase().includes(text) ||
        r.dbaName && r.dbaName.toLowerCase().includes(text) ||
        r.bcl && r.bcl.toLowerCase().includes(text) ||
        r.firstName && r.firstName.toLowerCase().includes(text) ||
        r.lastName && r.lastName.toLowerCase().includes(text) ||
        r.username && r.username.toLowerCase().includes(text));
    }

    // sort rows based on query params
    if (req.query.has('orderBy')) {
      const orderBy = req.query.get('orderBy')[0];
      const orderDir = (req.query.get('orderDir') || ['asc'])[0];
      const x = (orderDir === 'desc') ? -1 : 1;
      if (orderBy === 'displayName') {
        rows.sort((a, b) => (a.legalName || a.dbaName) > (b.legalName || b.dbaName) ? x : -x);
      } else if (orderBy === 'startDate') {
        rows.sort((a, b) => (new Date(a.startDate).valueOf() > new Date(b.startDate).valueOf()) ? x : -x);
      } else if (orderBy === 'userFullName') {
        rows.sort((a, b) => (a.firstName + ' ' + a.lastName) > (b.firstName + ' ' + b.lastName) ? x : -x);
      } else {
        rows.sort((a: any, b: any) => a[orderBy] > b[orderBy] ? x : -x);
      }
    }

    const count = rows.length; // count of filtered rows

    if (req.query.has('pageNumber')) {
      // return a page of the filtered rows
      const pageNumber = +req.query.get('pageNumber')[0];
      const pageSize = +(req.query.get('pageSize') || ['20'])[0];
      const start = Math.max(0, pageNumber * pageSize);
      rows = rows.slice(start, start + pageSize);
    }

    return createOkResponse$(req, { count, rows });
  }
}
