import { Component, OnInit } from '@angular/core';
import { AuthService } from '@core/services/auth-services/auth.service';
import { environment } from '@environment';

@Component({
  selector: 'sp-signed-out',
  template: `
    <div class="card-center standard">
      <p class="h3 mt-3 mb-3">Successfully Signed Out</p>
      <button type="text" (click)="signin()" class="btn btn-primary btn-cta">Return to Sign In</button>
    </div>
  `
})
export class SignedOutComponent implements OnInit {
  constructor(public authService: AuthService) {  }

  ngOnInit() {
    this.authService.clearReroute('SignedOutComponent');
  }

  signin() {
    this.authService.logEvent('SignedOutComponent called AuthService.signin() after user pressed button');
    this.authService.signin();
  }
}
