/*
 * Derived directly from post by Jonathan Petitcolas
 * https://www.jonathan-petitcolas.com/2014/11/27/creating-json-web-token-in-javascript.html
 * https://codepen.io/jpetitcolas/pen/zxGxKN
 *
 * Depends on two libraries, assumed to be loaded into window first
 * //cdnjs.cloudflare.com/ajax/libs/crypto-js/3.1.2/rollups/hmac-sha256.js
 * //cdnjs.cloudflare.com/ajax/libs/crypto-js/3.1.2/components/enc-base64-min.js
 */

declare global {
  var CryptoJS: {
    enc: any,
    HmacSHA256: any
  };
}

import { Injectable } from '@angular/core';

/** Parse a JWT token */
export function parseJwt (token: string) {
  const base64Url = token.split('.')[1];
  const base64 = base64Url.replace('-', '+').replace('_', '/');
  return JSON.parse(window.atob(base64));
}

/** The signing secret HA HA */
const secret = 'My very confidential secret!!!';

@Injectable({providedIn: 'root'})
export class JwtService {

  /** The only header we support in this library */
  readonly header = { 'alg': 'HS256', 'typ': 'JWT' };

  /**
   * Make adjusted base64 encoding so can pass in a URL.
   * Two differences from native base64:
   *   No = padding at the end,
   *   + and / characters are replaced by - and _ respectively.
   */
  base64url(source: {}) {
    // Encode in classical base64
    let encodedSource = CryptoJS.enc.Base64.stringify(source);

    // Remove padding equal characters
    encodedSource = encodedSource.replace(/=+$/, '');

    // Replace characters according to base64url specifications
    encodedSource = encodedSource.replace(/\+/g, '-');
    encodedSource = encodedSource.replace(/\//g, '_');

    return encodedSource;
  }

  createJwt(data: {}) {
    const encodedData = this.encode(data || {});
    const encodedHeader = this.encode(this.header);
    let signature = encodedHeader + '.' + encodedData;
    signature = CryptoJS.HmacSHA256(signature, secret);
    signature = this.base64url(signature);
    return encodedHeader + '.' + encodedData + '.' + signature;
  }

  /** Convert object source to base64url encoded, Utf8'd string */
  encode(source: {}) {
    return this.base64url(this.toUtf8(source));
  }

  /** Create JWT from data object with canned header and self-signing with HmacSHA256 */
  toUtf8(source: {}) {
    const data = typeof source === 'string' ? source : JSON.stringify(source);
    return CryptoJS.enc.Utf8.parse(data);
  }
}
