import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { MockSigninComponent } from './mock-signin.component';
import { SharedModule } from '@shared';

const routes: Routes = [
  {
    path: 'mock-signin',
    pathMatch: 'full',
    component: MockSigninComponent,
    data: { title: 'Mock Signin' }
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
    SharedModule
  ],
  exports: [RouterModule],
  declarations: [
    MockSigninComponent
  ],
})
export class MockSigninModule {}
