import { NgModule } from '@angular/core';
import { HttpClientInMemoryWebApiModule, InMemoryDbService } from 'angular-in-memory-web-api';

import { httpClientBackendServiceProvider } from './http-client-backend-service-sp';
import { InMemoryDataService } from './in-memory-data.service';
@NgModule({
  imports: [
    // The HttpClientInMemoryWebApiModule module intercepts HTTP requests
    // and returns simulated server responses.
    HttpClientInMemoryWebApiModule.forRoot(InMemoryDataService, {
      dataEncapsulation: false,
      // pretend to take 1/2 second for each server request
      delay: 500,
      passThruUnknownUrl: true
    })
  ],
  providers: [
    { provide: InMemoryDataService, useExisting: InMemoryDbService },
    httpClientBackendServiceProvider
  ]
})
export class MockServicesModule {
  constructor() {
    console.log('MockServicesModule created');
  }
}
