import { TaxTypeCode } from '@model/codes-enums';
import { CompanyEntityBase } from './company-entity-base';

/** Employee Tax code */
export class EmployeeTax extends CompanyEntityBase {

    /** The unique Id of the Employee this tax belongs to */
    employeeId: string;

    /** The unique identifier of the tax code. ex: FE0000-003  */
    mtsTaxCode: string;

    /** The tax code name, mainly used for display reasons */
    name: string;

    /** The state code. ex PA, can be null for federal taxes */
    stateCode: string;

    /** If the employee is exempt from paying this tax */
    isExempt = false;

    /** This is the reason code that describes why the employee is exempt." */
    exemptReasonCode: string;

    /** If the tax is reimbursable */
    isReimbursable: boolean;

    /** Married or head of Head of Household however its an api driven field. Ex: Married = M */
    filingStatus: string;

    /** Between 0 and 25 integer */
    withholdingAllowance: number;

    /** Fixed amount of $ representing withholding value */
    extraWithholding: number;

    /** Is the employee exempt from this task due to low income? */
    isLowIncomeTaxExempt: boolean;

    /** A code that describes if the tax is on the Federal, State, County, City or School district level */
    taxTypeCode: TaxTypeCode;

    /** When the code is active */
    isActive = true;

    /** True when marked-for-delete */
    isRemoved = false;

    static create(employeeTax?: Partial<EmployeeTax>): EmployeeTax {
        if (employeeTax == null || employeeTax.id == null || employeeTax.companyId == null ||
            employeeTax.employeeId == null) {
            throw Error('EmployeeTax can\'t be created without specifying a id, employee, company');
        }

        return Object.assign(new EmployeeTax(employeeTax.id, employeeTax.companyId), {
            withholdingAllowance: 0,
            extraWithholding: 0
        }, employeeTax);
    }
}
