import { Injectable, InjectionToken, Optional, Inject } from '@angular/core';
import { Observable, ReplaySubject, Subject, Observer } from 'rxjs';

import { LoggerService } from '@core/services/logger.service';
import { AnalyticsEvent } from './analytics-event';
import { AnalyticsEventProvider } from './analytics-event-provider';
import { AnalyticsEventReceiver } from './analytics-event-receiver';

export const ANALYTICS_EVENT_PROVIDER = new InjectionToken<AnalyticsEventProvider>('EventProvider');
export const ANALYTICS_EVENT_RECEIVER = new InjectionToken<AnalyticsEventReceiver>('EventConsumer');

// Maximum number of events to buffer for subscribers
const MAX_EVENTS_BUFFER = 100;
@Injectable({providedIn: 'root'})
export class AnalyticsService implements Observer<AnalyticsEvent> {

    closed?: boolean;
    public events$: Observable<AnalyticsEvent>;
    private eventsSubject: Subject<AnalyticsEvent>;
    constructor(
        @Optional() @Inject(ANALYTICS_EVENT_PROVIDER) private eventProviders: AnalyticsEventProvider[],
        @Optional() @Inject(ANALYTICS_EVENT_RECEIVER) private eventConsumers: AnalyticsEventReceiver[],
        private loggerService: LoggerService
    ) {
        this.eventsSubject = new ReplaySubject<AnalyticsEvent>(MAX_EVENTS_BUFFER);
        this.events$ = this.eventsSubject;
    }

    next (value: AnalyticsEvent) {
        this.eventsSubject.next(value);
    }

    complete(): void {
    }

    error(err: any) {
        this.loggerService.error(err);
    }

    initialize(): any {
        this.eventProviders.forEach(provider => {
            provider.events$.subscribe(this);
        });
        this.eventConsumers.forEach(consumer => {
            consumer.initialize(this.events$);
        });
    }
}
