import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { User as OidcUser } from 'oidc-client';

import { Observable, of, throwError, empty } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { environment } from '@environment';
import { titleCase, parseUrlParams, getCookies } from '@utils';
import { User } from '@model';
import { SignupLeadInfo } from '@app/signup/signup-leadinfo';
import { LoggerService } from '../logger.service';
import { getGuidComb } from '@core/guid-fns';

@Injectable({providedIn: 'root'})
export class AuthDataService {

  private url = `${environment.apiRoot}/account/user`;

  constructor(private http: HttpClient, private logger: LoggerService) {}

  /**
   * Get the authorized user from the server based on oidcSub
   * @param oidcSub The id of the user from IdentityServer
   * @returns  The corresponding application user with that oidcSub (or undefined if not found)
   */
  getAuthUser(oidcUser: OidcUser): Observable<User> {
    return (oidcUser && oidcUser.access_token) ? this.getAuthUserFromServer(oidcUser) : of(null);
  }

  /** Get the user from the server, and create if not exists */
  private getAuthUserFromServer(oidcUser: OidcUser): Observable<User> {
    const p = oidcUser.profile;
    const quser = {
      username: p.preferred_username,
      firstName: p.given_name && p.given_name.trim(),
      lastName: p.family_name && p.family_name.trim(),
      oidcSub: p.sub,
      email: p.email,
      phone: p.phone_number,
      bcl: p.bcl,
    };
    const leadInfo = this.getLeadInfo(p);
    this.callPardotFormHandler(quser, leadInfo);

    const params = {...quser, lead: leadInfo };

    return this.http.post<User>(this.url, params).pipe(
      map(user => repairUser(user, oidcUser)),
      catchError((error: HttpErrorResponse) => {
        if (error.status === 404) {
          const newError: HttpErrorResponse = {
            ...error,
            message: `Could not find an ECommerce user to match the signed-in user. Signing out.`
          };
          return throwError(newError);
        }
        return throwError(error);
      })
    );
  }

  private callPardotFormHandler(user: Partial<User>, leadInfo: SignupLeadInfo) {
    if (!leadInfo || !leadInfo.pardotVisitorId) {
      return;
    }
    if (!(environment as any).pardotVisitorHandler) {
      this.logger.warn('pardotVisitorHandler not defined');
      return;
    }
    const formData: FormData = new FormData();
    formData.append('pardot_visitor_id', leadInfo.pardotVisitorId);
    formData.append('email', user.email);
    formData.append('first_name', user.firstName);
    formData.append('last_name', user.lastName);
    formData.append('phone_number', user.phone);
    formData.append('username', user.username);
    formData.append('promo_campaign_id', leadInfo.promoCampaignId);
    formData.append('priceCampaignId', leadInfo.priceCampaignId);
    formData.append('promoCampaignId', leadInfo.promoCampaignId);
    formData.append('partner code', leadInfo.partnerCode);
    formData.append('ShortCode', leadInfo.shortCode);
    this.http.post((environment as any).pardotVisitorHandler, formData)
    .pipe(
        catchError(error => {
          this.logger.warn(`Pardot form handler error`, error);
          return empty();
        })).subscribe();
  }


  /** Get the CMS info from state.search */
  private getLeadInfo(profile: { cms_profile: string }): SignupLeadInfo {
    // formId=222&leadId=333&pardotVisitorId=444&partnerCode=CITI&shortCode=XYZ&promoCampaignId=ADRP&priceCampaignId=555
    if (!profile || !profile.cms_profile) {
      return null;
    }
    const params = parseUrlParams(profile.cms_profile);
    const { leadid, pardotvisitorid, partnercode, shortcode, promocampaignid, pricecampaignid, ...remains } = params;
    const info: SignupLeadInfo = {
      id: getGuidComb(),
      leadId: leadid,
      pardotVisitorId: pardotvisitorid,
      partnerCode: partnercode,
      priceCampaignId: pricecampaignid,
      promoCampaignId: promocampaignid,
      shortCode: shortcode,
      parameters: remains
    };

    if (!info.pardotVisitorId) {
      info.pardotVisitorId = this.getPardotVisitorIdFromCookie();
    }

    return info;
  }

  private getPardotVisitorIdFromCookie(): string {
    const pardotCookie = getCookies('visitor_id', '-hash');
    const visitorId = pardotCookie[0];
    return visitorId;
  }

}

function repairUser(user: User, oidcUser: OidcUser) {
  if (!user.firstName && !user.lastName) {
    // No name. Try to get name from oidcUser.
    const { family_name = null, given_name = null } = oidcUser ? oidcUser.profile : {};
    user.firstName = given_name && given_name.trim();
    user.lastName = family_name && family_name.trim();

    // If couldn't get name from oidcUser, get it from the 'first.last@surepayroll.com' username.
    if (!user.firstName && !user.lastName) {
      const name = user.username.match(/([^@])*/)[0];
      const [fn, ln] = titleCase(name).split('.');
      user.firstName = fn;
      user.lastName = ln;
    }

    // If still no name, set to unknown.
    if (!user.firstName && !user.lastName) {
      user.firstName = 'Unknown';
      user.lastName = 'User';
    }
  }
  return user;

}
