import { TaxCollectorService } from '@app/core/services/tax-services/tax-collector.service';
import { AsyncValidator, ValidationContextBase, ValidatorFactory } from '@validation';
import { combineLatest } from 'rxjs';
import { map, first } from 'rxjs/operators';
import { isCompanyTaxValid } from '@app/core/services/tax-services/tax-utils';

export class StateTaxesValidator extends AsyncValidator {

  static factory(taxCollectorService: TaxCollectorService): ValidatorFactory {
    return {
      name: 'stateTaxesValidator',
      fn: (group, context) => {
        return new StateTaxesValidator(taxCollectorService, group, context);
      }
    };
  }

  private static messageTemplate = 'State taxes information must be provided';

  constructor(taxCollectorService: TaxCollectorService,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('stateTaxesValidator', group,
      (value, context) => {
        return combineLatest(taxCollectorService.taxes$).pipe(
          map(([taxes]) => {
            if (taxes.companyStateTaxes == null) {
              return true;
            }

            const invalidStates = taxes.companyStateTaxes.reduce((acc, stateTaxes) => {
              for (let i = 0; i < stateTaxes.taxes.length; i++) {
                const stateTaxWithBehavior = stateTaxes.taxes[i];

                if (!isCompanyTaxValid(stateTaxWithBehavior)) {
                  acc.push(stateTaxes.state);
                  break;
                }
              }
              return acc;
            }, []);

            if (invalidStates.length > 0) {
              // console.error(`Missing Company Tax setup for these states: ${missingTaxStates.join()}`);
              context.missingTaxStates = invalidStates;
              return false; // failed validation
            } else {
              return true; // every tax state is accounted for.
            }
          }),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || StateTaxesValidator.messageTemplate
      }
    );
  }
}
