import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { of, Observable, throwError } from 'rxjs';
import { catchError, first, map } from 'rxjs/operators';

import { LoggerService } from '@core/services/logger.service';

export interface BankRoutingResult {
  message: string;
  rn: string;
  name: string;
  code: number;
}

@Injectable({ providedIn: 'root' })
export class BankRoutingNumberService {
  private cache: { [key: string]: string } = {};

  constructor(
    private http: HttpClient,
    private logger: LoggerService
  ) {}

  /**
   * Get bank name from routingNumber, preferably from cache
   * @param errorOk true (default) if call to service allowed to fail.
   * @returns Cold, terminating observable of string ('' if invalid RN or service failed and that is OK).
   * Must subscribe but need not unsubscribe.
   */
  getBankName(routingNumber: string, errorOk = true): Observable<string> {
    routingNumber = (routingNumber || '').trim();
    if (!routingNumber) {
      return of(''); // ignore when no routing number
    } else if (!/^\d{9}$/.test(routingNumber)) {
      return of(''); // ignore when no routing number is not 9 digits. Validation handles error
      // const emsg = 'Routing number must be a 9 digit number';
      // return errorOk ? of(emsg) : throwError(emsg);
    }

    const val = this.cache[routingNumber];
    if (val) {
      return of(val);
    }

    const url = 'https://www.routingnumbers.info/api/name.json?rn=' + routingNumber;
    return this.http.jsonp<BankRoutingResult>(url, 'callback').pipe(
      first(),
      map(r => {
        if (r.code === 200) {
          return this.cache[routingNumber] = r.name;
        } else if (r.code === 404) {
          return this.cache[routingNumber] = '';
        } else {
          const emsg = `Routing number service error '${JSON.stringify(r)}'`;
          this.logger.error(emsg);
          throw new Error(emsg);
        }
      }),
      catchError(error => {
        return errorOk ? of('') : throwError(error.toString()) ;
      })
    );
  }
}

/** Instance of a "Do nothing" version for when real BankRoutingNumberService is unavailable */
export const noopBankRoutingNumberService: BankRoutingNumberService = {
  getBankName: (rn: string) => of(
    { message: '', rn, name: '', code: 0 } as BankRoutingResult
  )
} as any;
