import { CompanyEntityBase } from './company-entity-base';
import { SectionStatus } from '../codes-enums/enums';
/** Track key account statistics in general and visit timing to dashboard sections in particular */
export class Stats extends CompanyEntityBase {
  id: string;
  companyId: string;

  constructor() {
    super();
    const dt = new Date().toISOString();

    // AboutYou is always started as a result of sign up
    this.aboutYouLastDate = this.aboutYouStartDate = dt;
  }

  // ISO DateTime strings for section start and last visit dates
  aboutYouStartDate: string;
  aboutYouLastDate: string;
  companyInfoStartDate: string;
  companyInfoLastDate: string;
  employeesStartDate: string;
  employeesLastDate: string;
  companyTaxInfoStartDate: string;
  companyTaxInfoLastDate: string;
  companyBankAccountStartDate: string;
  companyBankAccountLastDate: string;
  reviewStartDate: string;
  reviewLastDate: string;

  // Section statuses
  aboutYouStatus: SectionStatus;
  companyInfoStatus: SectionStatus;
  employeesStatus: SectionStatus;
  companyTaxInfoStatus: SectionStatus;
  companyBankAccountStatus: SectionStatus;
}
