import { Provider, ModuleWithProviders, Optional, SkipSelf, NgModule } from '@angular/core';
import { AnalyticsService } from './analytics.service';
import { ANALYTICS_PROVIDERS } from './providers/providers';
import { ANALYTICS_RECEIVERS } from './receivers/receivers';

@NgModule({
  providers: [
    ANALYTICS_PROVIDERS,
    ANALYTICS_RECEIVERS
  ]
})
export class AnalyticsModule {

    constructor (
        @Optional() @SkipSelf() parentModule: AnalyticsModule,
        private analyticsService: AnalyticsService
    ) {
        if (parentModule) {
          throw new Error(
            'AnalyticsModule is already loaded. Import it in the AppModule only');
        }
        this.analyticsService.initialize();
      }

    static forRoot(providers: Provider[]): ModuleWithProviders {
      return {
        ngModule: AnalyticsModule,
        providers,
      };
    }
}
