import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';

import * as Actions from './actions';
import { LoggerService } from '@core/services/logger.service';
import { SessionState } from './reducer';
import { AccountStatus, User } from '@model';

@Injectable({providedIn: 'root'})
export class SessionDispatcher {
  constructor(private store: Store<SessionState>, private logger: LoggerService) {}

  setAuthUser(authUser: User) {
    this.store.dispatch(new Actions.SetAuthUser(authUser));
  }

  setCompanyPackageLoaded(isLoaded: boolean) {
    this.store.dispatch(new Actions.SetCompanyPackageLoaded(isLoaded));
  }

  setCompanyPackageLoading(isLoading: boolean) {
    this.store.dispatch(new Actions.SetCompanyPackageLoading(isLoading));
  }

  setCurrentAccount(companyId: string, userId: string) {
    this.store.dispatch(new Actions.SetCurrentAccount(companyId, userId));
  }

  setCurrentCompanyEtag(etag: string) {
    this.store.dispatch(new Actions.SetCompanyEtag(etag));
  }

  setCurrentEmployeeId(employeeId: string) {
    this.store.dispatch(new Actions.SetCurrentEmployeeId(employeeId));
  }

  setReadOnly(readOnly: boolean) {
    this.store.dispatch(new Actions.SetReadOnly(readOnly));
  }

}
