import { Injectable } from '@angular/core';
import { Action, Store } from '@ngrx/store';

import {
  ClearCollections,
  EntityActionFactory, EntityCache, EntityCacheQuerySet, EntityOp,
  LoadCollections
} from 'ngrx-data';

import { Company, User } from '@model';
import { companyCollectionDataMapper } from '@entity/entity-metadata';
import { LoadCompanyPackageError } from '@session/actions';
import { LoggerService } from '@core/services/logger.service';

/** Dispatch custom actions to the EntityCache and its collections */
@Injectable({providedIn: 'root'})
export class EntityDispatcher {
  constructor(
    private entityActionFactory: EntityActionFactory,
    private logger: LoggerService,
    private store: Store<EntityCache>
  ) {}

  addAccount(company: Company, user: User) {
    const collections: EntityCacheQuerySet = {Company: [company], User: [user]};
    const action = new LoadCollections(collections, 'NewAccount');
    this.store.dispatch(action);
    return collections;
  }

  clearCollections(collectionNames: string[]) {
    const action = new ClearCollections(collectionNames);
    this.store.dispatch(action);
  }

  loadCompanyPackageSuccess(data: EntityCacheQuerySet): EntityCacheQuerySet {
    data = companyCollectionDataMapper(data);
    const action = new LoadCollections(data, 'CompanyPackage');
    this.store.dispatch(action);
    return data;
  }

  loadCompanyPackageError(currentCompanyId: string, error: any): any {
    this.logger.error(`Error loading the company package for ${currentCompanyId}`, error);
    const action = new LoadCompanyPackageError({currentCompanyId, error, tag: 'CompanyPackage'});
    this.store.dispatch(action);
    return error;
  }

  loadDocumentPackageSuccess(data: EntityCacheQuerySet): EntityCacheQuerySet {
    data = companyCollectionDataMapper(data);
    const action = new LoadCollections(data, 'DocumentPackage');
    this.store.dispatch(action);
    return data;
  }

  loadDocumentPackageError(currentCompanyId: string, error: any): any {
    this.logger.error(`Error loading the document package for ${currentCompanyId}`, error);
    const action = new LoadCompanyPackageError({currentCompanyId, error, tag: 'DocumentPackage'});
    this.store.dispatch(action);
    return error;
  }

  /** Replace all users in cache with this one */
  setUserInCache(user: User) {
    const action = this.entityActionFactory.create('User', EntityOp.ADD_ALL, user ? [user] : []);
    this.store.dispatch(action);
  }
}
