import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';

import { AuthService } from '@core/services/auth-services/auth.service';
import { environment } from '@environment';
import { InMemoryDataService } from '../mock-services/in-memory-data.service';
import { mockDbKey } from '../mock-services/http-client-backend-service-sp';
import { MockOidcManager } from '../mock-auth/mock-oidc-manager';
import { MockOidcManagerFactory } from '../mock-auth/mock-oidc-manager-factory';
import { ngIfAnim } from '@animations';
import { OidcManagerFactory } from '@core/services/auth-services/oidc-manager-factory';
import { SessionService } from '@session';
import { WellKnownRoutes } from '@core';

import { Observable } from 'rxjs';

@Component({
  selector: 'sp-mock-signin',
  templateUrl: './mock-signin.component.html',
  animations: [ngIfAnim]
})
export class MockSigninComponent implements OnInit {
  @ViewChild('form')
  form: NgForm;

  disableClearMockDb = false;
  errors$: Observable<string>;
  mockOidcManager: MockOidcManager;
  showTestAccounts = false;
  success: string;
  vm: { username: string; password: string };

  constructor(
    private authService: AuthService,
    private inMemoryDataService: InMemoryDataService,
    @Inject(OidcManagerFactory) mgrfactory: MockOidcManagerFactory,
    private sessionService: SessionService
  ) {
    this.mockOidcManager = mgrfactory.proxy.useMock();
    this.vm = {
      username: null,
      password: null
    };
  }

  ngOnInit() {
    this.errors$ = this.authService.errors$;
    this.sessionService.clearCurrentAccount();
    this.authService.logEvent(
      'MockSiginComponent initialized; calling sessionService.clearCurrentAccount()'
    );
  }

  resetMockedData() {
    this.disableClearMockDb = true;
    if (window.localStorage) {
      window.localStorage.removeItem(mockDbKey);
    }
    this.inMemoryDataService.createInitialMockDb().subscribe();
  }

  signin() {
    const { dirty, valid, value } = this.form;

    const username = value.username;
    const password = value.password;

    this.doSignin(username); // password doesn't matter
  }

  doSignin(username: string) {
    this.authService.logEvent(
      `MockSiginComponent subscribing to authService.signinRedirectCallback({${username}})`
    );
    this.authService.signinRedirectCallback({ username }).subscribe();
  }

  toggleShowTestAccounts() {
    this.showTestAccounts = !this.showTestAccounts;
  }
}
