import { CompanyTax, EmployeeTax, TaxCategoryCode } from '@model';
import { EmployeeTaxLocation } from '@entity';

export class TaxRate {
    code: string;
    stateCode: string;
    allowSubjectRateZero: boolean;
    minAllowedRate: number;
    maxAllowedRate: number;
    newEmployerMin: number;
    newEmployerMax: number;
    detailsLink: string;
}

// Tax metadata, at this point its more like tax metadata
export interface ITaxBehavior {
    /** If the tax should be collected without the user seeing it
     * ex: other federal taxes like MED */
    shouldShow: boolean;

    /** If the tax requires some user input
     * ex: if the tax has a Rate or Filing status */
    requireUserInput: boolean;

    // If the tax requires any of those inputs.
    // Note that does not mean a tax has no exempt prop or ein pro. It just implies were not showing it on the UI.
    hasExemption: boolean;
    hasExemptionReason: boolean;
    hasEin: boolean;
    hasRate: boolean;
    hasFilingStatus: boolean;
    hasLowIncomeExempt: boolean;
    hasWithholding: boolean;
    hasUbi: boolean;
    hasBusinessStartDate: boolean;

    // Useful data regarding various fields
    taxEinFormats: ITaxCodeFormat[];
    filingStatuses: IFilingStatus[];
    rate: TaxRate;

    /** This field holds the tax mts code of another tax this tax is linked to */
    useEINFromCode?: string;

    /** Unique code that identities the class of taxes */
    categoryCode: TaxCategoryCode;
}

export interface ITaxCodeFormat {
    regEx: string;
    format: string;
}

export interface IFilingStatus {
    code: string;
    description: string;
}

export type TaxBehaviorWrapper<T extends CompanyTax | EmployeeTax> = T & { behavior: ITaxBehavior, getTax(): T };
export function buildTaxBehavior<T extends CompanyTax | EmployeeTax>(tax: T, taxBehavior: ITaxBehavior): TaxBehaviorWrapper<T> {
    return <TaxBehaviorWrapper<T>>{
        ...(tax as {}),
        behavior: taxBehavior,
        getTax: function (): T {
            const { behavior, getTax, ...t } = this;
            return t;
        }
    };
}

// Tax Provider
export interface IAvailableTaxAddressPair {
    id: string;
    liveAddress: IAvailableTaxAddress;
    workAddress: IAvailableTaxAddress;
    taxes: IAvailableTax[];
}

export interface IAvailableTaxAddress {
    addressLine1: string;
    addressLine2: string;
    city: string;
    county: string;
    state: string;
    zip: string;
    geoCode: string;
    schoolDistrict?: number;
    schoolDistrictName?: string;
    jurisdiction?: string;
    psdCode?: number;
}

export interface IAvailableTax {
    code: string;  // "PA0000-001"
    description: string; // "PENNSYLVANIA WH"
    isEmployee: boolean;
    categoryDescription: string; // "State W/H"
    displayOrder: number;
    stateCode: string; // "PA"
    taxTypeCode: string; // FD, ST, CN (County), CT (City), SD (School District)
    isVariableRate?: boolean;
    taxFormats: IAvailableTaxEinFormat[];
    filingStatuses: IAvailableTaxFilingStatus[];
    useEINFromCode?: string; // This field holds the tax mts code of another tax this tax is linked to
    categoryCode: string;
    canBeExempt: boolean;
}

export interface IAvailableTaxEinFormat {
    stateCode: string; // "PA"
    jurisdiction: string; // "PA0386"
    format: string; // "##########"
    regEx: string; // "\\d{10}"
}

export interface IAvailableTaxFilingStatus {
    code: string; // "H"
    description: string; // "Head of Household"
}

export interface IAddressPairRequest {
    id: string;
    homeGeoCode: string;
    homeSchoolDistrictCode?: number;
    homePsdCode?: number;
    workGeoCode: string;
    workSchoolDistrictCode?: number;
    workPsdCode?: number;
}

export interface ITaxAddressValidation {
    id: string;
    isValid: boolean;

    homeGeoCode?: string;
    homeDsCode?: number;
    homePsdCode?: number;
    workGeoCode?: string;
    workDsCode?: number;
    workPsdCode?: number;

    addressPairs?: ITaxAddressValidationPair[];
}

export interface ITaxAddressValidationPair {
    home: IAvailableTaxAddress;
    work: IAvailableTaxAddress;
}


// Address Provider
export interface IAddressValidation {
    isValid: boolean;
    suggestedAddresses: IAvailableTaxAddress[];
}

// Tax Rate Provider
export interface ITaxRateRequestItem {
    code: string;
    state: string;
}

export const EMPTY_TAXES: ITaxes = {
    employeeTaxes: [],
    companyFederalTaxes: null,
    companyStateTaxes: []
};

export interface ITaxes {
    /** All the employees that have taxes */
    employeeTaxes: IEmployeeTaxes[];

    /** The taxes given the employees available in the state */
    companyFederalTaxes: ICompanyFederalTaxes;

    /** The taxes given the employees available in the state */
    companyStateTaxes: ICompanyStateTaxes[];
}

export interface IEmployeeTaxes {
    /** All the employees that have a state address */
    employeeLocation: EmployeeTaxLocation;

    /** If the employee address pair was truly verified this status is confirmed by calling the tax api.
     * Even if the employeeLocation is verified this status will reflect absolute reality  */
    addressVerified: boolean;

    /** The taxes given the employees available in the state */
    taxes: TaxBehaviorWrapper<EmployeeTax>[];
}

export interface ICompanyFederalTaxes {
    /** Are all company address verified */
    allCompanyAddressesVerified: boolean;

    /** The taxes given the employees available in the state */
    taxes: TaxBehaviorWrapper<CompanyTax>[];
}

export interface ICompanyStateTaxes {
    /** The state the taxes are on. '' for federal level */
    state: string;

    /** All the employees that have a state address */
    employeesLocations: EmployeeTaxLocation[];

    /** Are all the company state addresses tax valid? */
    allCompanyStateAddressesVerified: boolean;

    /** Are all the employee addresses tax valid? */
    allEmployeeStateAddressesVerified: boolean;

    /** The taxes given the employees available in the state */
    taxes: TaxBehaviorWrapper<CompanyTax>[];
}

// Tax save helper
export interface TaxSavePackage<T extends CompanyTax | EmployeeTax> {
    modelType: string;

    newTaxes: T[];

    updatedTaxes: T[];

    removedTaxes: T[];
}
