import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { combineLatest, EMPTY, Observable } from 'rxjs';
import { catchError, concatMap, first, map, debounceTime } from 'rxjs/operators';

import { EntityCacheQuerySet } from 'ngrx-data';

import { Document, Envelope, EnvelopeSigner, EnvelopeStatus } from '@model';
import { EntitySelectors } from '../entity-selectors';
import { EntityDispatcher } from '../entity-dispatcher';
import { environment } from '@environment';
import { SessionService } from '@session';

/** The entity collections that comprise the DocumentPackage */
export interface DocumentPackage {
  Document: Document[];
  Envelope: Envelope[];
  EnvelopeSigner: EnvelopeSigner[];
}

@Injectable({providedIn: 'root'})
export class DocumentPackageService {

  documentPackage$: Observable<DocumentPackage>;

  constructor(
    private entityCacheDispatcher: EntityDispatcher,
    entitySelectors: EntitySelectors,
    private http: HttpClient,
    private sessionService: SessionService,
  ) {

    this.documentPackage$ = combineLatest(
      entitySelectors.document.entities$,
      entitySelectors.envelope.entities$,
      entitySelectors.envelopeSigner.entities$
    ).pipe(
      debounceTime(1),
      map(([d, e, es]) => ({
        Document: d, Envelope: e, EnvelopeSigner: es
      }))
    );
  }

  /**
   * Loads the "DocumentPackage" data for the current company.
   * @returns Cold, terminating observable of the DocumentPackage.
   * You must subscribe but do not have to unsubscribe.
   */
  loadDocumentPackage(): Observable<DocumentPackage> {
    return this.sessionService.currentCompanyId$.pipe(
      first(),
      concatMap(companyId => this.loadData(companyId))
    );
  }

  /**
   * Loads the DocumentPackage, then returns the id of the first completed, non-removed
   * envelope for which the current user is an embedded envelope signer (if exists);
   * else returns undefined.
   */
  loadSignedEnvelopeId(): Observable<string> {
    return combineLatest(
      this.loadDocumentPackage(),
      this.sessionService.currentUserId$
      ).pipe(
      map(([pack, userId]) => {
        // first embedded envelope signer (if exists)
        const userSigner = pack.EnvelopeSigner.find(es => es.isEmbedded && es.userId === userId);
        // first completed, non-removed envelope (if it exists)
        const envelope = pack.Envelope.find(e => e.status === EnvelopeStatus.completed && !e.isRemoved);
        return userSigner && envelope && envelope.eSignEnvelopeId;
      })
    );
  }

  /**
   * Loads the "DocumentPackage" data for the given company.
   * @param companyId The id of the company whose data should be loaded.
   * @returns Terminating COLD observable of either the DocumentPackage if succeeded
   * or error observable if failed.
   * You must subscribe but do not have to unsubscribe.
   */
  private loadData(companyId: string): Observable<DocumentPackage> {
    const url = `${environment.apiRoot}/documentpackage/${companyId}`;
    return this.http.get<EntityCacheQuerySet>(url).pipe(
      map(data => this.entityCacheDispatcher.loadDocumentPackageSuccess(data)),
      concatMap(() => this.documentPackage$.pipe(first())),
      catchError(error => {
        this.entityCacheDispatcher.loadDocumentPackageError(companyId, error);
        return EMPTY;
      })
    );
  }

}
