import { AsyncValidator, ValidatorFactory, ValidationContextBase } from '@validation';
import { EntitySelectors } from '@entity';
import { map, first } from 'rxjs/operators';
import { EnvelopeStatus } from '@app/model/codes-enums';
import { combineLatest } from 'rxjs';

export class DocumentSigningCompleteValidator extends AsyncValidator {

  static factory(entitySelectors: EntitySelectors): ValidatorFactory {
    return {
      name: 'documentSigningComplete',
      fn: (group, context) => {
        return new DocumentSigningCompleteValidator(entitySelectors, group, context);
      }
    };
  }

  private static messageTemplate = 'The document signing must be complete';

  constructor(entitySelectors: EntitySelectors,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('documentSigningComplete', group,
      (value, context) => {
        return combineLatest(
          entitySelectors.envelope.entities$,
          entitySelectors.envelopeSigner.entities$
        ).pipe(
          map(([envelopes, signers]) =>
            envelopes.some(env => env.status === EnvelopeStatus.completed && !env.isRemoved &&
              signers.some(s => s.envelopeId === env.id && !s.isRemoved)) &&
            !envelopes.some(env => env.isWetSign && env.status === EnvelopeStatus.local && !env.isRemoved)
          ),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || DocumentSigningCompleteValidator.messageTemplate
      }
    );
  }
}
