import { environment } from '@environment';
import { HttpClient } from '@angular/common/http';

import { of } from 'rxjs';
import { catchError, first, map, shareReplay } from 'rxjs/operators';

import {
  ValidationContextBase,
  RegExpValidator,
  AsyncValidator,
  ValidatorFactory
} from '@validation';

export function makeUsernameInvalidCharacterValidator(
  group: string,
  context?: Partial<ValidationContextBase>
) {
  context = {
    ...context,
    expression: /^[a-zA-Z0-9\_\-\.]*$/,
    messageTemplate: 'Make a %displayName% from letters, digits, and the special characters "._-"'
  };

  return RegExpValidator.create('usernameInvalidCharacters', group, context);
}

export class UniqueUsernameValidator extends AsyncValidator {
  static factory(http: HttpClient): ValidatorFactory {
    return {
      name: 'uniqueUsername',
      fn: (group, context) => {
        return new UniqueUsernameValidator(http, group, context);
      }
    };
  }

  private static messageTemplate = '%displayName% %errorMsg%';

  constructor(http: HttpClient, group: string, validatorContext?: Partial<ValidationContextBase>) {
    super(
      'uniqueUsername',
      group,
      (value, context) => {
        // if (environment.mockServices) {
        //   return of(true);
        // }
        const uri = `${environment.spApiRoot}/authentication/username/valid/${value}`;
        return http.get(uri).pipe(
          map(() => true),
          catchError((error: any) => {
            context.messageTemplate = 'Please try a different %displayName%.';
            return of(false);
          }),
          shareReplay(1)
        );
      },
      {
        ...validatorContext,
        messageTemplate:
          (validatorContext && validatorContext.messageTemplate) ||
          UniqueUsernameValidator.messageTemplate
      }
    );
  }
}
