import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanActivateChild,
  RouterStateSnapshot
} from '@angular/router';

import { Observable } from 'rxjs';

import { AccountStatus } from '@model';
import { AccountStatusGuardHelper, AccountStatusGuardFns, okRoute } from './account-status-guard-helper';
import { WellKnownRoutes } from '@core';

const guardName = 'DataEntryPageGuard';

/**
 * Guard routes to pages where the user enters company data
 */
@Injectable({ providedIn: 'root' })
export class DataEntryPageGuard implements CanActivate, CanActivateChild {

  private adminRoutes: AccountStatusGuardFns;
  private regularRoutes: AccountStatusGuardFns;

  constructor(private accountStatusGuardHelper: AccountStatusGuardHelper) {
    // Any route with this activation guard can be activated if the account status is Open or Test
    // The route will be rejected and the user re-routed for any other account status.
    this.adminRoutes = { ...accountStatusGuardHelper.adminBaseRoutes };
    this.adminRoutes[AccountStatus.Open] = okRoute;
    this.adminRoutes[AccountStatus.Test] = okRoute;

    this.regularRoutes = { ...accountStatusGuardHelper.regularBaseRoutes };
    this.regularRoutes[AccountStatus.Open] = okRoute;
    this.regularRoutes[AccountStatus.Test] = okRoute;
  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.ifAccountStatusAllows$(state);
  }

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.ifAccountStatusAllows$(state);
  }

  private ifAccountStatusAllows$(state: RouterStateSnapshot) {
    return this.accountStatusGuardHelper.accountStatusAllows$(
      guardName,
      this.adminRoutes,
      this.regularRoutes,
      state
    );
  }
}
