import { Injectable } from '@angular/core';
import {
  addCompanyEtagResponseHeader, createOkResponse$, createBadRequestResponse$, Db,
  getCurrentUserIdFromRequest, RequestInfo, ResponseOptions, STATUS
} from './in-mem-service-utils';
import { Envelope, EnvelopeStatus, AccountStatus } from '@app/model';

@Injectable({providedIn: 'root'})
export class InMemCompanyPackageService {
  /** Return a package of company entity collections for a given Company id.*/
  getCompanyPackage(req: RequestInfo, db: Db) {
    const companyId = req.id;
    if (!companyId ) {
      return createBadRequestResponse$(req, {message: 'Missing companyId'});
    }

    const userId = getCurrentUserIdFromRequest(req, db);
    if (!userId) {
      return createBadRequestResponse$(req, {message: 'Missing userId'});
    }

    const company = db.Company.find(c => c.id === companyId);

    if (!company) {
      const errOptions: ResponseOptions = {
        url: req.url,
        status: STATUS.NOT_FOUND,
        statusText: 'Not Found',
        body: `Company with id='${companyId}' was not found`
      };
      return req.utils.createResponse$(() => errOptions);
    }

    const body = {
      // NB: the package property names are lower camel case, as the server will send them
      address: from(db.Address),
      company: [company],
      companyDeduction: from(db.CompanyDeduction),
      companyEarning: from (db.CompanyEarning),
      companyTax: from(db.CompanyTax),
      employee: from(db.Employee),
      employeePaymentMethod: from(db.EmployeePaymentMethod),
      employeeTax: from(db.EmployeeTax),
      householdFilers: from(db.HouseholdFilers),
      stats: from(db.Stats),
      user: from(db.User)
    };

    const user = body.user.find(u => u.id === userId);
    if (!user) {
      const errOptions: ResponseOptions = {
        url: req.url,
        status: STATUS.BAD_REQUEST,
        statusText: 'Bad Request',
        body: `User:${userId} is not a user of the company:${companyId}`
      };
      return req.utils.createResponse$(() => errOptions);
    }
    const headers = addCompanyEtagResponseHeader(db, company.id);

    return createOkResponse$(req, body, headers);

    function from<T extends {companyId: string}>(collection: T[]) {
      return collection ? collection.filter(c => c.companyId === companyId) : [];
    }
  }

  /** Return a package of document collections for a given Company id.*/
  getDocumentPackage(req: RequestInfo, db: Db) {
    const id = req.id;
    const company = db.Company.find(c => c.id === id);

    if (!company) {
      const errOptions: ResponseOptions = {
        url: req.url,
        status: STATUS.NOT_FOUND,
        statusText: 'Not Found',
        body: `Company with id='${id}' was not found`
      };
      return req.utils.createResponse$(() => errOptions);
    }

    const body = {
      // NB: the package property names are lower camel case, as the server will send them
      document: from(db.Document),
      envelope: from(db.Envelope),
      envelopeSigner: from(db.EnvelopeSigner),
    };

    const signed = (<any>window).iframeEvent === 'signing_complete';
    if (signed) {
      body.envelope.forEach(e => (<Envelope>e).status = EnvelopeStatus.completed);
      company.status = AccountStatus.Operations;
    }

    const headers = addCompanyEtagResponseHeader(db, company.id);

    return createOkResponse$(req, body, headers);

    function from(collection: { companyId: string, isRemoved: boolean }[]) {
      return collection ? collection.filter(c => c.companyId === id && !c.isRemoved) : [];
    }
  }

}
