import { Pipe, PipeTransform } from '@angular/core';
import { HouseholdFilersStatus } from '@app/model';

@Pipe({ name: 'householdFilerStatus' })
export class HouseholdFilerStatusPipe implements PipeTransform {
  transform(value: HouseholdFilersStatus): string {
    if (value === HouseholdFilersStatus.Single) {
      return 'Single';
    } else if (value === HouseholdFilersStatus.Joint) {
      return 'Joint';
    }
    return 'missing';
  }
}
