/**
 * Customer-oriented InMemoryDbService with method overrides.
 */
import { Injectable } from '@angular/core';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { ajax } from 'rxjs/ajax';

import {
  ParsedRequestUrl,
  RequestInfo,
  RequestInfoUtilities,
  ResponseOptions,
  STATUS
} from 'angular-in-memory-web-api';

import { createOkResponse$, createBadRequestResponse$, guardCompanyChange} from './in-mem-service-utils';
import { Db } from './db';
import { environment } from '@environment';
import { genExtraAccounts } from './in-mem-gen-extra-accounts';

import { InMemAccountService } from './in-mem-account.service';
import { InMemAddressValidatorService } from '@app/mock-services/in-mem-address-validator.service';
import { InMemAdminService } from './in-mem-admin.service';
import { InMemCompanyPackageService } from './in-mem-company-package.service';
import { InMemDeductionService } from './in-mem-deduction.service';
import { InMemEarningService } from './in-mem-earning.service';
import { InMemEinService } from './in-mem-ein.service';
import { InMemEsignService } from './in-mem-esign.service';
import { InMemEnrollmentService } from './in-mem-enrollment.service';
import { InMemIdentityService } from './in-mem-identity.service';
import { InMemSaveEntitiesService } from './in-mem-save-entities.service';
import { InMemSurepayrollService } from './in-mem-surepayroll.service';
import { InMemTaxService } from '@app/mock-services/in-mem-tax.service';
import { InMemTaxRateService } from '@app/mock-services/in-mem-tax-rate.service';


@Injectable({ providedIn: 'root' })
export class InMemoryDataService {
  /** True if in-mem service is intercepting (default); all requests pass thru when false. */
  active = true;
  db: Db;
  maxId = 0;
  resourceNameMap: { [name: string]: string };

  constructor(
    private inMemAccountService: InMemAccountService,
    private inMemAddressValidatorService: InMemAddressValidatorService,
    private inMemAdminService: InMemAdminService,
    private inMemCompanyPackageService: InMemCompanyPackageService,
    private inMemDeductionService: InMemDeductionService,
    private inMemEarningService: InMemEarningService,
    private inMemEinService: InMemEinService,
    private inMemEsignService: InMemEsignService,
    private inMemEnrollmentService: InMemEnrollmentService,
    private inMemIdentityService: InMemIdentityService,
    private inMemSaveEntitiesService: InMemSaveEntitiesService,
    private inMemTaxService: InMemTaxService,
    private inMemTaxRateService: InMemTaxRateService,
    private inMemSurepayrollService: InMemSurepayrollService,
  ) {
    this.createInitialEmptyDb();
    this.createResourceNameMap();
  }

  /** Create the in-memory database on start or by command */
  createDb(reqInfo?: RequestInfo): Observable<Db> {

    if (reqInfo) {
      const body = reqInfo.utils.getJsonBody(reqInfo.req) || {};
      this.active = !!body.active;
      if (body.clear === true) {
        this.createInitialEmptyDb();
        return of(this.db);
      }
    }

    return this.createInitialMockDb();
  }

  /**
   * Create/reset the in-mem database from mock data in JSON file,
   * extended with calculated accounts and account etags.
   * @returns Terminating observable. You must subscribe to execute but no need to unsubscribe.
   */
  createInitialMockDb(): Observable<Db> {
    this.createInitialEmptyDb();
    return getDbData().pipe(
      map(rawDb => genExtraAccounts({...this.db, ...rawDb} as Db)),
      tap(db => {
        // Ensure every initial mock company has an etag version.
        db.Company.forEach(c => {
          if (db.CompanyVersion[c.id] == null) {
            db.CompanyVersion[c.id] = 0;
          }
        });
        this.db = db;
      })
    );
  }

  /**
   * Create initial database with empty collections
   */
  createInitialEmptyDb() {
    this.db = new Db();
  }

  createResourceNameMap() {
    const db = new Db();
    this.resourceNameMap = Object.keys(db).reduce(
      (acc, collectionName) => {
        const key = collectionName.toLowerCase();
        acc[key] = collectionName;

        // Generate plural resource name mapping
        if (key.endsWith('y')) {
          acc[key.substr(0, key.length - 1) + 'ies'] = collectionName;
        } else if (key.endsWith('x')) {
          acc[key + 'es'] = collectionName;
        } else {
          acc[key + 's'] = collectionName;
        }

        return acc;
      },
      {} as { [name: string]: string }
    );
  }

  /**
   * Simulate generating new Id on the server
   */
  genId(collection: { id: number }[], collectionName: string) {
    throw new Error(`Cannot generate server-side id for '${collectionName}'.`);
  }

  /**
   * Remap a known singular collection name ("customer")
   * to the plural collection name ("customers"); else return the name
   * @param name - collection name from the parsed URL
   */
  mapCollectionName(name: string): string {
    return this.resourceNameMap[(name || '').toLowerCase()] || name;
  }

  /**
   * Override `parseRequestUrl`
   * Manipulates the request URL or the parsed result.
   * If in-mem is inactive, clear collectionName so that service passes request thru.
   * If in-mem is active, after parsing with the default parser,
   * @param url from request URL
   * @param utils for manipulating parsed URL
   */
  parseRequestUrl(url: string, utils: RequestInfoUtilities): ParsedRequestUrl {
    url = mapSpApiUrl(url);
    url = tweakUrl(url);
    const parsed = utils.parseRequestUrl(url);
    parsed.collectionName = this.active ? this.mapCollectionName(parsed.collectionName) : undefined;
    if (environment.mockSpApiOnly) {
      // When only mocking SP APIs, disable mocking based on collection name.
      // Now requests go first to method interceptors (GET/PUT) and,
      // if not intercepted, pass through to HTTP, without matching an in-mem DB collection.
      parsed.collectionName = '';
    }
    return parsed;
  }

  // #region GET cases
  /**
   * Special-cases for GET methods
   */
  get(req: RequestInfo) {
    if (environment.mockSpApiOnly) {
      return this.getMockSpApiOnly(req);
    } else {
      return this.getMocked(req);
    }
  }

  /**
   * Special-cases for GET methods to all ECommerce & SP APIs
   */
  getMocked(req: RequestInfo) {
    switch (req.collectionName) {
      case 'account':
        return this.inMemAccountService.getRequest(req, this.db) || createBadRequestResponse$(req);

      case 'admin':
        return this.inMemAdminService.getRequest(req, this.db) || createBadRequestResponse$(req);

      case 'companypackage':
        return (
          this.inMemCompanyPackageService.getCompanyPackage(req, this.db) ||
          createBadRequestResponse$(req)
        );
      case 'earnings':
        return (
          this.inMemEarningService.getRequest(req, this.db) ||
          createBadRequestResponse$(req)
        );
      case 'deductions':
        return (
          this.inMemDeductionService.getRequest(req, this.db) ||
          createBadRequestResponse$(req)
        );
      case 'documentpackage':
        return (
          this.inMemCompanyPackageService.getDocumentPackage(req, this.db) ||
          createBadRequestResponse$(req)
        );
      case 'ein':
        return this.inMemEinService.getRequest(req, this.db) || createBadRequestResponse$(req);

      case 'signing':
        if (req.url && req.url.includes('/signing/esigngeturl/')) {
          return this.inMemEsignService.getEsignUrl(req, this.db) || createBadRequestResponse$(req);
        }
        break;

      case 'surepayroll':
        return (
          this.inMemSurepayrollService.getRequest(req, this.db) || createBadRequestResponse$(req)
        );

      case 'taxes':
        if (req.url && req.url.includes('/taxapi/taxes')) {
          return this.inMemTaxRateService.getRequest(req, this.db) || createBadRequestResponse$(req);
        }
        break;

      // Disallow attempt to get more than one of these collection items
      case 'User':
      case 'Company':
        if (!req.id) {
          return errorOnGetCollection(req);
        }
        break;
    }
    // Let all other GET requests through
    return null;
  }

  /**
   * Special-cases for GET methods to SP APIs
   */
  getMockSpApiOnly(req: RequestInfo) {
    if (req.url.indexOf(environment.spApiRoot) > -1) {
      return (
        this.inMemSurepayrollService.getRequest(req, this.db) || createBadRequestResponse$(req)
      );
    }
    // Let all other GET requests through
    return null;
  }
  // #endregion GET cases

  // #region POST cases
  /**
   * Special-cases for POST methods
   */
  post(req: RequestInfo) {
    if (environment.mockSpApiOnly) {
      return this.postMockSpApiOnly(req);
    } else {
      return this.postMocked(req);
    }
  }

  /**
   * Special-cases for POST methods to ECommerce & SP APIs
   */
  postMocked(req: RequestInfo) {
    switch (req.collectionName) {
      case 'account':
        return this.inMemAccountService.postRequest(req, this.db) || createBadRequestResponse$(req);

      case 'Company':
        if (req.url.endsWith('company/event')) {
          return createOkResponse$(req);
        } else {
          return guardCompanyChange(req, this.db);
        }

      case 'enrollment':
        return (
          this.inMemEnrollmentService.postRequest(req, this.db) || createBadRequestResponse$(req)
        );

      case 'Envelope':
        if (req.url && req.url.includes('/envelope/complete')) {
          return this.inMemEsignService.postMarkEnvelopesCompleted(req, this.db);
        }
        break; // let the other envelope requests through

      // TODO: POST URLs beginning `/deductions/` should be `/save/deductions/company` and `/save/deductions/employee`
      // and InMemDeductionService should be folded under inMemSaveEntitiesService.
      // Then delete this case.
      case 'earnings': // fall through to InMemSaveEntitiesService which handles them.
      case 'deductions':         // fall through to InMemSaveEntitiesService which handles them.
      case 'CompanyEarning':         // fall through to InMemSaveEntitiesService which handles them.
      case 'company-deduction':  // fall through to InMemSaveEntitiesService which handles them.
      case 'employee-deduction': // fall through to InMemSaveEntitiesService which handles them.
      case 'save':
        return (
          this.inMemSaveEntitiesService.postRequest(req, this.db) || createBadRequestResponse$(req)
        );

      case 'surepayroll':
        return (
          this.inMemSurepayrollService.postRequest(req, this.db) || createBadRequestResponse$(req)
        );

      case 'signing':
        if (req.url && req.url.includes('/signing/esignsend/')) {
          return (
            this.inMemEsignService.postEsignSend(req, this.db) || createBadRequestResponse$(req)
          );
        }
        break;

      case 'taxes':
        if (req.url && req.url.includes('/taxapi/taxes')) {
          return this.inMemTaxService.getRequest(req, this.db) || createBadRequestResponse$(req);
        }
        break;

      case 'Address': // NOT the Address entity collection but rather the taxapi/address
        if (req.url && req.url.includes('/taxapi/address')) {
          return (
            this.inMemAddressValidatorService.getRequest(req, this.db) ||
            createBadRequestResponse$(req)
          );
        }
        break;
    }

    if (req.url.startsWith(environment.oidcAuthRealm)) {
      return this.inMemIdentityService.postRequest(req, this.db) || createBadRequestResponse$(req);
    }

    // Let all other POST requests through
    return null;
  }

  /**
   * Special-cases for POST methods to SP Only
   */
  postMockSpApiOnly(req: RequestInfo) {
    if (req.url.indexOf(environment.spApiRoot) > -1) {
      return (
        this.inMemSurepayrollService.postRequest(req, this.db) || createBadRequestResponse$(req)
      );
    }
    // Let all other POST requests through
    return null;
  }
  // #endregion POST cases

  // #region PUT cases
  /**
   * Special-cases for PUT methods
   */
  put(req: RequestInfo): Observable<any> {
    if (environment.mockSpApiOnly) {
      return null; //
    }
    switch (req.collectionName) {
      case 'Company':
        return guardCompanyChange(req, this.db);
    }
  }
  // #endregion PUT cases
}

// #region Helper Fns
function errorOnGetCollection(req: RequestInfo) {
  const errOptions: ResponseOptions = {
    url: req.url,
    status: STATUS.FORBIDDEN,
    statusText: 'Forbidden',
    body: `Requests for multiple '${req.collectionName}' records are forbidden`
  };
  return req.utils.createResponse$(() => errOptions);
}

/**
 * Get development data from json file
 */
function getDbData() {
  // TODO: enable alternative sources
  const url = '/assets/in-memory-data.json';
  return ajax.get(url).pipe(
    map(data => {
      // console.log('in-memory-data', data.response);
      return data.response;
    }),
    catchError(error => {
      console.error('in-memory-data', error);
      return of({});
    })
  );
}

/**
 * Map certain SurePayroll API request URLs to in-memory mock services.
 * @param url Original request URL
 */
function mapSpApiUrl(url: string) {
  let matched: RegExpMatchArray;
  if (url.startsWith(environment.spApiRoot)) {
    if (/\/company\/new$/.test(url)) {
      url = `api/surepayroll/company/new`;
    } else if ((matched = url.match(/authentication\/username\/valid\/(.*)$/))) {
      url = 'api/surepayroll/' + matched[0];
    } else if (/authentication\/password\/rules\/standard$/.test(url)) {
      url = 'api/surepayroll/authentication/password/rules/standard';
    }
    // let through, "as is", all other requests that begin with the spApiRoot
  }
  return url;
}

/**
 * Tweak the URL to make the parsing work for ECommerce requests whose root paths
 * don't end in "api" because that's what *this particular* in-memory web api demands.
 * @param url Original request URL
 */
function tweakUrl(url: string) {
  const apiRoot = environment.apiRoot;
  // HACK: If URL matches the environment ECommerce API root but that root doesn't end in 'api', insert '/api'.
  if (!apiRoot.endsWith('api') && url.startsWith(apiRoot)) {
    const ix = apiRoot.length;
    url = url.substring(0, ix) + '/api' + url.substring(ix);
  }
  return url;
}

// #endregion Helper fns
