import { AsyncValidator, ValidatorFactory, ValidationContextBase } from '@validation';
import { EntitySelectors } from '@entity';
import { combineLatest } from 'rxjs';
import { map, first } from 'rxjs/operators';

export class MustNotBeExemptValidator extends AsyncValidator {

  static factory(entitySelectors: EntitySelectors): ValidatorFactory {
    return {
      name: 'mustNotBeExempt',
      fn: (group, context) => {
        return new MustNotBeExemptValidator(entitySelectors, group, context);
      }
    };
  }

  private static messageTemplate = 'Company is exempt from taxes';

  constructor(entitySelectors: EntitySelectors,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('mustNotBeExempt', group,
      (value, context) => {
        return combineLatest(entitySelectors.employeeTax.entities$, entitySelectors.companyTax.entities$).pipe(
          map(([employeeTaxes, companyTaxes]) => {
            const FUTA_SUI_CODE = '0000-010';
            const exemptTaxes = [...employeeTaxes, ...companyTaxes]
              .filter(tax => !tax.isRemoved && tax.mtsTaxCode.endsWith(FUTA_SUI_CODE) && tax.isExempt);

            return exemptTaxes.length === 0;
          }),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || MustNotBeExemptValidator.messageTemplate
      }
    );
  }
}
