import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpClientModule, HttpClientJsonpModule } from '@angular/common/http';
import { NgbModule, NgbRadioGroup, NgbDate } from '@ng-bootstrap/ng-bootstrap';
// Monitor and debug rxjs Observables.  Hangs IE11.
// https://github.com/cartant/rxjs-spy/blob/master/README.md
// import { create as rxjsCreate } from 'rxjs-spy';
// rxjsCreate(); // Start rxjs-spy

import { ToastrModule } from 'ngx-toastr';

import { AccountDetailsModule } from '@app/account-details';
import { AppStoreModule } from './store/app-store.module';
import { AppRoutingModule } from './app-routing.module';
import { AuthComponentsModule } from '@app/auth-components/auth-components.module';
import { CoreModule } from '@core/core.module';
import { CoreComponentsModule } from './core-components/core-components.module';

import { AppComponent } from './app.component';
import { ConcurrencyFailureComponent } from './concurrency-failure/concurrency-failure.component';
import { FailedComponent } from './failed/failed.component';

import { AnalyticsModule } from '@core/analytics/analytics.module';
import { DecimalPipe, DatePipe } from '@angular/common';
import { modelValidatorProviders } from '@model/validators';
import { SentryErrorHandler } from '@core/error-handling/sentry-error-handler';
import { SentryModule } from '@core/error-handling/sentry.module';
import { SharedModule } from './shared/shared.module';
import { TaxCollectorService } from '@core/services/tax-services/tax-collector.service';
import { TermsComponent } from './terms/terms.component';
import { validationMetadata } from '@model/validation-metadata';
import { ValidationModule } from '@validation';
import { StatsService } from '@entity';
import { DateParserFormatter } from './shared/filter/filter-dateformat';
import { NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';

@NgModule({
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AccountDetailsModule,
    AnalyticsModule.forRoot([]),
    AuthComponentsModule,
    CoreModule,
    CoreComponentsModule,
    HttpClientModule,
    HttpClientJsonpModule,
    SharedModule,
    AppRoutingModule,
    AppStoreModule,
    NgbModule.forRoot(),
    ToastrModule.forRoot(),
    SentryModule.forRoot(),
    ValidationModule.forRoot({ validationMetadata })
  ],
  declarations: [
    AppComponent,
    ConcurrencyFailureComponent,
    FailedComponent,
    TermsComponent
  ],
  bootstrap: [AppComponent],
  providers: [
    modelValidatorProviders,
    DecimalPipe, // must inject for InputMaskCatalog
    DatePipe, // must inject for InputMaskCatalog
    { provide: ErrorHandler, useClass: SentryErrorHandler },
    NgbRadioGroup,
    {provide: NgbDateParserFormatter, useClass: DateParserFormatter}
  ]
})
export class AppModule {
  // Inject StatsService. Needs to be created upfront to hook into router and start tracking routes
  // The tax collector needs to be initialized for the entire application
  constructor(statsService: StatsService, taxCollector: TaxCollectorService) {
  }
}
