import { Injectable } from '@angular/core';

import { combineLatest, Observable } from 'rxjs';
import { distinctUntilChanged, map, shareReplay } from 'rxjs/operators';

import { Address, Employee, EmployeeType } from '@model';
import { EntitySelectors } from '../entity-selectors';
import { WellKnownRoutes } from '@core/well-known-routes';

import { getFullName } from '@utils';

/** A ViewModel of the Employee's work and home addresses for tax purposes */
export class EmployeeTaxLocation {
  /** id is Employee's id */
  id: string;
  link: string;
  name: string;

  isVerified: boolean;
  isTaxesExcluded: boolean;

  // homeAddress: Address;
  homeAddressId: string;
  isHomeAddressVerified: boolean;
  homeAddressName: string;
  homeLink: string;
  homeState: string;
  homeGeoCode: string;
  homeSchoolDistrictCode?: number;
  homePsdCode?: number;

  // workAddress: Address;
  workAddressId: string;
  isWorkAddressVerified: boolean;
  workAddressName: string;
  workLink: string;
  workState: string;
  workGeoCode: string;
  workSchoolDistrictCode?: number;
  workPsdCode?: number;

  constructor() { }

  static create(ee: Employee, addresses: Address[]): EmployeeTaxLocation {
    const homeId = ee.homeAddressId;
    const home = Address.create(
      homeId ? addresses.find(addr => addr.id === ee.homeAddressId) : undefined
    );
    const workId = ee.workAddressId;
    const work = Address.create(
      workId ? addresses.find(addr => addr.id === ee.workAddressId) : undefined
    );
    const eeLink = `${WellKnownRoutes.Employees}/${ee.id}`;
    const isExcludedFromTaxes = this.isEmployeeTaxExcluded(ee);

    // Don't exclusively trust the isVerified property as that can potentially be set erroneously
    const isHomeAddressVerified = home.isVerified && home.geoCode != null && home.geoCode.length !== 0;
    const isWorkAddressVerified = work.isVerified && work.geoCode != null && work.geoCode.length !== 0;

    return {
      /** id is Employee's id */
      id: ee.id,
      link: eeLink,
      name: getFullName(ee),

      isVerified: isWorkAddressVerified && isHomeAddressVerified,
      isTaxesExcluded: isExcludedFromTaxes,

      // homeAddress: home,
      homeAddressId: home.id,
      isHomeAddressVerified: isHomeAddressVerified,
      homeAddressName: home.toString(),
      homeLink: eeLink + '/personal',
      homeState: home.state,
      homeGeoCode: home.geoCode,
      homeSchoolDistrictCode: home.schoolDistrictCode,
      homePsdCode: home.psdCode,

      // workAddress: work,
      workAddressId: workId,
      isWorkAddressVerified: isWorkAddressVerified,
      workAddressName: work.toString(),
      workLink: eeLink + '/employment',
      workState: work.state,
      workGeoCode: work.geoCode,
      workSchoolDistrictCode: work.schoolDistrictCode,
      workPsdCode: work.psdCode
    } as EmployeeTaxLocation;
  }

  private static isEmployeeTaxExcluded(ee: Employee): boolean {
    return ee.employeeType === EmployeeType.Contractor;
  }
}

/**
 * Hash of the states for which taxes should be collected.
 * The values are the EE state tax calculation inputs for each EE in that state.
 * These are states in which an EE works or lives.
 */
export interface EmployeeTaxLocationsByState {
  [state: string]: EmployeeTaxLocation[];
}

/**
 * Hash of the states for which we collect Company-level state tax information.
 * The states are the keys.
 * The values are the count of EEs working or living in that state.
 * The count will be zero when the Company has a location in that state
 * but no EE lives or works in that location or any other location in that state.
 */
export interface CompanyTaxStateEmployeeCounts {
  [state: string]: number;
}

/** Hash of work address and the EEs working there */
export interface CompanyLocationEmployees {
  [addressId: string]: {
    address: Address;
    employees: Employee[];
  };
}

/**
 * Creates observables Address, Employee, and Company information that
 * drives how we calculate U.S. State taxes for the EEs who live and work in those states.
 * These observables also help determine the State-specific documents that must be signed.
 * And they reveal which addresses are associated with an EE and, therefore, cannot be removed.
 */
@Injectable({ providedIn: 'root' })
export class TaxLocationService {
  /**
   * Observable of array holding each Employee's work and home address for state tax purposes
   */
  employeeTaxLocations$: Observable<EmployeeTaxLocation[]>;

  /**
   * Observable of a hash of the states in which an EE lives or works.
   * The keys are the states.
   * The values are arrays of EE work and home address (EmployeeTaxLocation) in that state.
   */
  employeeTaxLocationsByState$: Observable<EmployeeTaxLocationsByState>;


  /** States in which tax will be owed and paid because an EE works or lives in that state */
  employeeTaxStates$: Observable<string[]>;

  /**
   * Observable of the states in which we collect State Tax information
   * because the Company has a location in that state or an EE living in that state.
   */
  companyTaxStates$: Observable<string[]>;

  /**
   * Observable of the addresses the company contains
   */
  companyLocations$: Observable<Address[]>;

  /**
   * Observable of the count of EEs in each state with a work location or an EE home address
   * We collect State Tax information about these states.
   * This observable tells us how many employees live or work in that state.
   * Could be zero if a company location in the state has no EEs working or living there
   */
  companyTaxStateEmployeeCounts$: Observable<CompanyTaxStateEmployeeCounts>;

  constructor(private entitySelectors: EntitySelectors) {
    this.initializeObservables();
  }

  initializeObservables() {

    // Addresses that are active and have a U.S. state
    const addresses$ = this.entitySelectors.address.entities$.pipe(
      map(addrs => addrs.filter(a => !a.isRemoved && a.state)),
      shareReplay(1)
    );

    // Employees that are "live" (not removed)
    const employees$ = this.entitySelectors.employee.entities$.pipe(
      map(ees => ees.filter(ee => !ee.isRemoved)),
      shareReplay(1)
    );

    // Work & home addresses for all live EEs
    this.employeeTaxLocations$ = combineLatest(
      addresses$,
      employees$
    ).pipe(
      map(([addrs, ees]) =>
        addrs == null || addrs.length === 0 ||
          ees == null || ees.length === 0
          ? [] : ees.map(ee => EmployeeTaxLocation.create(ee, addrs))),
      shareReplay(1)
    );

    // Hash of states with arrays of EE work/home address in that state
    this.employeeTaxLocationsByState$ = this.employeeTaxLocations$.pipe(
      map(eeInputs => {
        return eeInputs.reduce(
          (acc, input) => {
            updateAcc(input.homeState);
            updateAcc(input.workState);
            return acc;

            function updateAcc(stateName: string) {
              if (!stateName) {
                return; // ignore a no-state address
              }
              const stateArray = acc[stateName];
              if (stateArray) {
                if (-1 === stateArray.indexOf(input)) {
                  stateArray.push(input);
                }
              } else {
                acc[stateName] = [input];
              }
              return acc;
            }
          },
          {} as EmployeeTaxLocationsByState
        );
      }),
      shareReplay(1)
    );

    // The states where an EE is taxed, sorted alphabetically
    this.employeeTaxStates$ = this.employeeTaxLocationsByState$.pipe(
      map(locs => Object.keys(locs).sort()),
      shareReplay(1)
    );

    // The company's work locations
    this.companyLocations$ = addresses$.pipe(map(addrs => addrs.filter(a => !a.isHome)));

    // The list of states in which the Company has an address or an EE has a home address.
    this.companyTaxStates$ = addresses$.pipe(
      map(addresses => addresses.reduce((acc, address) => {
        if (!acc.includes(address.state)) { acc.push(address.state); }
        return acc;
      }, []).sort()),
      shareReplay(1)
    );
  }
}
