import { Injectable } from '@angular/core';

import { asapScheduler, combineLatest, forkJoin, Observable, of, Subject } from 'rxjs';
import {
  catchError,
  concatMap,
  first,
  map,
  observeOn,
  shareReplay,
  skip,
  switchMap,
  tap,
  withLatestFrom,
} from 'rxjs/operators';
import { tag } from 'rxjs-spy/operators/tag';

import { Address, CompanyTax, EmployeeTax, TaxRateType, TaxTypeCode } from '@model';

// Explicit file imports to avoid circular references
import { CompanyTaxService } from '@entity/entity-services/company-tax.service';
import { EmployeeTaxLocation, TaxLocationService } from '@entity/entity-services/tax-location.service';
import { EmployeeTaxService } from '@entity/entity-services/employee-tax.service';
import { SessionService } from '@session';

import {
  EMPTY_TAXES,
  IAddressPairRequest,
  IAvailableTax,
  IAvailableTaxAddressPair,
  ICompanyFederalTaxes,
  ICompanyStateTaxes,
  IEmployeeTaxes,
  ITaxes,
  TaxBehaviorWrapper,
  TaxRate
} from './tax-models';

import { TaxBehaviorService } from './tax-behavior.service';
import { TaxRatesService } from './tax-rates.service';
import { TaxSaveHelperService } from './tax-save-helper.service';
import { TaxService } from './tax.service';
import { ValidationTriggerService } from '@app/store/validation';

@Injectable({ providedIn: 'root' })
export class TaxCollectorService {
  /** Subject thats part of the tax recalculation trigger */
  private recomputeTaxesSubject = new Subject();

  /**
   * This observable contains the merger between persisted taxes and the taxes that need to be collected.
   */
  public taxes$: Observable<ITaxes>;

  constructor(
    private companyTaxService: CompanyTaxService,
    private employeeTaxService: EmployeeTaxService,
    private sessionService: SessionService,
    private taxBehaviorService: TaxBehaviorService,
    private taxLocationService: TaxLocationService,
    private taxProviderService: TaxService,
    private taxRateProviderService: TaxRatesService,
    private taxSaveHelperService: TaxSaveHelperService,
    private validationTriggerService: ValidationTriggerService
  ) {
    this.buildObservable();
  }

  /**
   * Recompute all the taxes that need to be collected based on the current employee addresses.
   * The method will also save new and removed Company and Employee taxes to keep everything in sync.
   */
  public computeTaxes(): Observable<boolean> {
    let triggered = false;
    return this.taxes$.pipe(
      // Close over triggered otherwise we will recompute twice
      tap(() => {
        if (triggered === false) {
          triggered = true;
          this.recomputeTaxesSubject.next();
        }
      }),
      skip(1), // We need to skip the current result. taxes$ is hot
      first(),
      concatMap(taxes => this.taxSaveHelperService.getAllTaxesSavePack(taxes)),
      concatMap(taxPacks => this.taxSaveHelperService.saveTaxPacks(taxPacks)),
      tag('computeTaxes')
    );
  }

  /** Save employee taxes */
  public saveEmployeeTaxes(employeesTaxes: IEmployeeTaxes): Observable<boolean> {
    return this.taxSaveHelperService
      .getEmployeeSavePack(employeesTaxes)
      .pipe(concatMap(taxPack => this.taxSaveHelperService.saveTaxPacks([taxPack])));
  }

  /** Save company federal taxes */
  public saveCompanyFederalTaxes(companyFederalTaxes: ICompanyFederalTaxes): Observable<boolean> {
    return this.taxes$.pipe(
      first(),
      concatMap(taxes =>
        this.taxSaveHelperService.getCompanyFederalSavePack(companyFederalTaxes, taxes)
      ),
      concatMap(taxPack => this.taxSaveHelperService.saveTaxPacks([taxPack]))
    );
  }

  /** Save company state taxes */
  public saveCompanyStateTaxes(companyStateTaxes: ICompanyStateTaxes): Observable<boolean> {
    return this.taxes$.pipe(
      first(),
      concatMap(taxes =>
        this.taxSaveHelperService.getCompanyStateSavePack(companyStateTaxes, taxes)
      ),
      concatMap(taxPack => this.taxSaveHelperService.saveTaxPacks([taxPack]))
    );
  }

  private buildObservable() {
    const taxesDataSources$ = combineLatest(
      this.sessionService.currentCompanyId$,
      this.taxLocationService.employeeTaxLocations$,
      this.taxLocationService.companyLocations$
    );

    const allTaxes$ = taxesDataSources$.pipe(
      first(),
      concatMap(result => {
        const [companyId, employeeWithLocations, companyLocations] = result;
        return forkJoin(
          this.retrieveAllAvailableTaxes(companyId, employeeWithLocations, companyLocations),
          // We must not allow this api to fail and taxes can work with an empty rates array
          this.taxRateProviderService.getTaxRates().pipe(catchError((error: any) => of([])))
        );
      }),
      observeOn(asapScheduler),
      withLatestFrom(
        this.companyTaxService.entities$, // CompanyTax[]
        this.employeeTaxService.entities$, // EmployeeTAx[]
        this.taxLocationService.companyTaxStates$ // UT, NY, ET
      ),
      map(([apiData, cachedCompanyTaxes, cachedEmployeesTaxes, companyTaxStates]) => {
        const [taxesToCollect, taxRates] = apiData;

        const employeeTaxes =
          taxesToCollect.employeesTaxes.length === 0
            ? []
            : this.buildEmployeesTaxes(
                taxesToCollect.companyId,
                taxesToCollect.employeesTaxes,
                cachedEmployeesTaxes
              );

        const companyFederalTaxes = this.buildCompanyFederalTaxes(
          taxesToCollect.companyId,
          taxesToCollect.companyTaxes,
          taxesToCollect.unverifiedCompanyStateAddresses,
          cachedCompanyTaxes,
          taxRates
        );

        const companyStatesTaxes =
          companyTaxStates.length === 0
            ? []
            : this.buildCompanyStateTaxes(
                taxesToCollect,
                companyTaxStates,
                cachedCompanyTaxes,
                taxRates
              );

        return {
          employeeTaxes: employeeTaxes,
          companyFederalTaxes: companyFederalTaxes,
          companyStateTaxes: companyStatesTaxes
        };
      })
    );

    this.taxes$ = this.recomputeTaxesSubject.pipe(
      concatMap(() => this.sessionService.companyPackageLoaded$.pipe(first())),
      switchMap(isCompanyPackageLoaded =>
        isCompanyPackageLoaded === true ? allTaxes$ : of(EMPTY_TAXES)
      ),
      shareReplay(1)
    );

    this.sessionService.companyPackageLoaded$
      .pipe(
        skip(1) // When first starting dont issue an event until we are ready to go
      )
      .subscribe(e => {
        return this.recomputeTaxesSubject.next();
      });

    this.taxes$.subscribe(() => {
      this.validationTriggerService.validate();
    });
  }

  private retrieveAllAvailableTaxes(
    companyId: string,
    employeesWithLocations: EmployeeTaxLocation[],
    companyLocations: Address[]
  ): Observable<IAllAvailableTaxes> {
    const addressPairs = this.getAllAddressPairs(employeesWithLocations, companyLocations);

    return this.taxProviderService.getAvailableTaxPairs(addressPairs).pipe(
      map(taxesAddressPairs => {
        // We filter out the unverified results from the tax pair api (duplicate ids)
        const verifiedCompanyTaxes = this.verifiedCompanyAddressTaxes(
          companyLocations,
          taxesAddressPairs
        );
        const verifiedEmployeeTaxes = this.verifiedEmployeeAddressTaxes(
          employeesWithLocations,
          taxesAddressPairs
        );

        // All states that have company unverified addresses
        const unverifiedCompanyAddressStates = companyLocations.reduce((acc, add) => {
          if (
            add.isVerified === false ||
            verifiedCompanyTaxes.unverifiedAddressIds.includes(add.id)
          ) {
            if (acc.includes(add.state) === false) {
              acc.push(add.state);
            }
          }
          return acc;
        }, []);

        return {
          unverifiedCompanyStateAddresses: unverifiedCompanyAddressStates,
          companyAvailableTaxPairs: verifiedCompanyTaxes.verifiedTaxAddressPairs,
          employeeAvailableTaxPairs: verifiedEmployeeTaxes.verifiedTaxAddressPairs
        };
      }),
      map(context => {
        // Build the employees. IEmployeeAvailableTaxes[]
        const empDefaultTaxes = this.taxBehaviorService.getEmployeeAlwaysAvailableTaxes();
        const employeeTaxes = employeesWithLocations.reduce(
          (acc, emp) => {
            const empAvailableTaxes = this.buildEmployeeAvailableTaxes(
              emp,
              context.employeeAvailableTaxPairs,
              empDefaultTaxes
            );
            acc.push(empAvailableTaxes);
            return acc;
          },
          <IEmployeeAvailableTaxes[]>[]
        );

        // Build the company taxes. IAvailableTax[]
        const companyDefaultTaxes = this.taxBehaviorService.getCompanyAlwaysAvailableTaxes();
        const companyTaxes = this.buildCompanyAvailableTaxes(
          context.employeeAvailableTaxPairs,
          context.companyAvailableTaxPairs,
          companyDefaultTaxes
        );

        return <IAllAvailableTaxes>{
          companyId: companyId,
          unverifiedCompanyStateAddresses: context.unverifiedCompanyStateAddresses,
          employeesTaxes: employeeTaxes,
          companyTaxes: companyTaxes
        };
      })
    );
  }

  private buildCompanyAvailableTaxes(
    employeeAvailableTaxPairs: IAvailableTaxAddressPair[],
    companyAvailableTaxPairs: IAvailableTaxAddressPair[],
    companyDefaultTaxes: IAvailableTax[]
  ) {
    const companyTaxes = employeeAvailableTaxPairs.reduce(
      (acc, taxPair) => {
        taxPair.taxes.forEach(tax => {
          if (acc.every(e => e.code !== tax.code)) {
            acc.push(tax);
          }
        });
        return acc;
      },
      <IAvailableTax[]>[]
    );

    companyAvailableTaxPairs.forEach(companyAddressPair => {
      companyAddressPair.taxes.forEach(tax => {
        if (companyTaxes.every(e => e.code !== tax.code)) {
          companyTaxes.push(tax);
        }
      });
    });

    companyDefaultTaxes.forEach(defaultTax => {
      if (companyTaxes.every(t => t.code !== defaultTax.code)) {
        companyTaxes.push(defaultTax);
      }
    });

    return companyTaxes;
  }

  private getAllAddressPairs(
    employeesWithLocations: EmployeeTaxLocation[],
    companyLocations: Address[]
  ): IAddressPairRequest[] {
    // Employee addresses
    const addressPairRequest = employeesWithLocations.reduce(
      (acc, emp) => {
        if (emp.isVerified && emp.isTaxesExcluded === false) {
          acc.push({
            // Were going to use the employee home Id to prevent guid collision with the address ids
            id: emp.homeAddressId,
            homeGeoCode: emp.homeGeoCode,
            homePsdCode: emp.homePsdCode,
            homeSchoolDistrictCode: emp.homeSchoolDistrictCode,
            workGeoCode: emp.workGeoCode,
            workPsdCode: emp.workPsdCode,
            workSchoolDistrictCode: emp.workSchoolDistrictCode
          });
        }
        return acc;
      },
      <IAddressPairRequest[]>[]
    );

    companyLocations.forEach(companyAddress => {
      if (companyAddress.isVerified) {
        addressPairRequest.push({
          id: companyAddress.id,
          homeGeoCode: companyAddress.geoCode,
          homePsdCode: companyAddress.psdCode,
          homeSchoolDistrictCode: companyAddress.schoolDistrictCode,
          workGeoCode: companyAddress.geoCode,
          workPsdCode: companyAddress.psdCode,
          workSchoolDistrictCode: companyAddress.schoolDistrictCode
        });
      }
    });

    return addressPairRequest;
  }

  private verifiedCompanyAddressTaxes(
    companyLocations: Address[],
    availableTaxPairs: IAvailableTaxAddressPair[]
  ) {
    const companyAddressesPairs = availableTaxPairs.filter(e =>
      companyLocations.some(cl => cl.id === e.id)
    );
    const unverifiedAddressIds = this.getUnverifiableTaxPairIds(companyAddressesPairs);
    return {
      unverifiedAddressIds: unverifiedAddressIds,
      verifiedTaxAddressPairs: companyAddressesPairs.filter(
        e => unverifiedAddressIds.includes(e.id) === false
      )
    };
  }

  private verifiedEmployeeAddressTaxes(
    employeeLocations: EmployeeTaxLocation[],
    availableTaxPairs: IAvailableTaxAddressPair[]
  ) {
    const employeeAddressesPairs = availableTaxPairs.filter(e =>
      employeeLocations.some(cl => cl.homeAddressId === e.id)
    );
    const unverifiedAddressIds = this.getUnverifiableTaxPairIds(employeeAddressesPairs);
    return {
      unverifiedAddressIds: unverifiedAddressIds,
      verifiedTaxAddressPairs: employeeAddressesPairs.filter(
        e => unverifiedAddressIds.includes(e.id) === false
      )
    };
  }

  private buildEmployeeAvailableTaxes(
    employeeWithLocation: EmployeeTaxLocation,
    employeesAvailableTaxPairs: IAvailableTaxAddressPair[],
    defaultTaxes: IAvailableTax[]
  ): IEmployeeAvailableTaxes {
    if (employeeWithLocation.isTaxesExcluded) {
      return {
        isVerified: employeeWithLocation.isVerified,
        employeeLocation: employeeWithLocation,
        taxes: []
      };
    }

    const employeeAvailableTaxPairs = employeesAvailableTaxPairs.find(
      e => e.id === employeeWithLocation.homeAddressId
    );

    // Did we find taxes for this employee?
    if (employeeAvailableTaxPairs == null) {
      return { isVerified: false, employeeLocation: employeeWithLocation, taxes: [] };
    } else {
      const employeeAvailableTaxes = employeeAvailableTaxPairs.taxes;
      defaultTaxes.forEach(defaultTax => {
        if (employeeAvailableTaxes.every(t => t.code !== defaultTax.code)) {
          employeeAvailableTaxes.push(defaultTax);
        }
      });
      return {
        isVerified: true,
        employeeLocation: employeeWithLocation,
        taxes: employeeAvailableTaxes
      };
    }
  }

  // --------------------
  // These methods create the final view model for employee taxes
  // --------------------
  private buildEmployeesTaxes(
    companyId: string,
    employeesAvailableTaxes: IEmployeeAvailableTaxes[],
    cachedEmployeesTaxes: EmployeeTax[]
  ): IEmployeeTaxes[] {
    const employeesTaxes = employeesAvailableTaxes.map(employeeAvailableTaxes => {
      const eeId = employeeAvailableTaxes.employeeLocation.id;
      const cachedEmployeeTaxes = cachedEmployeesTaxes.filter(
        t => t.employeeId === eeId && t.companyId === companyId
      );

      const taxesWithBehaviors = employeeAvailableTaxes.taxes.sort(this.orderTaxesFunc).reduce(
        (behaviors, toCollectTax) => {
          let cachedTax = cachedEmployeeTaxes.find(t => t.mtsTaxCode === toCollectTax.code);

          if (cachedTax == null) {
            cachedTax = EmployeeTax.create({
              id: this.sessionService.generateId(),
              companyId: companyId,
              employeeId: eeId,
              name: toCollectTax.description,
              mtsTaxCode: toCollectTax.code,
              stateCode: toCollectTax.stateCode,
              taxTypeCode: <TaxTypeCode>toCollectTax.taxTypeCode,
              isExempt: false
            });
          }

          const taxBehavior = this.taxBehaviorService.tryGetEmployeeTaxBehavior(
            cachedTax,
            toCollectTax,
            employeeAvailableTaxes.employeeLocation
          );

          if (taxBehavior != null) {
            behaviors.push(taxBehavior);
          }

          return behaviors;
        },
        <TaxBehaviorWrapper<EmployeeTax>[]>[]
      );

      const employeeTaxes = <IEmployeeTaxes>{
        employeeLocation: employeeAvailableTaxes.employeeLocation,
        addressVerified: employeeAvailableTaxes.isVerified,
        taxes: taxesWithBehaviors
      };

      return employeeTaxes;
    });

    return employeesTaxes;
  }

  private buildCompanyFederalTaxes(
    companyId: string,
    availableCompanyTaxes: IAvailableTax[],
    unverifiedCompanyAddressStates: string[],
    cachedCompanyTaxes: CompanyTax[],
    taxRates: TaxRate[]
  ): ICompanyFederalTaxes {
    const cachedFederalCompanyTaxes = cachedCompanyTaxes.filter(
      e => (e.stateCode == null || e.stateCode === '') && e.taxTypeCode === TaxTypeCode.Federal
    );

    const companyFederalTaxes = this.getCompanyFederalTaxes(availableCompanyTaxes)
      .sort(this.orderTaxesFunc)
      .reduce(
        (acc, toCollectTax) => {
          let cachedTax = cachedFederalCompanyTaxes.find(t => t.mtsTaxCode === toCollectTax.code);
          if (cachedTax == null) {
            cachedTax = this.createCompanyTax(companyId, '', toCollectTax);
          }
          const taxRate = taxRates.find(r => r.code === toCollectTax.code);

          const taxBehavior = this.taxBehaviorService.tryGetCompanyTaxBehavior(
            cachedTax,
            toCollectTax,
            taxRate,
            availableCompanyTaxes
          );
          if (taxBehavior != null) {
            acc.push(taxBehavior);
          }

          return acc;
        },
        <TaxBehaviorWrapper<CompanyTax>[]>[]
      );

    return <ICompanyFederalTaxes>{
      allCompanyAddressesVerified: unverifiedCompanyAddressStates.length !== 0,
      taxes: companyFederalTaxes
    };
  }

  private buildCompanyStateTaxes(
    availableTaxes: IAllAvailableTaxes,
    companyTaxStates: string[],
    cachedCompanyTaxes: CompanyTax[],
    taxRates: TaxRate[]
  ): ICompanyStateTaxes[] {
    const companyId = availableTaxes.companyId;
    const availableCompanyTaxes = availableTaxes.companyTaxes;
    const employeeAvailableTaxes = availableTaxes.employeesTaxes;
    const unverifiedCompanyStateAddresses = availableTaxes.unverifiedCompanyStateAddresses;

    const companyStatesTaxes = companyTaxStates.map(stateCode => {
      const stateTaxesToCollect = this.getCompanyStateTaxes(stateCode, availableCompanyTaxes);
      const cachedStateCompanyTaxes = cachedCompanyTaxes.filter(e => e.stateCode === stateCode);

      const taxesWithBehaviors = stateTaxesToCollect.sort(this.orderTaxesFunc).reduce(
        (acc, toCollectTax) => {
          let cachedTax = cachedStateCompanyTaxes.find(t => t.mtsTaxCode === toCollectTax.code);
          if (cachedTax == null) {
            cachedTax = this.createCompanyTax(companyId, stateCode, toCollectTax);
          }
          const taxRate = taxRates.find(r => r.code === toCollectTax.code);

          const taxBehavior = this.taxBehaviorService.tryGetCompanyTaxBehavior(
            cachedTax,
            toCollectTax,
            taxRate,
            availableCompanyTaxes
          );

          if (taxBehavior != null) {
            acc.push(taxBehavior);
          }

          return acc;
        },
        <TaxBehaviorWrapper<CompanyTax>[]>[]
      );

      const employeeLocationsInState = employeeAvailableTaxes.reduce(
        (acc, empAvailableTaxes) => {
          if (
            empAvailableTaxes.employeeLocation.homeState === stateCode ||
            empAvailableTaxes.employeeLocation.workState === stateCode
          ) {
            acc.push({
              isVerified: empAvailableTaxes.isVerified,
              employeeLocation: empAvailableTaxes.employeeLocation
            });
          }

          return acc;
        },
        <{ isVerified: boolean; employeeLocation: EmployeeTaxLocation }[]>[]
      );

      const allEmployeeAddressesVerified = employeeLocationsInState.every(e => e.isVerified);
      const employeeLocations = employeeLocationsInState.map(e => e.employeeLocation);
      const unverifiedStateCompanyAddress = unverifiedCompanyStateAddresses.includes(stateCode);

      const companyStateTaxes = <ICompanyStateTaxes>{
        state: stateCode,
        employeesLocations: employeeLocations,
        allCompanyStateAddressesVerified: unverifiedStateCompanyAddress,
        allEmployeeStateAddressesVerified: allEmployeeAddressesVerified,
        taxes: taxesWithBehaviors
      };

      return companyStateTaxes;
    });

    return companyStatesTaxes;
  }

  // --------------------
  // Helper methods
  // --------------------
  private getUnverifiableTaxPairIds(availableTaxes: IAvailableTaxAddressPair[]): string[] {
    const addressPairsGroup = availableTaxes.reduce(
      (a, t) => {
        if (a.hasOwnProperty(t.id) === false) {
          a[t.id] = 1;
        } else {
          a[t.id] = a[t.id] + 1;
        }

        return a;
      },
      <any>{}
    );

    const uncertainAddressIds = Object.keys(addressPairsGroup).reduce((a, k) => {
      if (addressPairsGroup[k] > 1) {
        a.push(k);
      }
      return a;
    }, []);

    return uncertainAddressIds;
  }

  private static taxTypeCodesPriority: { [taxTypeCode: string]: number } = {
    FD: 1,
    ST: 2,
    CN: 3,
    CT: 4,
    SD: 5
  };

  private orderTaxesFunc(a: IAvailableTax, b: IAvailableTax): number {
    const priority = TaxCollectorService.taxTypeCodesPriority;

    if (priority[a.taxTypeCode] < priority[b.taxTypeCode]) {
      return -1;
    } else if (priority[a.taxTypeCode] > priority[b.taxTypeCode]) {
      return 1;
    }

    if (a.displayOrder < b.displayOrder) {
      return -1;
    } else if (a.displayOrder > b.displayOrder) {
      return 1;
    }

    return 0;
  }

  private getCompanyFederalTaxes(availableTaxes: IAvailableTax[]): IAvailableTax[] {
    return availableTaxes.filter(
      e => (e.stateCode == null || e.stateCode === '') && e.taxTypeCode === 'FD'
    );
  }

  private getCompanyStateTaxes(state: string, availableTaxes: IAvailableTax[]): IAvailableTax[] {
    return availableTaxes.filter(e => e.stateCode === state && e.taxTypeCode !== 'FD');
  }

  private createCompanyTax(
    companyId: string,
    stateCode: string,
    availableTax: IAvailableTax
  ): CompanyTax {
    const rateType =
      availableTax.isVariableRate == null || availableTax.isVariableRate === false
        ? TaxRateType.Fixed
        : TaxRateType.Variable;

    return CompanyTax.create({
      id: this.sessionService.generateId(),
      companyId: companyId,
      name: availableTax.description,
      mtsTaxCode: availableTax.code,
      stateCode: stateCode,
      rateType: rateType,
      taxTypeCode: <TaxTypeCode>availableTax.taxTypeCode,
      isExempt: false
    });
  }
}

// Company Tax Collector
interface IAllAvailableTaxes {
  /** The company id */
  companyId: string;

  /** If a state has a company address that is unverified, ex: NY, CA ... */
  unverifiedCompanyStateAddresses: string[];

  /** The entire list of available taxes for all employees */
  employeesTaxes: IEmployeeAvailableTaxes[];

  /** The entire list of available taxes for the company */
  companyTaxes: IAvailableTax[];
}

interface IEmployeeAvailableTaxes {
  /** Employee with tax location */
  employeeLocation: EmployeeTaxLocation;

  /** If the employee address pair was truly verified this status is confirmed by calling the tax api.
   * Even if the employeeLocation is verified this status will reflect absolute reality  */
  isVerified: boolean;

  /** The array of available taxes */
  taxes: IAvailableTax[];
}
