import { HouseholdFilers } from '@model';

export interface HouseholdFilerDisplayVm {
  filingStatus: string;
  primaryName: string;
  primarySsn: string;
  spouseName: string;
  spouseSsn: string;
}

export function getFilerName(hhf: HouseholdFilers, primary = true) {
  const name = primary
    ? (hhf.primaryFirstName || '') + ' ' +  (hhf.primaryLastName || '')
    : (hhf.spouseFirstName || '') + ' ' +  (hhf.spouseLastName || '' );
  return (name.trim()) || 'missing';
}

export function getHouseholdFilerDisplayVm(hhf: HouseholdFilers) {
  return ( hhf ?
   {
      filingStatus: hhf.filingStatus,
      primaryName: getFilerName(hhf),
      primarySsn: hhf.primarySsn,
      spouseName: getFilerName(hhf, false),
      spouseSsn: hhf.spouseSsn,
    } :
    {
      filingStatus: null as string,
      primaryName: 'missing',
      primarySsn: null as string,
      spouseName: 'missing',
      spouseSsn: null as string
    }
  );
}
