import { NgModule } from '@angular/core';
import { Routes, RouterModule, RouteReuseStrategy } from '@angular/router';

import { environment } from '@environment';

import { AccountViewGuard, AdminConsoleGuard, DataEntryPageGuard, SignupGuard, SinglePageStatusGuard } from '@core/routing-guards';
import { SpRouteReuseStrategy } from '@core';

import { authRoutes } from './auth-components/auth-components.module';
import { ConcurrencyFailureComponent } from './concurrency-failure/concurrency-failure.component';
import { FailedComponent } from './failed/failed.component';
import { MockSigninModule } from './mock-signin/mock-signin.module';
import { TermsComponent } from './terms/terms.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'dashboard' },
  // {
  //   path: 'signup',
  //   canActivate: [SignupGuard],
  //   loadChildren: '@app/signup/signup.module#SignupModule',
  //   data: { title: 'Signup' }
  // },
  {
    path: 'about-you',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/about-you/about-you.module#AboutYouModule',
    data: { title: 'About You' }
  },
  {
    path: 'account-view',
    canActivate: [AccountViewGuard],
    loadChildren: '@app/account-view/account-view.module#AccountViewModule',
    data: { title: 'Account Details' }
  },
  {
    path: 'admin',
    canActivateChild: [AdminConsoleGuard],
    loadChildren: '@app/admin-console/admin-console.module#AdminConsoleModule',
    data: { title: 'Admin' }
  },
  {
    path: 'company-bank-account',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/company-bank-info/company-bank-info.module#CompanyBankInfoModule',
    data: { title: 'Bank Account' }
  },
  {
    path: 'company-info',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/company-info/company-info.module#CompanyInfoModule',
    data: { title: 'Company Info' }
  },
  {
    path: 'tax-info',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/company-tax-info/company-tax-info.module#CompanyTaxInfoModule',
    data: { title: 'Tax Info' }
  },
  {
    path: 'dashboard',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/dashboard/dashboard.module#DashboardModule',
    data: { title: 'Dashboard' }
  },
  {
    path: 'earnings',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/earnings/earnings.module#EarningsModule',
    data: { title: 'Earnings' }
  },
  {
    path: 'employees',
    canActivateChild: [DataEntryPageGuard],
    loadChildren: '@app/employees/employees.module#EmployeesModule',
    data: { title: 'Employees' }
  },
  {
    path: 'documents',
    canActivate: [SinglePageStatusGuard],
    loadChildren: '@app/documents/documents.module#DocumentsModule',
    data: { title: 'Documents' }
  },
  {
    path: 'review',
    canActivate: [AccountViewGuard],
    loadChildren: '@app/review/review.module#ReviewModule',
    data: { title: 'Review' }
  },
  {
    path: 'terms',
    component: TermsComponent
  },
  {
    path: 'enrolled',
    canActivate: [SinglePageStatusGuard],
    loadChildren: '@app/enrolled/enrolled.module#EnrolledModule',
    data: { title: 'Enrolled' }
  },
  {
    path: 'abandoned',
    canActivate: [SinglePageStatusGuard],
    loadChildren: '@app/abandoned/abandoned.module#AbandonedModule',
    data: { title: 'Welcome back!' }
  },
  {
    path: 'processing',
    canActivate: [SinglePageStatusGuard],
    loadChildren: '@app/processing/processing.module#ProcessingModule',
    data: { title: 'Processing' }
  },
  {
    path: 'failed',
    component: FailedComponent,
    data: { title: 'Failed' }
  },
  {
    path: 'concurrency-failure',
    component: ConcurrencyFailureComponent,
    data: { title: 'Concurrency Failure' }
  },
  // {
  //   path: 'deductions',
  //   canActivateChild: [DataEntryPageGuard],
  //   loadChildren: '@app/deductions/deductions.module#DeductionsModule',
  //   data: { title: 'Deductions' }
  // },
];

/** Must come after all other routes else can't get to those routes */
const lastRoutes: Routes = [
  { path: '**', redirectTo: '' } // bad routes redirect to root
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { enableTracing: environment.routerTracing }),
    RouterModule.forChild(authRoutes),
    environment.mockAuth || environment.mockServices ? MockSigninModule : [],
    RouterModule.forChild(lastRoutes)
  ],
  exports: [RouterModule],
  providers: [{ provide: RouteReuseStrategy, useClass: SpRouteReuseStrategy }]
})
export class AppRoutingModule {}
