import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable } from 'rxjs';
import { map} from 'rxjs/operators';

import { environment } from '@environment';
import { Envelope, EnvelopeSigner } from '@model';
import { BusyService } from '@app/core/services/busy.service';

export interface EsignEnvelopeResponse {
  envelope: Envelope;
  status: string;
}

@Injectable({providedIn: 'root'})
export class EsignService {
  constructor (private http: HttpClient, private busyService: BusyService) {}

  /**
   * Tell DocuSign to create an Envelope and optionally send an email to signer and spouse.
   * @returns Cold, terminating observable with envelope and DocuSign's status (e.g., 'sent' if the email was sent).
   * You must subscribe but do not have to unsubscribe.
   */
  postEnvelope(envelopeId: string, signers: EnvelopeSigner[]): Observable<EsignEnvelopeResponse> {
    const eurl = environment.apiRoot + `/signing/esignsend/${envelopeId}`;
    const params = { signers: signers };
    return this.busyService.busy$('Building Documents ...',
      this.http.post<EsignEnvelopeResponse>(eurl, params)
    );
  }

  /**
   * Get a temporary Esign URL which enables short-term access to an ESign envelope
   * @return Cold terminating observable emitting the URL
   * You must subscribe but do not have to unsubscribe.
   */
  getEnvelopeUrl(envelopeId: string): Observable<string> {
    const url = environment.apiRoot + `/signing/esigngeturl/${envelopeId}`;
    const options = { params: {
      apiRoot: environment.apiRoot,
      appUrl: location.origin + '/assets/esign.html'
    }};
    return this.busyService.busy$('Opening DocuSign ...',
      this.http.get<{ url: string }>(url, options).pipe(map(r => r.url))
    );
  }

  /**
   * Mark a company's envelopes as "signed and complete"
   * @param companyId
   * @returns Cold terminating boolean observable that emits true if operation was successful.
   * You must subscribe but do not have to unsubscribe.
   */
  markEnvelopesSigned(companyId: string): Observable<boolean> {
    const url = environment.apiRoot + `/envelope/complete`;
    const body = { companyId: companyId };
    return this.busyService.busy$('Updating document envelopes ...',
      this.http.post<boolean>(url, body)
    );
  }

}
