import { NgModule } from '@angular/core';

import { ChatComponent } from './chat/chat.component';
import { SpinnerComponent } from './spinner/spinner.component';
import { StepperComponent } from './stepper/stepper.component';
import { ToolbarComponent } from './toolbar/toolbar.component';
import { SharedModule } from '@shared/shared.module';

@NgModule({
  imports: [SharedModule],
  declarations: [ToolbarComponent, SpinnerComponent, StepperComponent,  ChatComponent],
  exports: [ToolbarComponent, SpinnerComponent, StepperComponent, ChatComponent],
})
/**
 * Non-routed components that appear once somewhere so not shared.
 */
export class CoreComponentsModule { }
