export interface AddressLike {
  street1?: string;
  street2?: string;
  city?: string; // locality
  state?: string; // "administrative_area_level_1"
  zip?: string; // postal_code (including possible suffix)
  country?: 'US';
}

export interface GeoAddressLike {
  geoCode: string;
  schoolDistrictCode?: number;
  psdCode?: number;
  isVerified: boolean;
}

export function addressToString(address: AddressLike) {
  const { street1 = '', street2 = '', city = '', state = '', zip = '' } = address;
  const result = (street2
    ? `${street1}, ${street2}, ${city}, ${state} ${zip}`
    : `${street1}, ${city}, ${state} ${zip}`
  ).replace(/\s\s+/g, ' ');
  return result === ', , ' ? '' : result;
}

/**
 * If the address has Street, City, State and Zip
 * are set and syntactically valid
 */
export function isComplete(address: AddressLike): boolean {
  return address.street1 != null && address.street1 !== '' &&
    address.city != null && address.city !== '' &&
    address.state != null && address.state.length === 2 &&
    address.zip != null && address.zip.length > 4;
}

export function formatZip(address: AddressLike): string {
  const zip = address.zip;
  if (zip.length === 9) {
    const separatorOffset = 5;
    return zip.substring(0, separatorOffset) + '-' + zip.substring(separatorOffset);
  }

  return zip;
}

export function zipFirstDigits(address: AddressLike): string {
  const separatorOffset = 5;
  return address.zip.substring(0, separatorOffset);
}
