import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

import {
  Dictionary, EntityCache, EntityCollectionServiceElementsFactory, EntitySelectors$, EntitySelectors$Factory
} from 'ngrx-data';

import * as Model from '@model';
import { Indexable } from '@utils';

export interface EntityInfo {
  entityType: string;
  entity: any;
}

/**
 * Get base-line EntitySelectors$ for any SP Entity collection
 * A registry of Observable selectors$.
 */
@Injectable({ providedIn: 'root' })
export class EntitySelectors {

  /** Observable of entire EntityCache, including the removed entities */
  entityCache$: Observable<EntityCache>;

  /** Observable of the entire "active" EntityCache (excludes removed entities) */
  entityCacheActive$: Observable<EntityCache>;

  /** Observable of all "active" entities as EntityInfos (excludes removed entities) */
  entityInfos$: Observable<EntityInfo[]>;

  private readonly collectionSelectors: { [name: string]: EntitySelectors$<any> } = {};

  constructor(
    private entitySelectorsFactory: EntityCollectionServiceElementsFactory,
    entitySelectors$Factory: EntitySelectors$Factory
  ) {
    this.entityCache$ = entitySelectors$Factory.entityCache$;
    this.entityCacheActive$ = this.entityCache$.pipe(map(toActiveCache), shareReplay(1));
    this.entityInfos$ = this.entityCache$.pipe(map(toEntityInfos), shareReplay(1));
  }

  /**
   * Get the Observable selectors$ for the given collection.
   * If not found in the registry, create and register, before returning.
   */
  getSelectors$<T>(collectionName: string): EntitySelectors$<T> {
    collectionName = (collectionName || '').trim();
    if (!collectionName) {
      throw new Error('Collection name is required');
    }
    let selectors$ = this.collectionSelectors[collectionName];
    if (!selectors$) {
      selectors$ = this.entitySelectorsFactory.create<T>(collectionName).selectors$;
      this.collectionSelectors[collectionName] = selectors$;
    }
    return selectors$;
  }

  register<T>(selectors$: EntitySelectors$<T>) {
    this.collectionSelectors[selectors$.entityName] = selectors$;
  }

  // #region well-known selectors$

  /** Address EntitySelectors$ */
  get address() {
    return this.getSelectors$<Model.Address>('Address');
  }

  /** Company EntitySelectors$ */
  get company() {
    return this.getSelectors$<Model.Company>('Company');
  }

  /** CompanyDeduction EntitySelectors$ */
  get companyDeduction() {
    return this.getSelectors$<Model.CompanyDeduction>('CompanyDeduction');
  }

  /** CompanyEarning EntitySelectors$ */
  get companyEarning() {
    return this.getSelectors$<Model.CompanyEarning>('CompanyEarning');
  }

  /** Deduction EntitySelectors$ */
  get deduction() {
    return this.getSelectors$<Model.Deduction>('Deduction');
  }

  /** Document EntitySelectors$ */
  get document() {
    return this.getSelectors$<Model.Document>('Document');
  }

  /** Earning EntitySelectors$ */
  get earning() {
    return this.getSelectors$<Model.Earning>('Earning');
  }

  /** Employee EntitySelectors$ */
  get employee() {
    return this.getSelectors$<Model.Employee>('Employee');
  }

  /** EmployeeDeduction EntitySelectors$ */
  get employeeDeduction() {
    return this.getSelectors$<Model.EmployeeDeduction>('EmployeeDeduction');
  }

  /** EmployeePaymentMethod EntitySelectors$ */
  get employeePaymentMethod() {
    return this.getSelectors$<Model.EmployeePaymentMethod>('EmployeePaymentMethod');
  }

  /** Envelope EntitySelectors$ */
  get envelope() {
    return this.getSelectors$<Model.Envelope>('Envelope');
  }

  /** EnvelopeSigner EntitySelectors$ */
  get envelopeSigner() {
    return this.getSelectors$<Model.EnvelopeSigner>('EnvelopeSigner');
  }

  /** HouseholdFilers EntitySelectors$ */
  get householdFilers() {
    return this.getSelectors$<Model.HouseholdFilers>('HouseholdFilers');
  }

  /** User EntitySelectors$ */
  get user() {
    return this.getSelectors$<Model.User>('User');
  }

  /** CompanyTax EntitySelectors$ */
  get companyTax() {
    return this.getSelectors$<Model.CompanyTax>('CompanyTax');
  }

  /** EmployeeTax EntitySelectors$ */
  get employeeTax() {
    return this.getSelectors$<Model.EmployeeTax>('EmployeeTax');
  }
  // #endregion well-known selectors$
}

// #region entity cache selector functions
// TODO: Only need these two helper variables until we do cascade delete
function employeeHelpers(entityCache: EntityCache) {
  const eeMap: Dictionary<Model.Employee> = entityCache['Employee'].entities;
  const removedEEs: Indexable = {};
  const removedHomeAddrs: Indexable = {};

  Object.keys(eeMap).forEach(eeId => {
    const ee = eeMap[eeId];
    if (ee.isRemoved) {
      removedEEs[eeId] = true;
      removedHomeAddrs[ee.homeAddressId] = true;
    }
  });

  return { removedEEs, removedHomeAddrs };
}

/** Re-create the EntityCache with just the active entities (none with isRemoved:true) */
function toActiveCache(entityCache: EntityCache) {
  const entityTypes = Object.keys(entityCache);
  if (entityTypes.length === 0) {
    return entityCache;
  }

  // TODO: Only need these two helper variables until we do cascade delete
  const { removedEEs, removedHomeAddrs } = employeeHelpers(entityCache);
  let activeCache: EntityCache;

  entityTypes.forEach(entityType => {
    let entry = entityCache[entityType];

    let entities = entry.entities;
    let ids = entry.ids as string[];
    let isMutated = false;

    Object.keys(entities).forEach(eid => {
      const entity = entities[eid];
      let removed = entity.isRemoved ? entity : null;

      // TODO: remove after cascade delete
      if (!removed) {
        if (entityType === 'EmployeePaymentMethod') {
          removed = removedEEs[entity.employeeId] ? entity : null;
        } else if (entityType === 'Address') {
          removed = removedHomeAddrs[eid] ? entity : null;
        }
      }

      if (removed) {
        if (!isMutated) {
          isMutated = true;
          ids = [...ids];
          entities = { ...entities };
        }
        ids = ids.filter(id => id !== eid);
        delete entities[eid];
      }
    });

    if (isMutated) {
      entry = { ...entry, ids, entities: { ...entities } };
      if (!activeCache) {
        activeCache = { ...entityCache };
      }
      activeCache[entityType] = entry;
    }
  });

  return activeCache || entityCache;
}

// Note: `toEntityInfos` does not delegate to `toActiveCache` because this function is on a
// reasonably hot code path in validation and this different implementation
// should perform better as it has less to do than `toActiveCache`.

/** Flatten EntityCache to array of EntityInfos of active (non-removed) entities */
function toEntityInfos(entityCache: EntityCache): EntityInfo[] {
  const entityTypes = Object.keys(entityCache);
  if (entityTypes.length === 0) {
    return [];
  }

  // TODO: Only need these two helper variables until we do cascade delete
  const { removedEEs, removedHomeAddrs } = employeeHelpers(entityCache);

  return entityTypes.reduce((acc, entityType) => {
    const entityMap = entityCache[entityType].entities;

    let entityInfos = Object.keys(entityMap)
      .map(id => ({ entityType, entity: entityMap[id] }))
      .filter(e => !e.entity.isRemoved);

    // TODO: Remove these filters once we "cascade delete"
    if (entityType === 'Address') {
      entityInfos = entityInfos.filter(ei => !removedHomeAddrs[ei.entity.id]);
    } else if (entityType === 'EmployeePaymentMethod') {
      entityInfos = entityInfos.filter(ei => !removedEEs[ei.entity.employeeId]);
    }

    return acc.concat(entityInfos);
  }, [] as EntityInfo[]);
}
// #endregion entity cache selectors
