import { AsyncValidator, ValidatorFactory, ValidationContextBase } from '@app/store/validation';
import { EntitySelectors, EarningService } from '@app/store/entity';
import { combineLatest, Observable } from 'rxjs';
import { map, first, shareReplay } from 'rxjs/operators';
import { EarningsCategory } from '@model';


/** Minimum properties for validation purposes of a company or default earning */
interface ValidatorEarning {
  name: string;
  description: string;
  code: string;
}

export class EarningsNameValidator extends AsyncValidator {
  static factory(
    entitySelectors: EntitySelectors,
    earningService: EarningService
  ): ValidatorFactory {
    const earnings$ = getValidatorEarnings$(entitySelectors, earningService);

    return {
      name: 'earningsNameValidator',
      fn: (group, context) => {
        return new EarningsNameValidator(earnings$, group, context);
      }
    };
  }

  private static messageTemplate = `Earnings Abbreviation cannot be the same as other earnings' name or reserved words
  - %name%`;

  constructor(
    earnings$: Observable<ValidatorEarning[]>,
    group: string,
    validatorContext?: Partial<ValidationContextBase>
  ) {
    super(
      'earningsValidator',
      group,
      (value, context) => {
        const name = context.model.name || value;
        return earnings$.pipe(
          first(),
          map(earnings => {
            return (
              null ==
              earnings
                .filter(ce => ce.code !== context.model.earningCode)
                .find(p => p.name.toUpperCase() === context.model.name.toUpperCase())
            );
          })
        );
      },
      {
        ...validatorContext,
        messageTemplate:
          (validatorContext && validatorContext.messageTemplate) ||
          EarningsNameValidator.messageTemplate
      }
    );
  }
}

export class EarningsDescriptionValidator extends AsyncValidator {
  static factory(
    entitySelectors: EntitySelectors,
    earningService: EarningService
  ): ValidatorFactory {
    const earnings$ = getValidatorEarnings$(entitySelectors, earningService);

    return {
      name: 'earningsDescriptionValidator',
      fn: (group, context) => {
        return new EarningsDescriptionValidator(earnings$, group, context);
      }
    };
  }

  private static messageTemplate = `Earnings Name cannot be the same as other earnings' name or reserved words
  - %description%`;

  constructor(
    earnings$: Observable<ValidatorEarning[]>,
    group: string,
    validatorContext?: Partial<ValidationContextBase>
  ) {
    super(
      'earningsValidator',
      group,
      (value, context) => {
        const description = context.model.description || value;
        return earnings$.pipe(
          first(),
          map(earnings => {
            return (
              null ==
              earnings
                .filter(ce => ce.code !== context.model.earningCode)
                .find(p => p.description.toUpperCase() === context.model.description.toUpperCase())
            );
          })
        );
      },
      {
        ...validatorContext,
        messageTemplate:
          (validatorContext && validatorContext.messageTemplate) ||
          EarningsDescriptionValidator.messageTemplate
      }
    );
  }
}

function getValidatorEarnings$(
  entitySelectors: EntitySelectors,
  earningService: EarningService
): Observable<ValidatorEarning[]> {
  return combineLatest(entitySelectors.companyEarning.entities$, earningService.earnings$).pipe(
    map(([companyEarnings, defaultEarnings]) =>
      (defaultEarnings.filter(
        e =>
          e.earningsCategory.toUpperCase() === EarningsCategory.DEFAULTEARNINGS ||
          e.earningsCategory.toUpperCase() === EarningsCategory.DEFAULTOTHERCOMPENSATION
      ) as ValidatorEarning[]).concat(companyEarnings
        .filter(e => !e.isRemoved)
        .map(ce => {
          return {
            code: ce.earningCode,
            name: ce.name,
            description: ce.description
          };
        }) as ValidatorEarning[])
    ),
    shareReplay(1)
  );
}
