import { Injectable } from '@angular/core';
import { createOkResponse$, Db, RequestInfo } from './in-mem-service-utils';

@Injectable({ providedIn: 'root' })
export class InMemTaxRateService {

  getRequest(req: RequestInfo, db: Db) {
    // tslint:disable:no-use-before-declare
    if (req.url && req.url.includes('/taxes/suirates')) {
      return createOkResponse$(req, suiRates);
    }
    if (req.url && req.url.includes('/taxes/holidays')) {
      return createOkResponse$(req, holidays);
    }
  }
}

