import { areDifferent } from '@core/utils';
import { Injectable } from '@angular/core';

import { EntityCacheDispatcher } from 'ngrx-data';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, map, first } from 'rxjs/operators';
import { tag } from 'rxjs-spy/operators/tag';

import { changeItemFactory as cif } from '@utils';
import { CompanyTax, EmployeeTax, TaxTypeCode } from '@model';
import { CompanyTaxService } from '@entity/entity-services/company-tax.service';
import { EmployeeTaxService} from '@entity/entity-services/employee-tax.service';
import { environment } from '@environment';

import {
    EMPTY_TAXES, ICompanyStateTaxes, ICompanyFederalTaxes, IEmployeeTaxes,
    TaxSavePackage, ITaxes, TaxBehaviorWrapper
} from './tax-models';

import { SessionService } from '@session';

@Injectable({ providedIn: 'root' })
export class TaxSaveHelperService {

    private saveTaxUrl = environment.apiRoot + '/save/taxes';

    constructor(
        private companyTaxService: CompanyTaxService,
        private employeeTaxService: EmployeeTaxService,
        private entityCacheDispatcher: EntityCacheDispatcher,
        private sessionService: SessionService
    ) { }

    /**
     * Merges Employee Taxes.
     * It looks at cached taxes and adds missing taxes, updates changed taxes and removes taxes that are no longer present.
     * The result is a package with the 3 CUD operations.
     *
     * @param {IEmployeeTaxes} employeesTaxes the collection of taxes with there associated employees.
     */
    public getEmployeeSavePack(employeesTaxes: IEmployeeTaxes): Observable<TaxSavePackage<EmployeeTax>> {
        return this.employeeTaxService.entities$.pipe(
            first(),
            map(allCachedEmployeeTaxes => {
                // Get all the current employee taxes
                const employeeTaxes: TaxBehaviorWrapper<EmployeeTax>[] = employeesTaxes.taxes;
                const employeeCachedTaxes = allCachedEmployeeTaxes.filter(e => e.employeeId === employeesTaxes.employeeLocation.id);

                const empSavePack = this.buildEmployeeSavePack(employeeTaxes, employeeCachedTaxes);

                return empSavePack;
            }),
            tag('getEmployeeSavePack')
        );
    }

    /**
     * Merges Company Federal Taxes.
     * It looks at cached taxes and adds missing taxes, updates changed taxes and removes taxes that are no longer present.
     * The result is a package with the 3 CUD operations.
     *
     * @param {ICompanyFederalTaxes} companyFederalTaxes the company federal taxes view model
     * @param {ITaxes} taxes all the taxes
     */
    public getCompanyFederalSavePack(companyFederalTaxes: ICompanyFederalTaxes, taxes: ITaxes)
        : Observable<TaxSavePackage<CompanyTax>> {
        return combineLatest(this.sessionService.currentCompany$,
            this.companyTaxService.entities$,
            this.employeeTaxService.entities$).pipe(
                first(),
                map(([company, allCachedCompanyTaxes]) => {
                    const companyTaxes = companyFederalTaxes.taxes;
                    const cachedCompanyFederalTaxes = allCachedCompanyTaxes.filter(e => e.taxTypeCode === TaxTypeCode.Federal);

                    // Get all available company taxes with updated ones taking priority then build the EIN map
                    const allCompanyTaxes = this.mergeAndFlattenAllCompanyTaxes(companyTaxes, taxes);
                    const einMap = this.buildTaxesEinMap(company.fein, allCompanyTaxes);

                    // Set the EINs of the taxes
                    this.setEinValues(companyTaxes, einMap);

                    const companySavePack = this.buildCompanySavePack(companyTaxes, cachedCompanyFederalTaxes);

                    return companySavePack;
                }),
                tag('getCompanyFederalSavePack')
            );
    }

    /**
     * Merges Company State Taxes.
     * It looks at cached taxes and adds missing taxes, updates changed taxes and removes taxes that are no longer present.
     * The result is a package with the 3 CUD operations.
     *
     * @param {ICompanyStateTaxes} companyStateTaxes the company federal taxes view model
     * @param {ITaxes} taxes all the taxes
     */
    public getCompanyStateSavePack(companyStateTaxes: ICompanyStateTaxes, taxes: ITaxes)
        : Observable<TaxSavePackage<CompanyTax>> {
        return combineLatest(this.sessionService.currentCompany$,
            this.companyTaxService.entities$).pipe(
                first(),
                map(([company, allCachedCompanyTaxes]) => {
                    const companyTaxes = companyStateTaxes.taxes;
                    const cachedCompanyStateTaxes = allCachedCompanyTaxes.filter(e =>
                        e.taxTypeCode !== TaxTypeCode.Federal && e.stateCode === companyStateTaxes.state);

                    // Get all available company taxes with updated ones taking priority then build the EIN map
                    const allCompanyTaxes = this.mergeAndFlattenAllCompanyTaxes(companyTaxes, taxes);
                    const einMap = this.buildTaxesEinMap(company.fein, allCompanyTaxes);

                    // Set the EINs of the taxes
                    this.setEinValues(companyTaxes, einMap);

                    const companySavePack = this.buildCompanySavePack(companyTaxes, cachedCompanyStateTaxes);

                    return companySavePack;
                }),
                tag('getCompanyStateSavePack')
            );
    }

    /**
     * Merges the Taxes ViewModel.
     * It looks at cached taxes and adds missing taxes, updates changed taxes and removes taxes that are no longer present.
     * The result is a package containing 2 packages (EE and Company taxes), each with the 3 CUD operations.
     *
     * @param {ITaxes} taxes all the taxes
     */
    public getAllTaxesSavePack(taxes: ITaxes): Observable<TaxSavePackage<EmployeeTax | CompanyTax>[]> {
        if (taxes == null || taxes === EMPTY_TAXES) {
            return of([]);
        }

        return combineLatest(this.sessionService.currentCompany$,
            this.companyTaxService.entities$,
            this.employeeTaxService.entities$).pipe(
                first(),
                map(([company, allCachedCompanyTaxes, allCachedEmployeeTaxes]) => {
                    // Get all the company and employee taxes
                    const allCompanyTaxes: TaxBehaviorWrapper<CompanyTax>[] = taxes.
                        companyFederalTaxes.taxes.concat(taxes.companyStateTaxes.reduce((acc, stateTaxes) => {
                            stateTaxes.taxes.forEach(t => {
                                acc.push(t);
                            });
                            return acc;
                        }, []));

                    const allEmployeeTaxes: TaxBehaviorWrapper<EmployeeTax>[] = taxes.employeeTaxes.reduce((acc, empTaxes) => {
                        empTaxes.taxes.forEach(t => {
                            acc.push(t);
                        });
                        return acc;
                    }, []);

                    // Ein map
                    const einMap = this.buildTaxesEinMap(company.fein, allCompanyTaxes);

                    // Set the EINs of the taxes
                    this.setEinValues(allCompanyTaxes, einMap);

                    const empSavePack = this.buildEmployeeSavePack(allEmployeeTaxes, allCachedEmployeeTaxes);
                    const companySavePack = this.buildCompanySavePack(allCompanyTaxes, allCachedCompanyTaxes);

                    return [companySavePack, empSavePack];
                }),
                tag('getAllTaxesSavePack')
            );
    }

    /**
     * Saves a collection of TaxSavePackage[] unless account is readOnly
     * @param packs An array of TaxSavePackages
     */
    public saveTaxPacks(packs: TaxSavePackage<CompanyTax | EmployeeTax>[]): Observable<boolean> {
      return this.sessionService.readOnly$.pipe(
        first(),
        concatMap(readOnly => readOnly ? of(true) : this.saveTaxPacks_(packs))
      );
    }

    private saveTaxPacks_(packs: TaxSavePackage<CompanyTax | EmployeeTax>[]): Observable<boolean> {
      const changes = <any[]>[];
      packs.forEach(pack => {
        if (!!pack.newTaxes && pack.newTaxes.length > 0) {
          changes.push(cif.add(pack.modelType, pack.newTaxes));
        }

        if (!!pack.updatedTaxes && pack.updatedTaxes.length > 0) {
          changes.push(cif.update(pack.modelType, pack.updatedTaxes));
        }

        if (!!pack.removedTaxes && pack.removedTaxes.length > 0) {
          // isRemoved is a special remove
          const actuallyRemoved = pack.removedTaxes.reduce((acc, element) => {
            if (element.isRemoved === false) {
              element.isRemoved = true;
              acc.push(element);
            }
            return acc;
          }, []);

          if (actuallyRemoved.length > 0) {
            changes.push(cif.update(pack.modelType, actuallyRemoved));
          }
        }
      });

      return changes.length === 0
        ? of(true)
        : this.entityCacheDispatcher.saveEntities({ changes }, this.saveTaxUrl).pipe(
          tag('saveTaxPacks'),
          map(() => true)
        );
    }

    private buildCompanySavePack(companyTaxes: TaxBehaviorWrapper<CompanyTax>[], cachedCompanyTaxes: CompanyTax[])
        : TaxSavePackage<CompanyTax> {
        const newTaxes: CompanyTax[] = [];
        const updatedTaxes: CompanyTax[] = [];
        const removeTaxes: CompanyTax[] = [];

        // Company taxes
        // Merge the taxes with there cached counterpart or create new cached taxes
        companyTaxes.forEach(companyTax => {
            const modifiedTax = companyTax.getTax();
            const cachedCompanyStateTax = cachedCompanyTaxes.find(t => t.id === modifiedTax.id);

            // General company tax rules
            this.interceptCompanyTaxEdit(modifiedTax, cachedCompanyStateTax);

            if (cachedCompanyStateTax) {
                // Update tax
                const uTax = Object.assign({}, cachedCompanyStateTax, modifiedTax, {
                    id: cachedCompanyStateTax.id,
                    companyId: cachedCompanyStateTax.companyId,
                    mtsTaxCode: cachedCompanyStateTax.mtsTaxCode
                });

                if (areDifferent(uTax, cachedCompanyStateTax, 'Company Tax')) {
                    updatedTaxes.push(uTax);
                }
            } else {
                // Add new tax
                newTaxes.push(modifiedTax);
            }
        });

        // Taxes that are no longer needed, removed them
        cachedCompanyTaxes
            .filter(t => companyTaxes.every(upt => upt.id !== t.id))
            .forEach(t => removeTaxes.push(t));

        return {
            modelType: 'CompanyTax',
            newTaxes: newTaxes,
            updatedTaxes: updatedTaxes,
            removedTaxes: removeTaxes
        };
    }

    private buildEmployeeSavePack(employeeTaxes: TaxBehaviorWrapper<EmployeeTax>[], cachedEmployeeTaxes: EmployeeTax[])
        : TaxSavePackage<EmployeeTax> {
        const newTaxes: EmployeeTax[] = [];
        const updatedTaxes: EmployeeTax[] = [];
        const removeTaxes: EmployeeTax[] = [];

        employeeTaxes.forEach(employeeTax => {
            const modifiedTax = employeeTax.getTax();
            const cachedEmployeeTax = cachedEmployeeTaxes.find(t => t.id === modifiedTax.id);

            // General company tax rules
            this.interceptEmployeeTaxEdit(modifiedTax, cachedEmployeeTax);

            if (cachedEmployeeTax) {
                // Update tax
                const uTax = Object.assign({}, cachedEmployeeTax, modifiedTax, {
                    id: cachedEmployeeTax.id,
                    employeeId: cachedEmployeeTax.employeeId,
                    companyId: cachedEmployeeTax.companyId,
                    mtsTaxCode: cachedEmployeeTax.mtsTaxCode
                });

                if (areDifferent(uTax, cachedEmployeeTax)) {
                    updatedTaxes.push(uTax);
                }
            } else {
                // Add new tax
                newTaxes.push(modifiedTax);
            }
        });

        // Taxes that are no longer needed, removed them
        cachedEmployeeTaxes
            .filter(t => employeeTaxes.every(upt => upt.id !== t.id))
            .forEach(t => removeTaxes.push(t));

        return {
            modelType: 'EmployeeTax',
            newTaxes: newTaxes,
            updatedTaxes: updatedTaxes,
            removedTaxes: removeTaxes
        };
    }

    private interceptCompanyTaxEdit(newVal: CompanyTax, oldVal: CompanyTax) {
        if (newVal.appliedForEin) { newVal.ein = ''; }

        newVal.isRemoved = false;
    }

    private interceptEmployeeTaxEdit(newVal: EmployeeTax, oldVal: EmployeeTax) {
        newVal.isRemoved = false;
    }

    private mergeAndFlattenAllCompanyTaxes(otherTaxes: TaxBehaviorWrapper<CompanyTax>[], taxes: ITaxes): TaxBehaviorWrapper<CompanyTax>[] {
        const allTaxesWithBehaviors = taxes.companyFederalTaxes.taxes.concat(taxes.companyStateTaxes.reduce((acc, stateTaxes) => {
            stateTaxes.taxes.forEach(t => {
                acc.push(t);
            });
            return acc;
        }, []));

        return allTaxesWithBehaviors.reduce((acc, tax) => {
            if (acc.every(t => t.id !== tax.id)) {
                acc.push(tax);
            }

            return acc;
        }, otherTaxes.slice());
    }

    private setEinValues(taxes: TaxBehaviorWrapper<CompanyTax>[], einMap: { [mtsCode: string]: IEinMapVal }): any {
        taxes.forEach(t => {
            const einVal = einMap[t.mtsTaxCode];
            if (einVal != null) {
                t.ein = einVal.ein;
                t.appliedForEin = einVal.appliedForEin;
            }
        });
    }

    private buildTaxesEinMap(fein: string, allCompanyTaxes: TaxBehaviorWrapper<CompanyTax>[])
        : { [mtsCode: string]: IEinMapVal } {

        const dependentTaxes: TaxBehaviorWrapper<CompanyTax>[] = [];

        // These taxes are the correct ein codes based on the useEINFromCode property
        const mainTaxEinMap: { [mtsCode: string]: IEinMapVal } = {};

        // Seed root EINs, either we know them or they do not depend on any other tax (root)
        allCompanyTaxes.forEach(companyTax => {
            if (companyTax.taxTypeCode === TaxTypeCode.Federal) {
                mainTaxEinMap[companyTax.mtsTaxCode] = { ein: fein, appliedForEin: false };
                return;
            }

            if (companyTax.behavior.useEINFromCode == null) {
                mainTaxEinMap[companyTax.mtsTaxCode] = { ein: companyTax.ein, appliedForEin: companyTax.appliedForEin };
                return;
            }

            dependentTaxes.push(companyTax);
        });

        // Now for dependent taxes, other taxes that are non root
        const dependentTaxEinMap: { [mtsCode: string]: IEinMapVal } = {};
        dependentTaxes.forEach(dependentTax => {
            const mainTax = mainTaxEinMap[dependentTax.behavior.useEINFromCode] || dependentTax;
            dependentTaxEinMap[dependentTax.mtsTaxCode] = { ein: mainTax.ein, appliedForEin: mainTax.appliedForEin };
        });

        return Object.assign(mainTaxEinMap, dependentTaxEinMap);
    }
}

interface IEinMapVal {
    ein: string;
    appliedForEin: boolean;
}
