import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

// Components & Directives
import { AccordionComponent } from './accordion';
import { AddressFormComponent } from './address/address-form.component';
import { AddressModalComponent } from './address/address-modal.component';
import { AmountOrPercentValueComponent } from './amount-or-percent-value/amount-or-percent-value.component';
import { ContactCustomerSupportComponent } from './contact-cust-support.component';
import { DatePickerPopupComponent } from './datepicker/datepicker-popup.component';
import { DateRangePayPeriodComponent } from './date-range-pay-period/date-range-pay-period.component';
import { DocumentsListComponent } from './docs/documents-list.component';
import { FilterComponent } from './filter/filter.component';
import { InputDateComponent } from './input/input-date.component';
import { InputSelectComponent, ButtonGroupComponent, ControlWrapComponent, MultiMaskDirective } from './input';
import { InputTextComponent } from './input/input-text.component';
import { PlaceholderComponent } from './placeholder.component';
import { ModelComponent } from './model.component';
import { NavBottomComponent } from './nav-bottom.component';
import { RadioGroupComponent, RadioButtonComponent, RadioGroupValueAccessorDirective } from './radio-group';
import { ReadMoreComponent } from './read-more/read-more.component';
import { ViewDocsComponent } from './docs/view-docs.component';

import { TextMaskModule } from 'angular2-text-mask';
import {
  ValidatePropertyDirective, AsyncValidatePropertyDirective, ValidationStatusDirective,
  ValidationFeedbackDirective, FormDirective
} from './validation/validate-property.directive';

// Pipes
import { AddressPipe } from './pipes/address.pipe';
import { CompanyKindPipe } from './pipes/company-kind.pipe';
import { FilterRemovedPipe } from './pipes/filter-removed.pipe';
import { FullNamePipe } from './pipes/fullname.pipe';
import { HouseholdFilerStatusPipe } from '@app/shared/pipes/household-filer-status.pipe';
import { PhonePipe } from './pipes/phone.pipe';
import { SsnPipe } from './pipes/ssn.pipe';
import { StateNamePipe } from './pipes/state-name.pipe';
import { SubjectExemptBooleanPipe } from './pipes/subject-exempt.pipe';
import { YesNoBooleanPipe } from './pipes/yes-no.pipe';

export const sharedModules = [
  CommonModule,
  FormsModule,
  ReactiveFormsModule,
  RouterModule,
  NgbModule,
  TextMaskModule
];

export const declarables = [
  AccordionComponent,
  AddressFormComponent,
  AddressModalComponent,
  AddressPipe,
  AmountOrPercentValueComponent,
  ButtonGroupComponent,
  CompanyKindPipe,
  ContactCustomerSupportComponent,
  ControlWrapComponent,
  DatePickerPopupComponent,
  DateRangePayPeriodComponent,
  DocumentsListComponent,
  FilterComponent,
  FilterRemovedPipe,
  FormDirective,
  FullNamePipe,
  HouseholdFilerStatusPipe,
  InputDateComponent,
  InputSelectComponent,
  InputTextComponent,
  MultiMaskDirective,
  ModelComponent,
  NavBottomComponent,
  ReadMoreComponent,
  PhonePipe,
  PlaceholderComponent,
  RadioButtonComponent,
  RadioGroupComponent,
  RadioGroupValueAccessorDirective,
  SsnPipe,
  StateNamePipe,
  SubjectExemptBooleanPipe,
  ValidatePropertyDirective, AsyncValidatePropertyDirective,
  ValidationStatusDirective, ValidationFeedbackDirective,
  ViewDocsComponent,
  YesNoBooleanPipe
];

/**
 * App modules and declarables (components, directives, pipes) consumed by other modules.
 * Other modules gain access to them by importing this.SharedModule.
 * It is eagerly loaded when the application starts.
 */
@NgModule({
  imports: [sharedModules],
  exports: [sharedModules, declarables],
  declarations: [declarables]
})
export class SharedModule { }
