import {
  ValidationContextBase, ValidationContext, ValidatorFactory, AsyncValidator,
  Validator, ValidatorFunctionContext
} from './validator';
import { EntityInfo } from '@entity';

export interface ValidatorConfig {
  name: string;
  context?: Partial<ValidationContextBase>;
  async?: boolean;
  condition?: string;
}

export interface ValidatorGroupConfig {
  group: string;
  validators: ValidatorConfigLike[];
}

export type ValidatorConfigLike = string | ValidatorConfig;

export type ValidatorOrGroupConfig = ValidatorConfigLike | ValidatorGroupConfig;

export interface PropertyValidationConfig {
  readonly displayName?: string;
  readonly required?: boolean;
  readonly minLength?: number;
  readonly maxLength?: number;
  readonly validators?: ValidatorOrGroupConfig[];
}

export interface PropertyValidationConfigMap {
  [propertyName: string]: PropertyValidationConfig;
}

export interface ModelValidationConfig {
  readonly extends?: string;
  readonly validators?: ValidatorOrGroupConfig[];
  readonly properties?: PropertyValidationConfigMap;
  readonly condition?: string;
}

export interface ModelValidationMetadataMap {
  [modelName: string]: ModelValidationConfig;
}

export function isValidatorGroupConfig(config: any): config is ValidatorGroupConfig {
  return !!config.group;
}

export class ValidatorMetadata {

  private config: ValidatorConfig;
  private condition: (context: ValidationContext, model: any, isClient: boolean, isServer: boolean) => boolean;

  constructor(config: ValidatorConfigLike, private validatorFactories: ValidatorFactory[], private groupConfig?: ValidatorGroupConfig) {
    if (typeof (config) === 'string') {
      this.config = { name: config };
    } else {
      this.config = config;
    }
  }

  get name(): string { return this.config.name; }

  get context() { return this.config.context; }

  get async() { return !!this.config.async; }

  get group() { return this.groupConfig && this.groupConfig.group; }

  evalCondition(context: ValidationContext): boolean {
    if (!this.config.condition) {
      return true;
    }

    try {
      /**
       * SECURITY: Safe because text passed to `Function` comes from our server, not the user
       */
      if (!this.condition) {
        const evalFunc = `function(context, model, isClient, isServer){ return ${this.config.condition} }`;
        this.condition = Function(`"use strict";return (${evalFunc})`)();
      }
      return this.condition(context, context.model, context.isClient, context.isServer);
    } catch (e) {
      const message = `Condition '${this.config.condition}' failed for validator '${this.name}'` +
        ` and model type '${context.modelType}' with error '${e.message}'`;
      throw new Error(message);
    }
  }

  getValidator() {
    const factory = this.validatorFactories.find(f => f.name === this.name);
    if (!factory) {
      throw new Error(`ValidatorMetadata: No factory found with name '${this.name}'.`);
    }

    const validator = factory.fn(this.group, this.context);
    if (this.async && !(validator instanceof AsyncValidator)) {
      throw new Error(`ValidationService: The validator with name '${this.name}' is not an asynchronous validator.`);
    } else if (!this.async && !(validator instanceof Validator)) {
      throw new Error(`ValidationService: The validator with name '${this.name}' is not a synchronous validator.`);
    }

    return validator;
  }
}

function asValidatorMetadata(validators: ValidatorOrGroupConfig[], validatorFactories: ValidatorFactory[]): ValidatorMetadata[] {
  return (validators || [])
    .map(config => {
      if (isValidatorGroupConfig(config)) {
        return (config.validators || []).map(validatorConfig => new ValidatorMetadata(validatorConfig, validatorFactories, config));
      } else {
        return [new ValidatorMetadata(config, validatorFactories)];
      }
    })
    .reduce((prev, curr) => [...prev, ...curr], []);
}

export class PropertyMetadata {

  private _validators: ValidatorMetadata[];

  constructor(readonly propertyName: string, private config: PropertyValidationConfig, private validatorFactories: ValidatorFactory[]) { }

  get displayName() { return this.config.displayName; }

  get validators() {
    if (this._validators) {
      return this._validators;
    }

    this._validators = asValidatorMetadata(this.config.validators || [], this.validatorFactories);
    // Add well-known default validators
    if (this.config.required && !this._validators.some(validator => validator.name === 'required' && !validator.group)) {
      this._validators.push(new ValidatorMetadata('required', this.validatorFactories));
    }
    if (this.config.minLength && !this._validators.some(validator => validator.name === 'minLength' && !validator.group)) {
      const config = {
        name: 'minLength',
        context: {
          minLength: this.config.minLength
        }
      };
      this._validators.push(new ValidatorMetadata(config, this.validatorFactories));
    }
    if (this.config.maxLength && !this._validators.some(validator => validator.name === 'maxLength' && !validator.group)) {
      const config = {
        name: 'maxLength',
        context: {
          maxLength: this.config.maxLength
        }
      };
      this._validators.push(new ValidatorMetadata(config, this.validatorFactories));
    }

    return this._validators;
  }
}

export class ModelMetadata {

  private _validators: ValidatorMetadata[];
  private _properties: PropertyMetadata[];
  private condition: (entityInfos: EntityInfo[], model: any, isClient: boolean, isServer: boolean) => boolean;

  constructor(private metadata: ValidationMetadata, readonly modelType: string,
    private config: ModelValidationConfig, private validatorFactories: ValidatorFactory[]) { }

  get validators() {
    if (this._validators) {
      return this._validators;
    }

    this._validators = [];
    if (this.config.extends) {
      const parent = this.metadata.getModelConfig(this.config.extends);
      if (parent) {
        this._validators = parent.validators;
      }
    }
    this._validators = [...this._validators, ...asValidatorMetadata(this.config.validators || [], this.validatorFactories)];
    return this._validators;
  }

  get properties() {
    if (this._properties) {
      return this._properties;
    }

    this._properties = [];
    if (this.config.extends) {
      const parent = this.metadata.getModelConfig(this.config.extends);
      if (parent) {
        this._properties = parent.properties;
      }
    }
    const properties = Object.keys(this.config.properties || {})
      .map(propertyName => new PropertyMetadata(propertyName, this.config.properties[propertyName], this.validatorFactories));
    this._properties = [...this._properties, ...properties];
    return this._properties;
  }

  getAsyncProperties(): PropertyMetadata[] {
    return this.properties.filter(property => property.validators.some(validator => validator.async));
  }

  evalCondition(entityInfos: EntityInfo[], model: any): boolean {
    const parent = this.config.extends && this.metadata.getModelConfig(this.config.extends);
    const condition = this.config.condition || parent && parent.config.condition;

    if (!condition) {
      return true;
    }

    try {
      /**
       * SECURITY: Safe because text passed to `Function` comes from our server, not the user
       */
      if (!this.condition) {
        const entityHelpers = [
          `function findEntity(id, type) {
            var ei = entityInfos.find(function(entityInfo) {
              return entityInfo.entity.id === id && entityInfo.entityType === type;
            });
            return ei && ei.entity;
          }`
        ].join('\n\n');
        const evalFunc = `function(entityInfos, model, isClient, isServer){ return ${condition}; ${entityHelpers} }`;
        this.condition = Function(`"use strict";return (${evalFunc})`)();
      }
      return this.condition(entityInfos, model, true, false);
    } catch (e) {
      const message = `Condition '${condition}' failed for model type '${this.modelType}' with error '${e.message}'`;
      throw new Error(message);
    }
  }
}

export class ValidationMetadata {

  private modelMetadata: ModelMetadata[];

  constructor(private metadata: ModelValidationMetadataMap[], private validatorFactories: ValidatorFactory[]) {
    validatorFactories.forEach((factory, ix) => {
      if (!factory.name) {
        throw new Error(`Validator factory #${ix} lacks a name`);
      }
      if (!factory.fn) {
        throw new Error(`Validator factory #${ix} lacks a validator function`);
      }
    });
  }

  getModelConfig(modelType: string): ModelMetadata {
    return this.getModelConfigs().find(config => config.modelType === modelType);
  }

  getModelConfigs(): ModelMetadata[] {
    return this.modelMetadata || (this.modelMetadata = this.getModelConfigsCore());
  }

  private getModelConfigsCore(): ModelMetadata[] {
    return this.metadata.map(metadata => {
      return Object.keys(metadata)
        .map(modelType => new ModelMetadata(this, modelType, metadata[modelType], this.validatorFactories));
    }).reduce((prev, curr) => [...prev, ...curr], []);
  }
}
