import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { environment } from '@environment';
import { ChatService } from '@core/services';
import { SessionService } from '@session';

const chatButtonId = environment.chatConfig.BUTTON_ID;

@Component({
  selector: 'sp-chat',
  templateUrl: './chat.component.html'
})
export class ChatComponent implements OnInit {

  chatAgent: any;
  // It is a lot simpler to use GetElementById but this is so more Angular-ish ;-)
  @ViewChild('online') onlineEl: ElementRef;
  @ViewChild('offline') offlineEl: ElementRef;

  constructor(private chatService: ChatService) {}

  ngOnInit() {
    this.chatService.chatAgent$.subscribe(chatAgent => {
      this.chatAgent = chatAgent;
      // WTF? Seems to work with just the code below
      if (!window._laq) {
        window._laq = [];
      }
      window._laq.push(
        () => {
          // Worked when we just called these two lines directly
          chatAgent.showWhenOnline(chatButtonId, this.onlineEl.nativeElement);
          chatAgent.showWhenOffline(chatButtonId, this.offlineEl.nativeElement);
        }
      );
    });
  }

  chatIconClicked() {
    if (this.chatAgent) {
      this.chatAgent.startChat(chatButtonId);
    }
  }

}
