import { Injectable } from '@angular/core';
import { createOkResponse$, Db, RequestInfo, } from './in-mem-service-utils';
import { Deduction } from '@model';
import { loadMockDeductions } from './master-data-deductions-loader';

@Injectable({ providedIn: 'root' })
export class InMemDeductionService {

  /** In-mem version of all deductions metadata */
  private deductionMetadata: Deduction[] = this.getDeductionTypeList();

  getRequest(req: RequestInfo, db: Db) {
    if (req.url && req.url.endsWith('/deductions')) {
      return createOkResponse$(req, this.deductionMetadata);
    }
  }
  // Post requests handled in InMemSaveEntitiesService (in-mem-save-entities.service.ts)

  private getDeductionTypeList(): Deduction[] {
    return loadMockDeductions();
  }
}
