import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';

import { filter } from 'rxjs/operators';

import { DashboardViewService } from '@app/dashboard/dashboard-view.service';
import { Company, companyKind } from '@model';
import { CompanyService } from '@entity';
import { SubSink, WellKnownRoutes } from '@core';

const COMPANY_STEP_INDEX = 1;

@Component({
  selector: 'sp-stepper',
  templateUrl: './stepper.component.html'
})
export class StepperComponent implements OnInit, OnDestroy {
  @Input() readOnly = false;
  company: Company;
  showStepper = false;
  private subs = new SubSink();

  private sourceSteps = [
    {
      name: 'About You',
      link: WellKnownRoutes.AboutYou,
      active: false,
      id: 'aboutYou',
      cssClass: {}
    },
    {
      name: 'Company Info',
      link: WellKnownRoutes.CompanyInfo,
      active: false,
      id: 'companyInfo',
      cssClass: {}
    },
    {
      name: 'Employees',
      link: WellKnownRoutes.Employees,
      active: false,
      id: 'employees',
      cssClass: {}
    },
    {
      name: 'Taxes',
      link: WellKnownRoutes.CompanyTaxInfo,
      active: false,
      id: 'companyTax',
      cssClass: {}
    },
    {
      name: 'Bank Account',
      link: WellKnownRoutes.BankAccount,
      active: false,
      id: 'bankInfo',
      cssClass: {}
    }
  ];

  steps = this.sourceSteps.map(s => ({ ...s }));

  constructor(
    private dashboardViewService: DashboardViewService,
    private companyService: CompanyService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.subs.sink = this.dashboardViewService.vm$.subscribe(vm => {
      this.steps.forEach(step => {
        step.cssClass = (vm as any)[step.id + 'Class'];
      });
    });

    this.subs.sink = this.router.events
      .pipe(
        filter(event => event instanceof NavigationEnd)
      )
      .subscribe((event: NavigationEnd) => {
        this.showStepper = false;
        const url = event.url;
        this.steps.forEach(step => {
          if (-1 < url.indexOf(step.link)) {
            this.showStepper = true;
            step.active = true;
          } else {
            step.active = false;
          }
        });
      });

    this.companyService.currentCompany$.subscribe(company => {
      this.company = company;
      this.steps[COMPANY_STEP_INDEX].name = `${companyKind(company)} Info`;
    });
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }
}
