import { Deduction, DeductionParameter, DeductionPlanType, DeductionPlanMatchOptionType
       } from '@model';
import { RawDeduction } from './master-data-deductions';
import { deductionModel } from './raw-deductions';
import { deductionGroups } from './raw-deductions-categories';

export function loadMockDeductions(): Deduction[] {
  const res: Deduction[] = [];
  const raw: RawDeduction[] = deductionModel;

  const distinctCodeTypes = Array.from(new Set(raw.map(i => i.codeTypeId)));
  distinctCodeTypes.map(dCodeId => {
    res.push(fixData(buildDeduction(dCodeId, deductionModel)));
  });
  return res;
}

function fixData(d: Deduction): Deduction {
  if ((50 <= d.codeTypeId && d.codeTypeId <= 59) ||  // Fix all Posttax deductions to be post-tax
      (30 <= d.codeTypeId && d.codeTypeId <= 34)     // Fix. Medical, Vision, Dental, LifeInsurance (33), Misc (34=) post-tax
     ) {
    d.deductionPlanTaxOptionCode = DeductionPlanType.POSTTAX;
  }
  if (d.codeTypeId === 23) {
    d.codeType = 'Miscellaneous Pre-Tax';  // add Pre-Tax to name
  }
  if (d.codeTypeId === 34) {
    d.codeType = 'Miscellaneous Post-Tax'; // add Post-Tax to name
  }
  return d;
}

function buildDeduction(codeTypeId: number, raw: RawDeduction[]): Deduction {
  const paramRows = raw.filter(d => d.codeTypeId === codeTypeId);
  const f = paramRows[0];

  const htmlDefinition = deductionGroups.find(it => it.codeTypeId === codeTypeId).htmlDescription;

  // Retrieve dfault description / abbreviation
  const description = raw.find(it => it.codeTypeId === codeTypeId).planDescription;
  const abbreviation = raw.find(it => it.codeTypeId === codeTypeId).planName;

  const ded: Deduction = {
    codeGroupId: f.codeGroupId,
    codeGroup: f.codeGroup,
    codeTypeId: f.codeTypeId ,
    codeType: f.codeType,
    definition: (htmlDefinition || '').trim(),
    defaultAbbreviation: (abbreviation || '').trim(),
    defaultDescription: (description || '').trim(),
    canChangeAbbreviation: canChangeAbbreviation(f),
    canChangeDescription: true,
    hasPercent: f.hasPercent,
    retireAddon: f.retireAddon,
    hasGoal: f.hasGoal,
    employerOnly: f.employerOnly,
    isSure401K: f.isSure401K,
    isCatchUp: f.isCatchUp,
    planCode: f.deductionPlanCode,
    deductionPlanTaxOptionCode: toDeductionPlanType(f.deductionPlanTaxOptionCode),
    deductionPlanMatchOptionCode: toDeductionPlanMatchOptionType(f.deductionPlanMatchOptionCode),

    parameters: paramRows.map(buildDeductionParameter)
  };
  return ded;
}

function canChangeAbbreviation(f: RawDeduction): boolean {
  // Can change all abbreviations except:
  //   All retirement plan deductions, HSA, 401k Loan Repayment.
  if (f.codeGroupId === 1 ||  // retirement plans
      f.codeGroupId === 6 ||  // retirement plans catch-ups
      f.codeGroupId === 7 ||  // HSA
      f.codeTypeId === 52     // 401k Loan Repayment.
     ) {
    return false;
  }
  return true;
}

function buildDeductionParameter(p: RawDeduction): DeductionParameter {
  const res: DeductionParameter = {
    cpdCode: p.cpdCode,
    baseAccm: p.baseAccm,
    codeTextDescription: p.codeTypeDesc,
    taxDeduction: p.taxDed,
    name: p.name,
    description: p.description,
    howType: p.howType,
    dedRptSw: p.dedRptSw,
    inNet: p.inNet,
    checkPrnSw: p.checkPrnSw,
    isDisplayed: p.isDisplayed,
    isActive: p.isActive,
    oneTimeDeduction: p.oneTimeDeduction,
    isEmployerDisplayed: p.isEmployerDisplayed,
    deductionPlanGroupCode: p.deductionPlanGroupCode,
    deductionPlanGroup: p.deductionPlanGroup,
    parameterName: p.parameterName,
    displayName: p.displayName,
    defaultDeductionRate: p.defaultDedRate
  };
  return res;
}

function toDeductionPlanType(val: string): DeductionPlanType {
  switch (val) {
    case 'PRETAX': return DeductionPlanType.PRETAX;
    case 'POSTTAX': return DeductionPlanType.POSTTAX;
    case 'NOEEDEDUCT': return DeductionPlanType.NOEEDEDUCT;
    default: return null;
  }
}
function toDeductionPlanMatchOptionType(val: string): DeductionPlanMatchOptionType {
  switch (val) {
    case 'TWOTIER': return DeductionPlanMatchOptionType.TWOTIER;
    case 'NONELECTIV': return DeductionPlanMatchOptionType.NONELECTIV;
    case 'NOORSINGLE': return DeductionPlanMatchOptionType.NOORSINGLE;
    case 'SNGLORTWO': return DeductionPlanMatchOptionType.SNGLORTWO;
    case 'NOMATCH': return DeductionPlanMatchOptionType.NOMATCH;
    default: return null;
  }
}
function toBool(val: string): boolean {
  return val === 'Y' ? true : val === 'N' ? false : null;
}
