import { Injectable } from '@angular/core';
import { Action, Store } from '@ngrx/store';

import { EntityCache, EntityCollectionServiceElementsFactory, EntityCacheDispatcher } from 'ngrx-data';

import { IdGeneratorService } from '@core/services/id-generator.service';
import { LoggerService } from '@core/services/logger.service';

@Injectable({ providedIn: 'root'})
export class EntityHelperService {
  constructor(
    public collectionServiceElementsFactory: EntityCollectionServiceElementsFactory,
    public entityCacheDispatcher: EntityCacheDispatcher,
    private idGenerator: IdGeneratorService,
    public logger: LoggerService,
    private store: Store<EntityCache>,
  ) { }

  /** Dispatch any action to the store */
  dispatch(action: Action) {
    this.store.dispatch(action);
  }

  generateId() {
    return this.idGenerator.generateId();
  }

}
