import { Injectable, NgModule } from '@angular/core';
import { ActivatedRouteSnapshot, CanDeactivate, RouterStateSnapshot, Routes } from '@angular/router';

import { Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';

import { AuthService } from '@core/services/auth-services/auth.service';
import { WellKnownRoutes } from '@core/well-known-routes';

@Injectable({ providedIn: 'root' })
export class AuthFailedGuard implements CanDeactivate<any> {

  canDeactivate(
    component: any,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState?: RouterStateSnapshot): Observable<boolean> {
    return this.authService.isSignedIn$.pipe(
      first(),
      map(isSignedIn => {
        if (isSignedIn) {
          this.authService.signout();
        }
        const isSignedOutPage = nextState && nextState.url === WellKnownRoutes.SignedOut;
        return isSignedOutPage;
      }),
    );
  }
  constructor(private authService: AuthService) {}
}
