import { Injectable } from '@angular/core';
import { createOkResponse$, createBadRequestResponse$, Db, RequestInfo } from './in-mem-service-utils';
import { getGuidComb } from '@core/guid-fns';
import { Envelope, EnvelopeStatus, EnvelopeSigner } from '@app/model';

@Injectable({providedIn: 'root'})
export class InMemEsignService {
  getEsignUrl(req: RequestInfo, db: Db) {
    const body = {
      // links expire after 5 minutes, so this will redirect right away.
      // url:
      //   'https://demo.docusign.net/Signing/StartInSession.aspx?t=f9e7440a-c66e-42e9-bd12-8a5199cb37c1'

      url: '/assets/documock.html'
    };
    return createOkResponse$(req, body);
  }

  postEsignSend(req: RequestInfo, db: Db) {
    const companyId = req.url.split('/').pop();

    let envelope = <Envelope> from(db.Envelope).find(e => true);
    if (!envelope) {
      envelope = {
        id: getGuidComb(),
        companyId: companyId,
        eSignEnvelopeId: getGuidComb(),
        status: EnvelopeStatus.sent,
        createdDate: new Date(),
        statusDate: new Date(),
        isWetSign: false,
        isRemoved: false
      };
      db.Envelope.push(envelope);
    }
    envelope.status = EnvelopeStatus.sent;
    envelope.statusDate = new Date();
    envelope.eSignEnvelopeId = getGuidComb();

    // envelopeSigner objects created on the server
    // let signer1 = <EnvelopeSigner> from(db.EnvelopeSigner).find(e => true);
    // { signerEmail, signerFirstName, signerLastName, signerTitle, spouseEmail, spouseFirstName, spouseLastName }

    const signers = (<any>req.req).body.signers as EnvelopeSigner[];
    if (signers && signers.length) {
      signers.forEach(sig => {
        const old = db.EnvelopeSigner.find(e => e.id === sig.id);
        if (old) {
          Object.assign(old, sig);
        } else {
          db.EnvelopeSigner.push(sig);
        }
      });
    }

    const body = {
      status: envelope.status,
      envelope: envelope
    };
    return createOkResponse$(req, body);

    function from(collection: { companyId: string }[]) {
      return collection ? collection.filter(c => c.companyId === companyId) : [];
    }
  }
  /**
   * Mark completable envelopes as completed.
   * See esign.service.markEnvelopesSigned
   */
   postMarkEnvelopesCompleted(req: RequestInfo, db: Db) {
    const originalRequest = req.req as any;
    const companyId: string = (originalRequest.body || {}).companyId;
    let changed = false;
    if (companyId) {
      db['Envelope'] = db['Envelope']
        .map((e: Envelope) => canComplete(e) ? complete(e) : e );
      const body = changed ? {companyId, id: 'complete'} : undefined;
      return createOkResponse$(req, body);
    } else {
      return createBadRequestResponse$(req);
    }

    // NB: Guessing at this logic
    function canComplete(e: Envelope) {
      return e.companyId === companyId && !e.isRemoved && (
        e.status === EnvelopeStatus.delivered ||
        e.status === EnvelopeStatus.draft ||
        e.status === EnvelopeStatus.local ||
        e.status === EnvelopeStatus.sent
      );
    }

    // NB: Guessing at this logic
    function complete(e: Envelope) {
      changed = true;
      e.status = EnvelopeStatus.completed;
      e.statusDate = new Date();
      return e;
    }
  }
}
