export interface FirstLastType {
  firstName: string;
  lastName: string;
}

/** Sort Comparer to sort the entity collection by its name property */
export function sortByName(a: { name: string }, b: { name: string }): number {
  try {
    return a.name.localeCompare(b.name);
  } catch (error) {
    return 0;
  }
}

/** Sort Comparer to sort the entity collection by its name property */
export function sortByFirstLast(a: FirstLastType, b: FirstLastType): number {
  try {
    const first = a.firstName.localeCompare(b.firstName);
    return first === 0 ? a.lastName.localeCompare(b.lastName) : first;
  } catch (error) {
    return 0;
  }
}
