import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanActivateChild,
  RouterStateSnapshot
} from '@angular/router';

import { Observable } from 'rxjs';

import { AccountStatus } from '@model';
import { AccountStatusGuardHelper, AccountStatusGuardFns, okRoute } from './account-status-guard-helper';
import { WellKnownRoutes } from '@core';

const guardName = 'SinglePageStatusGuard';

/**
 * Guard single-page route which can be activated only when the account's status permits it.
 * For example, if the route-to-activate is '/abandoned',
 * only an account whose status is "Abandoned" may activate it.
 * Not for routes with child routes.
 * Not for data entry page routes.
 */
@Injectable({ providedIn: 'root' })
export class SinglePageStatusGuard implements CanActivate, CanActivateChild {
  constructor(private accountStatusGuardHelper: AccountStatusGuardHelper) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.ifAccountStatusAllows$(state);
  }

  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    return this.ifAccountStatusAllows$(state);
  }

  private ifAccountStatusAllows$(state: RouterStateSnapshot) {

    // Start with route fns that reject activation of all routes.
    const adminRoutes = { ... this.accountStatusGuardHelper.adminBaseRoutes };
    const regularRoutes = { ... this.accountStatusGuardHelper.regularBaseRoutes };

    // Decide which account status(es) allow activation of the pending route.
    // For example, if the route-to-activate is '/abandoned',
    // only an account whose status is "Abandoned" may activate it.
    let okStatuses: string[] = [];

    let url = state.url;

    // strip off the query string (if any)
    const qi = url.indexOf('?');
    if (qi > -1) {
      url = url.substr(0, qi);
    }

    switch (url) {
      case WellKnownRoutes.Abandoned:
        okStatuses = [AccountStatus.Abandoned];
        break;
      case WellKnownRoutes.Documents:
        okStatuses = [AccountStatus.Docs, AccountStatus.Test];
        adminRoutes[AccountStatus.Open] = okRoute; // special case
        break;
      case WellKnownRoutes.Enrolled:
        okStatuses = [AccountStatus.Enrolled];
        break;
      case WellKnownRoutes.Processing:
        okStatuses = [AccountStatus.AutoMigrate, AccountStatus.Operations, AccountStatus.Migrating];
        break;
    }

    // This step allows activation of the pending route for "ok statuses"
    okStatuses.forEach(status => {
      adminRoutes[status] = okRoute;
      regularRoutes[status] = okRoute;
    });

    return this.accountStatusGuardHelper.accountStatusAllows$(
      guardName,
      adminRoutes,
      regularRoutes,
      state
    );
  }
}
