import { Directive, ElementRef, EventEmitter, HostListener, Input, OnInit, Output } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { MaskHelper, MaskMatch } from './multi-mask-helper';

// TODO:
// 1) hook the ngModel rather than a custom binned dataValue input?

@Directive({
    providers: [{ provide: NG_VALUE_ACCESSOR, useExisting: MultiMaskDirective, multi: true }],
    selector: '[spMultiMask]'
})
export class MultiMaskDirective implements OnInit, ControlValueAccessor {

    @Input()
    public masks: string[];

    @Input()
    private dataValue: string;

    @Input()
    public disabledText: string;

    @Output()
    public onValueChange = new EventEmitter<string>();

    private get value() {
        return this.nativeElement.value;
    }

    private set value(val) {
        this.nativeElement.value = val;
    }

    @Input()
    public set disabled(value: boolean) {
        this.nativeElement.disabled = value;
        if (value && this.disabledText != null) {
            this.value = this.disabledText;
        } else {
            this.value = this.dataValue || '';
        }
    }

    public get disabled() {
        return this.nativeElement.hasAttribute('disabled');
    }

    private get nativeElement(): HTMLInputElement {
        return this.elementRef.nativeElement;
    }

    private maskHelper: MaskHelper;

    private maskOptions = {
        format: '',
        guideChar: ''
    };

    private prevValue: string;
    private maskview: HTMLInputElement;
    private currentMask: string;

    private onTouchedCallback: () => void = () => { };
    private onChangeCallback: (_: any) => void = () => { };

    constructor(private elementRef: ElementRef) {
        this.maskHelper = new MaskHelper();
    }

    public ngOnInit(): void {

        if (this.masks != null) {
            this.currentMask = this.masks.length > 0 ? this.masks.sort((a, b) => a.length - b.length)[0] : '';

            // add element behind to show mask, (instead of placeholder which disappears)
            const cln = this.nativeElement.ownerDocument.createElement('input');
            cln.className = 'form-control maskview';
            this.nativeElement.parentElement.appendChild(cln);
            cln.value = this.currentMask;
            this.maskview = cln;
        }

        if (this.disabled === false) {
            this.nativeElement.value = this.dataValue || '';
        }
    }

    @HostListener('input', ['$event'])
    public onInputChanged(event: any): void {
        const val = this.value;

        // Only run the mask change logic if we have any masks
        if (this.disabled === false && this.masks != null && this.masks.length > 0) {
            // The value has been cleared so we reset everything
            if (val == null || val === '') {
                this.resetMaskState();
                this.setNewValue('');
                this.setMaskMatch({ mask: this.currentMask || this.masks[0], isExactMatch: false, parsedVal: ''});
                return;
            }

            // Validate the new value with given masks
            const maskMatch = this.maskHelper.getClosestMask(this.value, this.masks, this.currentMask);

            if (maskMatch.mask == null) {
                // We could not validate the value so keep the prev valid one or empty
                this.value = this.prevValue || '';
            }

            if (maskMatch.mask != null) { // && this.value !== maskMatch.parsedVal) {
                this.currentMask = maskMatch.mask;
                let cval = maskMatch.parsedVal;
                const mask = maskMatch.mask;
                // append the next mask char if it is a literal
                if (mask.length > cval.length && this.prevValue && this.prevValue.length <= cval.length) {
                    const ch = mask.charAt(cval.length);
                    if (this.maskHelper.isLiteral(ch)) {

                        // if there are multiple masks, only append the literal if it appears
                        // at the same position in all the possible masks.
                        if (this.masks.every(m => m.length <= cval.length || mask.charAt(cval.length) === ch)) {
                            cval += ch;
                        }
                    }
                }
                this.value = cval;
            }
            this.setMaskMatch(maskMatch);
        }

        this.setNewValue(this.value);
    }

    public writeValue(value: string) {
        if (value == null) { return; }

        if (value === '') {
            this.resetMaskState();
            this.setNewValue(value);
            return;
        }

        if (this.disabled === false && this.masks != null && this.masks.length > 0) {
            const maskMatch = this.maskHelper.getClosestMask(value, this.masks, this.currentMask);
            if (maskMatch.mask != null) {
                this.setNewValue(maskMatch.parsedVal);
                this.setMaskMatch(maskMatch);
            }
        }
    }

    public registerOnChange(fn: (_: any) => void) { this.onChangeCallback = fn; }
    public registerOnTouched(fn: () => void) { this.onTouchedCallback = fn; }

    private resetMaskState() {
        this.maskOptions.format = '';
    }

    private setNewValue(newVal: string) {
        this.prevValue = newVal;
        this.dataValue = newVal;

        this.onChangeCallback(this.dataValue);
        this.onValueChange.emit(this.value);
    }

    /** set the value of the maskview element to the mask, with the filled elements
     * replaced with spaces, so that only the unfilled portion of the mask is shown.
     * e.g. mask = '####', parsedVal = '12' => maskview.value = '  ##'
     */
    private setMaskMatch(maskMatch: MaskMatch) {
        if (maskMatch.mask === null) {
            return; // leave displayed mask as-is
        }
        const filledLength = maskMatch.parsedVal && maskMatch.parsedVal.length || 0;
        let mask = maskMatch.mask;
        mask = ' '.repeat(filledLength) + mask.substring(filledLength);
        this.maskview.value = mask;
   }
}
