import { CompanyEntityBase } from './company-entity-base';
import { Earning } from './earning';

export class CompanyEarning extends CompanyEntityBase {
  earningCode: string;
  name: string;
  description: string;
  isRemoved = false;

  constructor(earning: Earning) {
    super();
    this.earningCode = earning.code;
    this.description = earning.description;
    this.name = earning.name;
  }
}
