import { NgModule, ModuleWithProviders } from '@angular/core';
import { VALIDATION_ENTITY_METADATA } from './validation.service';
import { ModelValidationMetadataMap } from './validation-metadata';
import { defaultValidatorProviders } from './default-validators';

export interface ValidationModuleConfig {
  validationMetadata: ModelValidationMetadataMap[];
}

@NgModule({
  providers: [
    defaultValidatorProviders
  ]
})
export class ValidationModule {
  static forRoot(config: ValidationModuleConfig): ModuleWithProviders {
    return {
      ngModule: ValidationModule,
      providers: [
        {
          provide: VALIDATION_ENTITY_METADATA,
          useValue: config.validationMetadata || [],
          multi: true
        }
      ]
    };
  }
}
