import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

import { environment } from '@environment';

import { TaxRate } from './tax-models';

/**
 * Gets tax rate data
 */
@Injectable({ providedIn: 'root' })
export class TaxRatesService {
    private cachedTaxRates: TaxRate[] = null;

    constructor(private http: HttpClient) { }

    getTaxRates(): Observable<TaxRate[]> {
        if (this.cachedTaxRates != null) {
            return of(this.cachedTaxRates);
        }

        const url = environment.spApiRoot + '/taxapi/taxes/suirates';
        return this.http.get<TaxRate[]>(url).pipe(
            map(e => {
                this.cachedTaxRates = e;
                return e;
            }));
    }
}
