import { Injectable } from '@angular/core';
import { environment } from '@environment';

import { combineLatest, EMPTY, interval, Observable, of, ReplaySubject } from 'rxjs';
import { filter, map, skipWhile, take } from 'rxjs/operators';

import { getFullName } from '@core/utils';
import { LoggerService } from '@core/services/logger.service';
import { SessionService } from '@session';

const chatConfig = environment.chatConfig;

declare global {
  interface Window {
    liveagent?: any;
    _laq?: any[];
  }
}

let chatInstalled = false;

@Injectable({
  providedIn: 'root'
})
export class ChatService {

  private chatAgentSubject = new ReplaySubject<any>(1);
  /** Terminating observable that emits the chat's "liveagent" when it becomes available and a regular user is signed in. */
  chatAgent$ = this.chatAgentSubject.asObservable();

  constructor(private logger: LoggerService, private sessionService: SessionService) {
    const chatAgentLoaded$ = this.loadChatLibrary();

    const currentUser$ = this.sessionService.currentUser$.pipe(
      // only consider a "real" user (not an Admin)
      filter(currentUser => currentUser != null)
    );

    combineLatest(
      currentUser$,
      chatAgentLoaded$
    )
    // Chat liveagent only allows one initialization with one email
    .pipe(take(1))
    .subscribe(
      ([firstUser, agent]) => {
        if (agent) {
          agent.init(chatConfig.CHAT_URL, chatConfig.DEPLOYMENT_ID, chatConfig.ORGANIZATION_ID);
          const name = getFullName(firstUser);
          const email = firstUser.email;
          let msg: string;
          if (email) {
            agent.addCustomDetail('Email', email, true);
            msg = `Chat enabled for user "${name}" with email "${email}".`;
          } else {
            msg = `Chat enabled for user "${name}" who does not have email.`;
          }
          this.chatAgentSubject.next(agent);
          if (chatConfig.TRACING) {
            this.logger.log(msg);
          }
        } else {
          this.logger.error('Chat liveagent did not load properly. Look for error.');
        }
        this.chatAgentSubject.complete(); // release all subscribers
      }
    );
  }

  /**
   * Load the Salesforce Liveagent  (via dynamic script)
   */
  loadChatLibrary() {
    // Gets the URL from environment
    const liveagentScriptUrl = chatConfig.LIVEAGENT_SCRIPT_URL;

    // no library if no key
    if (!liveagentScriptUrl) {
      return EMPTY;
    }

    if (!chatInstalled) {
      chatInstalled = true; // only try to install it once
      // load after the page's first script element
      const ref = window.document.getElementsByTagName('script')[0];
      const script = window.document.createElement('script');
      script.src = liveagentScriptUrl;
      ref.parentNode.insertBefore(script, ref);
    }

    // RxJS coolness: iterate until liveagent arrives or 15 x 1 second passes
    return interval(1000).pipe(
      skipWhile(tick => window.liveagent == null && tick < 15),
      take(1),
      map(_ => {
        const agent = window.liveagent;
        if (agent) {
          if (chatConfig.TRACING) {
            this.logger.log('Chat script loaded');
          }
        } else {
          this.logger.error('Chat load timed out; look in console for script load error.');
        }
        return agent;
      })
    );
  }
}
