import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-failed',
  templateUrl: './failed.component.html'
})
export class FailedComponent implements OnInit {
  constructor(private router: Router) { }

  ngOnInit() {
    // Never let it fail on application launch
    // or user won't have a chance to recover with a browser refresh.
    if ((this.router as any).navigationId === 1) {
      this.router.navigate([WellKnownRoutes.Dashboard], { replaceUrl: true });
    }
  }
}
