import {
  AfterViewInit, Component, EventEmitter, ElementRef, Input,
  OnInit, OnDestroy, Output, ViewChild, Optional, ChangeDetectorRef
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, NgModel } from '@angular/forms';

import { ExitType, ExitEvent } from '@shared/interfaces';
import { ModelComponent } from '@shared/model.component';
import { InputMaskCatalog } from '@shared/input/input-mask-catalog';
import { InputMaskConfig } from '@shared/input/input-mask-config';
import { TextMaskConfig } from 'angular2-text-mask';

@Component({
  selector: 'sp-input',
  templateUrl: './input-text.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: InputTextComponent,
      multi: true
    }
  ],
})
export class InputTextComponent implements ControlValueAccessor, OnInit, AfterViewInit, OnDestroy {
  static nameCounter = 0;

  @Input() append: string;
  @Input() autofocus?: boolean;
  @Input() classNames: string;
  @Input() disabled = false;
  @Input() formatString = 'yyyy-MM-dd';

  @Input() set group(value: string) { this._group = value; }
  private _group: string;

  @Input() guide?: boolean;
  @Input() keepCharPositions?: boolean;
  @Input() label: string;
  // Mask properties
  @Input() mask: Array<string | RegExp> | ((raw: string) => Array<string | RegExp>) | false = false; // false => no mask

  @Input() set model(value: any) { this._model = value; }
  private _model: any;

  @Input() set modelType(value: string) { this._modelType = value; }
  private _modelType: string;

  @Input() name: string;
  @Input() placeholder = '';
  @Input() placeholderChar?: string;
  @Input() prepend: string;
  @Input() propertyName: string;
  @Input() showMask?: boolean;
  @Input() tooltip = false;
  @Input() type = 'text';

  @ViewChild('child') child: ElementRef;

  @Output() blur = new EventEmitter();
  @Output() focus = new EventEmitter();
  @Output() onChange = new EventEmitter<string>();
  @Output() onExit = new EventEmitter<ExitEvent>();
  @Output() onTouched = new EventEmitter();

  private accessorOnChange = (...args: any[]) => { };
  private accessorOnTouched = (...args: any[]) => { };
  private currentValue: string;
  private editing: boolean;
  private enteringEdit: boolean;
  private inputEl: HTMLInputElement;
  private isDestroyed = false;
  public maskConfig: InputMaskConfig;
  private  _maskType: string;
  open = false;
  private originalValue: string;
  private previousValue: string = undefined;
  textMask: TextMaskConfig = {mask : false};

  constructor(
    private inputMasks: InputMaskCatalog,
    private changeDetector: ChangeDetectorRef,
    @Optional() public modelContext?: ModelComponent) {
  }

  ngOnInit() {
    this.maskConfig = this.inputMasks.getMaskForType(this.maskType);
    if (this.maskConfig) {
      this.inputMasks.applyConfig(this, this.maskConfig);
    }
    this.name = (this.name || '') + '$' + InputTextComponent.nameCounter++;
    // this.classNames = 'form-control ' + (this.classNames || '');
  }

  ngAfterViewInit() {
    this.inputEl = this.child.nativeElement;
    if (this.autofocus) {
      this.inputEl.focus();
    }
  }

  /** Get the latest data-bound group. If not bound, get group from modelContext. */
  get group(): string {
    return this._group || (this.modelContext && this.modelContext.group);
  }

  /** Get the latest data-bound model. If not bound, get model from modelContext. */
  get model(): any {
    return this._model || (this.modelContext && this.modelContext.model);
  }

  /** Get the latest data-bound model type. If not bound, get model type from modelContext. */
  get modelType(): string {
    return this._modelType || (this.modelContext && this.modelContext.modelType);
  }

  get maskType(): string {
    return this._maskType;
  }

  @Input()
  set maskType(value: string) {
    this._maskType = value;
    if (!value) {
      this.textMask = {mask : false};
      this.detectChanges();
      return;
    }
    this.maskConfig = this.inputMasks.getMaskForType(this.maskType);
    if (this.maskConfig) {
      this.inputMasks.applyConfig(this, this.maskConfig);
      this.detectChanges();
    }
  }

  ngOnDestroy() {
    this.isDestroyed = true;
    this.exit('destroyed');
  }

  writeValue(obj: any): void {
    this.currentValue = obj;
  }

  registerOnChange(fn: any): void {
    this.accessorOnChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.accessorOnTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
  private detectChanges() {
    if (!this.isDestroyed) {
      this.changeDetector.detectChanges();
    }
  }

  /**
   * Emit onExit after user exits control
   * @param exitType {ExitType} How the user exited the control
   */
  exit(exitType: ExitType) {
    // Auto-trim on exit; could add a NoTrim option if necessary.
    const currentValuePreTrim = this.getValue();
    const currentValue = typeof currentValuePreTrim === 'string'
      ? (currentValuePreTrim || '').trim()
      : currentValuePreTrim;
    if (currentValuePreTrim !== currentValue) {
      // update and announce the change;
      this.onInput(currentValue);
    }
    this.onExit.emit({ exitType, isChanged: this.originalValue !== currentValue });
    this.originalValue = currentValue;
    this.detectChanges();
  }

  /** User pressed escape. Restores original value and exits */
  escaped() {
    // undo change
    const currentValue = this.inputEl.value;
    if (currentValue !== this.originalValue) {
      this.onInput(this.originalValue);
    }
    this.exit('escapeKey');
  }

  onInput(value: string) {
    if (this.originalValue == null) {
      this.originalValue = this.getValue();
    }
    if (this.maskConfig) {
      value =  this.maskConfig.toVm(value, this.modelType);
    }

    this.setValue(value);
    this.accessorOnChange(this.getValue());
    this.onChange.emit(this.getValue());
    if (this.modelContext) {
      this.modelContext.notifyOfModelChange();
    }
    this.detectChanges();
  }

  onFocus(): void {
    // Prevent Angular for raising errors due to modified binding values between detection cycles
    // by performing manual change detection during edit operations
    this.changeDetector.detach();
    this.focus.emit();
    this.enteringEdit  = true;
  }

  onBlur(): void {
    this.changeDetector.reattach();
    this.editing  = false;
    this.enteringEdit = false;
    this.accessorOnTouched();
    this.onTouched.emit();
    this.blur.emit();
    this.exit('blur');
    this.open = false;
  }

  getValue(): string {
    if (this.maskConfig) {
      const formattedValue = this.maskConfig.toView(this.getValueInternal(), this.editing, this.formatString);
      if (this.enteringEdit) {
          this.editing = true; // Set editing after first value read
          this.enteringEdit = false;
      }
      return formattedValue;
    }
    const val = this.getValueInternal();
    return val != null ? val.toString() : null;
  }

  private getValueInternal(): string {
    if (this.model && this.modelType && this.propertyName) {
      return this.model[this.propertyName];
    }

    return this.currentValue;
  }

  setValue(value: string): void {
    if (this.model && this.modelType && this.propertyName) {
      this.model[this.propertyName] = value;
    }

    this.currentValue = value;
  }
}
