import { Inject, Optional } from '@angular/core';
import { HttpRequest, HttpResponse, HttpErrorResponse } from '@angular/common/http';

import {
  HttpClientBackendService, InMemoryBackendConfig, InMemoryBackendConfigArgs, InMemoryDbService, RequestCore,
} from 'angular-in-memory-web-api';
import { XhrFactory } from '@angular/common/http';

import { asapScheduler, Observable, throwError } from 'rxjs';
import { catchError, observeOn, map, tap } from 'rxjs/operators';

import { Company } from '@model';
import { Db } from './db';
import { InMemoryDataService } from './in-memory-data.service';
import { createCompanyEtag, parseCompanyEtag } from './in-mem-service-utils';
import { requiresConcurrencyControl } from '@core/http-interceptors/concurrency-http-interceptor';

/**
 * Override InMemoryWebApi HttpClientBackendService
 * Mimics server-side etag/precondition header concurrency behavior documented in
 * https://docs.google.com/document/d/1WzMeGVaVcxp3q0QNV0dC0ZLaUAZal39DnErQ0hbdwjM/edit#
 * Companion to client app's ConcurrencyHttpInterceptor
 */
export class HttpServerConcurrencyInterceptor extends HttpClientBackendService {

  constructor(
    @Inject(InMemoryDbService) protected inMemDataService: InMemoryDataService,
    @Inject(InMemoryBackendConfig) @Optional() config: InMemoryBackendConfigArgs,
    xhrFactory: XhrFactory
  ) {
    super(inMemDataService, config, xhrFactory);
  }

  /** Process request and return an Observable of Http Response object */
  protected handleRequest_(reqCore: RequestCore): Observable<any> {
    const req: HttpRequest<any> = reqCore as any;
    return requiresConcurrencyControl(req)
      ? this.handleConcurrencyRequest(req)
      : super.handleRequest_(reqCore);
  }

  /** Process request that requires concurrency control */
  protected handleConcurrencyRequest(req: HttpRequest<any>): Observable<any> {
    let revertCompanyVersion = () => {};

    const precondition = req.headers.get('If-Match');
    if (!precondition) {
      return createErrorResponse$(428, 'Precondition Required', req.url);
    }

    const match = precondition.match(/"(.*)"/);
    const etag = match ? match[1] : '';
    const { companyId, version: requestVersion } = parseCompanyEtag(etag);

    if (!companyId || isNaN(requestVersion)) {
      return createErrorResponse$(400, 'Bad Request', req.url, `Badly formed "If-Match" header or etag: ${precondition}.`);
    }

    const currentVersion = this.inMemDataService.db.CompanyVersion[companyId];
    if (currentVersion == null) {
      return createErrorResponse$(404, 'Not Found', req.url, `Company for Etag:${etag} not found.`);
    }

    if (currentVersion !== requestVersion) {
      return createErrorResponse$(412, 'Precondition Failed', req.url, `Company version does not match Etag:${etag}.`);
    }

    // Conditionally update company version. Ready to revert if server responds with an error.
    const newVersion = requestVersion + 1;
    this.inMemDataService.db.CompanyVersion[companyId] = newVersion;

    const newEtag = createCompanyEtag(companyId, newVersion);

    /** Revert the version change in the DB */
    revertCompanyVersion = () => {
      this.inMemDataService.db.CompanyVersion[companyId] = currentVersion;
    };

    return super.handleRequest_(req).pipe(
      map((res: HttpResponse<any>) => {
        const reqDebug = req; // capture request for debugging
        const status = res.status;
        // replay with the new etag if recognized response status
        if (status === 200 || status === 201 || status === 204) {
          // set the header with the "weak" indicator
          // https://developer.mozilla.org/en-US/docs/Web/HTTP/Headers/ETag#Directives
          const headers = res.headers.set('Etag', `W/"${newEtag}"`);
          res = res.clone({ headers });
        }
        return res;
      }),
      catchError((error: HttpErrorResponse) => {
        const reqDebug = req; // capture request for debugging
        revertCompanyVersion();
        return throwError(error);
      })
    );
  }
}

function createErrorResponse$(status: number, statusText: string, url: string, error?: any) {
  const err = new HttpErrorResponse({ status, statusText, url, error });
  return throwError(err).pipe(observeOn(asapScheduler));
}
