import { ChangeSetOperation, ChangeSetAdd, ChangeSetDelete, ChangeSetUpdate, ChangeSetUpsert, Update } from 'ngrx-data';
import { raw } from './utils';
/**
 * Create a ChangeItem for a multi-entity save
 * Similar to the one in ngrx-data.
 * Extended with ability to take entity args
 * and to clone and strip the prototype from the entities in the ChangeItem.
 */
export class ChangeItemFactory {

  add<T>(entityName: string, entities: T | T[]): ChangeSetAdd<T> {
    entities = Array.isArray(entities) ? entities : entities ? [entities] : [];
    entities = entities.map(e => raw(e));
    return { entityName, op: ChangeSetOperation.Add, entities };
  }

  delete<T extends {id: string}>
    (entityName: string, entitiesOrIds: string | T | string[] | T[]): ChangeSetDelete {
    const data = Array.isArray(entitiesOrIds) ? entitiesOrIds : entitiesOrIds ? [entitiesOrIds] : [];
    const ids = data.map(e => typeof e === 'string' ? e : e.id);
    return {entityName, op: ChangeSetOperation.Delete, entities: ids };
  }

  update<T extends {id: string}>(entityName: string, entities: T | T[]): ChangeSetUpdate<T> {
    const updates = entities
      ? (Array.isArray(entities) ? entities : [entities]).map(e => toUpdate(raw(e)))
      : [];
    return {entityName, op: ChangeSetOperation.Update, entities: updates };
  }

  upsert<T>(entityName: string, entities: T | T[]): ChangeSetUpsert<T> {
    entities = Array.isArray(entities) ? entities : entities ? [entities] : [];
    entities = entities.map(e => raw(e));
    return {entityName, op: ChangeSetOperation.Upsert, entities };
  }
}

export const changeItemFactory = new ChangeItemFactory();

/**
 * Convert an SP entity (or part of one) into an ngrx-data Update<T> object
 */
export function toUpdate(entity: {id: string | number}): Update<any> {
  return {id: entity.id, changes: entity } as Update<any>;
}
