import { HttpClient } from '@angular/common/http';
import { of, Observable } from 'rxjs';
import { map, first } from 'rxjs/operators';
import { AsyncValidator, ValidatorFactory, ValidationContextBase, ValidatorFunctionContext } from '@validation';

export class PasswordPolicyValidator extends AsyncValidator {

  private static policies: { name: string, value: string }[];

  static factory(http: HttpClient): ValidatorFactory {
    return {
      name: 'passwordPolicy',
      fn: (group, context) => {
        return new PasswordPolicyValidator(http, group, context);
      }
    };
  }

  constructor(private http: HttpClient, group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('passwordPolicy', group,
      (value, context) => {
        // only checks that contains necessary chars; other validations check for length
        if (!value) { return of(true); }

        return this.fetchPolicies().pipe(
          map(policies => this.validatePassword(value, policies, context)),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate
      }
    );
  }

  private fetchPolicies(): Observable<{ name: string, value: string }[]> {

    /// TODO: Currently hardcoded. Change after OZ
    return of([]);

    // if (PasswordPolicyValidator.policies) {
    //   return of(PasswordPolicyValidator.policies);
    // }

    // const uri = `${environment.spApiRoot}/authentication/password/rules/standard`;
    // return this.http.get(uri).pipe(
    //   map(body => {
    //     if (!Array.isArray(body)) {
    //       return [];
    //     }

    //     return body.map(policy => {
    //       return {
    //         name: policy['name'],
    //         value: policy['value']
    //       };
    //     });
    //   }));
  }

  private validatePassword(password: string, policies: { name: string, value: string }[], context: ValidatorFunctionContext): boolean {
    // TODO: Currently hardcode standard policy
    const standardPolicy =
      '(?=.*[a-z])' + // at least 1 lower
      '(?=.*[A-Z])' + // at least 1 upper
      '(?=.*[0-9])' + // at least 1 digit
      '^\\S*$';       // no whitespace
    context.messageTemplate = 'Password must have at least 1 lowercase letter, 1 uppercase letter, 1 digit and no spaces';
    const regExp = new RegExp(standardPolicy);
    return regExp.test(password);
  }
}
