import { Provider } from '@angular/core';
import { DefaultDataServiceConfig, EntityDispatcherDefaultOptions, Pluralizer } from 'ngrx-data';
import { environment } from '@environment';

/** Configure the ngrx-data DefaultDataService for the ECommerce app */
const dataServiceConfig: DefaultDataServiceConfig = {
  root: environment.apiRoot || 'api', // default root path to the server's web api
  timeout: 60000, // request timeout, made large for docusign calls
};

/** All save operations are optimistic by default */
const entityDispatcherDefaultOptions: EntityDispatcherDefaultOptions = {
  optimisticAdd: true,
  optimisticDelete: true,
  optimisticUpdate: true,
  optimisticUpsert: true,
  optimisticSaveEntities: true
};

/**
 * This app does not pluralize. The entity name is its own collection resource name.
 * Ex: the collection resource name for 'Company' is 'Company' (not 'Companies')
 */
export class NoPluralizer extends Pluralizer {
  pluralize(name: string) { return name; }
}

/** All ngModule data service configuration providers for this app */
export const dataserviceConfigurationProviders: Provider[] = [
  {
    provide: DefaultDataServiceConfig,
    useValue: dataServiceConfig
  },
  {
    provide: EntityDispatcherDefaultOptions,
    useValue: entityDispatcherDefaultOptions
  },
  { provide: Pluralizer, useClass: NoPluralizer }
];
