import { Injectable } from '@angular/core';
import { ReviewService } from '@entity/entity-services';

import { Observable, combineLatest, of } from 'rxjs';
import { distinctUntilChanged, filter, first, map, shareReplay, tap } from 'rxjs/operators';

import { DashboardVm, ReviewStatusCss } from './dashboard-vm';
import { SectionStatus, User } from '@model';
import { SessionService } from '@session';
import { StatsService } from '@entity';

@Injectable({ providedIn: 'root' })
export class DashboardViewService {
  vm$: Observable<DashboardVm>;

  constructor(
    reviewService: ReviewService,
    sessionService: SessionService,
    statsService: StatsService
  ) {
    // show the intro text if the stats record is new (no id)
    const showIntro$ = statsService.stats$.pipe(
      map(stats => !stats.aboutYouStatus),
      distinctUntilChanged()
    );

    // Watch out! CombineLatest only supports a limited number of args before it loses types.
    const statuses$ = combineLatest(
      reviewService.aboutYouStatus$,
      reviewService.bankInfoStatus$,
      reviewService.companyInfoStatus$,
      reviewService.companyTaxStatus$,
      reviewService.employeesStatus$
    ).pipe(
      map(([aboutYou, bankInfo, companyInfo, companyTax, employees]) => ({
        aboutYou,
        bankInfo,
        companyInfo,
        companyTax,
        employees
      }))
    );

    // Watch out! CombineLatest only supports a limited number of args before it loses types.
    this.vm$ = combineLatest(
      sessionService.currentCompany$,
      sessionService.currentUser$,
      sessionService.isAdmin$,
      reviewService.isValid$,
      showIntro$,
      statuses$
    ).pipe(
      filter(([company, user]) => !!company && !!user),
      map(([company, user, isAdmin, isValid, showIntro, statuses]) => {
        const result = {
          company,
          user,
          isAdmin,
          isValid: canSignDocuments(isValid, statuses),
          showIntro,

          aboutYouAction: getStatusAction(statuses.aboutYou),
          aboutYouClass: getStatusCss(statuses.aboutYou),
          aboutYouIcon: getStatusIcon(statuses.aboutYou),
          bankInfoAction: getStatusAction(statuses.bankInfo),
          bankInfoClass: getStatusCss(statuses.bankInfo),
          bankInfoIcon: getStatusIcon(statuses.bankInfo),
          companyInfoAction: getStatusAction(statuses.companyInfo),
          companyInfoClass: getStatusCss(statuses.companyInfo),
          companyInfoIcon: getStatusIcon(statuses.companyInfo),
          companyTaxAction: getStatusAction(statuses.companyTax),
          companyTaxClass: getStatusCss(statuses.companyTax),
          companyTaxIcon: getStatusIcon(statuses.companyTax),
          employeesAction: getStatusAction(statuses.employees),
          employeesClass: getStatusCss(statuses.employees),
          employeesIcon: getStatusIcon(statuses.employees)
        } as DashboardVm;
        return result;
      }),
      shareReplay(1)
    );
  }
}

function canSignDocuments(isValid: boolean, statuses: any) {
  return (
    isValid &&
    statuses.aboutYou === SectionStatus.Valid &&
    statuses.bankInfo === SectionStatus.Valid &&
    statuses.companyInfo === SectionStatus.Valid &&
    statuses.companyTax === SectionStatus.Valid &&
    statuses.employees === SectionStatus.Valid
  );
}

function getStatusCss(status: SectionStatus): ReviewStatusCss {
  return {
    'status-action': status === SectionStatus.NotStarted,
    'status-inprogress': status === SectionStatus.Invalid,
    'status-complete': status === SectionStatus.Valid
  };
}

function getStatusAction(status: SectionStatus) {
  if (status === SectionStatus.NotStarted) {
    return 'Start';
  } else if (status === SectionStatus.Invalid) {
    return 'Update';
  } else {
    return 'Edit';
  }
}

function getStatusIcon(status: SectionStatus) {
  if (status === SectionStatus.NotStarted) {
    return 'adjust';
  } else if (status === SectionStatus.Invalid) {
    return 'error_outline';
  } else {
    return 'check';
  }
}
