import {
  Directive, Component, Input, Output, EventEmitter, OnDestroy, OnChanges,
  SimpleChanges, ElementRef, Renderer2, Optional, OnInit, HostListener
} from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Validator, AbstractControl, NG_VALIDATORS, NG_ASYNC_VALIDATORS, AsyncValidator, NgModel } from '@angular/forms';

import { Observable, of, timer, Subject, asyncScheduler, combineLatest, merge, BehaviorSubject } from 'rxjs';
import { debounceTime, delay, distinctUntilChanged, map, observeOn, startWith, switchMap } from 'rxjs/operators';

import { Indexable, SubSink } from '@core';
import { ModelComponent } from '../model.component';
import { ValidationService, ValidationError, ValidatorPredicate } from '@validation';

@Directive({
  selector: 'form[spForceFeedback], form',
  exportAs: 'spForm'
})
export class FormDirective implements OnInit, OnChanges, OnDestroy {

  @Input() get spForceFeedback(): boolean {
    return this._spForceFeedback || this.forceValidationFeedback;
  }
  set spForceFeedback(value: boolean) {
    this._spForceFeedback = true; // always set true, regardless of value (huh?)
  }
  private _spForceFeedback: boolean;

  change$: Observable<any>;
  private changesSubject = new Subject<SimpleChanges>();
  controls: { [key: string]: AbstractControl; } = {};
  private controls$: BehaviorSubject<{ [key: string]: AbstractControl; }>;
  private forceValidationFeedback: boolean;
  invalid$: Observable<boolean>;
  private subs = new SubSink();
  valid$: Observable<boolean>;

  constructor(private route: ActivatedRoute) {
    this.controls$ = new BehaviorSubject<{ [key: string]: AbstractControl; }>(this.controls);
    this.invalid$ = this.buildStatusObservable(controls => this.isInvalid(controls));
    this.valid$ = this.buildStatusObservable(controls => this.isValid(controls));
  }

  ngOnInit() {
    const changes$ = combineLatest(
      this.changesSubject.pipe(startWith<SimpleChanges>({})),
      ...this.getParamsFromRoot()
    );

    this.change$ = changes$.pipe(
      map(() => this.spForceFeedback),
      distinctUntilChanged()
    );

    // although probably OK to skip unsubscribe, do so for safety
    this.subs.sink = changes$.subscribe(([changes, ...params]) => {
      const param = params.find(p => p.forceValidationFeedback !== undefined);
      this.forceValidationFeedback = param && param.forceValidationFeedback;
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    this.changesSubject.next(changes);
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  addControl(dir: NgModel): void {
    this.controls[dir.name] = dir.control;
    this.controls$.next(this.controls);
  }

  removeControl(dir: NgModel): void {
    if (dir && dir.name) {
      delete this.controls[dir.name];
    }
    this.controls$.next(this.controls);
  }

  get valid(): boolean {
    return this.isValid(this.controls);
  }

  get invalid(): boolean {
    return this.isInvalid(this.controls);
  }

  get pending(): boolean {
    return Object.keys(this.controls).some(name => this.controls[name].pending);
  }

  get errors(): { [key: string]: any; } {
    return Object.keys(this.controls)
      .map(name => this.controls[name].errors || {})
      .reduce((prev, curr) => {
        return { ...prev, ...curr };
      }, {} as Indexable);
  }

  get pristine(): boolean {
    return Object.keys(this.controls).every(name => this.controls[name].pristine);
  }

  get dirty(): boolean {
    return Object.keys(this.controls).some(name => this.controls[name].dirty);
  }

  get touched(): boolean {
    return Object.keys(this.controls).some(name => this.controls[name].touched);
  }

  get untouched(): boolean {
    return Object.keys(this.controls).every(name => this.controls[name].untouched);
  }

  private getParamsFromRoot(route?: ActivatedRoute): Observable<Params>[] {
    route = route || this.route;

    if (!route.parent) {
      return [route.params];
    }

    return [route.params, ...this.getParamsFromRoot(route.parent)];
  }

  private isInvalid(controls: { [key: string]: AbstractControl; }) {
    return Object.keys(controls).some(name => controls[name].invalid && !controls[name].disabled);
  }

  private isValid(controls: { [key: string]: AbstractControl; }) {
    return Object.keys(controls).every(name => controls[name].valid || controls[name].disabled);
  }

  private buildStatusObservable<T>(status: (controls: { [key: string]: AbstractControl; }) => T) {
    return this.controls$.pipe(
      switchMap(controls => {
        return merge(...Object.keys(controls).map(key => controls[key].statusChanges)).pipe(
          map(() => status(controls)),
          startWith(status(controls))
        );
      }),
      distinctUntilChanged(),
      observeOn(asyncScheduler)
    );
  }
}

interface NgModelExtended extends NgModel {
  _spTouched$: Subject<boolean>;
}

@Directive({
  selector: '[spValidationStatus]'
})
export class ValidationStatusDirective implements OnChanges, OnInit, OnDestroy {

  @Input() spValidationStatus: NgModelExtended;
  @Input() validClass = 'is-valid';
  @Input() invalidClass = 'is-invalid';

  private subs = new SubSink();
  private touched$ = new Subject<boolean>();

  constructor(private el: ElementRef, private renderer: Renderer2, @Optional() private form: FormDirective) { }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['spValidationStatus']) {
      this.subs.unsubscribe();
      if (this.spValidationStatus) {

        const events = [
          this.spValidationStatus.statusChanges,
          ...(this.form ? [this.form.change$] : []),
          this.touched$.pipe(distinctUntilChanged())
        ];

        this.subs.sink = merge(...events).pipe(
          debounceTime(500),
        ).subscribe(() => {
          if (this.showFeedback && (this.spValidationStatus.valid || this.spValidationStatus.pending)) {
            this.renderer.addClass(this.el.nativeElement, this.validClass);
          } else {
            this.renderer.removeClass(this.el.nativeElement, this.validClass);
          }

          if (this.showFeedback && this.spValidationStatus.invalid) {
            this.renderer.addClass(this.el.nativeElement, this.invalidClass);
          } else {
            this.renderer.removeClass(this.el.nativeElement, this.invalidClass);
          }
        });

        // Monkey patch NgModel to communicate touched status to spValidationFeedback
        this.spValidationStatus._spTouched$ = new Subject<boolean>();
      }
    }
  }

  ngOnInit() {
    if (this.form) {
      this.form.addControl(this.spValidationStatus);
    }
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
    if (this.form && this.spValidationStatus) {
      this.form.removeControl(this.spValidationStatus);
    }
  }

  @HostListener('blur')
  blur() {
    this.touched$.next(true);
    if (this.spValidationStatus._spTouched$) { this.spValidationStatus._spTouched$.next(true); }
  }

  private get showFeedback(): boolean {
    return this.form && this.form.spForceFeedback || this.spValidationStatus.touched || this.spValidationStatus.dirty;
  }
}

@Directive({
  selector: '[spValidationFeedback]'
})
export class ValidationFeedbackDirective implements OnDestroy, OnChanges {

  @Input() spValidationFeedback: NgModelExtended;
  @Input() displayName: string;

  private subs = new SubSink();
  private el: HTMLElement;

  constructor(el: ElementRef, private renderer: Renderer2, @Optional() private form: FormDirective) {
    this.el = el.nativeElement;
  }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['spValidationFeedback']) {
      this.subs.unsubscribe();
      if (this.spValidationFeedback) {

        const events = [
          this.spValidationFeedback.statusChanges.pipe(
            switchMap(_ => {
              // Only emit PENDING (and display "Checking...) unless has been pending for more than 1/2 second
              // The 1/2 second value was empirically determined. Don't know why less doesn't work.
              return this.spValidationFeedback.pending ?  timer(500).pipe(map(() => _)) : of(_);
            })
          ),
          ...(this.form ? [this.form.change$] : []),
          ...(this.spValidationFeedback._spTouched$ ? [this.spValidationFeedback._spTouched$.pipe(distinctUntilChanged())] : [])
        ];

        this.subs.sink = merge(...events).pipe(
          debounceTime(500),
        ).subscribe(() => {
          this.renderer.removeClass(this.el, 'feedback');
          this.renderer.removeClass(this.el, 'valid-feedback');
          this.renderer.removeClass(this.el, 'invalid-feedback');
          this.el.innerText = '';

          if (!this.showFeedback) {
            return;
          }

          if (this.spValidationFeedback.pending) {
            this.renderer.addClass(this.el, 'valid-feedback');
            this.el.innerText = 'Checking...';
          }

          if (this.spValidationFeedback.invalid) {
            this.renderer.addClass(this.el, 'invalid-feedback');
            this.el.innerHTML = this.getValidationFeedback().map(feedback => {
              return `<div>${feedback}</div>`;
            }).join('\n');
          }

          if (this.spValidationFeedback.pending || this.spValidationFeedback.invalid) {
            this.renderer.addClass(this.el, 'feedback');
          }
        });
      }
    }
  }

  getValidationFeedback(): string[] {
    if (!this.spValidationFeedback && !this.spValidationFeedback.invalid) {
      return [];
    }

    return Object.keys(this.spValidationFeedback.errors || {})
      .map(key => this.spValidationFeedback.errors[key] as ValidationError)
      .filter(error => error.getErrorMessage)
      .map(error => error.getErrorMessage(this.displayName));
  }

  private get showFeedback(): boolean {
    return this.form && this.form.spForceFeedback || this.spValidationFeedback.touched || this.spValidationFeedback.dirty;
  }
}


@Directive({
  selector: '[spValidateProperty]',
  providers: [
    { provide: NG_VALIDATORS, useExisting: ValidatePropertyDirective, multi: true }
  ]
})
export class ValidatePropertyDirective implements Validator, OnDestroy {

  @Input() model: any;
  @Input() modelType: string;
  @Input() propertyName: string;
  @Input() group: string;
  @Input() modelContext: ModelComponent;

  private validationErrors: ValidationError[] = [];
  private lastModel: string;
  private subs = new SubSink();

  constructor(private validationService: ValidationService) { }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  validate(control: AbstractControl): { [key: string]: any; } {
    if (!this.model || !this.modelType || !this.propertyName) {
      return null;
    }

    const model = { ...this.model };
    model[this.propertyName] = control.value;
    const modelAsString = JSON.stringify(model);

    if (this.lastModel !== modelAsString) {
      this.lastModel = modelAsString;
      const errors = this.validationService.validateModel(model, this.modelType, false, validator => {
        return !this.group && !validator.group || this.group && validator.group === this.group;
      }) || [];
      this.validationErrors = errors.filter(error => error.context.propertyName === this.propertyName);
    }

    const validationErrors = this.validationErrors.reduce((prev, curr) => {
      prev[curr.key] = curr;
      return prev;
    }, {} as Indexable);
    return !!this.validationErrors.length ? validationErrors : null;
  }

  registerOnValidatorChange?(fn: () => void): void {
    this.subs.unsubscribe();
    if (this.modelContext) {
      this.subs.sink = this.modelContext.modelChange.subscribe(fn);
    }
  }
}

@Directive({
  selector: '[spAsyncValidateProperty]',
  providers: [
    { provide: NG_ASYNC_VALIDATORS, useExisting: AsyncValidatePropertyDirective, multi: true }
  ]
})
export class AsyncValidatePropertyDirective implements AsyncValidator, OnDestroy {

  @Input() model: any;
  @Input() modelType: string;
  @Input() propertyName: string;
  @Input() group: string;
  @Input() modelContext: ModelComponent;

  private validationErrors: ValidationError[] = [];
  private lastPropertyValue: any;
  private subs = new SubSink();

  constructor(private validationService: ValidationService) { }

  ngOnDestroy() {
    this.subs.unsubscribe();
  }

  validate(control: AbstractControl): Observable<{ [key: string]: any; }> {
    if (!this.model || !this.modelType || !this.propertyName) {
      return of(null);
    }
    const entityMetadata = this.validationService.validationMetadata.getModelConfig(this.modelType);
    if (!entityMetadata) {
      return of(null);
    }

    const hasAsyncValidators = entityMetadata.getAsyncProperties().some(property => {
      return property.propertyName === this.propertyName &&
        property.validators.some(validator => !this.group && !validator.group || this.group && validator.group === this.group);
    });
    if (!hasAsyncValidators) {
      return of(null);
    }

    const toErrorMap = () => {
      return this.validationErrors.reduce((prev, curr) => {
        prev[curr.key] = curr;
        return prev;
      }, {} as Indexable);
    };

    if (this.lastPropertyValue !== control.value) {
      // debounce with timer
      return timer(500).pipe(
        switchMap(() => {
          const model = { ...this.model };
          model[this.propertyName] = control.value;

          const predicate: ValidatorPredicate = (validator, property) => {
            return property && property.propertyName === this.propertyName &&
              (!this.group && !validator.group || this.group && validator.group === this.group);
          };
          return this.validationService.validateModel(model, this.modelType, true, predicate).pipe(
            map(errors => {
              this.lastPropertyValue = control.value;
              this.validationErrors = (errors || []).filter(error => error && error.context.propertyName === this.propertyName);

              return !!this.validationErrors.length ? toErrorMap() : null;
            })
          );
        })
      );
    }

    return of(!!this.validationErrors.length ? toErrorMap() : null);
  }

  registerOnValidatorChange?(fn: () => void): void {
    this.subs.unsubscribe();
    if (this.modelContext) {
      this.subs.sink = this.modelContext.modelChange.subscribe(fn);
    }
  }
}
