/**
 * Generate extra accounts to expand the account list for filtering and paging
 */
export function genExtraAccounts(db: Db): Db {
  const dayInMs = 1000 * 60 * 60 * 24;

  for (let i = 1; i <= 60; i++) {
    const counter = ('00' + i).substr(-3);
    const companyId = `1fb${counter}ee54333aef601683b89a011`;
    const startDt = (new Date(new Date('2017-12-31T17:00:00.000Z').valueOf() + (dayInMs * i))).toUTCString();

    const co = {
      ...companyTemplate,
      id: companyId,
      bcl: 'EX' + counter,
      enrollmentStartDate: startDt,
      name: 'TestCo ' + counter
    } as Company;

    const hf = {
      ...householdFilersTemplate,
      id: '8b132db2fa574036903401683b89a' + counter,
      companyId,
    } as HouseholdFilers;

    const stats = {
      ...statsTemplate,
      id: '6884fa7cc43f4665bab501683b89a' + counter,
      companyId,
      aboutYouStartDate: startDt,
    } as Stats;

    const user = {
      ...userTemplate,
      id: `92e${counter}2b7e6e423b96b101683b89a011`,
      companyId,
      email: `test.user${counter}@testco${counter}.com`,
      lastName: 'User' + counter,
      oidcSub: 'NewCustUser:8372' + counter,
      phone: '9809809' + counter,
      username: 'testuser' + counter
    } as User;

    db.Company.push(co);
    db.HouseholdFilers.push(hf);
    db.Stats.push(stats);
    db.User.push(user);
  }
  return db;
}
