/**
 * Router route re-use strategy that NEVER reuses a component instance
 * Essential because when you reuse a component, bad things happen.
 * In particular, the sp-input and validation controls aren't expecting the model to change.
 * UPDATE FEB 2019: those controls now listen for updates as of PKSP-22907 "Element wrapper model fields"
 * Can work around it with clever games like
 *   this.vm = null; // make sure there's an *ngIf="vm"
 *   setTimeout(()=> this.vm = newVm);
 * But this is maddening and confusing. Don't bother!
 * Component re-use is a (dubious) performance enhancer that we don't need in this app.
 */
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot,  RouteReuseStrategy } from '@angular/router';

/**
 * Router route re-use strategy that NEVER reuses a component instance.
 */
@Injectable() // np providedIn because multi-provided
export class SpRouteReuseStrategy extends RouteReuseStrategy {
  shouldDetach(route: ActivatedRouteSnapshot): boolean {
    return false;
  }
  store(route: ActivatedRouteSnapshot, handle: {}): void {

  }
  shouldAttach(route: ActivatedRouteSnapshot): boolean {
    return false;
  }
  retrieve(route: ActivatedRouteSnapshot): {} {
    return null;
  }
  shouldReuseRoute(future: ActivatedRouteSnapshot, curr: ActivatedRouteSnapshot): boolean {
    return false;
  }
}
