import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Dictionary } from 'ngrx-data';

import { combineLatest, defer, Observable, of, throwError } from 'rxjs';
import {
  catchError, concatMap, distinctUntilChanged, finalize, first,
  map, shareReplay, switchMap, withLatestFrom, tap
} from 'rxjs/operators';
import { tag } from 'rxjs-spy/operators/tag';

import { AccountStatus, AccountStatusCodes, Company, Employee, User } from '@model';
import { AccountStateTransition, AccountStateTransitions } from './account-state-transition';
import { CompanyPackageService } from '@entity/company-package.service';
import { EntityDispatcher } from '../entity/entity-dispatcher';
import { EntitySelectors } from '../entity/entity-selectors';
import { getFullName } from '@core/utils';
import { environment } from '@environment';
import { entityMetadata, isCompanyEntity } from '@entity/entity-metadata';
import { IdGeneratorService } from '@core/services/id-generator.service';
import { LoggerService } from '@core/services/logger.service';
import { SessionDispatcher } from './session-dispatcher';
import { SessionSelectors } from './session-selectors';

@Injectable({ providedIn: 'root' })
export class SessionService {

  /** Cached account status transitions  */
  private accountStateTransitionsCached: AccountStateTransitions;

  /** Account status transitions. Invariant during the session. */
  accountStateTransitions$: Observable<AccountStateTransitions>;

  /** The authenticated (signed in) user when not null */
  authUser$: Observable<User>;

  /** True when there is a loaded CompanyPackage */
  companyPackageLoaded$: Observable<boolean>;

  /** True when in the process of loading the CompanyPackage */
  companyPackageLoading$: Observable<boolean>;
  currentCompanyEtag$: Observable<string>;

  /** The current company. Null when there is no current company */
  currentCompany$: Observable<Company>;

  /** The id of the current company. Empty string when there is no current company */
  currentCompanyId$: Observable<string>;

  /** The current employee. Null when there is no current employee */
  currentEmployee$: Observable<Employee>;

  /** The current employeeId. Null when there is no current employee */
  currentEmployeeId$: Observable<string>;

  /**
   * The current user. Null when there is no current user
   * Not necessarily the authenticated user who could be an admin acting on the current user's behalf.
   */
  currentUser$: Observable<User>;

  /** The id of the current user. Empty string when there is no current company */
  currentUserId$: Observable<string>;

  /** Emit true when the signed-in AuthUser is an administrator. Will change w/ each sign-in and sign-out */
  isAdmin$: Observable<boolean>;

  /** True if there is a signed-in user */
  isSignedIn$: Observable<boolean>;

  /** emits true when the current company is "read-only" and saves are disallowed. */
  readOnly$: Observable<boolean>;

  constructor(
    private companyPackageService: CompanyPackageService,
    private entityDispatcher: EntityDispatcher,
    private entitySelectors: EntitySelectors,
    private http: HttpClient,
    private idGenerator: IdGeneratorService,
    public logger: LoggerService,
    private sessionDispatcher: SessionDispatcher,
    private sessionSelectors: SessionSelectors
  ) {
    this.authUser$ = sessionSelectors.authUser$;
    this.companyPackageLoaded$ = sessionSelectors.companyPackageLoaded$;
    this.companyPackageLoading$ = sessionSelectors.companyPackageLoading$;
    this.currentCompanyId$ = sessionSelectors.currentCompanyId$;
    this.currentEmployeeId$ = sessionSelectors.currentEmployeeId$;
    this.currentUserId$ = sessionSelectors.currentUserId$;
    this.currentCompanyEtag$ = sessionSelectors.currentCompanyEtag$;
    this.isAdmin$ = sessionSelectors.isAdmin$;
    this.isSignedIn$ = sessionSelectors.isSignedIn$;
    this.readOnly$ = sessionSelectors.readOnly$;

    this.currentCompany$ = combineLatest(
      this.currentCompanyId$,
      entitySelectors.company.entityMap$
    ).pipe(
      map(([id, entityMap]) => entityMap[id]),
      distinctUntilChanged(),
      tag('currentCompany$'),
      shareReplay(1)
    );

    this.currentEmployee$ = combineLatest(
      this.currentEmployeeId$,
      entitySelectors.employee.entityMap$
    ).pipe(
      map(([id, entityMap]) => entityMap[id]),
      distinctUntilChanged(),
      tag('currentEmployee$'),
      shareReplay(1)
    );

    this.currentUser$ = combineLatest(
      this.currentUserId$,
      entitySelectors.user.entityMap$
    ).pipe(
      map(([id, entityMap]) => id ? entityMap[id] : null),
      distinctUntilChanged(),
      tag('currentUser$'),
      shareReplay(1)
    );

    this.loadAccountWhenRegularUserSignsIn();
    this.setAccountStateTransitions$();
  }

  /** Clear the store of all company-specific collections + the User collection. A must during sign out */
  clearCurrentAccount(): void {
    this.setReadOnly(false);
    this.sessionDispatcher.setCurrentAccount(null, null);
    const companyEntityTypes = Object.keys(entityMetadata).filter(isCompanyEntity);
    this.entityDispatcher.clearCollections(companyEntityTypes);
  }

  /**
   * Generate a new GUID-like string identifier
   */
  generateId(): string {
    return this.idGenerator.generateId();
  }

  /**
   * Load the company package for a {companyId, userId} pair
   * Clears the company collections
   * and loads the company package for the given company and user.
   * Set current company and user.
   * @param companyId The id of the company to load.
   * @param userId The id of the current user for the account.
   *
   * @returns A COLD, terminating observable like an HttpClient method.
   * You MUST SUBSCRIBE to make it work.
   * You do not have to unsubscribe.
   * Emits true if succeeded; else returns error on the error channel.
   */
  loadAccountByCompanyUserIds(companyId: string, userId: string): Observable<boolean> {
    this.clearCurrentAccount();

    if (!companyId) {
      return throwError(`Cannot load account without a company id`);
    }

    if (!userId) {
      return throwError(`Cannot load account without a user id`);
    }

    this.sessionDispatcher.setCurrentAccount(companyId, userId);

    return this.companyPackageService.loadCompanyPackageByCompanyId(companyId).pipe(
      catchError(error => {
        this.clearCurrentAccount();
        // TODO: The app will die because bound to data no longer in cache. Navigate to safety first?
        const emsg = `SessionService.loadAccountByCompanyUserIds failed for company: ${companyId} and user: ${userId}`;
        this.logger.error(emsg);
        return throwError(emsg);
      }),
      // return true upon success; don't want caller looking at the raw server data.
      concatMap(_ => this.isAdmin$),
      first(),
      map(isAdmin => {
        // admin starts with ReadOnly view of account; regular user can edit
        this.setReadOnly(isAdmin);
        return true;
      }),
      tag('loadAccountByCompanyUserIds')
    );
  }

  private loadAccountWhenRegularUserSignsIn() {
    this.authUser$.pipe(
      withLatestFrom(this.isAdmin$),
      switchMap(([authUser, isAdmin]) => {
        if (authUser && !isAdmin) {
          // This changed AuthUser is a regular user
          // Change the current account to this user, who becomes the current user,
          // and load that user's account data.
          // Do not wait for this to succeed.
          // If something must wait, such as a navigation guard,
          // it should look at the state of companyPackageLoading$.
          return this.loadAccountByCompanyUserIds(authUser.companyId, authUser.id);
        } else {
          return of(false);
        }
      })
    )
    // No need to unsubscribe because this service never dies.
    .subscribe();
  }

  /**
   * Reload the CompanyPackage for the current company and user.
   * Error if either is missing/null
   * @returns Cold, terminating observable which succeeds if the package reloaded
   * or fails if it did not.
   * You must subscribe but do not have to unsubscribe.
   * Screwed if it fails because the attempt first erases the data in cache.
   * TODO: Could recover by holding EntityCache copy and restoring it on failure.
   */
  reloadCurrentCompanyPackage(): Observable<boolean> {
    return combineLatest(
      this.currentCompanyId$,
      this.currentUserId$
    ).pipe(
      first(),
      concatMap(([companyId, userId]) => {
        if (companyId && userId) {
          return this.loadAccountByCompanyUserIds(companyId, userId);
        } else {
          return throwError('No companyId or userId');
        }
      }),
      tag('reloadCurrentCompanyPackage')

    );
  }

  /** Setup the observable of AccountStateTransitions */
  private setAccountStateTransitions$() {
    this.accountStateTransitions$ = defer(() => {
      if (this.accountStateTransitionsCached) {
        return of(this.accountStateTransitionsCached);
      }

      return this.http.get(`${environment.apiRoot}/account/accountStateTransitions`).pipe(
        map((response: AccountStateTransition[]) => {
          return response.reduce((prev, curr) => {
            prev[curr.current] = prev[curr.current] || {};
            prev[curr.current][curr.next] = {
              next: curr.next,
              automaticTransition: curr.automaticTransition,
              allowAdminOverride: curr.allowAdminOverride,
              allowWithErrors: curr.allowWithErrors,
              hideInUi: curr.hideInUi
            };
            return prev;
          }, {} as AccountStateTransitions);
        }),
        map(t => this.accountStateTransitionsCached = t)
      );
    }).pipe(shareReplay(1));
  }

  /**
   * Change the current company's AccountStatus by sending a setStatus request to the server.
   * If the server replies with success, reload the company package;
   * the company should have the updated AccountStatus.
   * @param status Change to this AccountStatus
   * @returns Cold, terminating observable which succeeds if the status changed
   * or fails if it did not.
   * You must subscribe but do not have to unsubscribe.
   */
  setAccountStatus(status: AccountStatus): Observable<string[]> {
    return combineLatest(
      this.currentUserId$,
      this.currentCompany$
    ).pipe(
      first(),
      concatMap(([userId, company]) => {
        if (!userId || !company) {
          throw new Error('No current user or company');
        } else if (!AccountStatusCodes.getByValue(status)) {
          throw new Error(`Invalid status code, "${status}"`);
        }
        if (company.status === status) {
          return of([]); // no need to change
        }
        const companyId = company.id;
        const url = `${environment.apiRoot}/account/${companyId}/status`;
        return this.http.post<string[]>(url, { status }).pipe(
          concatMap(r => this.loadAccountByCompanyUserIds(companyId, userId).pipe(map(_ => r)))
        );
      }),
      tag('setAccountStatus')
    );
  }

  /** Set the etag for the current company  */
  setCurrentCompanyEtag(etag: string) {
    this.sessionDispatcher.setCurrentCompanyEtag(etag);
  }

  setCurrentEmployeeId(id: string) {
    this.sessionDispatcher.setCurrentEmployeeId(id);
  }

  /** Set session read-only flag to true or false. */
  setReadOnly(readOnly: boolean) {
    this.sessionDispatcher.setReadOnly(readOnly);
  }
}
