import { Injectable } from '@angular/core';
import { Store, createFeatureSelector, createSelector } from '@ngrx/store';

import { Observable } from 'rxjs';

import { User, UserRole } from '@model';
import { initialState, SessionState, SESSION_STATE } from './reducer';

// #region selectors
export const selectSessionState = createFeatureSelector<SessionState>(SESSION_STATE);

const selectAuthUser = createSelector(
  selectSessionState,
  state => state.authUser
);

const selectCompanyPackageLoaded = createSelector(
  selectSessionState,
  state => state.companyPackageLoaded
);

const selectCompanyPackageLoading = createSelector(
  selectSessionState,
  state => state.companyPackageLoading
);

const selectCurrentCompanyEtag = createSelector(
  selectSessionState,
  state => state.currentCompanyEtag
);

const selectCurrentCompanyId = createSelector(
  selectSessionState,
  state => state.currentCompanyId
);

const selectCurrentEmployeeId = createSelector(
  selectSessionState,
  state => state.currentEmployeeId
);

const selectCurrentUserId = createSelector(
  selectSessionState,
  state => state.currentUserId
);

const selectIsAdmin = createSelector(
  selectSessionState,
  state => state.authUser && state.authUser.userRole === UserRole.Administrator
);

const selectIsSignedIn = createSelector(
  selectSessionState,
  state => state.authUser != null
);

const selectReadOnly = createSelector(
  selectSessionState,
  state => state.readOnly
);

// #endregion selectors
@Injectable({providedIn: 'root'})
export class SessionSelectors {

  /** The full session state */
  session$: Observable<SessionState>;

  /** The authenticated (signed in) user. Null when not signed in. */
  authUser$: Observable<User>;

  /** True when there is a loaded CompanyPackage */
  companyPackageLoaded$: Observable<boolean>;

  /** True when in the process of loading the CompanyPackage */
  companyPackageLoading$: Observable<boolean>;
  currentCompanyEtag$: Observable<string>;

  /** The id of the current company. Null when there is no current company */
  currentCompanyId$: Observable<string>;

  /** The id of the current employee. Null when there is no current employee */
  currentEmployeeId$: Observable<string>;

  /** The id of the current user. Null when there is no current user */
  currentUserId$: Observable<string>;

  /** True if there is a signed-in user and that user is an Admin */
  isAdmin$: Observable<boolean>;

  /** True if there is a signed-in user */
  isSignedIn$: Observable<boolean>;
  readOnly$: Observable<boolean>;

  constructor(private store: Store<SessionState>) {
    this.session$ = store.select(selectSessionState);

    this.authUser$ = store.select(selectAuthUser);

    this.companyPackageLoaded$ = store.select(selectCompanyPackageLoaded);
    this.companyPackageLoading$ = store.select(selectCompanyPackageLoading);

    this.currentCompanyEtag$ = store.select(selectCurrentCompanyEtag);
    this.currentCompanyId$ = store.select(selectCurrentCompanyId);
    this.currentEmployeeId$ = store.select(selectCurrentEmployeeId);
    this.currentUserId$ = store.select(selectCurrentUserId);
    this.isAdmin$ = store.select(selectIsAdmin);
    this.isSignedIn$ = store.select(selectIsSignedIn);
    this.readOnly$ = store.select(selectReadOnly);
  }
}
