import { Injectable } from '@angular/core';

import { combineLatest } from 'rxjs';
import * as Raven from 'raven-js';

import { AuthService } from '@core/services/auth-services/auth.service';
import { environment } from '@environment';
import { getFullName, Indexable } from '@core';
import { SentryEnricher } from '@core/error-handling/sentry-enricher';
import { SessionService } from '@session';
import { UserRole } from '@model';

@Injectable()
export class AuthErrorEnricher implements SentryEnricher {
  constructor(private authService: AuthService, private sessionService: SessionService) {}

  initialize(): void {
    combineLatest(this.sessionService.authUser$, this.sessionService.currentUser$).subscribe(
      ([authUser, currUser]) => {
        let name: string;
        if (authUser == null) {
          Raven.setUserContext({
            version: environment.version
          });
          return;
        }
        if (currUser) {
          const context: Indexable = {
            userId: currUser.id,
            email: currUser.email,
            companyId: currUser.companyId,
            version: environment.version
          };
          if (currUser.id === authUser.id) {
            // regular user
            context.username = getFullName(currUser);
          } else {
            // admin user
            context.username = `${getFullName(authUser)} (for ${getFullName(currUser)})`;
            context.adminEmail = authUser.email;
          }
          Raven.setUserContext(context);
        } else {
          // No current user
          name = getFullName(authUser);
          Raven.setUserContext({
            username: name,
            userId: authUser.id,
            email: authUser.email,
            companyId: authUser.companyId,
            version: environment.version
          });
        }
      }
    );

    this.authService.errors$.subscribe(err => {
      if (err) {
        Raven.captureMessage(err, {
          level: 'error',
          tags: { location: 'auth' }
        });
      }
    });
  }
}
