import { Injectable } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { isE2E } from '@core/e2e-check';
import { LoggerService } from '@core/services/logger.service';

@Injectable({ providedIn: 'root' })
export class ToastService {
  constructor(private logger: LoggerService, private toastr: ToastrService) {}

  openSnackBar(message: string, action: string) {
    this.show(message, action, 'success');
  }

  show(message: string, title: string, type: 'success'|'info'|'warning'|'error') {
    this.logger.log(`${message} - ${title}`);
    if (isE2E) {
      return; // no toast during E2E tests
    }
    // setTimeout guards against `ExpressionChangedAfterItHasBeenCheckedError`
    setTimeout(() => {
      this.toastr.show(message, title, {
        timeOut: 2000
      }, type);
    }, 0);

  }
}
