import { CompanyEntityBase } from './company-entity-base';
import { TaxRateType, TaxTypeCode } from '@model/codes-enums';

/** Company Tax code */
export class CompanyTax extends CompanyEntityBase {

    /** The unique identifier of the tax code. ex: FE0000-003  */
    mtsTaxCode: string;

    /** The tax code name, mainly used for display reasons */
    name: string;

    /** The state code. ex PA, can be null for federal taxes */
    stateCode: string;

    /** Tax authority id for this company and this tax code. */
    ein: string;

    /** Applied for a ein. If this is active the ein is not required. */
    appliedForEin = false;

    /** If the company is exempt from paying this tax */
    isExempt = false;

    /** If the tax is reimbursable */
    isReimbursable: boolean;

    /** Default === Fixed or Variable */
    rateType: TaxRateType;

    /** The rate of the tax code. Applicable based on the rate type. */
    rate: number;

    /** Specific identifier code for Washington */
    ubi: string;

    /** When started business in this state for this tax. ex: "2018-09-11T17:00:00Z" */
    businessStartDate: string;

    /** A code that describes if the tax is on the Federal, State, County, City or School district level */
    taxTypeCode: TaxTypeCode;

    /** When the code is active */
    isActive = true;

    /** True when marked-for-delete */
    isRemoved = false;

    static create(companyTax?: Partial<CompanyTax>): CompanyTax {
        if (companyTax == null || companyTax.id == null || companyTax.companyId == null) {
            throw Error('CompanyTax can\'t be created without specifying a id, company');
        }

        return Object.assign(new CompanyTax(companyTax.id, companyTax.companyId), companyTax);
    }
}
