import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Observable, of, throwError, timer, empty } from 'rxjs';
import { catchError, concatMap, delay, map, tap } from 'rxjs/operators';

import { dateToday, getFullName } from '@core/utils';
import { AccountStatus, Company, CompanyType, User, UserRole, HouseholdFilers, Stats } from '@model';
import { AuthService } from '@core/services/auth-services/auth.service';
import { BusyService } from '@core/services/busy.service';
import { environment } from '@environment';
import { OidcTokenResponse } from '@core/services/auth-services/oidc-token-response';
import { OidcTokenService } from '@core/services/auth-services/oidc-token.service';
import { SessionService } from '@session';
import { SignupVm } from './signup.vm';
import { WellKnownRoutes } from '@core/well-known-routes';
import { SignupLeadInfo } from './signup-leadinfo';

export const newEcAccountUrl = environment.apiRoot + '/account/user';
export const newSpAccountUrl = environment.spApiRoot + '/company/new';
export const subPrefix = 'NewCustUser:';

const serviceName = 'SignupService';
const pardotVisitorHandler = environment.pardotFormHandlerUri;
/**
 * SurePayroll Account creation error.
 * The user might be able to fix (e.g, username or password rejection)
 */
export class SPAccountCreationError {
  constructor(
    public error: any,
    public company: Partial<Company>,
    public user: User,
    public userMessage?: string
  ) {}
}

/**
 * ECommerce app Account creation error
 * User can't fix. Must fail.
 */
export class ECAccountCreationError {
  constructor(
    public error: any,
    public company: Partial<Company>,
    public user: User,
    public userMessage?: string
  ) {}
}

/*
 * Get unique BCL code (4 character customer identifier)
 */
@Injectable({ providedIn: 'root' })
export class SignupService {
  constructor(
    private authService: AuthService,
    private busyService: BusyService,
    private http: HttpClient,
    private router: Router,
    private sessionService: SessionService,
    private tokenService: OidcTokenService,
  ) {
    authService.clearReroute('SignupService'); // no lingering routes
  }

  /** Clear the current user and current company data  */
  clearCurrentAccount() {
    this.sessionService.clearCurrentAccount();
  }

  /** Is test account if email ends with '@test.com' or user's name like 'Test User' (e.g.'Test123 UserX') */
  isTestAccount(vm: SignupVm) {
    return vm.email.endsWith('@test.com') || /^(e2e[-]?)?test.* user/i.test(vm.firstName + ' ' + vm.lastName);
  }

  signin() {
    this.authService.logEvent('SignupService.signin() is calling AuthService.signin');
    this.authService.signin();
  }

  /** Signup */
  signUp(vm: SignupVm): Observable<boolean> {
    this.tokenService.clearSignupToken(serviceName);
    const companyName = vm.isHousehold ? getFullName(vm) : '';

    const company: Partial<Company> = {
      ...new Company(),
      id: this.sessionService.generateId(),
      bcl: null, // from the SP new account AP
      isHousehold: vm.isHousehold,
      name: companyName,
      legalName: companyName,
      enrollmentStartDate: dateToday(),
      status: this.isTestAccount(vm) ? AccountStatus.Test : AccountStatus.Open,
    };

    const user: User = {
      id: this.sessionService.generateId(),
      companyId: company.id,
      oidcSub: null, // from the SP new account AP
      firstName: vm.firstName,
      lastName: vm.lastName,
      email: vm.email,
      phone: vm.phone,
      username: vm.username,
      userRole: UserRole.Approver
    };

    const householdFilers: Partial<HouseholdFilers> = {
      id: this.sessionService.generateId(),
      companyId: company.id,
    };

    const stats: Partial<Stats> = {
      id: this.sessionService.generateId(),
      companyId: company.id,
      aboutYouStartDate: new Date().toISOString()
    };

    const cmsInfo: Partial<SignupLeadInfo> = {
      id: vm.id,
      pardotVisitorId: vm.pardotVisitorId,
      leadId: vm.leadId,
      partnerCode: vm.partnerCode,
      shortCode: vm.shortCode,
      promoCampaignId: vm.promoCampaignId,
      priceCampaignId: vm.priceCampaignId,
      parameters: vm.parameters
    };

    this.callFormHandler(vm);

    return this.busyService.busy$(
      'Signing up',
      this.createEcAccount(company, user, vm.password, householdFilers, stats, cmsInfo, vm.partnerCode).pipe(
        concatMap(cu =>
          this.tokenService.getAccessTokenFromIdentityServer(cu.user, vm.password, serviceName)
        ),
        concatMap(tokenResponse => {
          if (tokenResponse) {
            // Can use the token response to sign-in.
            this.signin();
            // Hack: give token processing some time before turning off spinner
            // TODO: explain why this hack is needed
            return timer(1000).pipe(map(() => true));
          } else {
            // Tell the user to sign-in
            this.router.navigate([WellKnownRoutes.SignupSuccess]);
            return of(true);
          }
        })
      )
    );
  }

  private callFormHandler(vm: SignupVm) {
    if (pardotVisitorHandler) {
      const formData: FormData = new FormData();
      formData.append('pardot_visitor_id', vm.pardotVisitorId);
      formData.append('email', vm.email);
      formData.append('first_name', vm.firstName);
      formData.append('last_name', vm.lastName);
      formData.append('phone_number', vm.phone);
      formData.append('username', vm.username);
      formData.append('priceCampaignId', vm.priceCampaignId);
      formData.append('promoCampaignId', vm.promoCampaignId);
      formData.append('partner code', vm.partnerCode);
      formData.append('ShortCode', vm.shortCode);
      this.http.post(pardotVisitorHandler, formData)
      .pipe(
        catchError(error => {
            this.sessionService.logger.warn(`Pardot formhandler error`, error);
            return empty();
        }));
      }
  }

  /** Create Company and User in ECommerce app, after creating them in SurePayroll. */
  private createEcAccount(company: Partial<Company>, user: User, password: string,
    householdFilers: Partial<HouseholdFilers>, stats: Partial<Stats>, lead: Partial<SignupLeadInfo>, partnerCode: string) {
    return this.createSpAccount(company, user, password, partnerCode).pipe(
      catchError(error => {
        const msg = this.interpretSpNewAccountError(error);
        error = new SPAccountCreationError(error, company, user, msg);
        this.sessionService.logger.error(`createSpAccount error`, error);
        return throwError(error);
      }),
      map(data => {
        company.bcl = data.bclCode;
        // Add label that Identity Server will prefix to the userKey
        user.oidcSub = subPrefix + data.userKey;
        company.companyType = company.isHousehold ? CompanyType.Household : null;
        return { company, user, householdFilers, stats, lead };
      }),
      concatMap(payload =>
        this.http.post(newEcAccountUrl, payload).pipe(
          map(() => payload),
          catchError(error => {
            // TODO: Analyze failure and try to recover?
            error = new ECAccountCreationError(error, company, user);
            this.sessionService.logger.error(`createEcAccount error`, error);
            return throwError(error);
          })
        )
      )
    );
  }

  /** Creates the SurePayroll user and account */
  private createSpAccount(company: Partial<Company>, user: User, password: string, partnerCode: string) {
    const { username: username, firstName, lastName, email, phone } = user;

    const cleanPhone = phone.replace(/[^0-9]/g, ''); // must strip all non-digits from phone

    const payload: SpNewAccountPayload = {
      partnerCode: partnerCode || 'SURE', // per EPetrisor
      username,
      firstName,
      lastName,
      email,
      phone: cleanPhone,
      password
    };
    return this.http.post<SpNewAccountResponseData>(newSpAccountUrl, payload);
  }

  /** Interpret the SP New Account error in a user friendly way. */
  private interpretSpNewAccountError(error: any) {
    this.sessionService.logger.error('SP New Account error', error);
    const { status, body } = error;
    let message: string;
    if (status && status > 400 && status < 500 && body && Array.isArray(body)) {
      // A known SP Account error. Prepare it for the user.
      message = body[0]; // error text is a string in the body array
      if (/already exists/.test(message)) {
        // rephrase this one
        message = 'Please try a different username';
      }
    } else {
      // Unrecognized message
      message = `Sorry but we had trouble creating your account.
      Please try signing in again. If you are unable to sign in,
       use the "Chat" bubble in the bottom right corner to contact support.`;
    }
    return message;
  }
}

// https://devcoreapi.surepayroll.com/company/swagger/index.html
/** Payload for request to SP API to create new account */
interface SpNewAccountPayload {
  partnerCode: string;
  username: string;
  password: string;
  firstName: string;
  lastName: string;
  email: string;
  phone: string;
}

/** SP API response data when it creates new account */
interface SpNewAccountResponseData {
  bclCode: string;
  userKey: number; // integer
}
