import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  RouterStateSnapshot
} from '@angular/router';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { AccountStatusGuardHelper } from './account-status-guard-helper';

const guardName = 'AccountDetailsGuard';

/**
 * Guard route to Account Details page
 */
@Injectable({ providedIn: 'root' })
export class AccountViewGuard implements CanActivate {

  constructor(private accountStatusGuardHelper: AccountStatusGuardHelper) {  }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    return this.signedInWithCompanyPackage$(state);
  }

  private signedInWithCompanyPackage$(state: RouterStateSnapshot) {
    return this.accountStatusGuardHelper.signedInWithCompanyPackage$(
      guardName,
      state
    ).pipe(
      map(company => {
        return company ? true : false;
      })
    );
  }
}
