import { Component, OnInit } from '@angular/core';
import { AuthService } from '@core/services/auth-services/auth.service';
import { environment } from '@environment';

@Component({
  selector: 'sp-auth-failed',
  template: `
    <div class="card-center standard">
      <p class="h3 mt-3 mb-3">We're sorry</p>
      <p>
        <b>We've experienced an unexpected error that signed you out of the application.</b>
      </p>
      <div *ngIf="authService.errors$ | async as error">
        The error message was:
        <p><i>{{error}}</i><p>
      </div>
      <p>
        You may be able to try again by signing back in.
        If that doesn't work, <sp-contact-support></sp-contact-support>.
      </p>
      <button type="text" (click)="signout()" class="btn btn-primary btn-cta">Continue</button>
    </div>
  `
})
export class AuthFailedComponent implements OnInit {

  constructor(public authService: AuthService) { }

  ngOnInit() {
    this.authService.clearReroute('AuthFailedComponent');
  }

  signout() {
    // Actually signs-out first
    this.authService.logEvent('AuthFailedComponent called AuthService.signout() after user pressed button');
    this.authService.signout();
  }
}
