import { ValidatorFactory, ValidationContextBase, Validator } from '@validation';
import { EntitySelectors } from '@entity';
import { map, first } from 'rxjs/operators';


export class DynamicRangeValidator extends Validator {
    static create(group: string, context?: Partial<ValidationContextBase>) {
        return new DynamicRangeValidator(group, context);
    }

    private static minMessageTemplate = '%displayName% must be greater than %minValue%';
    private static maxMessageTemplate = '%displayName% must be less than %maxValue%';
    private static betweenMessageTemplate = '%displayName% must be between the values of %minValue% and %maxValue%';
    private static messageTemplate = '%displayName% does not conform to limit range.';

    constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
        super('dynamicRange', group,
            (value, context) => {
                if (!value) { return true; }

                // Can't find a min/max range factories
                let minIsValid = true;
                let maxIsValid = true;

                if (context.allowZeroProvider != null) {
                    const allowZero = this.getVal(context, context.allowZeroProvider);
                    context.allowZero = allowZero;
                    if (context.allowZero === true && Number.parseFloat(value) === 0) {
                        return true;
                    }
                }

                if (context.minProvider != null) {
                    const minValue = this.getVal(context, context.minProvider);
                    context.minValue = minValue;

                    if (minValue != null && !(value >= minValue)) {
                        minIsValid = false;
                    }
                }

                if (context.maxProvider != null) {
                    const maxValue = this.getVal(context, context.maxProvider);
                    context.maxValue = maxValue;

                    if (maxValue != null && !(value <= maxValue)) {
                        maxIsValid = false;
                    }
                }

                if (minIsValid === false && maxIsValid === false) {
                    context.messageTemplate = DynamicRangeValidator.betweenMessageTemplate;
                    return false;
                }

                if (minIsValid === false) {
                    context.messageTemplate = DynamicRangeValidator.minMessageTemplate;
                    return false;
                }

                if (maxIsValid === false) {
                    context.messageTemplate = DynamicRangeValidator.maxMessageTemplate;
                    return false;
                }

                return true;
            },
            {
                ...validatorContext,
                messageTemplate: validatorContext && validatorContext.messageTemplate || DynamicRangeValidator.messageTemplate
            });
    }

    private getVal(project: any, myPath: string) {
        return myPath.split('.').reduce((res, prop) => {
            if (res == null) { return null; }
            return res[prop];

        }, project);
    }
}
