import { ValidationContextBase, Validator } from '@validation';

export class VerifiedAddressesValidator extends Validator {

    static create(group: string, context?: Partial<ValidationContextBase>) {
        return new VerifiedAddressesValidator(group, context);
    }

    private static messageTemplate = 'The address must be verified';
    private static employeeMessageTemplate = 'The home address must be verified.';
    private static companyMessageTemplate = 'Not all company addresses are verified. Please validate or remove them.';

    constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
        super('addressMustBeVerified', group,
            (value, context) => {
                if (!value) { return true; }

                const isValid = value.isVerified && value.geoCode != null && value.geoCode !== '';

                if (value.isHome != null) {
                    if (value.isHome === false) {
                        context.messageTemplate = VerifiedAddressesValidator.companyMessageTemplate;
                    }

                    if (value.isHome === true) {
                        context.messageTemplate = VerifiedAddressesValidator.employeeMessageTemplate;
                    }
                }


                return isValid;
            },
            {
                ...validatorContext,
                messageTemplate: validatorContext && validatorContext.messageTemplate || VerifiedAddressesValidator.messageTemplate
            });
    }
}
