import { ClassProvider, ErrorHandler, Injectable, Injector } from '@angular/core';

import * as Raven from 'raven-js';

import { configureLogging } from './sentry.functions';
import { environment } from '@environment';
import { Router } from '@angular/router';
import { WellKnownRoutes } from '@app/core';

configureLogging();

@Injectable()
export class CustomErrorHandler extends ErrorHandler {

  constructor(protected injector: Injector) {
    super();
  }

  handleError(err: any): void {
    if (!environment.production) {
      super.handleError(err);

      // Can't inject Router directly. Causes circular dependency
      // TODO: Discover who commented out and why.
      // const router = this.injector.get(Router, null);
      // if (router) {
      //   router.navigate([WellKnownRoutes.Failed])
      //     .catch(navErr => {}); // swallow nav error to avoid endless loop
      // }
    }
  }
}

@Injectable()
export class SentryErrorHandler extends CustomErrorHandler {

  constructor(injector: Injector) {
    super(injector);
  }

  handleError(err: any): void {
    try {
      Raven.captureException(err.originalError || err);
    } finally {
      super.handleError(err);
    }
  }
}

export function errorProviders() {
  const ERROR_HANDLER_SENTRY: (ClassProvider)[] = [
    { provide: ErrorHandler, useClass: SentryErrorHandler },
  ];
  const ERROR_HANDLER: (ClassProvider)[] = [
    { provide: ErrorHandler, useClass: CustomErrorHandler },
  ];
  if (environment.enableSentry) {
    return ERROR_HANDLER_SENTRY;
  } else {
    return ERROR_HANDLER;
  }
}
