import { CompanyEntityBase } from './company-entity-base';
import { dateToday } from '@utils/date-utils';

import { AccountStatus, CompanyType, FeinStatus, PayrollFrequency } from '@model/codes-enums';

/** Customer company */
export class Company extends CompanyEntityBase {
  id: string;
  bcl: string;
  /** Informal entity name. Might be the DBA. */
  name: string;
  status: AccountStatus = AccountStatus.Open;
  /** Household or Company discriminator */
  isHousehold = false;
  companyType: CompanyType;
  /** True if the user is finished adding Ees (or intends not to add any at all) */
  doneAddingEmployees = false;
  /** Account name as known to the bank. */
  bankAccountName: string;
  /** Bank account's routing number. */
  bankAccountRn: string;
  /** Bank name that goes with the RN number. Set when have verified the RN */
  bankName = '';
  /** The bank account number */
  bankAccountNumber: string;
  /** checking */
  // TODO: Remove this field. Per Zorian, can only be a Checking account.
  bankAccountType: 'checking';
  /** How often the customer expects to run payroll. */
  payrollFrequency: PayrollFrequency;
  /** If this company had a payroll in the current year */
  hasPriorPayroll: boolean;
  /** If this account requires special deductions */
  needDeductionSetup: boolean;
  /**
   * ISO date string when this customer's enrollment process began.
   * Time is deliberately pinned to midday in Chicago
   */
  enrollmentStartDate = dateToday();
  /**
   * ISO date/time string when this company enrollment is closed,
   * for reasons identified in the status field.
   * We actually care about the TIME (not just the date). Store in proper GMT?
   */
  enrollmentCloseDate: string;

  // Taxes ...

  /** Federal employer identification number */
  fein: string;

  /** If the use applies for a fein, is invalid or verified */
  feinStatus: FeinStatus;

  /** Number of times validated. Will prevent further (expensive) validation after some number of tries */
  feinValidationCount = 0;

  /** Legal entity name, as entered by the user. Must be same as legalNameVerified to enroll. */
  legalName: string;

  /** Verified legal entity name (via FEIN verification). User cannot set. */
  legalNameVerified: string;

  /** Reason to Skip verification: 'ServiceUnavailable' if TinCheck Service was not available */
  legalNameVerificationSkipReason: string;

  /** Is the Company an agricultural company */
  isAgricultural = false;

  /** When the customer would like to cut the first payroll checks */
  firstCheckDate: string;

  /** First pay period start date */
  periodStartDate: string;

  /** First pay period end date used to build schedule */
  periodEndDate: string;

  /** When the customer would like to cut the second payroll checks */
  secondCheckDate: string;

  /** Second pay period start date */
  secondPeriodStartDate: string;

  /** Second pay period end date used to build schedule */
  secondPeriodEndDate: string;

  /** Determines if period should always fall on the last day of the month for first period */
  firstPeriodEndOfMonth: boolean;

  /** Determines if check should always fall on last day of month for first check */
  firstCheckEndOfMonth: boolean;

  /** Determines if period should always fall on the last day of the month for second period */
  secondPeriodEndOfMonth: boolean;

  /** Determines if check should always fall on last day of month for second check */
  secondCheckEndOfMonth: boolean;

  /** do we show earnings tab */
  showEarningsTab = false;

}

export const REASON_SERVICE_UNAVAILABLE = 'ServiceUnavailable';

/** Returns the displayable "kind" of customer - 'Household' or 'Company' */
export function companyKind(company: Company): string {
  return company ? company.isHousehold ? 'Household' : 'Company' : '-';
}
