export enum WellKnownRoutes {
  Abandoned = '/abandoned',
  AboutYou = '/about-you',
  AccountView = '/account-view',
  Admin = '/admin',
  AdminAccountView = '/admin/account-view',
  AuthFailed = '/error',
  BankAccount = '/company-bank-account',
  CompanyDeductions = '/employees/company-deductions',
  CompanyInfo = '/company-info',
  CompanyTaxInfo = '/tax-info',
  ConcurrencyFailure = '/concurrency-failure',
  Dashboard = '/dashboard',
  Documents = '/documents',
  Earnings = '/employees/earnings',
  Employees = '/employees',
  Enrolled = '/enrolled',
  Failed = '/failed',
  Processing = '/processing',
  Review = '/review',
  SignedOut = '/signed-out',
  Signup = '/signup',
  SignupSuccess = '/signup/success',
  Unauthorized = '/unauthorized',
}
export const WellKnownRoutes_entries = Object.keys(WellKnownRoutes).map(k => WellKnownRoutes[k as any] as WellKnownRoutes);

export function isWellKnownRoute(path: string) {
  return WellKnownRoutes_entries.some(entry => path.startsWith(entry));
}
