import { AsyncValidator, ValidationContextBase, ValidatorFactory } from '@validation';
import { EntitySelectors } from '@entity';
import { combineLatest } from 'rxjs';
import { map, first } from 'rxjs/operators';

export class MustHaveEmployeesValidator extends AsyncValidator {

  static factory(entitySelectors: EntitySelectors): ValidatorFactory {
    return {
      name: 'mustHaveEmployees',
      fn: (group, context) => {
        return new MustHaveEmployeesValidator(entitySelectors, group, context);
      }
    };
  }

  private static messageTemplate = `You have not entered employees. Confirm this is OK by answering "No" to the question %displayName%`;

  constructor(entitySelectors: EntitySelectors,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('mustHaveEmployees', group,
      (value, context) => {
        return entitySelectors.employee.entities$.pipe(
          map(ees => {
            ees = ees.filter(ee => !ee.isRemoved);
            // valid if has employees
            return ees.length > 0;
          }),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || MustHaveEmployeesValidator.messageTemplate
      }
    );
  }
}
