import { Injectable } from '@angular/core';
import { UserManager } from 'oidc-client';

import { oidcManagerConfig } from './oidc-manager-config';
import { OidcManager } from './oidc-manager';

@Injectable({providedIn: 'root'})
export class OidcManagerFactory {
  /** Create a new, configured oidc-client UserManager */
  create(): OidcManager {
    return new UserManager(oidcManagerConfig);
  }
}
