import {
  Component, Input, Output, EventEmitter, ContentChildren,
  OnDestroy, forwardRef, AfterViewInit, OnInit, Optional, Directive,
  HostListener, AfterContentInit, Renderer2, ElementRef, ViewContainerRef, ViewChild
} from '@angular/core';
import { RadioButtonComponent } from './radio-button.component';
import { Subscription } from 'rxjs';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { ModelComponent } from '../model.component';

@Directive({
  selector: '[spRadioGroupValueAccessor]',
  providers: [
    { provide: NG_VALUE_ACCESSOR, useExisting: RadioGroupValueAccessorDirective, multi: true }
  ]
})
export class RadioGroupValueAccessorDirective implements AfterViewInit, OnDestroy, ControlValueAccessor {

  private subscriptions: Subscription[] = [];
  private disabled = false;
  private accessorOnChange = (...args: any[]) => { };
  private accessorOnTouched = (...args: any[]) => { };
  private currentValue: string;

  @Input() radioButtons: RadioButtonComponent[];
  @Input() name: string;
  @Output() modelChange = new EventEmitter();

  writeValue(val: any) {
    this.currentValue = val;
    this.syncUiState(val);
    this.modelChange.emit(val);
  }
  registerOnChange(fn: (_: any) => void) {
    this.accessorOnChange = fn;
  }
  registerOnTouched(fn: (_: any) => void) {
    this.accessorOnTouched = fn;
  }
  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  @HostListener('blur')
  blur() {
    this.accessorOnTouched();
  }

  ngAfterViewInit() {
    this.cleanUp();
    // setTimeout: hack to avoid: ExpressionChangedAfterItHasBeenCheckedError
    // https://github.com/angular/angular/issues/17572#issuecomment-309229619
    setTimeout(() => {
      if (this.radioButtons) {
        this.radioButtons.forEach(
          rb => {
            rb.name = this.name; // propagate name
            const sub1 = rb.valueChange.subscribe((v: any) => this.valueChanged(v));
            this.subscriptions.push(sub1);
          });
      }
      this.syncUiState(this.currentValue);
    }, 0);
  }

  ngOnDestroy() {
    this.cleanUp();
  }

  private valueChanged(v: any) {
    this.currentValue = v;
    this.accessorOnChange(this.currentValue);
    this.syncUiState(this.currentValue);
  }
  private cleanUp() {
    this.subscriptions.forEach(s => s.unsubscribe());
    this.subscriptions.splice(0, this.subscriptions.length);
  }

  /** Sync UI with respect to model changes if needed: propagate state to children */
  private syncUiState(val: any): void {
    if (!this.radioButtons) {
      return;
    }
    this.radioButtons.forEach(rb => rb.selectedValue = val);
  }
}

const CLICK_THRESHOLD = 150; // ms

@Component({
  selector: 'sp-radio-group',
  templateUrl: './radio-group.component.html'
})
export class RadioGroupComponent implements OnInit {
  static nameCounter = 0;

  @Input() label: string;
  @Input() name: string;
  @Input() propertyName: string;
  @Input() tooltip = false;
  @Output() change = new EventEmitter<string>();
  @ViewChild('tooltipButton') tooltipButton: any;
  @ViewChild('root') root: any;

  @Input() set group(value: string) { this._group = value; }
  private _group: string;
  @Input() set model(value: any) { this._model = value; }
  private _model: any;
  @Input() set modelType(value: string) { this._modelType = value; }
  private _modelType: string;

  @ContentChildren(RadioButtonComponent) radioButtons: RadioButtonComponent[];

  private currentValue: string;
  open = false;
  tClose = 0;

  constructor(
    private renderer: Renderer2,
    private elementRef: ElementRef,
    private viewContainer: ViewContainerRef,
    @Optional() public modelContext?: ModelComponent
  ) { }

  ngOnInit() {
    this.name = (this.name || '') + '$' + RadioGroupComponent.nameCounter++;
  }

  /** Get the latest data-bound group. If not bound, get group from modelContext. */
  get group(): string {
    return this._group || (this.modelContext && this.modelContext.group);
  }

  /** Get the latest data-bound model. If not bound, get model from modelContext. */
  get model(): any {
    return this._model || (this.modelContext && this.modelContext.model);
  }

  /** Get the latest data-bound model type. If not bound, get model type from modelContext. */
  get modelType(): string {
    return this._modelType || (this.modelContext && this.modelContext.modelType);
  }

  openTooltip() {
    const d = Date.now() - this.tClose;
    if (d < CLICK_THRESHOLD) {
      return;
    }
    if (!this.open) {
      const myRoot = this.root.nativeElement;
      // give some millisecs to open the tooltip-div
      this.open = true;
      setTimeout(() => {
        const tooltipDiv = myRoot.querySelector('#tooltipZone') as HTMLDivElement;
        if (tooltipDiv) {
          tooltipDiv.focus();
        }
      });
    } else {
      this.open = false;
    }
  }
  openKeypress(event: any) {
    if (event.charCode === 13 || event.charCode === 32) {
      // intro or space
      this.openTooltip();
    }
  }

  closeTooltip() {
    this.tClose = Date.now();
    this.open = false;
    this.tooltipButton.nativeElement.focus();
  }

  closeKeypress(event: any) {
    if (event.charCode === 13 || event.charCode === 32) {
      // intro or escape or space
      this.closeTooltip();
    }
  }

  getValue(): string {
    if (this.model && this.modelType && this.propertyName) {
      return this.model[this.propertyName];
    }

    return this.currentValue;
  }

  setValue(value: string): void {
    if (this.model && this.modelType && this.propertyName) {
      this.model[this.propertyName] = value;
    }

    this.currentValue = value;
    if (this.modelContext) {
      this.modelContext.notifyOfModelChange();
    }
    this.change.emit(this.currentValue);
  }

}
