import { Component, Input } from '@angular/core';
import { EsignService, LoggerService } from '@app/core';
import { IframeService } from '@app/core/services/iframe.service';

@Component({
  selector: 'sp-view-docs',
  template: `
  <button *ngIf="!!envelopeId" class="btn btn-secondary btn-icon-combo mt-3" type="button"
  (click)="viewDocs(envelopeId);">
    <span>View Signed Documents </span>
    <i class="material-icons icon d-inline">chevron_right</i>
  </button>
  `
})
export class ViewDocsComponent {

  @Input() envelopeId: string;

  constructor(
    private esignService: EsignService,
    private iframeService: IframeService,
    private logger: LoggerService,
  ) {
    this.esignDone = this.esignDone.bind(this);
    this.esignError = this.esignError.bind(this);
  }

  viewDocs(envelopeId: string) {
    this.esignService.getEnvelopeUrl(envelopeId).subscribe(url => {
      if (url) {
        this.iframeService.show(url, this.esignDone);
      } else {
        this.esignError('Document Signing service cannot find the documents',
          `viewDocs: No envelope URL for envelope id=${envelopeId}`);
      }
    });
  }

  private esignDone(status: string) {
    if (status !== 'viewing_complete') {
      this.esignError(null, `Unexpected document status ${status}`);
    }
  }

  private esignError(message: string, applicationError?: string) {
    if (applicationError) {
      // error info for a programmer
      this.logger.error(`ViewDocsComponent: ${applicationError}`);
    }
  }

}
