import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders, HttpParams } from '@angular/common/http';
import { ajax } from 'rxjs/ajax';

import { from, Observable, of, throwError } from 'rxjs';
import { catchError, concatMap, map, tap } from 'rxjs/operators';

import { environment } from '@environment';
import { OidcSignupUser } from './oidc-signup-user';
import { OidcTokenResponse } from './oidc-token-response';
import { User } from '@model';

const serviceName = 'OidcTokenService';

@Injectable({ providedIn: 'root' })
export class OidcTokenService {
  /** Log a significant Auth cycle event. Set by the AuthService into which this token service is injected. */
  logEvent = (event: string) => {};

  /** Key of the accessToken in browser storage that was captured during sign-up */
  signupTokenKey = 'signupToken';

  constructor(private http: HttpClient) {}

  /**
   * Clear the access token info acquired during sign-up in browser storage.
   * @param [caller] caller's name
   */
  clearSignupToken(caller = serviceName) {
    let token: string;
    if (window.sessionStorage) {
      token = sessionStorage.getItem(this.signupTokenKey);
      if (token) {
        sessionStorage.removeItem(this.signupTokenKey);
        const msg = `${caller || 'caller'} cleared an existing signup token from browser storage`;
        this.logEvent(msg);
      }
    }
    return token;
  }

  /**
   * Get the access token info acquired during sign-up from browser storage if it's available.
   * @param [caller] caller's name
   */
  getSignupToken(caller = serviceName): OidcTokenResponse {
    let token: string;
    if (window.sessionStorage) {
      token = sessionStorage.getItem(this.signupTokenKey);
      const msg = `${caller || 'caller'} asked for signupToken from browser storage and will get ${
        token ? token.substr(0, 50) : 'null'
      }"`;
      this.logEvent(msg);
    }
    try {
      return JSON.parse(token) as OidcTokenResponse;
    } catch (e) {
      return null;
    }
  }

  /**
   * Get the SP API AccessToken after successful signup,
   * with the username and password entered by the user during signup
   */
  getAccessTokenFromIdentityServer(
    user: User,
    password: string,
    caller = serviceName
  ): Observable<OidcTokenResponse> {
    this.clearSignupToken();
    const options = {
      grant_type: 'password',
      scope: environment.oidcScope,
      client_id: environment.oidcClientId,
      username: user.username,
      password
    };

    // Strictly for mocking; the IdentityServer would error if we sent these
    if (environment.mockServices) {
      (options as any).email = user.email;
      (options as any).sub = user.oidcSub;
    }

    let result$: Observable<OidcTokenResponse>;
    const url = `${environment.oidcAuthRealm}/connect/token`;

    if (environment.mockServices) {
      // Cannot make HttpClient succeed.
      // Browser refuses to give the client the results even though it gets them
      // Missing a configuration step that isn't needed by RxJS.ajax or jQuery
      // But ... if mocking, we will use HttpClient because in-mem-web-api is setup to intercept :-)
      const body = new HttpParams({fromObject: options}).toString();
      const headers = new HttpHeaders({'Content-Type': 'application/x-www-form-urlencoded' });
      result$ = this.http.post<OidcTokenResponse>( url, body, { headers });
    } else {
      result$ = postWithRxjsAjax(url, options);
    }

    return result$.pipe(
      tap(tokenResponse => this.setSignupToken(tokenResponse, caller)),
      catchError(error => of(null)) // OK if errors; user will just have to sign-in.
    );
  }

  /**
   * Set the access token info into browser storage .
   * @param token API access token info acquired during sign-up
   * @param [caller] caller's name
   */
  setSignupToken(token: OidcTokenResponse, caller = serviceName) {
    if (window.sessionStorage) {
      const toke = token ? JSON.stringify(token) : null;
      if (toke) {
        sessionStorage.setItem(this.signupTokenKey, toke);
        const msg = `${caller || 'caller'} set signupToken to ${
          toke ? toke.substr(0, 50) : 'null'
        }"`;
        this.logEvent(msg);
      } else {
        this.clearSignupToken(caller);
      }
    }
  }
}

function postWithRxjsAjax(url: string, tokenRequest: {}) {
  return ajax({
    method: 'POST',
    crossDomain: true,
    url,
    body: tokenRequest,
    headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
  }).pipe(map(r => r.response as OidcTokenResponse));
}

// <script src="https://ajax.googleapis.com/ajax/libs/jquery/3.3.1/jquery.min.js" type="text/javascript"></script>
// return postWithjQuery(url, options) // works, if you load jQuery just to get Ajax
function postWithjQuery(url: string, tokenRequest: {}) {
  return from((window as any)['$'].ajax({
    type: 'POST',
    crossDomain: true,
    url: url,
    data: tokenRequest,
    dataType: 'json',
    contentType: 'application/x-www-form-urlencoded'
  }) as Observable<OidcTokenResponse>);
}
