import { Pipe, PipeTransform } from '@angular/core';

import { USStateCodes } from '@model/codes-enums';

// Transform an address object into a single line of text
@Pipe({ name: 'stateName', pure: true })
export class StateNamePipe implements PipeTransform {
  transform(stateCode: string, ...args: any[]) {
    return USStateCodes.getDisplayName((stateCode || '').toUpperCase()) || 'missing or invalid';
  }
}
