import { InjectionToken, Injectable, Optional, Inject } from '@angular/core';

import { Observable, forkJoin, of, combineLatest, Subject } from 'rxjs';
import { map, switchMap, startWith, shareReplay } from 'rxjs/operators';
import { tag } from 'rxjs-spy/operators/tag';

import { EntityInfo } from '@entity/entity-selectors';
import { ModelValidationMetadataMap, ValidationMetadata, ValidatorMetadata, PropertyMetadata } from './validation-metadata';
import { ValidatorFactory, ValidationError, ValidationContext, ValidationContextBase } from './validator';
import { ValidationTriggerService } from '@validation/validation-trigger.service';

export type ModelValidationMetadataLike = (ModelValidationMetadataMap | ModelValidationMetadataMap[])[];

export const VALIDATION_ENTITY_METADATA = new InjectionToken<ModelValidationMetadataLike>('ValidationEntityMetadata');
export const VALIDATOR_FACTORY = new InjectionToken<ValidatorFactory[]>('ValidationFactory');

export type ValidationResult = ValidationError[] | null;

export type ValidatorPredicate = (validator: ValidatorMetadata, property?: PropertyMetadata) => boolean;

@Injectable({ providedIn: 'root' })
export class ValidationService {

  readonly validationMetadata: ValidationMetadata;

  private modelValidated = new Subject<{ model: any, validationErrors: ValidationError[] }>();

  constructor(
    private validationTriggerService: ValidationTriggerService,
    @Optional() @Inject(VALIDATION_ENTITY_METADATA) validationMetadata: ModelValidationMetadataLike,
    @Optional() @Inject(VALIDATOR_FACTORY) factories: ValidatorFactory[]
  ) {
    const metadata = (validationMetadata || [])
      .map(item => Array.isArray(item) ? item : [item])
      .reduce((prev, curr) => [...prev, ...curr], []);
    this.validationMetadata = new ValidationMetadata(metadata, factories || []);
  }

  get modelValidated$() {
    return this.modelValidated.asObservable();
  }

  /**
   * Executes all asynchronous validation rules defined for the provided entityType.
   * @param model The model to be validated
   * @param modelType The type of the model to be validated
   * @param async true to execute asynchronous validators, false to execute synchronous validators
   * @param predicate Optional predicate to control execution of provided validator
   */
  validateModel(model: any, modelType: string, async: true,
    predicate?: ValidatorPredicate): Observable<ValidationResult>;

  /**
   * Executes all synchronous validation rules defined for the provided entityType.
   * @param model The model to be validated
   * @param modeltype The type of the model to be validated
   * @param async true to execute asynchronous validators, false to execute synchronous validators
   * @param predicate Optional predicate to control execution of provided validator
   */
  validateModel(model: any, modeltype: string, async?: boolean,
    predicate?: ValidatorPredicate): ValidationResult;

  validateModel(model: any, modelType: string, async?: boolean,
    predicate?: ValidatorPredicate): Observable<ValidationResult> | ValidationResult {

    let validationErrors: (Observable<ValidationError> | ValidationError)[] = [];
    predicate = predicate || (() => true);

    const modelValidationMetadata = this.validationMetadata.getModelConfig(modelType);
    if (modelValidationMetadata) {
      // Execute model level validators
      const modelValidators = this.filterValidators(modelValidationMetadata.validators, async, predicate);
      validationErrors = [...validationErrors, ...modelValidators.map(modelValidator => {
        const context: ValidationContext = {
          model: model,
          modelType: modelType,
          metadata: modelValidationMetadata,
          isClient: true,
          isServer: false,
          ...modelValidator.context as ValidationContextBase
        };
        if (!modelValidator.evalCondition(context)) {
          return async ? of(null) : null;
        }
        const validator = modelValidator.getValidator();
        return validator.validate(model, context);
      })];

      // Execute property validators
      const properties = modelValidationMetadata.properties;
      properties.forEach(propertyMetadata => {
        const propertyValidators = this.filterValidators(propertyMetadata.validators, async,
          validator => predicate(validator, propertyMetadata));
        validationErrors = [...validationErrors, ...propertyValidators.map(propertyValidator => {
          const context: ValidationContext = {
            model: model,
            modelType: modelType,
            metadata: modelValidationMetadata,
            propertyName: propertyMetadata.propertyName,
            displayName: propertyMetadata.displayName,
            isClient: true,
            isServer: false,
            ...propertyValidator.context as ValidationContextBase
          };
          if (!propertyValidator.evalCondition(context)) {
            return async ? of(null) : null;
          }
          const validator = propertyValidator.getValidator();
          return validator.validate(model[propertyMetadata.propertyName], context);
        })];
      });
    }

    if (async) {
      const asyncErrors = validationErrors as Observable<ValidationError>[];
      const asyncResult = forkJoin(asyncErrors).pipe(
        map(errors => {
          errors = errors.filter(error => !!error);
          return errors.length ? errors : null;
        })
      );
      asyncResult.subscribe(errors => {
        this.modelValidated.next({ model: model, validationErrors: errors });
      });

      return asyncResult;
    }

    validationErrors = validationErrors.filter(error => !!error);
    const result = validationErrors.length ? validationErrors as any : null;
    this.modelValidated.next({ model: model, validationErrors: result });
    return result;
  }

  /**
   * Executes all asynchronous and synchronous validation rules for all entities in the cache
   * @param predicate Optional predicate to control execution of provided validator
   * @returns Observable emitting validation errors and entity info
   */
  validateEntityCache(predicate?: ValidatorPredicate) {
    return this.validationTriggerService.triggerValidations$.pipe(
      switchMap(entities => {
        if (entities.length === 0) {
          return of({ entities, validationErrors: [] as ValidationError[] });
        }

        const syncValidations = of(this.validateEntities(entities, false, predicate));
        const asyncValidations = this.validateEntities(entities, true, predicate)
          .map(o => o.pipe(startWith<ValidationError[]>([])));
        return combineLatest(syncValidations, ...asyncValidations).pipe(
          map(results => results.reduce((prev, curr) => [...prev, ...(curr || [])], [])),
          map(validationErrors => {
            return { entities, validationErrors };
          })
        );
      }),
      tag('validateEntityCache'),
      shareReplay(1)
    );
  }

  private filterValidators(validators: ValidatorMetadata[], async: boolean,
    predicate: (validator: ValidatorMetadata) => boolean): ValidatorMetadata[] {

    if (!async) {
      return validators.filter(validator => !validator.async && predicate(validator));
    }

    return validators.filter(validator => validator.async && predicate(validator));
  }

  private validateEntities(entities: EntityInfo[], async?: boolean, predicate?: ValidatorPredicate): ValidationError[];
  private validateEntities(entities: EntityInfo[], async?: true, predicate?: ValidatorPredicate): Observable<ValidationError[]>[];
  private validateEntities(entities: EntityInfo[], async?: boolean,
    predicate?: ValidatorPredicate): ValidationError[] | Observable<ValidationError[]>[] {

    const conditionalEntities = entities
      .filter(entityInfo => {
        const modelMetadata = this.validationMetadata.getModelConfig(entityInfo.entityType);
        return modelMetadata && modelMetadata.evalCondition(entities, entityInfo.entity);
      });

    if (!async) {
      return conditionalEntities
        .map(entityInfo => this.validateModel(entityInfo.entity, entityInfo.entityType, false, predicate))
        .reduce((prev, curr) => [...prev, ...(curr || [])], []);
    }

    return conditionalEntities
      .map(entityInfo => this.validateModel(entityInfo.entity, entityInfo.entityType, true, predicate));
  }
}
