import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { stringify } from '@app/core/utils';

@Injectable({providedIn: 'root'})
export class LoggerService {

  public entries$: Observable<LogEntry>;
  private entriesSubject$: Subject<LogEntry>;

  constructor() {
      this.entriesSubject$ = new Subject<LogEntry>();
      this.entries$ = this.entriesSubject$.asObservable();
  }

  log(message?: any, ...optionalParams: any[]) {
    const args = [message, ...optionalParams];
    console.log.apply(console, args);
    this.nextEntry('log', args);
  }

  debug(message?: any, ...optionalParams: any[]) {
    const args = [message, ...optionalParams];
    console.debug ? console.debug.apply(console, args) : console.log.apply(console, args);
    this.nextEntry('debug', args);
  }

  error(message?: any, ...optionalParams: any[]) {
    const args = [message, ...optionalParams];
    console.error.apply(console, args);
    this.nextEntry('error', args);
  }

  warn(message?: any, ...optionalParams: any[]) {
    const args = [message, ...optionalParams];
    console.warn.apply(console, args);
    this.nextEntry('warn', args);
  }

  private nextEntry(type: logType, args: any[]) {
    this.entriesSubject$.next({
      type: type,
      entry: stringify(args).join('\n')
    });
  }
}

export type logType = 'log' | 'debug' | 'error' | 'warn';

export interface LogEntry {
  type: logType;
  entry: string;
}
