import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'sp-amount-or-percent-value',
  templateUrl: './amount-or-percent-value.component.html'
})
export class AmountOrPercentValueComponent implements OnInit {
  @Input() model: any;
  @Input() isAmountPropertyName: string;
  @Input() propertyNameValue: string;

  @Output() isAmountChange = new EventEmitter();
  @Output() valueChange = new EventEmitter();

  constructor() {
  }
  ngOnInit() {
  }
  setIsAmount(value: boolean): void {
    if (this.model && this.isAmountPropertyName) {
      this.model[this.isAmountPropertyName] = value;
      this.isAmountChange.emit();
    }
  }
  setValue(value: string) {
    const num = Number.parseFloat(value);

    if (this.model && this.propertyNameValue) {
      this.model[this.propertyNameValue] = num;
    }
    this.valueChange.emit();
  }
}
