import { Injectable } from '@angular/core';
import { DatePipe, DecimalPipe } from '@angular/common';

import { createNumberMask, createAutoCorrectedDatePipe } from 'text-mask-addons/dist/textMaskAddons';
import { InputMaskConfig } from './input-mask-config';
import { InputTextComponent } from '@shared/input/input-text.component';
import { LoggerService } from '@core/services';

@Injectable({ providedIn: 'root' }) // register in root so other modules can register presets of their own
export class InputMaskCatalog {

  private maskPresets: { [type: string]: InputMaskConfig } = {};

  constructor(
    private datePipe: DatePipe,
    private decimalPipe: DecimalPipe,
    private logger: LoggerService
  ) {

    this.registerCurrencyMasks(decimalPipe);

    this.registerMaskType({
      maskType: 'digits',
      toView: defaultToView,
      toVm: integerToVm,
      type: 'text',
      mask: createNumberMask({
        prefix: '',
        suffix: '',
        allowDecimal: false,
        includeThousandsSeparator: false
      })
    });

    this.registerMaskType({
      maskType: 'integer',
      toView: defaultToView,
      toVm: integerToVm,
      type: 'text',
      mask: createNumberMask({
        prefix: '',
        suffix: '',
        allowDecimal: false
      })
    });

    // withholding allowance 0 - 25
    this.registerMaskType({
      maskType: 'whAllowance',
      toView: defaultToView,
      toVm: viewVal => viewVal == null ? null : parseInt(viewVal, 10),
      type: 'text',
      placeholderChar: ' ',
      mask: withholdingAllowanceMask,
      showMask : false
    });

    this.registerMaskType({
      maskType: 'percent',
      toView: defaultToView,
      toVm: viewVal => viewVal == null ? null : parseFloat(viewVal.replace(/\,+/g, '')),
      type: 'text',
      mask: createNumberMask({
        prefix: '',
        suffix: '',
        allowDecimal: true,
        decimalLimit: 2
      }),
      append: '%',
      prepend: ''
    });

    this.registerMaskType({
      maskType: 'date',
      toView: (vmVal, editing, format) => datePipe.transform(vmVal, format || 'MM-dd-yyyy'),
      toVm: viewVal => viewVal == null ? null :
        datePipe.transform(new Date(viewVal), 'yyyy-MM-dd') + 'T17:00:00.000Z', // Always use Chicago timezone
      type: 'date',
      mask: false
    });

    this.registerMaskType({
      maskType: 'phone',
      toView: defaultToView,
      toVm: viewVal => viewVal == null ? null : viewVal.replace(/[\(\)\-\s_]+/g, ''),
      type: 'text',
      mask: ['(', /[1-9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/],
      showMask: false,
      placeholder: '(xxx) xxx-xxxx'
    });

    this.registerMaskType({
      maskType: 'ssn',
      toView: defaultToView,
      toVm: dashedNumberToVm,
      type: 'text',
      mask: [/\d/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/],
      showMask: false,
      placeholder: 'xxx-xx-xxxx',
      keepCharPositions: false
    });

    this.registerMaskType({
      maskType: 'fein',
      toView: defaultToView,
      toVm: viewVal => viewVal == null ? null : viewVal.replace(/[\s_]+/g, ''),
      type: 'text',
      mask: [/\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/],
      showMask: false,
      placeholder: 'xx-xxxxxxx',
      keepCharPositions: false
    });

    this.registerMaskType({
      maskType: 'legalname',
      toView: (vmVal, editing, format) => vmVal == null ? null : vmVal.toString().toUpperCase(),
      toVm: viewVal => viewVal == null ? null : viewVal.toString().toUpperCase(),
      type: 'text',
      mask: false,
      showMask : false,
      placeholder: '',
      keepCharPositions: false
    });

    this.registerMaskType({
      maskType: 'ubi',
      toView: (vmVal, editing, format) => vmVal ? vmVal.toString() : null,
      toVm: viewVal => viewVal !== null ? viewVal.replace(/[\s_]+/g, '') : null,
      type: 'text',
      mask: [/\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/],
      showMask: false,
      placeholder: 'xxx-xxx-xxx',
      keepCharPositions: false
    });

    this.registerMaskType({
      maskType: 'routingnumber',
      toView: defaultToView,
      toVm: dashedNumberToVm,
      type: 'text',
      //  \d{9}
      mask: [/\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/],
      showMask: true,
      placeholder: '_________',
      keepCharPositions: false
    });

    this.registerMaskType({
      maskType: 'accountnumber',
      toView: defaultToView,
      toVm: dashedNumberToVm,
      type: 'text',
      //  \d{7,11}
      mask: [/\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d?/, /\d?/, /\d?/, /\d?/],
      showMask: true,
      placeholder: '___________',
      keepCharPositions: false
    });

  }

  private registerCurrencyMasks(decimalPipe: DecimalPipe) {
    const toView = (vmVal: any, editing: boolean): string => vmVal == null ? null :
      (vmVal % 1 === 0 || editing) ? vmVal.toString() : decimalPipe.transform(vmVal, '1.2-2');

    const toVm = (viewVal: string) => viewVal == null ? null : parseFloat(viewVal.replace(/\,+/g, ''));

    const baseMask = {
      toView,
      toVm,
      type: 'text',
      mask: createNumberMask({
        prefix: '',
        suffix: '',
        allowDecimal: true,
        decimalLimit: 2
      }),
      append: '',
      prepend: '$'
    };

    const regMask = (maskType: string, append = '') =>
      this.registerMaskType({...baseMask, maskType, append });

    regMask('currency');
    regMask('currency_per_hour', '/hr' );
    regMask('currency_per_mile', '/mile' );
    regMask('currency_per_unit', '/unit' );
    regMask('currency_per_year', '/yr' );
  }

  public registerMaskType(cv: InputMaskConfig) {
    this.maskPresets[cv.maskType] = cv;
  }

  getMaskForType(maskType: string): InputMaskConfig | null {
    if (maskType == null) {
      return null;
    }
    if (!(maskType in this.maskPresets)) {
      this.logger.error(`Input mask preset with name: [${maskType}] does not exist!`);
      return null;
    }
    return this.maskPresets[maskType];
  }

  applyConfig(inputText: InputTextComponent, maskConfig: InputMaskConfig) {
    inputText.type = maskConfig.type;
    inputText.mask = maskConfig.mask;
    inputText.append = maskConfig.append;
    inputText.prepend = maskConfig.prepend;
    inputText.guide = maskConfig.guide;
    inputText.placeholderChar = maskConfig.placeholderChar;
    inputText.keepCharPositions = maskConfig.keepCharPositions;
    inputText.showMask = maskConfig.showMask;
    if (maskConfig.placeholder) {
      inputText.placeholder = maskConfig.placeholder;
    }

    inputText.textMask = {
      mask: inputText.mask,
      guide: inputText.guide,
      placeholderChar: inputText.placeholderChar,
      keepCharPositions: inputText.keepCharPositions,
      showMask: inputText.showMask
    };
  }
}

// #region helper fns
/** Convert string view value to VM by removing dashes and underscores */
function dashedNumberToVm(viewVal: string): string {
  return viewVal == null ? null : viewVal.replace(/[\s\-_]+/g, '');
}

/** Convert VM value to view with simple toString() */
function defaultToView(vmVal: any, editing: boolean, format: string): string {
  return vmVal == null ? null : vmVal.toString();
}

/** Convert string view value to an integer VM  */
function integerToVm(viewVal: string): number {
  return viewVal == null ? null : parseInt(viewVal.replace(/\,+/g, ''), 10);
}

/** Constrains withholding allowance input to integer 0-25 */
function withholdingAllowanceMask(rawValue: string) {
  if (rawValue.length === 0 || /^\d$/.test(rawValue)) {
    return [/\d/];
  } else if (/^1\d$/.test(rawValue)) {
    return [/1/, /\d/];
  } else {
    return [/2/, /[0-5]/];
  }
}
// #endregion helper fns

