import { NgModule, Optional, SkipSelf } from '@angular/core';
import { environment } from '@environment';

import {
  AddressFinderService,
  BrowserGeolocationService,
  GeolocationService,
  GooglePlacesService
} from './services/location-services';
import { httpInterceptorProviders } from './http-interceptors';
import { throwIfAlreadyLoaded } from './module-import-check';
import { TitleService } from './services/title.service';

/**
 * Application services and components that are core to running the application.
 * It is eagerly loaded when the application starts.
 * This module should be imported exactly once, in the AppModule.
 */
@NgModule({
  // DO NOT declare components here. Put them in `core-components/` or `shared/
  providers: [
    { provide: AddressFinderService, useClass: GooglePlacesService },
    { provide: GeolocationService, useClass: BrowserGeolocationService },
    ...httpInterceptorProviders
  ]
})
/**
 * Core app services
 */
export class CoreModule {
  constructor(
    @Optional()
    @SkipSelf()
    parentModule: CoreModule,
    addressFinder: AddressFinderService,
    titleService: TitleService // inject it to start it
  ) {
    throwIfAlreadyLoaded(parentModule, 'CoreModule');
    addressFinder.loadLibrary();
  }
}
