import { AsyncValidator, ValidatorFactory, ValidationContextBase } from '@validation';
import { EntitySelectors } from '@entity';
import { map, first } from 'rxjs/operators';

export class HouseholdFilerMustExistValidator extends AsyncValidator {

  static factory(entitySelectors: EntitySelectors): ValidatorFactory {
    return {
      name: 'householdFilerMustExist',
      fn: (group, context) => {
        return new HouseholdFilerMustExistValidator(entitySelectors, group, context);
      }
    };
  }

  private static messageTemplate = `Household Federal Tax Information must be provided`;

  constructor(entitySelectors: EntitySelectors,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('householdFilerMustExist', group,
      (value, context) => {
        return entitySelectors.householdFilers.entities$.pipe(
          map(householdFilers => householdFilers && householdFilers.length === 1),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || HouseholdFilerMustExistValidator.messageTemplate
      }
    );
  }
}
