import { Injectable } from '@angular/core';
import { HttpResponse } from '@angular/common/http';

import { ChangeSet, ChangeSetOperation } from 'ngrx-data';

import { Observable } from 'rxjs';

import { createBadRequestResponse$, Db, RequestInfo } from './in-mem-service-utils';
import { CrudCount, saveCompanyChangeSet, saveEmployeeChangeSet,
         saveCompanyDeductionChangeSet, saveEmployeeDeductionChangeSet, saveCompanyEarningChangeSet
       } from './in-mem-save-entities-utils';

/** Metadata that turns SaveEntities requests into in-mem database updates and HTTP responses */
interface SaveAction {
  /** Matches this save action with a request URL that ends in this way */
  endsWith: string;
  /** Name of the action as it should appear in messages */
  actionName: string;
  /** Function that performs the save after applying validations such as the CRUD counts */
  action: (actionName: string, crudCount: CrudCount[], reqInfo: RequestInfo, db: Db) => Observable<HttpResponse<ChangeSet>> ;
  /** Array describing which entity types can be included in a ChangeSet and how many (min/max) of each type are allowed. */
  crudCounts: CrudCount[];
}

@Injectable({ providedIn: 'root' })
export class InMemSaveEntitiesService {

  /** Actions that save an ngrx-data ChangeSet for request URLs */
  private saveActions: SaveAction[] = [
    { endsWith: 'save/company', actionName: 'a company+address pair', action: saveCompanyChangeSet, crudCounts: [
        { entityName: 'Company', min: 0, max: 1 },
        { entityName: 'Address', min: 0, max: 1 }
      ]
    },
    { endsWith: 'save/companyuser', actionName: 'a company+user pair', action: saveCompanyChangeSet, crudCounts: [
        { entityName: 'Company', min: 0, max: 1 },
        { entityName: 'User', min: 0, max: 1 }
      ]
    },
    { endsWith: 'companyearning', actionName: 'company earnings', action: saveCompanyEarningChangeSet, crudCounts: [
      { entityName: 'CompanyEarning', min: 0, max: 50 }
    ]
    },
    { endsWith: 'save/company-deduction', actionName: 'company deductions', action: saveCompanyDeductionChangeSet, crudCounts: [
        { entityName: 'CompanyDeduction', min: 0, max: 50 }
      ]
    },
    { endsWith: 'save/employee-deduction', actionName: 'employee deductions', action: saveEmployeeDeductionChangeSet, crudCounts: [
        { entityName: 'EmployeeDeduction', min: 0, max: 5000 }
      ]
    },
    { endsWith: 'save/employee', actionName: 'an employee graph', action: saveEmployeeChangeSet, crudCounts: [
        { entityName: 'Employee', min: 1, max: 1 },
        { entityName: 'Address', min: 1, max: 1 },
        { entityName: 'EmployeePaymentMethod', min: 0, max: 1}
      ]
    },
    { endsWith: 'save/employee/removed', actionName: 'the isRemoved flag of an employee graph', action: saveEmployeeRemoved, crudCounts: [
        { entityName: 'Employee', min: 1, max: 1 },
        { entityName: 'Address', min: 1, max: 1 },
        { entityName: 'EmployeePaymentMethod', min: 0, max: 1}
      ]
    },
    { endsWith: 'save/taxes', actionName: 'company and/or EE taxes', action: saveCompanyChangeSet, crudCounts: [
        { entityName: 'CompanyTax', min: 0, max: 50 },
        { entityName: 'EmployeeTax', min: 0, max: 5000 }
      ]
    }
  ];

  postRequest(reqInfo: RequestInfo, db: Db): Observable<HttpResponse<ChangeSet>> {
    const url = reqInfo.url;
    const saveAction = this.saveActions.find(action => url.endsWith(action.endsWith));
    if (saveAction) {
      const { action, actionName, crudCounts } = saveAction;
      return action(actionName, crudCounts, reqInfo, db);
    } else {
      return null; // Unknown request. Let regular request processing handle it.
    }
  }
}

/** Restricts SaveEntities request to Update operation that sets the isRemoved flag. Delegates to saveEmployeeChangeSet() */
function saveEmployeeRemoved(actionName: string, crudCounts: CrudCount[], reqInfo: RequestInfo, db: Db) {
  const changeSet = (reqInfo.req as any).body as ChangeSet;
  changeSet.changes.forEach(element => {
    if (element.op !== ChangeSetOperation.Update) {
      const emsg = `Can only update ${element.entityName} when saving ${actionName}`;
      return createBadRequestResponse$(reqInfo, emsg);
    }
  });
  return saveEmployeeChangeSet(actionName, crudCounts, reqInfo, db);
}
