import { Injectable } from 'node_modules/@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { Subject } from 'rxjs';
import { AnalyticsEvent, AnalyticsEventType } from '@core/analytics/analytics-event';
import { AnalyticsEventProvider } from '@core/analytics/analytics-event-provider';

@Injectable()
export class RouterAnalyticsNavigationProvider implements AnalyticsEventProvider {
    events$ = new Subject<AnalyticsEvent>();

    constructor(public router: Router) {
        this.router.events.subscribe(event => {
            if (event instanceof NavigationEnd) {
                this.events$.next({
                    type: AnalyticsEventType.Navigate,
                    payload: event.urlAfterRedirects
                });
            }
        });
    }
}
