import { NgModule } from '@angular/core';
import { Routes } from '@angular/router';

import { AuthFailedGuard } from '@core/routing-guards';
import { SharedModule } from '@app/shared/shared.module';

import { AuthFailedComponent } from './auth-failed.component';
import { OidcCallbackComponent } from './oidc-callback.component';
import { SignedOutComponent } from './signed-out.component';
import { UnauthorizedComponent } from './unauthorized.component';

/** Routes to blend with the main routes */
export const authRoutes: Routes = [
  {
    path: 'error',
    component: AuthFailedComponent,
    data: { title: 'Signin Failed' },
    canDeactivate: [AuthFailedGuard]
  },
  {
    path: 'oidc-callback',
    component: OidcCallbackComponent
    // never visible
  },
  {
    path: 'signed-out',
    component: SignedOutComponent,
    data: { title: 'Signed-out' }
  },
  {
    path: 'unauthorized',
    component: UnauthorizedComponent,
    data: { title: 'Unauthorized' }
  },
];

@NgModule({
  imports: [SharedModule],
  declarations: [
    AuthFailedComponent,
    OidcCallbackComponent,
    SignedOutComponent,
    UnauthorizedComponent
  ]
})
export class AuthComponentsModule {}
