import { Injectable, Optional, Provider } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpParams } from '@angular/common/http';

import { Observable} from 'rxjs';
import { map } from 'rxjs/operators';

import { DefaultDataService, DefaultDataServiceFactory, DefaultDataServiceConfig, HttpUrlGenerator } from 'ngrx-data';

/**
 * A basic, generic entity data service suitable for persistence of most entities.
 * Overrides selected features of ngrx-data/DefaultDataService
 */
export class SpEntityCollectionDataService<T> extends DefaultDataService<T> {

  constructor(
    entityName: string,
    protected http: HttpClient,
    protected httpUrlGenerator: HttpUrlGenerator,
    config?: DefaultDataServiceConfig
  ) {
    super(entityName, http, httpUrlGenerator, config);
  }

  // TODO: Remove once the server APIs are fixed.
  getById(key: number | string): Observable<T> {
    return super.getById(key).pipe(
      map(data => {
        if (Array.isArray(data)) {
          // Server returned array, which it shouldn't.
          if (data.length === 1) {
            return data[0];
          } else if (data.length === 0) {
            throw new HttpErrorResponse({
              status: 404,
              statusText: 'Not Found',
            });
          } else {
            throw new HttpErrorResponse({
              status: 500,
              statusText: 'Internal Server Error',
              error: `Returned ${data.length} ${this.entityName} items`
            });
          }

        } else {
          return data;
        }
      })
    );
  }

}

/**
 * Create a generic entity data service for SP.
 * Suitable for persistence of most entities.
 * Replaces ngrx-data/DefaultDataServiceFactory
 */
@Injectable()
export class SpEntityCollectionDataServiceFactory implements Partial<DefaultDataServiceFactory> {
  constructor(
    protected http: HttpClient,
    protected httpUrlGenerator: HttpUrlGenerator,
    @Optional() protected config?: DefaultDataServiceConfig
  ) {
    config = config || {};
    httpUrlGenerator.registerHttpResourceUrls(config.entityHttpResourceUrls);
  }

  /**
   * Create a default {EntityCollectionDataService} for the given entity type
   * @param entityName {string} Name of the entity type for this data service
   */
  create<T>(entityName: string): SpEntityCollectionDataService<T> {
    return new SpEntityCollectionDataService<T>(entityName, this.http, this.httpUrlGenerator, this.config);
  }
}

export const defaultDataServiceProvider: Provider = {
  provide: DefaultDataService, useClass: SpEntityCollectionDataServiceFactory
};
