import { CompanyEntityBase } from './company-entity-base';
import { Deduction, DeductionKeyValue, DeductionPlanType } from './deduction';

/**
 * A deduction of a Company
 * Company has a list of CompanyDeductions with some parametrization
 * where deductions types are a subset of available deductions.
 */
export class CompanyDeduction extends CompanyEntityBase {
  deductionCodeTypeId: number;
  abbreviation: string;
  description: string;
  parameters: DeductionKeyValue[] = [];
  isRemoved = false;
  deductionPlanTaxOptionCode: DeductionPlanType;

  constructor(deduction: Deduction) {
    super();
    this.deductionCodeTypeId = deduction.codeTypeId;
    this.abbreviation = deduction.defaultAbbreviation;
    this.description = deduction.defaultDescription;
    this.deductionPlanTaxOptionCode = deduction.deductionPlanTaxOptionCode;
  }
}
