// This example displays an address form, using the autocomplete feature
// of the Google Places API to help users fill in the information.
// https://developers.google.com/maps/documentation/javascript/places-autocomplete#place_autocomplete_service

// This example requires the Places library. Include the libraries=places
// parameter when you first load the API. For example:
// <script src="https://maps.googleapis.com/maps/api/js?key=YOUR_API_KEY&libraries=places">
import { Inject, Injectable, Optional } from '@angular/core';

import { empty, Observable, of } from 'rxjs';
import { first, map } from 'rxjs/operators';

import { environment } from '@environment';

import { AddressLike } from '@utils';
import { AddressFinderService } from './address-finder.service';
import { LoggerService } from '@core/services/logger.service';
import { GeolocationService } from './geolocation.service';
import { WindowToken } from '@core/window';

import {
  Autocomplete,
  AutocompletePrediction,
  AutocompleteService,
  nullAutocomplete,
  nullAutocompleteService,
  AutocompletionRequest,
  LatLngLiteral,
  PlaceResult
} from './interfaces';

const emptyAddress: AddressLike = {
  street1: '',
  street2: '',
  city: '',
  state: '',
  zip: '',
  country: 'US'
};

// Relies on Google Places AutocompleteService
// https://developers.google.com/maps/documentation/javascript/reference/3/#AutocompleteService
// Uses the getPlacePredictions() method (not the QueryPrediction with its search terms)

// Install google maps asynchronously as soon as possible
let googleMapsInstalled = false;

/**
 * Google Places Service
 * Look up addresses using the Google Places Web API (AutoCompleteService)
 * Presupposes that Google Places library has been loaded from a script on the host web page.
 */
@Injectable() // provided explicitly in CoreModule as AddressFinderService
export class GooglePlacesService implements AddressFinderService {
  private _autocompleteService: AutocompleteService;
  private get autocompleteService(): AutocompleteService {
    return this._autocompleteService ? this._autocompleteService : this.setAutocompleteService();
  }
  private isChrome: boolean;
  private okStatus: string;

  constructor(
    private logger: LoggerService,
    @Inject(WindowToken) private window: Window,
    @Optional() private geolocationService?: GeolocationService
  ) {
    this.disableBrowserAutocomplete = this.disableBrowserAutocomplete.bind(this);
    this.isChrome = !!(<any>window).chrome && !!(<any>window).chrome.app && !!(<any>window).chrome.csi;

    if (!geolocationService) {
      // Nullo version for when real GeolocationService is unavailable.
      this.geolocationService = new GeolocationService();
    }
  }

  getGoogleMapsService() {
    const google = (this.window as any).google;
    return google && google.maps;
  }

  getAutocompleteAddress(inputEl: HTMLInputElement): Observable<AddressLike> {
    const ac = this.getAutocompletePlace(inputEl);
    if (!ac) {
      return null;
    }

    if (this.isChrome) {
      // wait for response, then disable autocomplete.  Hack to wait until after Places has set `autocomplete="off"` on inputEl.
      this.autocompleteService.getPlacePredictions({ input: 'home', componentRestrictions: { country: 'us' }, types: ['geocode'] }, () => {
        this.disableBrowserAutocomplete(inputEl);
      });
    }

    return ac.pipe(map(toAddress));
  }

  getAutocompletePlace(inputEl: HTMLInputElement): Observable<PlaceResult> {
    const maps = this.getGoogleMapsService();
    if (!maps) {
      // not available yet
      return null;
    }

    const placeChanges$ = new Observable<PlaceResult>(observer => {
      let autocomplete: Autocomplete = new maps.places.Autocomplete(inputEl, {
        componentRestrictions: { country: 'us' },
        types: ['geocode']
      });

      autocomplete.addListener('place_changed', () => {
        const place = autocomplete.getPlace();
        observer.next(place);
      });

      const geoSubscription = this.geolocationService.currentPosition$.subscribe(position => {
        if (!position) {
          return;
        }
        try {
          const center: LatLngLiteral = {
            lat: position.coords.latitude,
            lng: position.coords.longitude
          };
          const radius = position.coords.accuracy;
          const circle = new maps.Circle({ center, radius });
          autocomplete.setBounds(circle.getBounds());
        } catch (error) {
          // do nothing
        }
      });

      // Teardown on unsubscribe
      return () => {
        geoSubscription.unsubscribe();
        // https://developers.google.com/maps/documentation/javascript/reference/3/?csw=1#MapsEventListener
        maps.event.clearInstanceListeners(autocomplete);
        autocomplete = null;
      };
    });

    return placeChanges$;
  }

  private setAutocompleteService(): AutocompleteService {
    try {
      const maps = this.getGoogleMapsService();
      if (!maps) {
        // not available yet; return the nullo and try again next time
        return nullAutocompleteService;
      }
      this._autocompleteService = new maps.places.AutocompleteService();
      this.okStatus = maps.places.PlacesServiceStatus.OK;
    } catch (error) {
      // failed to create AutoComplete; use the nullo from now on.
      this.logger.error('GooglePlacesService autocompleteService setup error: ', error);
      this._autocompleteService = nullAutocompleteService;
    }
    return this._autocompleteService;
  }

  getAutocompletePredictions(input: string): Observable<AutocompletePrediction[]> {
    return new Observable(observer => {
      if (!input) {
        resolve(null);
        return;
      }

      const callback = (predictions: AutocompletePrediction[], status: string) => {
        if (status !== this.okStatus) {
          this.logger.error('GooglePlacesService status error: ' + status);
          predictions = null;
        }
        resolve(predictions);
      };

      const req: AutocompletionRequest = {
        input,
        componentRestrictions: { country: 'us' },
        types: ['geocode']
      };

      this.geolocationService.currentPosition$.pipe(first()).subscribe(position => {
        if (position) {
          req.location = {
            lat: position.coords.latitude,
            lng: position.coords.longitude
          };
          req.radius = position.coords.accuracy;
        }
        this.autocompleteService.getPlacePredictions(req, callback);
      });

      function resolve(predictions: AutocompletePrediction[]) {
        observer.next(predictions || null);
        observer.complete();
      }
    });
  }

  /**
   * Load the Google Maps Service (via dynamic script)
   * Instantiate and call this method early (as in the module that provides this service)
   * so that google maps installs before you need it.
   */
  loadLibrary() {
    const Maps = this.getGoogleMapsService();
    if (Maps || googleMapsInstalled) {
      return; // already installed
    }
    googleMapsInstalled = true; // only try to install it once

    // Gets the API url from the environment (`googlePlacesApiUrl`).
    // E.g. https://maps.googleapis.com/maps/api/js?key=AIzaSyBl3MM25oNKAgODg9RzYYDqkiYNPQRNqY4&libraries=places
    const apiUrl = environment.googlePlacesApiUrl;

    // no library if no url
    if (!apiUrl) {
      return;
    }

    // load after the page's first script element
    const ref = this.window.document.getElementsByTagName('script')[0];
    const script = this.window.document.createElement('script');
    script.src = apiUrl;
    ref.parentNode.insertBefore(script, ref);
  }

  /**
   * Disable the browser's built-in autocomplete because it conflicts with Google Places
   * @param inputEl
   */
  disableBrowserAutocomplete(inputEl: HTMLInputElement) {
    // use "inactive" (or other random string, not "off") to disable autocomplete in Chrome browser
    const off = this.isChrome ? 'inactive' : 'off';
    inputEl.setAttribute('autocomplete', off);
  }
}

/**
 * Map Autocomplete {PlaceResult} to an application {Address}
 */
function toAddress({ address_components }: PlaceResult): AddressLike {
  if (!address_components) {
    return emptyAddress;
  }
  return address_components.reduce(
    (parts: AddressLike, part) => {
      switch (part.types[0]) {
        case 'street_number': {
          parts.street1 = (part.long_name + ' ' + parts.street1).trim();
          break;
        }
        case 'route': {
          parts.street1 = (parts.street1 + ' ' + part.long_name).trim();
          break;
        }
        case 'premise': {
          parts.street2 = part.long_name;
          break;
        }
        case 'locality': {
          parts.city = part.long_name;
          break;
        }
        case 'administrative_area_level_1': {
          parts.state = part.short_name;
          break;
        }
        case 'postal_code': {
          parts.zip = part.long_name + (parts.zip || '').trim();
          break;
        }
        case 'postal_code_suffix': {
          parts.zip = ((parts.zip || '') + '-' + part.long_name).trim();
          break;
        }
      }
      return parts;
    },
    { ...emptyAddress, street1: '' }
  );
}
