import { Observable, of} from 'rxjs';
import { catchError, last, map } from 'rxjs/operators';
import { ModelMetadata } from './validation-metadata';

export interface ValidationContextBase {
  messageTemplate: string;   // E.g. %displayName% is required
  displayName?: string;
  [propertyName: string]: any;
}

export interface ValidationContext extends ValidationContextBase {
  model: any;
  modelType: string;
  propertyName?: string;
  metadata: ModelMetadata;
  isClient: boolean;
  isServer: boolean;
}

export interface ValidatorFactory {
  name: string;
  fn: (group: string, context?: Partial<ValidationContextBase>) => Validator | AsyncValidator;
}

export interface ValidatorFunctionContext extends ValidationContext {
  value: any;
  validatorName: string;
}

/**
 * Validate the value within a context containing a validation fn
 * @returns true if the value is valid; false if invalid.
 * Beware: validation framework evaluates result as truthy or falsy.
 */
export type ValidatorFunction = (value: any, context: ValidatorFunctionContext) => boolean;

/**
 * Validate the value within a context containing a validation fn that returns an observable of boolean.
 * @returns Observable<true> if the value is valid; Observable<false> if invalid.
 * Beware: validation framework evaluates result as truthy or falsy.
 */
export type AsyncValidatorFunction = (value: any, context: ValidatorFunctionContext) => Observable<boolean>;

export class ValidationError {
  constructor(readonly key: string, readonly context: ValidatorFunctionContext, readonly validator: ValidatorBase) {
    if (!context) {
      this.context = ((validator && validator.context) || {}) as ValidatorFunctionContext;
    }
  }

  static genericErrorMessage = 'Invalid value; contact customer support for assistance.';

  getErrorMessage(displayName?: string) {
    const ctx = {
      ...this.context,
      displayName: displayName || this.context.displayName || this.context.propertyName || ''
    };

    return this.formatMessage(ctx.messageTemplate, ctx);
  }

  protected formatMessage(template: string, context: ValidationContext): string {
    return template
      // replace tokens between % (e.g, %displayName%) with corresponding context property value.
      ? template.replace(/%([\w|\.]+)%/g, (match, p1) => this.getPropertyValue(context, p1))
      // Template is required. Getting here is a programmer error.
      : ValidationError.genericErrorMessage;
  }

  protected getPropertyValue(context: Partial<ValidationContext>, path: string): any {
    const propertyPath = path && path.split('.');
    const result =  propertyPath && context
      ? propertyPath.length === 1
        ? context[propertyPath[0]]
        : this.getPropertyValue(context[propertyPath[0]], propertyPath.slice(1).join('.'))
      : '';
    return result;
  }
}

export abstract class ValidatorBase {
  protected constructor(readonly name: string, readonly group: string, readonly context: ValidationContextBase) {
    this.context = context || {} as ValidationContextBase;
  }

  protected getKey(context: ValidationContext): string {
    let key = this.name;
    if (this.group) {
      key = `${key}_${this.group}`;
    }
    if (context && context.propertyName) {
      key = `${key}_${context.propertyName}`;
    }

    return key;
  }

  protected getValidatorFunctionContext(value: any, context?: ValidationContext): ValidatorFunctionContext {
    if (!context) {
      context = {} as ValidationContext;
    }

    return {
      ...this.context,
      ...context,
      displayName:
        context.displayName ||
        this.context.displayName ||
        context.propertyName ||
        this.context.propertyName ||
        '',
      value: value,
      validatorName: this.name
    };
  }
}

/**
 * Validator whose synchronous validate method returns a boolean.
 */
export class Validator extends ValidatorBase {
  constructor(name: string, group: string, private validatorFn: ValidatorFunction, context: ValidationContextBase) {
    super(name, group, context);
  }

  /** Run this validator against the specified value.
  @param value {Object} Value to validate
  @param additionalContext {ValidationContext} Any additional contextual information that the Validator can make use of.
  @return {ValidationError|null}  null if the value is valid; a ValidationError if the value fails validation.
  */
  validate(value: any, context?: ValidationContext): ValidationError | null {
    const ctx = this.getValidatorFunctionContext(value, context);
    return this.validatorFn(value, ctx) ? null : new ValidationError(this.getKey(ctx), ctx, this);
  }
}

/**
 * Validator whose validate method returns an observable boolean.
 * This observable is often asynchronous (as when validating on the server)
 * but could be synchronous (as any Observable can be).
 */
export class AsyncValidator extends ValidatorBase {
  constructor(name: string, group: string, private validatorFn: AsyncValidatorFunction, context: ValidationContextBase) {
    super(name, group, context);
  }

  /** Run this validator against the specified value.
  @param value {Object} Value to validate
  @param additionalContext {ValidationContext} Any additional contextual information that the Validator can make use of.
  @return {Observable<ValidationError | null>} A completing Observable of
  null if the value is valid; a ValidationError if the value fails validation.
  The observable must complete! If it emits multiple times, the last value is returned.
  */
  validate(value: any, context?: ValidationContext): Observable<ValidationError | null> {
    const ctx = this.getValidatorFunctionContext(value, context);

    try {
      return this.validatorFn(value, ctx).pipe(
        last(),
        map(isValid => isValid ? null : this.genError(ctx)),
        catchError(error => of(this.genError(ctx)))
      );
    } catch (error) {
      return of(this.genError(ctx));
    }
  }

  private genError(ctx?: ValidatorFunctionContext) {
    return new ValidationError(this.getKey(ctx), ctx, this);
  }
}

