import { Injectable, OnDestroy } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';

import { Observable, Subscription } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';

@Injectable({ providedIn: 'root' })
export class TitleService implements OnDestroy {
  private routerEventsSubscription: Subscription;

  constructor(private router: Router, private ngTitle: Title) {
    this.routerEventsSubscription = router.events
      .pipe(
        filter(event => event instanceof NavigationEnd),
        map((event: NavigationEnd) => this.router.routerState.root),
        map(route => {
          while (route.firstChild) {
            route = route.firstChild;
          }
          return route;
        }),
        filter(route => route.outlet === 'primary'),
        switchMap(route => route.data)
      )
      .subscribe(data => this.ngTitle.setTitle(`SP ${data['title'] || ''}`));
  }

  ngOnDestroy() {
    this.routerEventsSubscription.unsubscribe();
  }
}
