import { EntityMetadataMap } from 'ngrx-data';
import { sortByFirstLast, sortByName } from './sort-comparers';
import { upperCamelCase } from '@utils';

import { environment } from '@environment';
import { Earning } from '@app/model';
import { Deduction } from '@app/model';

/** True if should throw when mapping collection whose name is bad. */
export const throwOnBadCollectionName = !environment.production;

export const entityMetadata: EntityMetadataMap = {
  Address: {},
  Ancillary: {},
  Company: { sortComparer: sortByName },
  CompanyDeduction: {},
  CompanyTax: {},
  CompanyAncillary: {},
  CompanyEarning: {},
  Deduction: { selectId: selectDeductionId },
  Document: {},
  Earning: { selectId: selectCode },
  Employee: { sortComparer: sortByFirstLast },
  EmployeeDeduction: {},
  EmployeePaymentMethod: {},
  EmployeeTax: {},
  Envelope: {},
  EnvelopeSigner: {},
  HouseholdFilers: {},
  Stats: {},
  User: { sortComparer: sortByName },
  TaxRate: {},
};

/**
 * Entity types do NOT have a required companyId property.
 * All others are presumed to require a non-null companyId.
 */
export const entitiesWithoutCompanyId: { [entityName: string]: boolean } = {
  Ancillary: true,
  Company: true,
  Deduction: true,
  Earning: true,
  TaxRate: true,
  User: true
};

/**
 * Entity types that do NOT belong to a specific company.
 * See CompanyEffects.
 */
export const nonCompanyEntity: { [entityName: string]: boolean } = {
  Ancillary: true,
  Deduction: true,
  Earning: true,
  TaxRate: true
};

/** Does this entity type exist in metadata and belong to the set of Company-specific entity types */
export function isCompanyEntity(entityName: string): boolean {
  return entityMetadata[entityName] && !nonCompanyEntity[entityName];
}

/** This app does not pluralize. The entity name is the collection resource name */

/**
 * Translate table names to UpperCamelCase and keep only those that are company collections.
 * @param data Entity collection data from the server.
 * The server collection name may be lowerCamelCase.
 * This function accounts for that.
 */
export function companyCollectionDataMapper(
  data: { [collection: string]: any[] },
  shouldThrow = throwOnBadCollectionName
) {
  const keys = Object.keys(data);
  const result = keys.reduce((acc, key) => {
    const entityName = upperCamelCase(key);
    if (isCompanyEntity(entityName)) {
      acc[entityName] = data[key];
    } else {
      if (shouldThrow) {
        throw new Error(`Unexpected data collection name "${key}"`);
      }
    }
    return acc;
  }, {} as { [collection: string]: any[] });
  return result;
}

/**
 * Return PK of Deduction metadata record
 */
export function selectDeductionId(deduction: Deduction) {
  return deduction.codeTypeId;
}
/**
 * Return PK of Entity metadata record
 */
export function selectCode(earning: Earning) {
  return earning.code;
}
