import { TaxCollectorService } from '@app/core/services/tax-services/tax-collector.service';
import { AsyncValidator, ValidationContextBase, ValidatorFactory } from '@validation';
import { combineLatest } from 'rxjs';
import { map, first } from 'rxjs/operators';
import { isCompanyTaxValid } from '@app/core/services/tax-services/tax-utils';

export class FederalTaxesValidator extends AsyncValidator {

  static factory(taxCollectorService: TaxCollectorService): ValidatorFactory {
    return {
      name: 'federalTaxesValidator',
      fn: (group, context) => {
        return new FederalTaxesValidator(taxCollectorService, group, context);
      }
    };
  }

  private static messageTemplate = 'Fed taxes information must be provided';

  constructor(taxCollectorService: TaxCollectorService,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('federalTaxesValidator', group,
      (value, context) => {
        return combineLatest(taxCollectorService.taxes$).pipe(
          map(([taxes]) => {
            if (taxes.companyFederalTaxes == null) {
              return true;
            }

            let areTaxesValid = true;

            for (let i = 0; i < taxes.companyFederalTaxes.taxes.length; i++) {
              const fedTaxWithBehavior = taxes.companyFederalTaxes.taxes[i];

              if (!isCompanyTaxValid(fedTaxWithBehavior)) {
                areTaxesValid = false;
                break;
              }
            }

            return areTaxesValid;
          }),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || FederalTaxesValidator.messageTemplate
      }
    );
  }
}
