import { Injectable } from '@angular/core';

import { Company, User } from '@model';
import { createOkResponse$, createBadRequestResponse$, Db, RequestInfo } from './in-mem-service-utils';
import { environment } from '@environment';
import { JwtService } from '../mock-auth/jwt.service';
import { OidcTokenResponse } from '@core/services/auth-services/oidc-token-response';

const tokenPrototype = {
  'nbf': 1535425661,
  'exp': 1535429261,
  'iss': 'https://devidentity.surepayroll.com',
  'aud': [
    'https://devidentity.surepayroll.com/resources',
    'document',
    'enrollment',
    'enrollment_api'
  ],
  'client_id': 'ecommerce',
  'sub': '',
  'auth_time': 1535425661,
  'idp': 'local',
  'email': '',
  'role': 'Approver',
  'permission': 'unknown',
  'scope': environment.oidcScope.split(' '),
  'amr': [
    'password'
  ]
};

@Injectable({providedIn: 'root'})
export class InMemIdentityService {

  constructor(private jwtService: JwtService) {}
  /**
   * POST requests to Identity Server
   */
  postRequest(req: RequestInfo, db: Db) {
    if (req.url.endsWith('connect/token')) {
      const reqBody = decodeFormUrlBody((req.req as any).body);
      const resBody = this.getMockToken(reqBody);
      return createOkResponse$(req, resBody);
    }
    // all other POST SurePayroll requests that make it here are bad
    return undefined;
  }

  getMockToken({ email, sub }: { email: string, sub: string }): OidcTokenResponse {
    const data = { ...tokenPrototype, email, sub };
    const token = this.jwtService.createJwt(data);
    return {
      // Example. Not valid for communicating with real server
      'access_token': token,
      'expires_in': 3600,
      'token_type': 'Bearer'
    };
  }
}

function decodeFormUrlBody(body: string) {
  const keyValuePairs = body.split('&');
  const json: any = {};
  for (let i = 0, len = keyValuePairs.length, tmp, key, value; i < len; i++) {
      tmp = keyValuePairs[i].split('=');
      key = decodeURIComponent(tmp[0]);
      value = decodeURIComponent(tmp[1]);
      if (key.search(/\[\]$/) !== -1) {
          tmp = key.replace(/\[\]$/, '');
          json[tmp] = json[tmp] || [];
          json[tmp].push(value);
      } else {
        json[key] = value;
      }
  }
  return json;
}
