import { Action } from '@ngrx/store';
import { AccountStatus, User } from '@model';

export const LOAD_COMPANY_PACKAGE_ERROR = 'sp/company-package-load/error';

export const SET_AUTH_USER = 'sp/auth-user/set';
export const SET_CURRENT_ACCOUNT = 'sp/current/account/set';
export const SET_CURRENT_COMPANY_ETAG = 'sp/current/company/etag/set';
export const SET_CURRENT_EMPLOYEE_ID = 'sp/current/employee/employee-id/set';
export const SET_COMPANY_PACKAGE_LOADED = 'sp/company-package-loaded/set';
export const SET_COMPANY_PACKAGE_LOADING = 'sp/company-package-loading/set';
export const SET_READ_ONLY = 'sp/read-only/set';

/** Create action containing the error when fail to load data */
export class LoadCompanyPackageError implements Action {
  readonly type = LOAD_COMPANY_PACKAGE_ERROR;
  constructor(public readonly payload: {currentCompanyId: string, error: any, tag?: string}) {}
}

export class SetAuthUser implements Action {
  readonly type = SET_AUTH_USER;
  constructor(public readonly payload: User) {}
}

export class SetCompanyEtag implements Action {
  readonly type = SET_CURRENT_COMPANY_ETAG;
  constructor(public readonly payload: string) {}
}

export class SetCompanyPackageLoaded implements Action {
  readonly type = SET_COMPANY_PACKAGE_LOADED;
  constructor(public readonly payload: boolean) {}
}

export class SetCompanyPackageLoading implements Action {
  readonly type = SET_COMPANY_PACKAGE_LOADING;
  constructor(public readonly payload: boolean) {}
}

export class SetCurrentAccount implements Action {
  readonly type = SET_CURRENT_ACCOUNT;
  readonly payload: { currentCompanyId: string, currentUserId: string };
  constructor(currentCompanyId: string, currentUserId: string) {
    this.payload = { currentCompanyId, currentUserId };
  }
}

export class SetCurrentEmployeeId implements Action {
  readonly type = SET_CURRENT_EMPLOYEE_ID;
  constructor(public readonly payload: string) {}
}

export class SetReadOnly implements Action {
  readonly type = SET_READ_ONLY;
  constructor(public readonly payload: boolean) {}
}

export type All =
  | LoadCompanyPackageError
  | SetAuthUser
  | SetCompanyEtag
  | SetCompanyPackageLoaded
  | SetCompanyPackageLoading
  | SetCurrentAccount
  | SetCurrentEmployeeId
  | SetReadOnly;
