import { AsyncValidator, ValidatorFactory, ValidationContextBase } from '@validation';
import { EntitySelectors } from '@entity';
import { map, first } from 'rxjs/operators';
import { Address } from '@app/model';

export class AddressCriteriaValidator extends AsyncValidator {

  static factory(entitySelectors: EntitySelectors): ValidatorFactory {
    return {
      name: 'addressCriteria',
      fn: (group, context) => {
        return new AddressCriteriaValidator(entitySelectors, group, context);
      }
    };
  }

  constructor(entitySelectors: EntitySelectors,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('addressCriteria', group,
      (value, context) => {
        return entitySelectors.entityInfos$.pipe(
          map(entityInfos => {
            const addresses = entityInfos.filter(ei => ei.entityType === 'Address').map(ei => ei.entity as Address);

            try {
              /**
               * SECURITY: Safe because text passed to `Function` comes from our server, not the user
               */
              if (!context.criteriaFn) {
                const evalFunc = `function(context, model, isClient, isServer){ return ${context.criteria} }`;
                context.criteriaFn = Function(`"use strict";return (${evalFunc})`)();
              }
              return addresses.every(address => {
                const ctx = { ...context, value: address };
                return context.criteriaFn(ctx, ctx.model, ctx.isClient, ctx.isServer);
              });
            } catch (e) {
              const message = `Criteria '${context.criteria}' failed for validator '${this.name}'` +
                ` and model type '${context.modelType}' with error '${e.message}'`;
              return false;
            }
          }),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || 'Addresses are invalid'
      }
    );
  }
}
