import { Injectable } from '@angular/core';
import {
  addCompanyEtagResponseHeader,
  createOkResponse$,
  createNoContentResponse$,
  Db,
  getSubFromRequest,
  RequestInfo,
  ResponseOptions,
  STATUS
} from './in-mem-service-utils';
import { AccountStatus, AccountStatusCodes, Company, User } from '@model';
import { accountStateTransitions, AccountStateTransition } from './in-mem-account-state-transitions';

@Injectable({ providedIn: 'root' })
export class InMemAccountService {
  getRequest(req: RequestInfo, db: Db) {
    if (req.url && req.url.includes('/account/user')) {
      return this.getUser(req, db);
    }

    // Account status transitions: GET `${environment.spApiRoot}/account/accountStateTransitions`
    if (req.url && req.url.endsWith('/account/accountStateTransitions')) {
      return this.accountStateTransitions(req);
    }
  }

  postRequest(req: RequestInfo, db: Db) {
    if (req.url && req.url.includes('/account/new')) {
      return this.createNewAccount(req, db);
    }
    if (req.url && req.url.includes('/account/user')) {
      return this.getUser(req, db);
    }
    return this.ifSetAccountStatusReq(req, db);
  }

  // simulate creating User and Company
  private createNewAccount(reqInfo: RequestInfo, db: Db) {
    const originalRequest = reqInfo.req as any;
    const { company, user, householdFilers, stats } = originalRequest.body;
    db.CompanyVersion[company.id] = 0; // force version of new company to be 0.
    db.Company.push(company);
    db.HouseholdFilers.push(householdFilers);
    db.Stats.push(stats);
    db.User.push(user);

    const headers = addCompanyEtagResponseHeader(db, company.id);

    // return createOkResponse$(reqInfo, originalRequest.body); // alpha.2 - must return a ChangeSet in the body
    return createNoContentResponse$(reqInfo, headers); // alpha.3 - not necessary if no changes to request ChangeSet.
  }

  private getUser(req: RequestInfo, db: Db) {
    const oidcSub = getSubFromRequest(req);
    let user = oidcSub && db.User.find(u => u.oidcSub === oidcSub);
    if (!user && oidcSub) {
      // Hey, we're mocking here!  Maybe it's a made-up new account. Look for a user name match.
      const username = oidcSub.substring(oidcSub.indexOf(':') + 1).toLowerCase(); // everything after the colon.
      user = db.User.find(u => u.username.toLowerCase() === username);

      if (!user && /@surepayroll.com$/i.test(username)) {
        // A SurePayroll admin who isn't in the db.
        // Do as the server does and fake a user for this
        user = {
          // Data from 'ward.bell@surepayroll.com'
          id: 'eb82bed78eca487198e82bba9c8ec6e6',
          oidcSub: 'Ldap:bDzBC0nYjbGdgbmDRTYG',
          userRole: 'Administrator',
          username
        } as User;
      }
    }
    if (user) {
      return createOkResponse$(req, user);
    } else {
      const errOptions: ResponseOptions = {
        url: req.url,
        status: STATUS.NOT_FOUND,
        statusText: 'Not Found',
        body: {}
      };
      return req.utils.createResponse$(() => errOptions);
    }
  }

  private ifSetAccountStatusReq(req: RequestInfo, db: Db) {
    const setStatusReq = req.url.match(/\/account\/(.*)\/status$/);
    if (setStatusReq) {
      const companyId = setStatusReq[1];
      const companyIx = db.Company.findIndex(c => c.id === companyId);
      const status: AccountStatus = ((req.req as any).body || {}).status;
      let replyOptions: ResponseOptions;
      if (-1 === companyIx) {
        replyOptions = { status: STATUS.NOT_FOUND, statusText: 'Not Found', body: { companyId } };
      } else if (!status) {
        replyOptions = {
          status: STATUS.BAD_REQUEST,
          statusText: 'Bad Request',
          body: 'no status specified'
        };
      } else if (!AccountStatusCodes.getByValue(status)) {
        replyOptions = {
          status: STATUS.BAD_REQUEST,
          statusText: 'Bad Request',
          body: `invalid status, "${status}"`
        };
      } else {
        replyOptions = this.setAccountStatus(companyIx, db, status);
      }
      replyOptions.url = req.url;
      return req.utils.createResponse$(() => replyOptions);
    }
    return null; // not a setStatus request
  }

  private setAccountStatus(companyIx: number, db: Db, status: AccountStatus): ResponseOptions {
    // Will likely get more complicated. Return w/o errors
    db.Company[companyIx] = { ...db.Company[companyIx], status: status as AccountStatus };
    return { status: STATUS.OK, statusText: 'OK', body: [] as any };
  }

  private accountStateTransitions(reqInfo: RequestInfo) {
    return createOkResponse$(reqInfo, accountStateTransitions);
  }
}
