import { Injectable } from '@angular/core';

import { filter } from 'rxjs/operators';
import * as Raven from 'raven-js';

import { LoggerService } from '@core/services';
import { SentryEnricher } from './sentry-enricher';

@Injectable()
export class LoggerServiceEnricher implements SentryEnricher {

    constructor(private logger: LoggerService) {}

    initialize(): void {
        this.logger.entries$
        .pipe(filter(e => e.type === 'error' || e.type === 'warn'))
        .subscribe( err => {
            Raven.captureMessage(err.entry, {
                level: err.type,
                tags: { location: 'Logger Service' }
            });
        });
    }
}
