import { CompanyEntityBase } from './company-entity-base';
import { EmployeeStatus, EmployeeType, FedFilingStatus, PayrollFrequency, Sex, StateFilingStatus } from '@model/codes-enums';
import { getFullName, hasName } from '@utils/ee-utils';

// NB: Do not leave CLASS fields uninitialized (i.e, `undefined`)
//    The update/upsert operations will not clear previous fields that are undefined.
//    Set these fields to null, which will be propagated on save!
//    Example: canContact?: boolean = null;
//    This advice applies to CLASSes not interfaces

/** Employee on the payroll */
export class Employee extends CompanyEntityBase {
  id: string;
  companyId: string;
  firstName = '';
  lastName = '';
  employeeType: EmployeeType = null;
  homeAddressId = '';
  workAddressId = '';

  /**
   * True if the system can contact or notify the employee
   * If false, email and phone can be (will be) empty
   */
  canContact?: boolean = null;
  email?: string = null;
  phone?: string = null;
  isNewHire = false;
  isTerminated = false;
  hireDate?: string = null;
  birthDate?: string = null;
  sex: Sex = null;
  /** hourly, salary */
  payType?: string = null;
  /** Amount per payFrequency */
  payRate?: number = null;
  /** Overtime hourly rate */
  overtimeRate?: number = null;
  /** hourly, weekly, monthly, yearly */
  payFrequency?: PayrollFrequency = null;
  /** Social Security Number */
  ssn = '';

  /** True when marked-for-delete */
  isRemoved = false;

  constructor(id?: string, companyId?: string) {
    super(id, companyId);
  }

  /** Create Employee instance that is a clone of the source EE */
  static create(ee?: Partial<Employee>): Employee {
    ee = ee || {};
    // Must use `assign` to get the Employee prototype
    return Object.assign(new Employee(ee.id, ee.companyId), ee);
  }

  /** get and set EE status flags (synthetic property) */
  get employeeStatus() {
    return this.isTerminated ? EmployeeStatus.Terminated : this.isNewHire ? EmployeeStatus.NewHire : EmployeeStatus.Active;
  }

  set employeeStatus(status: EmployeeStatus) {
    // set the corresponding Employee flags
    this.isTerminated = status === EmployeeStatus.Terminated;
    this.isNewHire = status === EmployeeStatus.NewHire;
  }

  get fullName() {
    return getFullName(this);
  }

  get hasName() {
    return hasName(this);
  }
}
