import * as Actions from './actions';
import { User } from '@model';

export const SESSION_STATE = 'session';

export interface SessionState {
  authUser: User;
  companyPackageLoaded: boolean;
  companyPackageLoading: boolean;
  currentCompanyEtag: string;
  currentCompanyId: string;
  currentEmployeeId: string;
  currentUserId: string;
  readOnly: boolean;
}

export const initialState: SessionState = {
  authUser: null as User,
  companyPackageLoaded: false,
  companyPackageLoading: false,
  currentCompanyId: null,
  currentCompanyEtag: '',
  currentEmployeeId: null,
  currentUserId: null,
  readOnly: false
};

export function sessionReducer(
  state = initialState,
  action: Actions.All
): SessionState {

  switch (action.type) {
    // Keep cases in frequency of call order

    // Set ETAG is called a ton
    case Actions.SET_CURRENT_COMPANY_ETAG: {
      const currentCompanyEtag = (action.payload || '').trim();
      const currentCompanyId = state.currentCompanyId;

      // Set it the etag if empty string OR
      // if etag changed AND the current companyId is the same as the etag's companyId
      const setIt = currentCompanyEtag  === '' ||
        (
          // etag changed and ...
          state.currentCompanyEtag !== currentCompanyEtag &&
          // new etag is for the current company
          // etag format is `{companyId}/{some-string}`
          currentCompanyId && currentCompanyId === currentCompanyEtag.split('/')[0]
        );

      return setIt ? { ...state, currentCompanyEtag } : state;
    }

    case Actions.SET_CURRENT_EMPLOYEE_ID: {
      const currentEmployeeId = normalizeId(action.payload);
      // Remove extra spaces and normalize empty to null
      return currentEmployeeId === state.currentEmployeeId ? state : { ...state, currentEmployeeId };
    }

    case Actions.SET_COMPANY_PACKAGE_LOADED: {
      const companyPackageLoaded = !!action.payload;
      return companyPackageLoaded === state.companyPackageLoaded ? state : { ...state, companyPackageLoaded };
    }

    case Actions.SET_COMPANY_PACKAGE_LOADING: {
      const companyPackageLoading = !!action.payload;
      return companyPackageLoading === state.companyPackageLoading ? state : { ...state, companyPackageLoading };
    }

    case Actions.SET_CURRENT_ACCOUNT: {
      const currentCompanyId = normalizeId(action.payload.currentCompanyId);
      const currentUserId = normalizeId(action.payload.currentUserId);

      // If no change in company and user ids, the account hasn't changed => same state
      if (currentCompanyId === state.currentCompanyId &&
          currentUserId === state.currentUserId) {
        return state;
      }

      // Changing (or clearing) the current account
      const currentEmployeeId: string = null;

      // Assume change ids before loading new account
      // Therefore the new account means cannot be loaded.
      const companyPackageLoaded = false;

      // Clear the etag if the proposed current companyId is null or not same as the etag's companyId
      let currentCompanyEtag = state.currentCompanyEtag || '';
      if (!currentCompanyId || currentCompanyEtag.split('/')[0] !== currentCompanyId) {
        currentCompanyEtag = '';
      }

      return {
        ...state,
        currentCompanyEtag,
        currentCompanyId,
        currentEmployeeId,
        currentUserId,
        companyPackageLoaded
     };
    }

    case Actions.SET_READ_ONLY: {
      const readOnly = !!action.payload;
      return readOnly === state.readOnly ? state : { ...state, readOnly };
    }

    case Actions.SET_AUTH_USER: {
      const authUser = action.payload;
      return authUser === state.authUser ? state : { ...state, authUser };
    }
  }

  return state;
}

/** Return trimmed string id or return null if is falsy (null, undefined, empty string). */
function normalizeId(id: string) {
  return id ? id.toString().trim() || null : null;
}
