import { Injectable } from '@angular/core';
import { NavigationExtras, Router, RouterStateSnapshot } from '@angular/router';

import { asapScheduler, Observable, of } from 'rxjs';
import { concatMap, filter, first, map, observeOn, tap } from 'rxjs/operators';

import { AccountStatus } from '@model';
import { AuthService } from '@core/services/auth-services/auth.service';
import { Company } from '@model';
import { SessionService } from '@session';
import { WellKnownRoutes } from '@core/well-known-routes';

export interface AccountStatusGuardFns {
  [status: string]: (guardName: string, originalRoute: string, status: string) => boolean;
}

export const okRoute = () => true;

/**
 * Helper service whose primary job is to determine if a route can be activated
 * based on the route, the user, and the current Account status.
 * Always re-routes if there is no signed-in user or no company package loaded.
 * See accountStatusAllows$.
 * Includes other utility methods.
 * Injected by router guards. It is not itself a router guard.
 */
@Injectable({ providedIn: 'root' })
export class AccountStatusGuardHelper {
  constructor(
    private authService: AuthService,
    private sessionService: SessionService,
    public router: Router
  ) {
    this.isAdminOnce$ = authService.isAdmin$.pipe(first());
  }

  /** Terminating observable emitting true if the signed-in user is an administrator. */
  isAdminOnce$: Observable<boolean>;

  /**
   * Log a message about what this guard did
   * when auth tracing is turned on.
   * Typically log a re-routing event.
   */
  logEvent(guardName: string, message: string) {
    this.authService.logEvent(`${guardName} ${message}`);
  }

  /**
   * Create a function that will reroute to another app page,
   * after logging this fact (if Auth Tracing is turned on).
   * Delegates to Router.navigate().
   * By default includes option that replaces original route URL in history.
   */
  rerouteFactory = (newRoute: string, extras?: NavigationExtras) =>
    (guardName: string, originalRoute: string, status: string) => {
      this.logEvent(
        guardName,
        `Company status is "${status}"; re-routing from "${originalRoute}" to "${newRoute}"`
      );
      this.router.navigate([newRoute], extras || { replaceUrl: true });
      return false;
  }

  /**
   * Returns a router canActivate's Observable<boolean> that determines if the user may
   * activate this route or must not do so.
   * In the later case, it re-routes the user to an appropriate page.
   * If there is no signed in user or current company, the page cannot be activated and
   * the side-effect of the guard re-routes to a safe page (see signedInWithCompanyPackage$).
   * If there is a current company package, a status/route map determines the outcome.
   * A status/route map is a map from CompanyStatus to a function that either allows activation
   * or denies it and re-routes the user to another page.
   * The caller supplies two such maps, one for admins and one for regular users.
   * @param guardName The name of the guard that calls this function; for reporting.
   * @param adminRoutes The admin's status/routes map.
   * @param regularRoutes The regular user's status/routes map.
   * @param state The router state indicating where the user intended to go; if the user is not signed-in
   * this observable arranges for the app to try the route again after sign-in.
   */
  accountStatusAllows$(
    guardName: string,
    adminRoutes: AccountStatusGuardFns,
    regularRoutes: AccountStatusGuardFns,
    state: RouterStateSnapshot,
  ): Observable<boolean> {
      const byStatusOperator = this.byAccountStatusOperatorFactory(adminRoutes, regularRoutes);
      return this.signedInWithCompanyPackage$(guardName, state).pipe(
        // if have company, decide based on CompanyStatus, else deny activation and re-route
        concatMap(company => company ? byStatusOperator(company, guardName, state.url) : of(false))
      );
  }

  // Given the two base status/route maps below, `accountStatusAllows$` would
  // reject activation of all pages and re-route to an appropriate page regardless of status.
  // A consumer of this helper will clone these maps and
  // replace a re-route function with the `okRoute` function
  // when the page-to-activate is permitted for the company's current AccountStatus

  private toAdminAccountView = this.rerouteFactory(WellKnownRoutes.AdminAccountView);

  /**
   * Status/route map that re-routes ADMIN users to a safe page for all account statuses.
   * Clone and modify to allow activation of a page for a particular status.
   */
  adminBaseRoutes: AccountStatusGuardFns = {
    [AccountStatus.Abandoned]: this.toAdminAccountView,
    [AccountStatus.Docs]: this.toAdminAccountView,
    [AccountStatus.Enrolled]: this.toAdminAccountView,
    [AccountStatus.Open]: this.toAdminAccountView,
    [AccountStatus.AutoMigrate]: this.toAdminAccountView,
    [AccountStatus.Operations]: this.toAdminAccountView,
    [AccountStatus.Migrating]: this.toAdminAccountView,
    [AccountStatus.Test]: this.toAdminAccountView
  };

  /**
   * Status/route map that re-routes REGULAR users to a safe page for all account statuses.
   * Clone and modify to allow activation of a page for a particular status.
   */
  regularBaseRoutes: AccountStatusGuardFns = {
    [AccountStatus.Abandoned]: this.rerouteFactory(WellKnownRoutes.Abandoned),
    [AccountStatus.Docs]: this.rerouteFactory(WellKnownRoutes.Documents),
    [AccountStatus.Enrolled]: this.rerouteFactory(WellKnownRoutes.Enrolled),
    [AccountStatus.Open]: this.rerouteFactory(WellKnownRoutes.Dashboard),
    [AccountStatus.AutoMigrate]: this.rerouteFactory(WellKnownRoutes.Processing),
    [AccountStatus.Operations]: this.rerouteFactory(WellKnownRoutes.Processing),
    [AccountStatus.Migrating]: this.rerouteFactory(WellKnownRoutes.Processing),
    [AccountStatus.Test]: this.rerouteFactory(WellKnownRoutes.Dashboard),
  };

  /**
   * Observable emits the current user's company if that user is signed in and the company package is loaded.
   * This is a terminating observable with side-effects.
   * If signed out, navigates to sign-in and emits null.
   * If signed in, waits for company package to finish loading, before emitting the company.
   * If company package is not loaded, navigates to sign-out and emits null.
   */
  signedInWithCompanyPackage$(guardName: string, state: RouterStateSnapshot): Observable<Company> {
    const signedIn$ = this.authService.isSignedIn$.pipe(
      tap(signedIn => {
        if (!signedIn) {
          // Not signed in. Remember where user was trying to go and sign in.
          this.authService.setReroute(state.url, guardName);
          this.logEvent(guardName, `calling AuthService.signin() because user is not signed-in`);
          this.authService.signin();
        }
      }),
      first()
    );

    const doneLoading$ = this.sessionService.companyPackageLoading$.pipe(
      tap(loading => {
        if (loading) {
          this.logEvent(guardName, 'SessionService is loading current user data');
        }
      }),
      filter(loading => !loading),
      first(),
      observeOn(asapScheduler)
    );

    const withData$ = doneLoading$.pipe(
      concatMap(() => this.sessionService.companyPackageLoaded$),
      first(), // ensure terminates after first attempt
      concatMap(loaded => (loaded ? this.sessionService.currentCompany$ : of<Company>(null))),
      map(company => {
        if (company) {
          this.logEvent(guardName, `The current user's company is "${company.name}".`);
        } else {
          this.logEvent(guardName,
            'SessionService data load must have failed because still not loaded after loading:false. Signing out.'
          );
          this.authService.signout();
        }
        return company;
      })
    );

    return signedIn$.pipe(concatMap(signedIn => signedIn ? withData$ : of<Company>(null)));
  }


  /**
   * Creates a function that uses a company's status to determine if the router should activate
   * the current route or deny activation and re-route.
   * Caller supplies two status/route maps, one for admins and one for regular users.
   * A status/route map is a map from CompanyStatus to a function that either allows activation
   * or denies it and re-routes the user to another page.
   * @param guardName The name of the guard that calls this function; for reporting.
   * @param adminRoutes The admin's status/routes map.
   * @param regularRoutes The regular user's status/routes map.
   * @returns A function that takes a Company and the guard name and returns
   * a cold terminating Observable<boolean> that determines activation.
   *
   */
  private byAccountStatusOperatorFactory = (
    adminRoutes: AccountStatusGuardFns,
    regularRoutes: AccountStatusGuardFns
  ) => (company: Company, guardName: string, originalRoute: string) => this.isAdminOnce$.pipe(
      map(isAdmin => {
        const status = company.status;
        let routeFn = isAdmin ? adminRoutes[status] : regularRoutes[status];
        routeFn = routeFn || this.rerouteFactory(WellKnownRoutes.Failed);
        return routeFn(guardName, originalRoute, status);
      })
    )
}
