import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { asapScheduler, Observable, throwError } from 'rxjs';
import { catchError, finalize, map, observeOn, tap } from 'rxjs/operators';

import { EntityCacheQuerySet } from 'ngrx-data';

import { Address, Employee } from '@model';
import { BusyService } from '@core/services/busy.service';
import { EntityDispatcher } from './entity-dispatcher';
import { environment } from '@environment';
import { LoggerService } from '@core/services/logger.service';
import { SessionDispatcher } from '@session/session-dispatcher';

@Injectable({providedIn: 'root'})
export class CompanyPackageService {

  constructor(
    private entityCacheDispatcher: EntityDispatcher,
    private busyService: BusyService,
    private http: HttpClient,
    private logger: LoggerService,
    private sessionDispatcher: SessionDispatcher,
  ) {}

  /**
   * Loads the "CompanyPackage" data for the given company.
   * Toggles the companyPackageLoading$ flag.
   * @param companyId The id of the company whose data should be loaded.
   * @returns Terminating COLD observable of either the data collections if succeeded
   * or error observable if failed.
   * You must subscribe but you do not have to unsubscribe.
   */
  loadCompanyPackageByCompanyId(companyId: string): Observable<EntityCacheQuerySet> {
    this.sessionDispatcher.setCompanyPackageLoading(true);
    this.sessionDispatcher.setCompanyPackageLoaded(false);
    const load$ = this.loadData(companyId).pipe(
      tap(() => this.sessionDispatcher.setCompanyPackageLoaded(true)),
      finalize(() => {
        this.sessionDispatcher.setCompanyPackageLoading(false);
      })
    );
    return this.busyService.busy$('Loading your account ...', load$);
  }

  private loadData(companyId: string): Observable<EntityCacheQuerySet> {
    const url = `${environment.apiRoot}/companypackage/${companyId}`;
    return this.http.get<EntityCacheQuerySet>(url).pipe(
      // Ensure this takes at least one tick so that company.loading$ goes true, then false.
      observeOn(asapScheduler),
      map(data => this.scrubCompanyPackage(data)),
      map(data => this.entityCacheDispatcher.loadCompanyPackageSuccess(data)),
      catchError(error => {
        this.entityCacheDispatcher.loadCompanyPackageError(companyId, error);
        return throwError(error);
      })
    );
  }

  /**
   * HACK: remove "bad" data from the Company Package
   * Ex: we have orphaned addresses, perhaps because during EE creation, the home addr made it but not the EE.
   * @param data Company Package fresh from server
   */
  private scrubCompanyPackage(data: EntityCacheQuerySet) {
    // hide orphaned addresses by locally setting isRemoved:false
    const ees: Employee[] = data['employee'];
    (data['address'] as Address[]).forEach(addr => {
        if (addr.isHome && null == ees.find(ee => ee.homeAddressId === addr.id)) {
          // orphan: report and hide by setting isRemoved: true
          this.logger.error(`Orphaned home address: ${JSON.stringify(addr)}`);
          addr.isRemoved = true;
        }
        return addr;
    });
    return data;
  }
}
