import { Injectable } from '@angular/core';
import { User as OidcUser, UserManager } from 'oidc-client';

import { MockOidcManager } from './mock-oidc-manager';
import { OidcManager } from '@core/services/auth-services/oidc-manager';
import { oidcManagerConfig } from '@core/services/auth-services/oidc-manager-config';
import { OidcManagerProxy } from './mock-oidc-manager-proxy';
import { OidcManagerFactory } from '@core/services/auth-services/oidc-manager-factory';

@Injectable() // provided explicitly by MockAuthModule
export class MockOidcManagerFactory implements OidcManagerFactory {

  // N.B.: Cannot inject AuthService because AuthServices injects this class (indirectly).

  /** Inject mock version of the oidc-client manager */
  constructor(private mockOidcManager: MockOidcManager) { }

  public proxy: OidcManagerProxy;

  /** Create a proxy oidc-client UserManager that can switch between real and mock version */
  create(): OidcManager {
    const realOidcManager = new UserManager(oidcManagerConfig);
    this.proxy = new OidcManagerProxy(this.mockOidcManager, realOidcManager);
    return this.proxy;
  }

  /** Switch to the mock oidc-client UserManager */
  useMock() {
    this.proxy.useMock();
  }

  /** Switch to the real oidc-client UserManager */
  useReal() {
    this.proxy.useReal();
  }
}
