/** Get Employee status from the Employee */
export function employeeStatus(ee: {isTerminated: boolean, isNewHire: boolean}) {
  return ee.isTerminated ? 'terminated' : ee.isNewHire ? 'newHire' : 'active';
}

/** Get the {firstName, lastName}, trimmed of extra spaces */
export function getTrimmedNameParts(ee: { firstName: string, lastName: string }) {
  return ee ? {
      firstName: (ee.firstName || '').trim(),
      lastName: (ee.lastName || '').trim()
    } : {
      firstName: '',
      lastName: ''
    };
}

/** Return full name as (first + ' ' + last) */
export function getFullName(person: { firstName: string, lastName: string }) {
  const { firstName, lastName } = getTrimmedNameParts(person);
  return (firstName + ' ' + lastName).trim();
}

/** Return true if this employee view model has both first and last name */
export function hasName(ee: { firstName: string, lastName: string }): boolean {
  const { firstName, lastName } = getTrimmedNameParts(ee);
  return '' !== firstName && '' !== lastName;
}
