import { Inject, ModuleWithProviders,  NgModule, Optional } from '@angular/core';

import { environment } from '@environment';

import { AuthErrorEnricher } from './auth-error-enricher';
import { LoggerServiceEnricher } from './logger-enricher';
import { SENTRY_ENRICHER, SentryEnricher } from './sentry-enricher';

@NgModule({ })
export class SentryModule {

    constructor(
        @Optional() @Inject(SENTRY_ENRICHER) private eventProviders: SentryEnricher[]
    ) {
        if (environment.enableSentry && this.eventProviders) {
            this.eventProviders.forEach( ep => {
                ep.initialize();
            });
        }
    }

    static forRoot(): ModuleWithProviders {
        return {
            ngModule: SentryModule,
            providers: [
                { provide: SENTRY_ENRICHER, useClass: AuthErrorEnricher, multi: true },
                { provide: SENTRY_ENRICHER, useClass: LoggerServiceEnricher, multi: true },
            ]
        };
    }
}
