import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable, of, throwError } from 'rxjs';
import { catchError, concatMap, first, map, tap } from 'rxjs/operators';

import { environment } from '@environment';
import { LoggerService } from '@core/services/logger.service';
import { sanitizeLegalName } from '@core/utils';
import { SessionService } from '@session';

const tinCheckUrl = environment.apiRoot + '/ein/lookup';

@Injectable({providedIn: 'root'})
export class TinCheckService {

  // Cache of TinCheck Responses accumulated in this user session.
  private responses: {[key: string]: TinCheckResponse } = {};

  private _tinCheckCount = 0;

  private readOnly$: Observable<boolean>;

  constructor(
    private http: HttpClient,
    private logger: LoggerService,
    sessionService: SessionService
  ) {
    this.readOnly$ = sessionService.readOnly$;
  }

  /**
   * Check with the TinCheck service if an FEIN/Legal Name combination is valid
   * @returns Terminating observable of the TinCheck service response.
   * Must subscribe but need not unsubscribe.
   */
  check(fein: string, legalNameRaw: string): Observable<TinCheckResponse> {

    const legalName = sanitizeLegalName(legalNameRaw);

    if (isBadFeinNumber(fein) || !legalName) {
      return throwError(
        new Error(`Bad FEIN "${fein}" or empty legal name`)
      );
    }

    // Check the cached responses first.
    const key = fein + '|' + legalName;
    const cachedResponse = this.responses[key];
    if (cachedResponse) {
      return of({ ...cachedResponse, cached: true });
    }

    const mockEinResponse = einLookupInterceptor(fein, legalName);
    if (mockEinResponse) {
      mockEinResponse.name = sanitizeXmlEntities(mockEinResponse.name);
      const tinCheckResponse = this.toTinCheckResponse(fein, legalName, mockEinResponse);
      this.cacheTinCheckResponse(tinCheckResponse);
      return of(tinCheckResponse);
    }

    return this.readOnly$.pipe(
      first(),
      concatMap(readOnly => {
        if (readOnly) {
          throw new Error('Cannot verify FEIN in read-only mode');
        }
        return this.callTinCheckService(fein, legalName, key);
      })
    );
  }

  private callTinCheckService(fein: string, legalName: string, cacheKey: string): Observable<TinCheckResponse> {
    const params = new HttpParams()
    .set('ein', fein)
    .set('name', legalName);

    return this.http
      .get(tinCheckUrl, { params })
      .pipe(
        map((r: EinLookupResponse) => this.toTinCheckResponse(fein, legalName, r)),
        tap(r => {
            this.cacheTinCheckResponse(r);
            if (r.serviceUnavailable) {
              return;  // do not count attempt
            }
            this._tinCheckCount++; // bump count of successful TinCheck service calls
        }),
        catchError(error => {
          this.logger.error(error);
          const res: TinCheckResponse = {
            fein,
            originalNameProvided: legalName,
            matchedName: null,
            verified: false,
            invalid: false,
            error: true,
            serviceUnavailable: false,
            serviceResponse: error
          };
          return of(res);
        })
      );
  }

  private cacheTinCheckResponse(r: TinCheckResponse) {
    if (r.serviceUnavailable) {
      return; // Do not cache "service unavailable" responses. Want to retry them.
    }

    // cache the converted response.
    const cacheKey = r.fein + '|' + r.originalNameProvided;
    r.matchedName = sanitizeXmlEntities(r.matchedName);
    this.responses[cacheKey] = r;
    if (!r.verified && r.matchedName) {
      //  also cache what the service says is the proper matching legal name
      const key2 = r.fein + '|' + r.matchedName;
      this.responses[key2] = {
        fein: r.fein,
        originalNameProvided: r.originalNameProvided,
        matchedName: r.matchedName,
        verified: true,
        invalid: false,
        error: false,
        serviceUnavailable: false,
        serviceResponse: r.serviceResponse
      };
    }
  }

  // Just in case and for testing
  clearCachedResponses() {
    this.responses = {};
  }

  get tinCheckCount() {
    return this._tinCheckCount;
  }

  private toTinCheckResponse(fein: string, originalNameProvided: string, r: EinLookupResponse): TinCheckResponse {
    const verified = r.matchStatus === 'Exact';
    const invalid = r.matchStatus === 'Disallowed' ;
    const serviceUnavailable = r.matchStatus === 'ServiceUnavailable';
    const possibleMatch = (r.matchStatus === 'Partial' || r.matchStatus === 'Exact' || r.matchStatus === 'Error') ? r.name : '';
    const res: TinCheckResponse = {
      fein,
      originalNameProvided,
      matchedName: possibleMatch,
      verified,
      invalid,
      error: false,
      serviceUnavailable,
      serviceResponse: r
    };
    return res;
  }
}

export interface TinCheckResponse {
  fein: string;
  originalNameProvided: string;
  matchedName: string;
  verified: boolean;
  invalid: boolean;
  error: boolean;
  serviceUnavailable: boolean;
  serviceResponse: EinLookupResponse;
  /** Response came from services cache of responses. Don't increment the attempts count */
  cached?: boolean;
}

type EinLookupMatchStatus = 'Exact' | 'Partial'| 'None' | 'Disallowed' | 'Error' | 'ServiceUnavailable';

export interface EinLookupResponse {
  ein: string;
  name: string;
  matchStatus: EinLookupMatchStatus;
}

function sanitizeXmlEntities(text: string): string {
  if (!text) {
    return null;
  }
  return text.replace('&amp;', '&')
             .replace('&amp;', '&')   // Repeated for double encoding in TIN CHECK responses
             .replace('&apos;', '\'')
             .replace('&quote;', '"')
             .replace('&lt;', '<')
             .replace('&gt;', '>');
}

/**
 * Intercept well-known test FEINs and emulate the appropriate TinCheck server response data
 * @param ein: The FEIN
 * @param name: The candidate legal name
 * @returns Simulated TinCheck Service response data if intercepted, else null
 * Intercepted eins:
 *   11-1111111: 'partial' match,
 *   12-3456789: 'error' match,
 *   22-2222222: 'exact' match,
 *   33-3333333: 'disallowed' match,
 *   44-4444444: 'serviceUnavailable' match,
 *   55-5555555: 'none' match
 */
export function einLookupInterceptor(ein: string, name: string): EinLookupResponse {
  if (!ein || !name) {
    return null;
  }

  switch (ein) {
    // Dev, Test, and Stage environments use these mocks
    case '11-1111111':
      return {
        ein,
        name: name.substr(0, 25) + ' TEST',
        matchStatus: 'Partial'
      };

    // Known to return matchStatus:'Error'
    case '12-3456789':
      return {
        ein,
        name: 'NOT A REAL COMPANY',
        matchStatus: 'Error'
      };

    case '22-2222222':
      return {
        ein,
        name: name,
        matchStatus: 'Exact'
      };

    case '33-3333333':
      return {
        ein,
        name: '',
        matchStatus: 'Disallowed'
      };

    case '44-4444444':
      return {
        ein,
        name: '',
        matchStatus: 'ServiceUnavailable'
      };

    case '55-5555555':
      return {
        ein,
        name: '',
        matchStatus: 'None'
      };
    default:
      return null;

  }
}

/**
 * Return true if the fein value is a badly-formed FEIN number.
 */
export function isBadFeinNumber(fein: string = ''): boolean {
  return !isGoodFeinNumber(fein);
}

/**
 * Return true if the fein value is a well-formed FEIN number although it might not be a real FEIN.
 */
export function isGoodFeinNumber(fein: string = ''): boolean {
  return fein && /\d{2}-\d{7}/.test(fein);
}
