import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { concatMap } from 'rxjs/operators';

import {SessionService} from '@session';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-concurrency-failure',
  templateUrl: './concurrency-failure.component.html'
})
export class ConcurrencyFailureComponent implements OnInit {
  constructor(private router: Router, private sessionService: SessionService) { }

  ngOnInit() {
    // Never let it fail on application launch
    // or user won't have a chance to recover with a browser refresh.
    if ((this.router as any).navigationId === 1) {
      this.router.navigate([WellKnownRoutes.Dashboard], { replaceUrl: true });
    }
  }

  refresh() {
    return this.sessionService.reloadCurrentCompanyPackage().subscribe(
      _ => window.history && window.history.length > 1 ? window.history.back()
        : this.router.navigate([WellKnownRoutes.Dashboard], { replaceUrl: true }),
      error => this.router.navigate([WellKnownRoutes.Failed]),
    );
  }
}
