import { Injectable } from '@angular/core';

import { combineLatest, ReplaySubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { EntityInfo, EntitySelectors } from '@entity/entity-selectors';

// TODO: this service can be used to manually trigger validations.
// Its needed to trigger validation after taxes buildup or potentially other none store stateful data services.
@Injectable({ providedIn: 'root' })
export class ValidationTriggerService {
    private triggerValidationsSubject = new ReplaySubject(1);
    public triggerValidations$: Observable<EntityInfo[]>;

    constructor(private entitySelectors: EntitySelectors) {
        this.triggerValidations$ = combineLatest(
            this.triggerValidationsSubject,
            this.entitySelectors.entityInfos$
        ).pipe(
            map(([_, entities]) => entities)
        );

        this.triggerValidationsSubject.next();
    }

    public validate() {
        this.triggerValidationsSubject.next();
    }
}
