import { ValidationContextBase, Validator } from '@validation';
import { isValidEin } from '@app/core/services/tax-services/tax-utils';

export class EinMaskValidator extends Validator {

    static create(group: string, context?: Partial<ValidationContextBase>) {
        return new EinMaskValidator(group, context);
    }

    private static messageTemplate = '%displayName% is required';
    private static formatMessageTemplate = '%displayName% must be a valid format';

    constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
        super('einValidator', group,
            (value, context) => {
                if (context.model == null) {
                    return true;
                }

                const isValid = isValidEin(context.model);
                const ein = context.model.ein;
                if (ein != null && ein.length > 0) {
                    context.messageTemplate = EinMaskValidator.formatMessageTemplate;
                }

                return isValid;
            },
            {
                ...validatorContext,
                messageTemplate: validatorContext && validatorContext.messageTemplate || EinMaskValidator.messageTemplate
            });
    }
}
