import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { EntityDispatcher } from 'ngrx-data';

import { Observable, of } from 'rxjs';
import { map, shareReplay, concatMap, catchError, withLatestFrom } from 'rxjs/operators';

import { Earning } from '@app/model';
import { EntityHelperService } from './entity-helper.service';
import { environment } from '@environment';
import { LoggerService } from '@app/core';

const earningsUrl = environment.apiRoot + '/earnings';

@Injectable({providedIn: 'root'})
export class EarningService {

  /**
   * Non-terminating observable of ECommerce Earning master metadata
   * Return cached earnings if already loaded, else loads from server.
   * If server load fails, returns previous earnings (if any)
   * @returns Cold, non-terminating observable of earnings. Must subscribe and unsubscribe.
   */
  earnings$: Observable<Earning[]>;

  /** Non-terminating, boolean observable indicating if earnings have been loaded successfully */
  loaded$: Observable<boolean>;

  private dispatcher: EntityDispatcher<Earning>;

  constructor(
    entityHelper: EntityHelperService,
    private http: HttpClient,
    private logger: LoggerService
  ) {
    const elements = entityHelper.collectionServiceElementsFactory.create<Earning>('Earning');

    this.loaded$ = elements.selectors$.loaded$;

    this.earnings$ = elements.selectors$.entities$.pipe(
      withLatestFrom(this.loaded$),
      concatMap(([earnings, loaded]) => {
        if (loaded) {
          return of(earnings);
        } else {
          return this.http.get<Earning[]>(earningsUrl).pipe(
            map(newEarnings => {
              this.dispatcher.addAllToCache(newEarnings);
              return newEarnings;
            }),
            catchError(error => {
               // Swallow error and return previous earnings (if any)
               this.logger.error('Earnings service failed to return earnings', error);
               this.dispatcher.setLoaded(true); // pretend it loaded successfully
               return of(earnings);
            })
          );
        }
      }),
      shareReplay(1)
    );

    this.dispatcher = elements.dispatcher;

  }

  /**
   * Clear the Earnings master metadata in cache.
   */
  clearCache() {
    this.dispatcher.clearCache(); // also clears loaded$ flag
  }

}
