/** Deduction metadata - general information about a deduction */
export class Earning {
  code: string;
  name: string;
  description: string;
  shortDescription: string;
  earningsCategory: string;
  earningsGroupId: number;
  earningTypeId: number;
  earningTypeName: string;
  earningTaxTypeId: number;
  earningTaxType: string;
}

export class EarningType {
  earningTypeId: number;
  earningTypeName: string;
  isActive = true;
}

export enum EarningsCategory {
  DEFAULTEARNINGS = 'DEFAULTEARNINGS',
  DEFAULTOTHERCOMPENSATION = 'DEFAULTOTHERCOMPENSATION',
  ADDITIONALEARNINGS = 'ADDITIONALEARNINGS'
}
