import { Injectable } from '@angular/core';

import { CompanyTax, TaxTypeCode } from '@model';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { map } from 'rxjs/operators';

@Injectable({ providedIn: 'root' })
export class CompanyTaxService extends CurrentCompanyEntityCollectionService<CompanyTax> {
  constructor(protected helper: CompanyEntityHelperService) {
    super('CompanyTax', helper);
  }

  federalTaxes$ = this.entities$.pipe(
    map(taxes => taxes.filter(e => e.taxTypeCode === TaxTypeCode.Federal))
  );

  stateTaxes$ = this.entities$.pipe(
    map(taxes => taxes.filter(e => e.taxTypeCode === TaxTypeCode.State))
  );

  stateAndLocalTaxes$ = this.entities$.pipe(
    map(taxes => taxes.filter(e => e.taxTypeCode !== TaxTypeCode.Federal))
  );

}
