import { Injectable, Provider } from '@angular/core';
import { EntityAction, EntityActionFactory, EntityActionPayload, EntityActionOptions, EntityOp } from 'ngrx-data';

import { entitiesWithoutCompanyId } from './entity-metadata';
import { IdGeneratorService } from '@core/services/id-generator.service';
import { SessionSelectors } from '@session/session-selectors';

const ADD_ONE_OPS = [
  EntityOp.ADD_ONE,
  EntityOp.SAVE_ADD_ONE,
  EntityOp.UPSERT_ONE
];

@Injectable({providedIn: 'root'})
export class SpEntityActionFactory extends EntityActionFactory {
  constructor(private idGenerator: IdGeneratorService, private sessionSelectors: SessionSelectors, ) {
    super();

    // Can change as administrator moves from company to company
    this.sessionSelectors.currentCompanyId$.subscribe(id => this.currentCompanyId = id);
  }

  /// NEVER MAKE THIS SNAPSHOT PUBLIC!
  /// EASILY ABUSED. IT WILL CHANGE AND CREATE SUBTLE BUGS.
  private currentCompanyId: string;

  /**
   * Create an EntityAction to perform an operation (op) for a particular entity type
   * (entityName) with optional data and other optional flags
   * @param payload Defines the EntityAction and its options
   */
  protected createCore<P = any> (payload: EntityActionPayload<P>) {
    const data = payload.data;

    // if payload has data, inspect and possibly correct it
    if (data != null) {
      const { entityName, entityOp } = payload;

      // deletes are disallowed
      if (entityOp === EntityOp.SAVE_DELETE_ONE) {
        payload.error = new Error(`This app does not support deletion of ${entityName}`);
      } else if (ADD_ONE_OPS.find(aop => aop === entityOp)) {
        // This is an add operation for a single entity
        // Try to preserve data's object identity but
        // clone it if changes are necessary.
        let entity: any = data; // entity is potential clone
        if (!entity.id) {
          // generate missing entity.id
          entity = Object.assign({}, entity); // clone entity, stripping off any prototype
          entity.id = this.idGenerator.generateId();
        }
        if (!entity.companyId && !entitiesWithoutCompanyId[entityName] ) {
          // This entity lacks its required companyId. Assign it to current company id
          if (!this.currentCompanyId) {
            payload.error = new Error(`${entityName} is missing "companyId" and there is no current Company`);
          } else {
            if (entity === data) {
              // clone if not already cloned, stripping off any prototype
              entity = Object.assign({}, entity);
            }
            entity.companyId = this.currentCompanyId;
          }
        }
        if (entity !== data) {
          payload = { ...payload, data: entity };
        }
      }
    }

    return super.createCore<P>(payload);
  }
}

export const entityActionFactoryProvider: Provider = {
  provide: EntityActionFactory,
  useClass: SpEntityActionFactory
};
