import {
  AfterContentInit,
  Component,
  ContentChild,
  ElementRef,
  Input,
  Renderer2,
  ViewContainerRef,
  ViewChild
} from '@angular/core';

/**
 * Wrap a control in a div with a label, using bootstrap styles.
 * Saves a few keystrokes and makes the html somewhat nicer.
 * Example usage:
      <sp-control label="First Name">
        <input type="text" #input name="firstName" [(ngModel)]="viewModel.firstName">
      </sp-control>
 * NOTE the #input hash is required on the inner <input> or <select> control.
 * Output:
      <sp-control label="First Name">
        <div class="form-group">
          <label for="firstName">First Name</label>
          <input name="firstName" type="text" id="firstName" class="form-control" [(ngModel)]="viewModel.firstName">
        </div>
      </sp-control>
 *
 */

 const CLICK_THREHOLD = 150; // ms

@Component({
  selector: 'sp-control',
  templateUrl: './control-wrap.component.html'
})
export class ControlWrapComponent implements AfterContentInit {
  @Input() label: string;
  @Input() tooltip = false;
  @ContentChild('input') input: any;
  @ViewChild('tooltipButton') tooltipButton: any;
  @ViewChild('root') root: any;

  id: string;
  open = false;
  tClose = 0;

  constructor(
    private renderer: Renderer2,
    private elementRef: ElementRef,
    private viewContainer: ViewContainerRef
  ) {}

  ngAfterContentInit(): void {
    const el = this.input && this.input.nativeElement;
    if (!el) {
      return;
    }
    // Get the input's id. Assume we have name, since it's required by ngModel in a form
    const id = el.id || el.name;
    if (!el.id) {
      // Add id to the input element so that the label will be tied to it (for screenreaders)
      el.id = id;
    }
    this.id = id;

    // add class="form-control" if missing
    el.classList.add('form-control');
  }

  openTooltip() {
    const d = Date.now() - this.tClose;
    if (d < CLICK_THREHOLD) {
      return;
    }
    if (!this.open) {
      const myRoot = this.root.nativeElement;
      // give some millisecs to open the tooltip-div
      this.open = true;
      setTimeout(() => {
        const tooltipDiv = myRoot.querySelector('#tooltipZone') as HTMLDivElement;
        if (tooltipDiv) {
          tooltipDiv.focus();
        }
      });
    } else {
      this.open = false;
    }
  }
  openKeypress(event: any) {
    if (event.charCode === 13 || event.charCode === 32) {
      // intro or space
      this.openTooltip();
    }
  }

  closeTooltip(event: any = null) {
    if (isOpeningALink(event)) {
      // cancel closing to allow a.click() proceed
      event.preventDefault = false;
      return false;
    }
    this.tClose = Date.now();
    this.open = false;
    this.tooltipButton.nativeElement.focus();
  }
  closeKeypress(event: any) {
    if (event.charCode === 13 || event.charCode === 32) {
      // intro or escape or space
      this.closeTooltip();
    }
  }
}

function isOpeningALink(event: any): boolean {
  return event &&
         event.type === 'blur' &&
         event.relatedTarget &&
         event.relatedTarget.localName === 'a';
}
