import { TaxCollectorService } from '@app/core/services/tax-services/tax-collector.service';
import { AsyncValidator, ValidationContextBase, ValidatorFactory } from '@validation';
import { map, first } from 'rxjs/operators';
import { isEmployeeTaxValid } from '@app/core/services/tax-services/tax-utils';

export class EmployeeTaxesValidator extends AsyncValidator {

  static factory(taxCollectorService: TaxCollectorService): ValidatorFactory {
    return {
      name: 'employeeTaxesValidator',
      fn: (group, context) => {
        return new EmployeeTaxesValidator(taxCollectorService, group, context);
      }
    };
  }

  private static messageTemplate = 'Employee taxes information must be provided';

  constructor(taxCollectorService: TaxCollectorService,
    group: string, validatorContext?: Partial<ValidationContextBase>) {

    super('employeeTaxesValidator', group,
      (value, context) => {
        return taxCollectorService.taxes$.pipe(
          map(taxes => {
            if (taxes.employeeTaxes == null) {
              return true;
            }

            const employeeId = value.id;

            const employeeTaxesVm = taxes.employeeTaxes.find(e => e.employeeLocation.id === employeeId);
            if (employeeTaxesVm == null) {
              return false;
            }

            let areTaxesValid = true;

            for (let i = 0; i < employeeTaxesVm.taxes.length; i++) {
              const empTaxWithBehavior = employeeTaxesVm.taxes[i];

              if (!isEmployeeTaxValid(empTaxWithBehavior)) {
                areTaxesValid = false;
                break;
              }
            }

            return areTaxesValid;
          }),
          first()
        );
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || EmployeeTaxesValidator.messageTemplate
      }
    );
  }
}
