import { Injectable } from '@angular/core';
import { Router, NavigationEnd, Event } from '@angular/router';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, catchError, filter, map, withLatestFrom, shareReplay, distinctUntilChanged } from 'rxjs/operators';

import { Address, Employee, Stats, SectionStatus} from '@model';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { WellKnownRoutes } from '@core/well-known-routes';  // '@core' introduces circular dependency
import { ValidationService, ValidationError } from '@validation';
import { EntitySelectors } from '../entity-selectors';
import { SessionService } from '@session';
import { LoggerService } from '@app/core';

@Injectable({ providedIn: 'root' })
export class StatsService extends CurrentCompanyEntityCollectionService<Stats> {
  /** Stats for the current company */
  stats$: Observable<Stats>;

  private trackedRoutes = [
    WellKnownRoutes.AboutYou,
    WellKnownRoutes.CompanyInfo,
    WellKnownRoutes.Employees,
    WellKnownRoutes.CompanyTaxInfo,
    WellKnownRoutes.BankAccount,
    WellKnownRoutes.Review
  ];

  private validationErrors$: Observable<ValidationError[]>;
  aboutYouErrors$: Observable<ValidationError[]>;
  bankInfoErrors$: Observable<ValidationError[]>;
  companyInfoErrors$: Observable<ValidationError[]>;
  companyTaxErrors$: Observable<ValidationError[]>;
  employeeSetupErrors$: Observable<ValidationError[]>;
  employeesErrors$: Observable<ValidationError[]>;

  aboutYouStatus$: Observable<SectionStatus>;
  companyTaxStatus$: Observable<SectionStatus>;
  companyInfoStatus$: Observable<SectionStatus>;
  employeesStatus$: Observable<SectionStatus>;
  bankInfoStatus$: Observable<SectionStatus>;

  isValid$: Observable<boolean>;

  statuses$: Observable<{
    aboutYou: SectionStatus;
    companyTax: SectionStatus;
    companyInfo: SectionStatus;
    employees: SectionStatus;
    bankInfo: SectionStatus;
  }>;

  constructor(
    protected helper: CompanyEntityHelperService,
    private validationService: ValidationService,
    private entitySelectors: EntitySelectors,
    private sessionService: SessionService,
    private logger: LoggerService,
    private router: Router
  ) {
    super('Stats', helper);

    this.stats$ = this.entities$.pipe(
      map(statsCollection => {
        // "Cheating" by assuming that collection only holds the current company stats
        // new Stats should not be created here, only in SignupService, but this is a fallback for existing companies
        return statsCollection[0] || new Stats();
      })
    );

    this.getValidationErrors();
    this.aboutYou();
    this.bankInfo();
    this.companyInfo();
    this.companyTax();
    this.employees();

    this.statuses$ = combineLatest(
      this.aboutYouStatus$,
      this.bankInfoStatus$,
      this.companyInfoStatus$,
      this.companyTaxStatus$,
      this.employeesStatus$
    ).pipe(
      map(([
        aboutYou,
        bankInfo,
        companyInfo,
        companyTax,
        employees
      ]) => ({
          aboutYou,
          bankInfo,
          companyInfo,
          companyTax,
          employees
        })
      ),
      distinctUntilChanged(),
      shareReplay(1)
    );

    /**
     * Listen for navigation to a tracked route (section).
     * Store the time for that section in Stats and save it
     */
    this.router.events.pipe(
      filter(isNavigationEnd),
      filter(event => this.trackedRoutes.some(route => event.url.startsWith(route))),
      withLatestFrom(sessionService.readOnly$, this.stats$, this.statuses$),
      concatMap(([event, readOnly, stats, statuses]) => {
        const dt = new Date().toISOString();
        const url = event.url;
        if (url.startsWith(WellKnownRoutes.AboutYou)) {
          stats.aboutYouLastDate = dt;
          stats.aboutYouStartDate = stats.aboutYouStartDate || dt;
        } else if (url.startsWith(WellKnownRoutes.CompanyInfo)) {
          stats.companyInfoLastDate = dt;
          stats.companyInfoStartDate = stats.companyInfoStartDate || dt;
        } else if (url.startsWith(WellKnownRoutes.Employees)) {
          stats.employeesLastDate = dt;
          stats.employeesStartDate = stats.employeesStartDate || dt;
        } else if (url.startsWith(WellKnownRoutes.CompanyTaxInfo)) {
          stats.companyTaxInfoLastDate = dt;
          stats.companyTaxInfoStartDate = stats.companyTaxInfoStartDate || dt;
        } else if (url.startsWith(WellKnownRoutes.BankAccount)) {
          stats.companyBankAccountLastDate = dt;
          stats.companyBankAccountStartDate = stats.companyBankAccountStartDate || dt;
        } else if (url.startsWith(WellKnownRoutes.Review)) {
          stats.reviewLastDate = dt;
          stats.reviewStartDate = stats.reviewStartDate || dt;
        }
        // Get sections statuses
        stats.aboutYouStatus = statuses.aboutYou;
        stats.companyInfoStatus = statuses.companyInfo;
        stats.employeesStatus = statuses.employees;
        stats.companyTaxInfoStatus = statuses.companyTax;
        stats.companyBankAccountStatus = statuses.bankInfo;

        // Always save stats (unless ReadOnly) because always update a `LastDate`
        return readOnly ? of(stats) : this.upsert(stats).pipe(
          catchError(err => {
            // should do something
            // must catch here for error isolation else outer subscribe fails
            logger.warn('Error saving stats', err, stats);
            // Pretend everything was OK
            return of(stats);
          })
        );
      })
    ).subscribe();

    function isNavigationEnd(event: Event): event is NavigationEnd {
      return event instanceof NavigationEnd;
    }
  }

  private getValidationErrors() {
    // Validate entire cache, excluding account status transition rules
    this.validationErrors$ = this.validationService.validateEntityCache(v => !(v.group && v.group.startsWith('transition-')))
      .pipe(
        map(result => result.validationErrors),
        shareReplay(1)
      );

    this.isValid$ = this.validationErrors$.pipe(
      map(errors => errors.length === 0),
      distinctUntilChanged(),
      shareReplay(1)
    );
  }

  private aboutYou() {
    this.aboutYouErrors$ = this.validationErrors$.pipe(
      map(ves => {
        return ves.filter(ve => ve.context.modelType === 'User');
      }),
      shareReplay(1)
    );

    this.aboutYouStatus$ = combineLatest(this.stats$, this.aboutYouErrors$).pipe(
      map(([stats, aboutYouErrors]) => calcSectionStatus(stats.aboutYouStartDate, aboutYouErrors)),
      distinctUntilChanged(),
      shareReplay(1)
    );
  }

  private bankInfo() {
    this.bankInfoErrors$ = this.validationErrors$.pipe(
      map(ves => {
        return ves.filter(ve => {
          return ve.validator.group === 'bank';
        });
      }),
      shareReplay(1)
    );

    this.bankInfoStatus$ = combineLatest(this.stats$, this.bankInfoErrors$).pipe(
      map(([stats, bankInfoErrors]) => calcSectionStatus(stats.companyBankAccountStartDate, bankInfoErrors)),
      distinctUntilChanged(),
      shareReplay(1)
    );
  }

  private companyInfo() {
    this.companyInfoErrors$ = this.validationErrors$.pipe(
      map(ves => {
        return ves.filter(ve => {
          return !ve.validator.group &&
            (ve.context.modelType === 'Company' || ve.context.modelType === 'Address' && !(ve.context.model as Address).isHome);
        });
      }),
      shareReplay(1)
    );

    this.companyInfoStatus$ = combineLatest(this.stats$, this.companyInfoErrors$).pipe(
      map(([stats, companyInfoErrors]) => calcSectionStatus(stats.companyInfoStartDate, companyInfoErrors)),
      distinctUntilChanged(),
      shareReplay(1)
    );
  }

  private companyTax() {
    this.companyTaxErrors$ = this.validationErrors$.pipe(
      map(ves => ves.filter(ve => {
        return ve.validator.group === 'fedTax' || ve.validator.group === 'stateTax';
      })),
      shareReplay(1)
    );

    this.companyTaxStatus$ = combineLatest(this.stats$, this.companyTaxErrors$).pipe(
      map(([stats, companyTaxErrors]) => calcSectionStatus(stats.companyTaxInfoStartDate, companyTaxErrors)),
      distinctUntilChanged(),
      shareReplay(1)
    );
  }

  private employees() {

    this.employeesErrors$ = combineLatest(
      this.entitySelectors.employee.entities$,
      this.validationErrors$
    ).pipe(
      map(([ees, ves]) => {
        return ves.filter(ve => {
          const modelType = ve.context.modelType;
          return (
            ve.validator.group === 'employeeSetup' ||
            (!ve.validator.group && (
              modelType === 'Employee' ||
              modelType === 'EmployeePaymentMethod' ||
              this.isActiveEEHomeAddressError(ees, ve)
            ))
          );
        });
      }),
      shareReplay(1)
    );


    this.employeesStatus$ = combineLatest(
      this.stats$,
      this.employeesErrors$).pipe(
        map(([stats, employeesErrors]) => calcSectionStatus(stats.employeesStartDate, employeesErrors)),
        distinctUntilChanged(),
        shareReplay(1)
      );
  }

  private isActiveEEHomeAddressError(ees: Employee[], ve: ValidationError): boolean {
    const addr = ve.context.model as Address;
    if (ve.context.modelType === 'Address' && addr.isHome && !addr.isRemoved) {
      const addrId = addr.id;
      const ee = ees.find(e => e.homeAddressId === addrId);
      if (ee == null) {
        // This should never happen after we have transactional saves!
        // this.sessionService.logger.error(`Home address ${addrId} does not belong to any company EE`);
        return false;
      }
      // Only report Address errors for active EEs.
      return !ee.isRemoved;
    }
    return false;
  }

}

function calcSectionStatus(startDate: string, errors: ValidationError[]) {
  return startDate ? errors.length > 0 ? SectionStatus.Invalid : SectionStatus.Valid : SectionStatus.NotStarted;
}
