import { AsyncValidator, ValidationContextBase, ValidatorFactory } from '@validation';
import { EntitySelectors } from '@entity';
import { combineLatest } from 'rxjs';
import { map, first } from 'rxjs/operators';
import { HolidaysService } from '@core/services/holidays.service';
import { getFirstAllowedCheckDate, getDateFromNgb } from '@utils';

/*
 * Validator to ensure that the selected check date is at least two business days away
 * TODO: Describe it better.
 * */
export class CheckDateCriteriaValidator extends AsyncValidator {
  static factory(holidaysService: HolidaysService): ValidatorFactory {
    return {
      name: 'checkDateCriteria',
      fn: (group, context) => {
        return new CheckDateCriteriaValidator(holidaysService, group, context);
      }
    };
  }

  private static messageTemplate = `%displayName% needs to be at least two business days from today's date`;

  constructor(
    holidaysService: HolidaysService,
    group: string,
    validatorContext?: Partial<ValidationContextBase>
  ) {
    super(
      'checkDateCriteria',
      group,
      (value, context) => {
        return holidaysService.getHolidays().pipe(
          first(),
          map(holidays => {
            if (value) {
              const holidayTimes = holidays.map(x => x.getTime());
              let checkDate = value.year ? getDateFromNgb(value) : new Date(value);

              if (checkDate) {
                checkDate = new Date(
                  checkDate.getFullYear(),
                  checkDate.getMonth(),
                  checkDate.getDate()
                );

                if (checkDate && !isNaN(checkDate.getTime())) {
                  return checkDate >= getFirstAllowedCheckDate(holidayTimes);
                }
              }
            }
            return true; // no error
          })
        );
      },
      {
        ...validatorContext,
        messageTemplate:
          (validatorContext && validatorContext.messageTemplate) ||
          CheckDateCriteriaValidator.messageTemplate
      }
    );
  }
}
