import { NgModule } from '@angular/core';

import { NgrxDataModule } from 'ngrx-data';

import { dataserviceConfigurationProviders } from './data-service-config';
import { entityMetadata } from './entity-metadata';
import { EntityServicesModule } from './entity-services/entity-services.module';
import { entityActionFactoryProvider } from './sp-entity-action-factory';
import { defaultDataServiceProvider } from './entity-services/sp-entity-collection-dataservice';

@NgModule({
  imports: [
    NgrxDataModule.forRoot({ entityMetadata }),
    EntityServicesModule
  ],
  providers: [
    dataserviceConfigurationProviders,
    defaultDataServiceProvider,
    entityActionFactoryProvider
  ]
})
export class EntityStoreModule {}
