import { User as OidcUser } from 'oidc-client';

import { environment } from '@environment';
import { JwtService } from './jwt.service';
import { mockOidcUserData, MockOidcUserData } from './mock-oidc-user-data';
import { subPrefix } from '../signup/signup.service';

const default_scope = 'document enrollment enrollment_api openid profile role permission';
const fixed_session_state = 'lo70JbROwyGbU35ndfehFr2bfHLoy2t1PwHhU1FGaPo.8553c24b2a45ea16d18ea2dde0691e56';

export class MockOidcUser implements OidcUser, MockOidcUserData {

  isMockOidcUser = true;

  id_token: string;
  session_state = fixed_session_state;
  access_token: string;
  token_type = 'Bearer';
  scope = environment.oidcScope;
  profile: OidcUserProfile;
  expires_at: number;
  state: 'MOCK-OIDC-USER';
  username: string;

  // Where we stash the current OIDC user in browser storage
  static oidcStorageKey = 'oidc.user:mock-oidc-user';

  constructor (
    jwtService: JwtService,
    userData: MockOidcUserData | OidcUser
  ) {
    if ((userData as MockOidcUser).isMockOidcUser) {
      this.createFromOidcUser(userData as MockOidcUser);
    } else {
      this.createFromUserData(jwtService, userData as MockOidcUserData);
    }
  }

  private createFromOidcUser(user: MockOidcUser) {
    const profile = user.profile;
    if (!profile || !profile.sub) {
      throw new Error('Tried to create OidcUser from bad MockOidcUser source');
    }
    this.id_token = user.id_token;
    this.session_state = user.session_state;
    this.access_token = user.access_token;
    this.token_type = user.token_type;
    this.scope = user.scope;
    this.profile = profile;
    this.expires_at = user.expires_at;
    this.state = user.state;
    this.username = user.username;
  }

  private createFromUserData(jwtService: JwtService, data: MockOidcUserData) {
    let { username, role, email } = data;
    let sub = createMockSub(username);

    // Use user data from well know source if found
    const known = mockOidcUserData.find(d => d.sub === sub || d.username === username );
    if (known) {
      ({ sub, username, role, email } = known);
    }

    this.id_token = createMockIdToken(jwtService, sub);
    this.access_token = createMockAccessToken(jwtService, sub, username, role, email);
    this.expires_at = jwtExpire();
    this.profile = createProfile(sub, username, role);
  }

  get expires_in() {
    return Math.max(0, this.expires_at - Math.floor(Date.now() / 1000));
  }

  get expired() {
    return this.expires_in <= 0;
  }

  get scopes() {
    return this.scope.split(' ');
  }

  toStorageString() {
    return MockOidcUser.oidcStorageKey;
  }
}

