// #region helper fns

/**
 * Create JWT Identity Token.
 * This mock creates one from a username by our mocking convention.
 */
export function createMockIdToken(jwtService: JwtService, sub: string) {
  const data = {
    ...identityTokenPrototype,
    sub,
    nbf: jwtDate() - 60,
    exp: jwtExpire()
  };
  return jwtService.createJwt(data);
}

/**
 * Create JWT Access Token.
 * This mock creates one from a username using our mocking convention.
 * @param username
 * @param [role] a string of one or more, space-separated, roles.
 * @param [email] user's email address
 */
export function createMockAccessToken(jwtService: JwtService, sub: string, username: string, role?: string, email?: string) {
  const roleArray = role ? role.trim().split(' ') : accessTokenPrototype.role;
  const data = {
    ...accessTokenPrototype,
    sub,
    nbf: jwtDate() - 60,
    exp: jwtExpire(),
    iat: jwtDate(),
    email: email || accessTokenPrototype.email,
    role: roleArray
  };
  return jwtService.createJwt(data);
}
/**
 * Create the (sub)ject, which is the permanent, cross-SP app identifier.
 * This mock creates one from a username by our mocking convention.
 */
export function createMockSub(username: string) {
  return subPrefix + ((username || '').trim() || generateSubKey());
}

export interface OidcUserProfile {
  sub: string;
  preferred_username: string;
  role: string[];
}

/**
 * Create an SP profile with a sub, username and role
 * @param [sub] The "Subject" id; if missing, will create one
 * @param [username] with which the user signed in, used to create the sub if necessary.
 * @param [role] a string of one or more, space-separated, roles.
 */
export function createProfile(sub?: string, username?: string, role?: string) {
  const roleArray = role ? role.trim().split(' ') : profilePrototype.role;
  return {
    ...profilePrototype,
    sub: sub || createMockSub(username),
    preferred_username: username,
    role: roleArray
  };
}

/** Generate a pseudo-random SP sub key for mocking purposes */
export function generateSubKey() {
  return (Math.floor(Math.random() * 100000) + '01234').substr(0, 5);
}

/** return the date/time value in a JWT (it's in seconds)  */
export function jwtDate(date?: Date): number {
  const value = date ? date.valueOf() : Date.now();
  return Math.floor(value / 1000);
}

export function jwtExpire(date?: Date, addHours?: number) {
  return jwtDate(date) +  (addHours || 3) * 60 * 60;
}

// #endregion helper fns
