import { Component, Input, Output, EventEmitter, ViewChild } from '@angular/core';

@Component({
  selector: 'sp-radio-button',
  templateUrl: './radio-button.component.html'
})
export class RadioButtonComponent {
  private _value: any;
  private _selectedValue: any;

  isSelected = false;
  hasFocus = false;
  currentValue = '';

  @ViewChild('label')
  label: HTMLLabelElement;

  @Input()
  name: string;

  @Input() disabled = false;

  @Input()
  get selectedValue(): any {
    return this._selectedValue;
  }
  set selectedValue(v: any) {
    this._selectedValue = v;
    this.calcIsSelected();
  }

  @Input()
  get value(): any {
    return this._value;
  }
  set value(v: any) {
    this._value = v;
    this.calcIsSelected();
    this.select();
  }
  @Output() valueChange = new EventEmitter();

  constructor() {}

  select() {
    this.valueChange.emit(this._value);
  }
  onFocus() {
    this.hasFocus = true;
  }
  onBlur() {
    this.hasFocus = false;
  }
  private calcIsSelected(): void {
    this.isSelected = (this._selectedValue === this._value) ? true : false;

    // HACK: work-around to avoid an error in dirty checking inside
    // ngClass when reusing a component
    this.enforceStyle('active', this.isSelected);
  }
  private enforceStyle(styleName: string, active: boolean) {
    const cl = (<any> this.label).nativeElement.classList;
    if (active) {
      cl.add(styleName);
    } else {
      cl.remove(styleName);
    }
  }

}
