/* Data modeling based on sample: https://docs.google.com/spreadsheets/d/1NNeU0hMa-7UJE7__PhnHD5uMhQrjbLxt43elLI2cqA0/edit#gid=922537075 */

export enum WellKnownDeductionsCodeTypeId {
  MiscellaneousWithGoal =  56,
  MiscellaneousNoGoal = 57
}

/** Deduction metadata - general information about a deduction */
export class Deduction {
  codeGroupId: number;            // Group Id
  codeGroup: string;              // Group name
  codeTypeId: number;             // PK / Deduction Identifier
  codeType: string;               // Label
  parameters: DeductionParameter[];
  definition: string;
  defaultAbbreviation: string;    // Provide a default value if available
  defaultDescription: string;     // Provide a default value if available
  canChangeAbbreviation: boolean; // True if user can customize abbrv.
  canChangeDescription: boolean;  // True if user can customize description.
  hasPercent: boolean;
  retireAddon: number;
  hasGoal: boolean;
  employerOnly: boolean;
  isSure401K: boolean;
  isCatchUp: boolean;
  planCode: string;
  deductionPlanTaxOptionCode: DeductionPlanType;
  deductionPlanMatchOptionCode: DeductionPlanMatchOptionType;

}

export function deductionIsCustomType(deduction: Deduction): boolean {
  return deduction.codeTypeId === 56 || // Miscellaneous with Goal
         deduction.codeTypeId === 57;   // Miscellaneous No Goal
}

/**
 * This class captures values for Deductions's paremeters when instanciated
 */
export class DeductionKeyValue {
  /**
   * Constructor:
   * @param key Name/Key of the parameter
   * @param type Type of the value
   * @param value Actual value stored for the parameter (Amount)
   * @param goal Numeric goal or limit for the value (optional)
   */
constructor(
    public key: string,
    public type: string,
    public value: number | boolean | string | null,
    public goal?: number) {}
}

export enum DeductionPlanType {
  PRETAX = 'PRETAX',
  POSTTAX = 'POSTTAX',
  NOEEDEDUCT = 'NOEEDEDUCT'
}

export enum DeductionPlanMatchOptionType {
  NOMATCH = 'NOMATCH',
  NONELECTIV = 'NONELECTIV',
  NOORSINGLE = 'NOORSINGLE',
  SNGLORTWO = 'SNGLORTWO',
  TWOTIER = 'TWOTIER'
}

export enum RetirementPlans {
  IRA = 'IRA',
  Plan401K = '401K',
  Plan403B = '403B'
}

export enum RetirementPlanTypes {
  P401KTraditional = '401KTraditional',
  P401KSafeHarbor = '401KSafeHarbor',
  P401KSimple = '401KSimple',
  SimpleIRA = 'SimpleIRA',
  SepIRA = 'SepIRA'
}
export enum RetirementPlanApply {
  PreTax = 'PreTax',
  PostTax = 'PostTax',
}
export enum MatchTypes {
  NoMatch = 'NoMatch',
  SingleTier = 'SingleTier',
  TwoTiers = 'TwoTiers'
}
export enum EmployeeContributionModel {
  PreTax = 'PRETAX',
  PostTax = 'POSTTAX',
  Both = 'PRE_AND_POSTTAX'
}

export class DeductionParameter {
  cpdCode: string;
  baseAccm: string;
  // primaryType: string  // always null in samples, needed??? type???
  codeTextDescription: string;
  taxDeduction: string; // ?? always = D  Meaning?
  name: string;
  description: string;
  howType: string;
  dedRptSw: boolean;
  inNet: boolean;
  checkPrnSw: boolean;
  isDisplayed: boolean;
  isActive: boolean;
  oneTimeDeduction?: boolean;
  isEmployerDisplayed?: boolean;
  deductionPlanGroupCode: string;
  deductionPlanGroup: string;
  parameterName: string;
  displayName: string;
  defaultDeductionRate?: number;
}
