/**
 * Location services interfaces and types
 * Drawn from Google Maps API
 * https://developers.google.com/maps/documentation/javascript/reference/3
 */

// https://developers.google.com/maps/documentation/javascript/reference/3/#Autocomplete
/** Nullo version for when Autocomplete is unavailable. */
export const nullAutocomplete = {
  getPlace: () => null as PlaceResult,
  setBounds: (bounds: LatLngBounds) => {},
  setOptions: (options: AutocompleteOptions) => {},
  addListener: (eventName: string, callback: () => void) => {}
};

// https://developers.google.com/maps/documentation/javascript/reference/3/#Autocomplete
/** AutoComplete class (partial) that attaches to an input element */
export type Autocomplete = typeof nullAutocomplete;

// https://developers.google.com/maps/documentation/javascript/reference/3/#AutocompleteService
/** Nullo version for when AutocompleteService is unavailable. */
export const nullAutocompleteService = {
  getPlacePredictions: (
    args: AutocompletionRequest,
    callBack: (predictions: AutocompletePrediction[], status: string) => void
  ) => {
    callBack([], null);
  }
};

// https://developers.google.com/maps/documentation/javascript/reference/3/#AutocompleteService
/** Autocomplete Service class (partial), independent of any HTML control. */
export type AutocompleteService = typeof nullAutocompleteService;

// Partial interface
// https://developers.google.com/maps/documentation/javascript/reference/3/#AutocompleteOptions
/** Argument to Autocomplete constructor and setOptions */
export interface AutocompleteOptions {
  componentRestrictions: ComponentRestrictions;
  input: string;
  types: string[]; // use ['geocode']
}

// Partial interface
// https://developers.google.com/maps/documentation/javascript/reference/3/#AutocompletePrediction
/** Result of AutocompleteService.getPlacePredictions() */
export interface AutocompletePrediction {
  /** Complete address as a string */
  description: string;
  /** Id to look up address details (see PlacesService.getDetails) */
  place_id: string;
}

// Partial interface
// https://developers.google.com/maps/documentation/javascript/reference/3/#AutocompletionRequest
/** Argument to AutocompleteService.getPlacePredictions() */
export interface AutocompletionRequest {
  componentRestrictions: ComponentRestrictions;
  /** User's string input */
  input: string;
  location?: LatLngLiteral;
  radius?: number; // meters
  types: string[]; // use ['geocode']
}

// Partial interface
/** Limit addresses to these countries (always limit to 'us' in this app) */
export interface ComponentRestrictions {
  country: string | string[]; // must be 'us' for this app
}

// https://developers.google.com/maps/documentation/javascript/reference/3/#GeocoderAddressComponent
/** Fragment of an address */
export interface GeocoderAddressComponent {
  /** The full text of the address component, e.g., 'California' */
  long_name: string;
  /** The abbreviated, short text of the given address component, e.g., 'CA' */
  short_name: string;
  // https://developers.google.com/maps/documentation/geocoding/start#Types
  /** An array of {GeocoderAddressType} strings denoting the type of this address component, e.g. ['route'] */
  types: GeocoderAddressType[]; //
}

// Address fragments that we care about
// https://developers.google.com/maps/documentation/geocoding/start#Types
/** String denoting the type of an address component */
export type GeocoderAddressType =
  | 'street_number'
  | 'route' // street
  | 'premise' // suite or building number
  | 'locality' // city
  | 'administrative_area_level_2' // county
  | 'administrative_area_level_1' // state
  | 'postal_code' // zip
  | 'postal_code_suffix' // zip+4
  | 'country'; // US

/** Latitude Longitude Literal */
export interface LatLngLiteral {
  lat: number;
  lng: number;
}

// https://developers.google.com/maps/documentation/javascript/reference/3/#LatLngBounds
/**
 * Autocomplete.setBounds() arg that represents a rectangle in geographical coordinates.
 * Can calculate with google.maps.Circle()
 */
// tslint:disable-next-line:no-empty-interface
export interface LatLngBounds {}

// Partial interface
// https://developers.google.com/maps/documentation/javascript/reference/3/#PlaceResult
/** Result of Autocomplete.getPlace() */
export interface PlaceResult {
  place_id: string;
  address_components: GeocoderAddressComponent[];
}
