import { Injectable } from '@angular/core';
import { UserManagerSettings } from 'oidc-client';
import { environment } from '@environment';

const client_url = `${window.location.protocol}//${window.location.host}/`;

export const oidcManagerConfig: UserManagerSettings = {
  authority: environment.oidcAuthRealm,
  client_id: environment.oidcClientId,
  redirect_uri: `${client_url}${environment.oidcRedirectUri}`,
  response_type: 'id_token token',
  scope: environment.oidcScope,
  post_logout_redirect_uri: `${client_url}index.html`
  // Brock says: "Use the default Session Storage unless you have a great reason to do otherwise"
  // userStore: new WebStorageStateStore({ store: window.localStorage })
};
