import { Injectable } from '@angular/core';
import { Router } from '@angular/router';

import {
  HttpEvent,
  HttpEventType,
  HttpInterceptor,
  HttpHandler,
  HttpRequest,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError, concatMap, first, map, tap } from 'rxjs/operators';

import { CompanyService } from '@entity/entity-services/company.service';
import { environment } from '@environment';
import { LoggerService } from '@core/services/logger.service';
import { SessionService } from '@session';
import { newEcAccountUrl, newSpAccountUrl } from '@app/signup/signup.service';
import { WellKnownRoutes } from '@core/well-known-routes';

/**
 * Implements client-side etag/precondition header concurrency behavior documented in
 * https://docs.google.com/document/d/1WzMeGVaVcxp3q0QNV0dC0ZLaUAZal39DnErQ0hbdwjM/edit#
 */
@Injectable() // do not use providedIn because multi-provided
export class ConcurrencyHttpInterceptor implements HttpInterceptor {

  private logger: LoggerService;

  constructor(
    private companyService: CompanyService,
    private router: Router,
    private sessionService: SessionService,
  ) {
    this.logger = sessionService.logger;
  }

  intercept(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const res$ = requiresConcurrencyControl(req)
      ? this.handleConcurrencyRequest(req, next)
      : next.handle(req);

    return res$.pipe(
      tap((res: HttpResponse<any>) => {
        const reqDebug = req; // capture request for debugging
        // Only extract etag from a full response
        // Ignore other response types such as "Sent" which arrives when browser sends an OPTION
        // https://angular.io/api/common/http/HttpEventType
        if (res.type !== HttpEventType.Response ) {
          return res;
        }

        const etag = getEtagFromHeader(res.headers && res.headers.get('Etag'));
        if (etag) {
          this.sessionService.setCurrentCompanyEtag(etag);
        }
      }),
    );
  }

  private handleConcurrencyRequest(
    req: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const method = req.method.toUpperCase();
    const url = req.url;
    return this.sessionService.currentCompanyEtag$.pipe(
      first(),
      concatMap(etag => {
        if (etag) {
          const headers = { 'If-Match': `W/"${etag}"`};
          req = req.clone({ setHeaders: headers });
        } else {
          this.logger.error(`No Etag; cannot set 'If-Match' header for ${method} ${url}`);
        }
        return next.handle(req).pipe(res$ => this.handleConcurrencyResponse(req, res$));
      })
    );
  }

  private handleConcurrencyResponse(req: HttpRequest<any>, res$: Observable<HttpEvent<any>>) {
    return res$.pipe(
      catchError((error: HttpErrorResponse) => {
        const method = req.method.toUpperCase();
        const url = req.url;

        if (error && error.status === 412 && req.headers.get) {
          const ifMatchHeader = req.headers.get('If-Match');
          if (!ifMatchHeader) {
            // some other precondition failed; pass errant response along;
            return res$;
          }
          // tslint:disable-next-line:max-line-length
          const msg = `${method} ${url} failed because another user saved conflicting changes. Navigating to failure page.`;
          this.sessionService.logger.warn(msg, error);
          this.gotoConcurrencyFailure();
          // TODO: send error response that indicates it was handled and doesn't make a big noise
          return res$; // pass errant response along
        } else {
          return res$; // pass errant response along
        }
      })
    );
  }

  private gotoConcurrencyFailure() {
    this.sessionService.readOnly$.pipe(
      first(),
      map(ro => {
        this.sessionService.setReadOnly(true); // forces discard of changes on navigation
        this.router.navigate([WellKnownRoutes.ConcurrencyFailure]).then(
          // restore previous read-only setting regardless
          _ => this.sessionService.setReadOnly(ro),
          error => this.sessionService.setReadOnly(ro),
        );
      })
    ).subscribe();
  }
}


/** Requests that require etag/precondition header concurrency control */
export function requiresConcurrencyControl(req: HttpRequest<any>) {
  const method = req.method.toUpperCase(); // UPPERCASE for comparison
  const url = req.url.toLowerCase();       // lowercase for comparison

  // All methods except GET and JSONP that are
  // requests to eCommerce APIs (for now; expand later?).
  // Certain requests are NOT under concurrency control
  // such as account creation and Tax API requests
  return 'GET' !== method &&

    (url.startsWith(environment.apiRoot) || url.startsWith(environment.spApiRoot)) &&

    // POST to Stats
    !url.includes('/stats/') &&

    // POST to Tax Service API is actually a GET
    !url.includes('/taxapi/') &&

    // POST to EventLog
    !url.includes('/eventlog/') &&

    // POST to SP to create the account in NewCust
    url !== newSpAccountUrl &&

    // POST to EC to create the account in eCommerce
    url !== newEcAccountUrl &&

    // Rare and never under concurrency control.
    method !== 'JSONP';
}

/**
 * Extract normalized etag value from an Etag header
 * @param etagHeaderValue typically from `headers.get('Etag')`
 * @returns normalized "weak" etag or empty string if no header or can't normalize.
 * @examples:
 * ```
 *   null                 // -> '' from null or empty string Etag header
 *   "adfadf8768af/321"   // -> 'adfadf8768af/321' from strong etag header
 *   W/"adfadf8768af/321" // -> 'adfadf8768af/321' from weak etag header
 * ```
 * @see  https://docs.google.com/document/d/1WzMeGVaVcxp3q0QNV0dC0ZLaUAZal39DnErQ0hbdwjM/edit#
 */
export function getEtagFromHeader(etagHeaderValue: string): string {
  const match = etagHeaderValue ? etagHeaderValue.match(/(W\/"|")?([^\/]*\/[\d]*)["]?$/) : null;
  return match ? match[2] || '' : '';
}
