import { ValueProvider, FactoryProvider } from '@angular/core';
import { VALIDATOR_FACTORY } from './validation.service';
import { Validator, ValidationContextBase, ValidatorFactory } from './validator';
import { timer, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { NgbDateStruct, NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';

export class RequiredValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new RequiredValidator(group, context);
  }

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('required', group,
      (value, context) => value != null && (typeof value !== 'string' || value !== ''),
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || '%displayName% is required'
      }
    );
  }
}

export class MinLengthValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new MinLengthValidator(group, context);
  }

  private static messageTemplate = '%displayName% must be a string with at least %minLength% characters';

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('minLength', group,
      (value, context) => {
        if (!value) { return true; }
        return typeof (value) === 'string' && value.length >= context.minLength;
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || MinLengthValidator.messageTemplate
      }
    );
  }
}

export class MaxLengthValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new MaxLengthValidator(group, context);
  }

  private static messageTemplate = '%displayName% must be a string with at most %maxLength% characters';

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('maxLength', group,
      (value, context) => {
        if (!value) { return true; }
        return typeof (value) === 'string' && value.length <= context.maxLength;
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || MaxLengthValidator.messageTemplate
      }
    );
  }
}

export class RegExpValidator extends Validator {

  static create(name: string, group: string, context?: Partial<ValidationContextBase>) {
    return new RegExpValidator(name, group, context);
  }

  private static messageTemplate = 'The %displayName% \'%value%\' does not match \'%expression%\'';

  constructor(name: string, group: string, validatorContext?: Partial<ValidationContextBase>) {
    super(name, group,
      (value, context) => {
        if (!value) { return true; }
        const expression = context.expression as RegExp;
        return typeof (value) === 'string' && expression.test(value);
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || RegExpValidator.messageTemplate
      }
    );
  }
}

export class RangeValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new RangeValidator(group, context);
  }

  private static messageTemplate = '%displayName% must be between the values of %minValue% and %maxValue%';

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('range', group,
      (value, context) => {
        if (!value) { return true; }
        return value >= context.minValue && value <= context.maxValue;
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || RangeValidator.messageTemplate
      });
  }
}

export class InvalidValuesValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new InvalidValuesValidator(group, context);
  }

  private static messageTemplate = 'The value \'%value%\' is not a valid input for %displayName%';

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('invalidValues', group,
      (value, context) => {
        if (!value) { return true; }
        return !(context.values || []).some((v: any) => this.test(v, value));
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || InvalidValuesValidator.messageTemplate
      });
  }

  private test(expression: string, value: string) {
    const match = expression.match(/\/(.*)\//);
    if (match) {
      return new RegExp(match[1]).test(value);
    }

    return expression === value;
  }
}

export class CurrencyValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new CurrencyValidator(group, context);
  }

  private static messageTemplate = '%displayName% must be a valid currency amount';

  private regExpValidator: Validator;

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('currency', group,
      (value, context) => {
        if (!value) { return true; }
        if (typeof(value) === 'number') { return true; }

        return typeof(value) === 'string' && !this.regExpValidator.validate(value, context);
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || CurrencyValidator.messageTemplate
      });

    this.regExpValidator = makeCurrencyValidator(group, validatorContext);
  }
}

export class CompareValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new CompareValidator(group, context);
  }

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('compare', group,
      (value, context) => {
        if (!value) { return true; }
        const otherValue = context.model[context.other];
        const op = context.op;

        if (op === '==') {
          return value === otherValue;
        } else if (op === '<') {
          return value < otherValue;
        } else if (op === '<=') {
          return value <= otherValue;
        } else if (op === '>') {
          return value > otherValue;
        } else if (op === '>=') {
          return value >= otherValue;
        } else if (op === '!=') {
          return value !== otherValue;
        }

        return false;
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || 'Invalid value'
      });
  }
}

export class DateValidator extends Validator {

  static factory(calendar: NgbCalendar): ValidatorFactory {
    return {
      name: 'date',
      fn: (group, context) => {
        return new DateValidator(calendar, group, context);
      }
    };
  }

  private static messageTemplate = '%displayName% must be a valid date';

  constructor(private calendar: NgbCalendar, group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('date', group,
      (value, context) => {
        if (!value) { return true; }

        if (typeof value === 'string') {
          try {
            const date = new Date(value);
            if (!this.__isDate(date)) {
              return false;
            }
            value = this.__parse(value);
            if (!value) {
              return false;
            }
          } catch (e) {
            return false;
          }
        }

        if (this.__isDate(value)) {
          const ngbDate = new NgbDate(value.getFullYear(), value.getMonth() + 1, value.getDate());
          return this.calendar.isValid(ngbDate);
        } else if (this.__isDateStruct(value)) {
          const ngbDate = new NgbDate(value.year, value.month, value.day);
          return this.calendar.isValid(ngbDate);
        } else {
          return false;
        }
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || DateValidator.messageTemplate
      });
  }

  private __isDate(o: any): o is Date {
    return o instanceof Date && !isNaN(o.getTime());
  }

  private __isDateStruct(o: NgbDateStruct): o is NgbDateStruct {
    return !!(o.year && o.month && o.day);
  }

  private __parse(o: string): NgbDateStruct {
    let match: RegExpMatchArray;
    if (match = o.match(/^(\d{4}|\d{2})\s*-\s*(\d{2}|\d{1})\s*-\s*(\d{2}|\d{1})$/)) {
      return { year: +match[1], month: +match[2], day: +match[3] };
    // tslint:disable-next-line:max-line-length
    } else if (match = o.match(/^(\d{4})\D?(0[1-9]|1[0-2])\D?([12]\d|0[1-9]|3[01])(\D?([01]\d|2[0-3])\D?([0-5]\d)\D?([0-5]\d)?\D?(\d{3})?([zZ]|([\+-])([01]\d|2[0-3])\D?([0-5]\d)?)?)?$/)) {
      return { year: +match[1], month: +match[2], day: +match[3] };
    } else if (match = o.match(/^(\d{1,2})\s*[\/-]\s*(\d{1,2})\s*[\/-]\s*(\d{4}|\d{2})$/)) {
      // CASE 3: MM/dd/yyyy  (render values sent to validator)
      return { year: +match[3], month: +match[1], day: +match[2] };
    }

    return null;
  }
}

export function makeZipCodeValidator(group: string, context?: Partial<ValidationContextBase>) {
  context = {
    ...context,
    expression: /^\d{5}([\-]\d{4})?$/,
    messageTemplate: '%displayName% is not a valid U.S. zipcode'
  };

  return RegExpValidator.create('zipCode', group, context);
}

export function makeEmailValidator(group: string, context?: Partial<ValidationContextBase>) {
  // tslint:disable-next-line:max-line-length
  const reEmailAddress = /^((([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+(\.([a-z]|\d|[!#\$%&'\*\+\-\/=\?\^_`{\|}~]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])+)*)|((\x22)((((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(([\x01-\x08\x0b\x0c\x0e-\x1f\x7f]|\x21|[\x23-\x5b]|[\x5d-\x7e]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(\\([\x01-\x09\x0b\x0c\x0d-\x7f]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))))*(((\x20|\x09)*(\x0d\x0a))?(\x20|\x09)+)?(\x22)))@((([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|\d|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.)+(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])|(([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])([a-z]|\d|-|\.|_|~|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])*([a-z]|[\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])))\.?$/i;

  context = {
    ...context,
    expression: reEmailAddress,
    messageTemplate: '%displayName% is not a valid email address'
  };

  return RegExpValidator.create('email', group, context);
}

export function makePhoneValidator(group: string, context?: Partial<ValidationContextBase>) {
  context = {
    ...context,
    // Too forgiving
    // expression: /^((\+|(0(\d+)?[-/.\s]?))[1-9]\d{0,2}[-/.\s]?)?((\(\d{1,6}\)|\d{1,6})[-/.\s]?)?(\d+[-/.\s]?)+\d+$/,
    // The following forces 10-digit US w/ no country code. You can insert punctuation and spaces
    // We will strip these out before sending to SP.
    expression: /^\(?\d{3}\)?[\s.-]?\d{3}[\s.-]?\d{4}$/,
    messageTemplate: '%displayName% must be 10-digit phone number'
  };

  return RegExpValidator.create('phone', group, context);
}

export function makeCurrencyValidator(group: string, context?: Partial<ValidationContextBase>) {
  context = {
    ...context,
    expression: /^[+-]?[0-9]{1,3}(?:,?[0-9]{3})*(?:\.[0-9]{2})?$/,
    messageTemplate: '%displayName% must be a valid currency amount'
  };

  return RegExpValidator.create('currency', group, context);
}

export function makeSocialSecurityNumberValidator(group: string, context?: Partial<ValidationContextBase>) {
  context = {
    ...context,
    expression: /^(\d{3}-\d{2}-\d{4})|(\d{3}\d{2}\d{4})$/,
    messageTemplate: '%displayName% must be a valid Social Security number'
  };

  return RegExpValidator.create('social', group, context);
}

export function makeNumericValidator(group: string, context?: Partial<ValidationContextBase>) {
  const messageTemplate = '%displayName% must be a numeric value';
  const messageTemplateLeadingZeros = '%displayName% must be a numeric value with optional leading zeros';

  context = {
    ...context,
    expression: context && context.allowLeadingZeros ? /^([0-9]*)$/ : /^([1-9][0-9]*)$/,
    messageTemplate: context && context.messageTemplate ||
      context && context.allowLeadingZeros ? messageTemplateLeadingZeros : messageTemplate
  };

  return RegExpValidator.create('numeric', group, context);
}

export function makeFeinValidator(group: string, context?: Partial<ValidationContextBase>) {
  context = {
    ...context,
    expression: /^\d{2}?-\d{7}$/,
    messageTemplate: '%displayName% format is incorrect. Expected format is \'##-#######\''
  };

  return RegExpValidator.create('fein', group, context);
}

export class ExpressionValidator extends Validator {

  static create(group: string, context?: Partial<ValidationContextBase>) {
    return new ExpressionValidator(group, context);
  }

  constructor(group: string, validatorContext?: Partial<ValidationContextBase>) {
    super('expression', group,
      (value, context) => {
        if (!value) { return true; }

        try {
          /**
           * SECURITY: Safe because text passed to `Function` comes from our server, not the user
           */
          if (!context.expressionFn) {
            const evalFunc = `function(context, model, isClient, isServer){ return ${context.expression} }`;
            context.expressionFn = Function(`"use strict";return (${evalFunc})`)();
          }
          return context.expressionFn(context, context.model, context.isClient, context.isServer);
        } catch (e) {
          const message = `Expression '${context.expression}' failed for validator '${this.name}'` +
            ` and model type '${context.modelType}' with error '${e.message}'`;
          return false;
        }
      },
      {
        ...validatorContext,
        messageTemplate: validatorContext && validatorContext.messageTemplate || 'Invalid value'
      });
  }
}

export const defaultValidatorProviders: (ValueProvider | FactoryProvider)[] = [
  { provide: VALIDATOR_FACTORY, useValue: { name: 'required', fn: RequiredValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'minLength', fn: MinLengthValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'maxLength', fn: MaxLengthValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'zipCode', fn: makeZipCodeValidator }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'phone', fn: makePhoneValidator }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'email', fn: makeEmailValidator }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'currency', fn: CurrencyValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'social', fn: makeSocialSecurityNumberValidator }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'fein', fn: makeFeinValidator }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'compare', fn: CompareValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'range', fn: RangeValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'numeric', fn: makeNumericValidator }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'invalidValues', fn: InvalidValuesValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useValue: { name: 'expression', fn: ExpressionValidator.create }, multi: true },
  { provide: VALIDATOR_FACTORY, useFactory: DateValidator.factory, deps: [NgbCalendar], multi: true }
];

