import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { environment } from '@environment';
import { LoggerService } from '@core/services/logger.service';


/**
 * Gets list of Holidays for check date validation as string[]
 */
@Injectable({ providedIn: 'root' })
export class HolidaysService {
    private cachedHolidays: Date[] = null;

    constructor(private http: HttpClient, private logger: LoggerService) { }

    /**
     * Get bank holidays
     * @returns Terminating observable of cached bank holidays. Do not have to unsubscribe.
     */
    getHolidays(): Observable<Date[]> {
        if (this.cachedHolidays != null) {
            return of(this.cachedHolidays);
        }

        const url = environment.spApiRoot + '/taxapi/taxes/holidays';
        return this.http.get<string[]>(url).pipe(
            map(e => {
                const holidays = e
                ? e.map(d => new Date(d.substr(0, 19))).filter(d => !isNaN(d.getTime()))
                : [];
                return this.cachedHolidays = holidays;
            }),
            catchError(error => {
              this.logger.error('Failed getting holidays', error);
              return of([]);
            })
        );
    }
}
