import { Injectable } from '@angular/core';
import {
  createOkResponse$,
  createResponse$,
  Db,
  RequestInfo,
  STATUS
} from './in-mem-service-utils';

import { Observable } from 'rxjs';
import { EinLookupResponse, einLookupInterceptor } from '@core/services/tin-check.service';

/**
 * TinCheck service, proxied by ECommerce API
 */
@Injectable({ providedIn: 'root' })
export class InMemEinService {
  getRequest(req: RequestInfo, db: Db): Observable<any> {
    if (req.url && /\/ein\/lookup\?/.test(req.url)) {
      return this.tinCheck$(req, db);
    }
    // all other GET `ein` requests that make it here are bad
    return undefined;
  }

  // #region TinCheck
  tinCheck$(reqInfo: RequestInfo, db: Db): Observable<EinLookupResponse> {
    const originalRequest = reqInfo.req as any;
    const ein = originalRequest.params.get('ein');
    const name = originalRequest.params.get('name');
    return tinCheckValidate();

    function tinCheckValidate(): Observable<EinLookupResponse> {
      if (!ein) {
        return createResponse$(reqInfo, STATUS.BAD_REQUEST, 'Bad Request', [ 'FEIN not provided.' ]);
      }
      if (!name) {
        return createResponse$(reqInfo, STATUS.BAD_REQUEST, 'Bad Request', [ 'Legal Name not provided.' ]);
      }

      let mockEinResponse = einLookupInterceptor(ein, name);

      // When local mocking enabled, act as if fein were the partial-match interceptor FEINs
      // In other environments, request would go through to real service
      if (!mockEinResponse) {
          mockEinResponse = einLookupInterceptor('11-1111111', name);
      }
      return createOkResponse$(reqInfo, mockEinResponse);
    }
  }
  // #endregion TinCheck

}
