import { Injectable } from '@angular/core';
import { BehaviorSubject, of } from 'rxjs';
import { GeolocationService } from './geolocation.service';
/**
 * Find user's current geo location (Position) using browser's geoLocation feature
 */
@Injectable() // providedIn not working, see GeolocationService
export class BrowserGeolocationService implements GeolocationService {
  protected currentPositionSubject = new BehaviorSubject<Position>(null);
  currentPosition$ = this.currentPositionSubject.asObservable();

  update() {
    if (navigator.geolocation) {
      try {
        navigator.geolocation.getCurrentPosition(position => {
          this.currentPositionSubject.next(position);
        });
      } catch (error) {
        // do nothing
      }
    }
  }
}
