import { NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { PayrollFrequency } from '@app/model';
import { getNgbDate,  getDateFromNgb, getDaysInMonth } from '@app/core';

export class DateRangePayPeriodvm {
  private _selectedFrequency: string;
  private _fromDate: NgbDate;
  private _toDate: NgbDate;
  private _checkDate: NgbDate;
  private _secondFromDate: NgbDate;
  private _secondToDate: NgbDate;
  private _secondCheckDate: NgbDate;
  private _isFromDate: boolean;

  constructor() {}

  set selectedFrequency(frequency: string) {
    this._selectedFrequency = frequency;
    this._toDate = null;
    this._fromDate = null;
    this._checkDate = null;
    this._secondFromDate = null;
    this._secondToDate = null;
    this._secondCheckDate = null;
  }
  get selectedFrequency() {
    return this._selectedFrequency;
  }
  set toDate(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._toDate = value;
    }
  }
  get toDate() {
    return this._toDate;
  }
  set fromDate(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._fromDate = value;
    }
  }
  get fromDate() {
    return this._fromDate;
  }
  set checkDate(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._checkDate = value;
    }
  }
  get checkDate() {
    return this._checkDate;
  }
  get secondFromDate() {
    return this._secondFromDate;
  }
  set secondFromDate(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._secondFromDate = value;
    }
  }
  get secondToDate() {
    return this._secondToDate;
  }
  set secondToDate(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._secondToDate = value;
    }
  }
  get secondCheckDate() {
    return this._secondCheckDate;
  }
  set secondCheckDate(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._secondCheckDate = value;
    }
  }
  get frequencyPeriodOffset() {
    let daysOffset = 0;
    if (this.selectedFrequency === PayrollFrequency.BiWeekly) {
      daysOffset = 13;
    } else if (this.selectedFrequency === PayrollFrequency.Weekly) {
      daysOffset = 6;
    }
    return daysOffset;
  }

  private isValidNgbDate(value: NgbDate) {
    if (!value) {
      return true;
    }
    const tDate = new Date(value.year, value.month - 1, value.day);
    if (!isNaN(tDate.getTime())) {
      return true;
    } else {
      return false;
    }
  }
  getQuarterlyStartDateFromEndDate(date: NgbDate) {
    const tempDate = getDateFromNgb(date);
    if (tempDate) {
      const daysInMonth = getDaysInMonth(tempDate.getFullYear(), tempDate.getMonth() - 3);
      if (tempDate.getDate() > daysInMonth) {
        tempDate.setDate(daysInMonth);
      }
      tempDate.setMonth(tempDate.getMonth() - 3);
      tempDate.setDate(tempDate.getDate() + 1);
    }
    return getNgbDate(tempDate);
  }

  getQuarterlyEndDateFromStartDate(date: NgbDate) {
    const tempDate = getDateFromNgb(date);
    if (tempDate) {
      const daysInMonth = getDaysInMonth(tempDate.getFullYear(), tempDate.getMonth() + 3);
      if (tempDate.getDate() > daysInMonth) {
        tempDate.setDate(daysInMonth);
      }
      tempDate.setMonth(tempDate.getMonth() + 3);
      tempDate.setDate(tempDate.getDate() - 1);
    }
    return getNgbDate(tempDate);
  }

  setFromDateWithSync(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._fromDate = value;
    }
    if ( this._selectedFrequency === PayrollFrequency.SemiMonthly) {
      return;
    }

    let tempDate: Date;
    if (this._selectedFrequency === PayrollFrequency.Monthly) {
      let day: number;

      // If the To date falls on a day that is not present on the next month
      // Set to the end of the month day
      if ((value.day - 1) > getDaysInMonth(value.year, value.month)) {
        day = getDaysInMonth(value.year, value.month);
      } else {  // Set to the day-1 on the next month
        day = value.day - 1;
      }
      tempDate = new Date (value.year, value.month, day);
    } else if (this._selectedFrequency === PayrollFrequency.Quarterly) {
      tempDate =  getDateFromNgb(this.getQuarterlyEndDateFromStartDate(value));
    } else {
      tempDate = new Date(value.year, value.month - 1, value.day + this.frequencyPeriodOffset);
    }

    this._toDate = new NgbDate(tempDate.getFullYear(), tempDate.getMonth() + 1, tempDate.getDate());
  }

  setToDateWithSync(value: NgbDate) {
    if (this.isValidNgbDate(value)) {
      this._toDate = value;
    }
    if (this._selectedFrequency === PayrollFrequency.SemiMonthly) {
      if (value === null) {
        this.secondFromDate = null;
      } else {
        const tDate = new Date(value.year, value.month - 1, value.day + 1);
        this.secondFromDate = new NgbDate(tDate.getFullYear(), tDate.getMonth() + 1, tDate.getDate());
      }
      return;
    }

    let tempDate: Date;
    if (this._selectedFrequency === PayrollFrequency.Monthly) {
      let day: number;
      let month: number;

      // If the From Date falls on a day that is not present in the previous month
      // Set it to first day of the current month.
      if ((value.day + 1) > getDaysInMonth(value.year, value.month - 2)) {
        month = value.month - 1 ;
        day = 1;
      } else {  // Set to the day+1 on the previous month
        day = value.day + 1;
        month = value.month - 2;
      }
      tempDate = new Date(value.year, month, day);
    } else if (this._selectedFrequency === PayrollFrequency.Quarterly) {
      tempDate =  getDateFromNgb(this.getQuarterlyStartDateFromEndDate(value));
    } else {
      tempDate = new Date(value.year, value.month - 1, value.day - this.frequencyPeriodOffset);
    }

    this._fromDate = new NgbDate(tempDate.getFullYear(), tempDate.getMonth() + 1, tempDate.getDate());
  }
}
