import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { tag } from 'rxjs-spy/operators/tag';

import { Address } from '@model';
import { BusyService } from '@core/services/busy.service';
import { environment } from '@environment';
import {
  IAvailableTaxAddressPair, IAddressPairRequest,
  ITaxAddressValidation, IAvailableTaxAddress
} from './tax-models';
import { LoggerService } from '../logger.service';

const taxApiUrl = environment.spApiRoot + '/taxapi/taxes';

/**
 * Gets the raw data from the SurePayroll Tax service.
 */
@Injectable({ providedIn: 'root' })
export class TaxService {
  constructor(private http: HttpClient, private busyService: BusyService, private logger: LoggerService) { }

  validateAddresses(
    employeeId: string,
    homeAddress: Address,
    workAddress: Address
  ): Observable<ITaxAddressValidation> {
    if (homeAddress == null || workAddress == null) {
      return of({
        id: employeeId,
        isValid: false,
        addressPairs: []
      });
    }

    const pairs = [
      <IAvailableTaxAddressPair>{
        id: employeeId,
        workAddress: {
          city: workAddress.city,
          county: null,
          state: workAddress.state,
          zip: workAddress.zip,
          addressLine1: workAddress.street1,
          addressLine2: workAddress.street2
        },
        liveAddress: {
          city: homeAddress.city,
          county: null,
          state: homeAddress.state,
          zip: homeAddress.zip,
          addressLine1: homeAddress.street1,
          addressLine2: homeAddress.street2
        }
      }
    ];

    const loadData$ = this.http.post<IAvailableTaxAddressPair[]>(taxApiUrl, pairs).pipe(
      catchError((err: HttpErrorResponse) => {
        this.logger.error('Error retrieving taxes', err, pairs);
        return of([]);
      }),
      // If we get multiple sets back, the address pair is invalid
      map(res => {
        const isValid = res.length === 1;

        const suggestedAddresses = res.map(e => ({
          home: e.liveAddress,
          work: e.workAddress
        }));

        return {
          id: employeeId,
          isValid: isValid,

          homeGeoCode: isValid ? suggestedAddresses[0].home.geoCode : '',
          homeDsCode: isValid ? suggestedAddresses[0].home.schoolDistrict : null,
          workGeoCode: isValid ? suggestedAddresses[0].work.geoCode : '',
          workDsCode: isValid ? suggestedAddresses[0].work.schoolDistrict : null,

          addressPairs: suggestedAddresses
        };
      }),
      tag('TaxProviderService.validateAddresses')
    );

    return this.busyService.busy$('Loading tax data ...', loadData$);

  }

  getAvailableTaxPairs(addressPairs: IAddressPairRequest[]): Observable<IAvailableTaxAddressPair[]> {
    const pairs = addressPairs
      // The api throws a 500 error if we send addresses that are empty
      .filter(pair => pair.homeGeoCode && pair.workGeoCode)
      .map(pair => {
        const req = {
          id: pair.id,
          workAddress: <IAvailableTaxAddress>{
            geoCode: pair.workGeoCode
          },
          liveAddress: <IAvailableTaxAddress>{
            geoCode: pair.homeGeoCode
          }
        };

        // Do not send schoolDistrict or psdCode to taxes api if there not available as the api will crash
        if (pair.workSchoolDistrictCode != null) {
          req.workAddress.schoolDistrict = pair.workSchoolDistrictCode;
        }
        if (pair.workPsdCode != null) {
          req.workAddress.psdCode = pair.workPsdCode;
        }
        if (pair.homeSchoolDistrictCode != null) {
          req.liveAddress.schoolDistrict = pair.homeSchoolDistrictCode;
        }
        if (pair.homePsdCode != null) {
          req.liveAddress.psdCode = pair.homePsdCode;
        }

        return req;
      });

    if (pairs.length === 0) {
      return of([]);
    }

    const loadData$ = this.http.post<IAvailableTaxAddressPair[]>(taxApiUrl, pairs)
    .pipe(
      catchError((err: HttpErrorResponse) => {
        this.logger.error('Error retrieving taxes', err, pairs);
        return of([]);
      }),
      tag('TaxProviderService.getAvailableTaxPairs')
    );

    return this.busyService.busy$('Loading tax data ...', loadData$);
  }
}
