import { HttpHeaders, HttpResponse } from '@angular/common/http';

import { Observable } from 'rxjs';

import { RequestInfo, ResponseOptions, STATUS } from 'angular-in-memory-web-api';
export { RequestInfo, ResponseOptions, STATUS };

import { Company } from '@model';
import { Db } from './db';
export { Db };

import { parseJwt } from '../mock-auth/jwt.service';

export function createOkResponse$(req: RequestInfo, body?: any, headers?: HttpHeaders) {
  return createResponse$(req, STATUS.OK, 'OK', body, headers);
}

export function createNoContentResponse$(req: RequestInfo, headers?: HttpHeaders): Observable<HttpResponse<any>> {
  return createResponse$(req, STATUS.NO_CONTENT, 'NO CONTENT', null, headers);
}

/** Generic bad request response */
export function createBadRequestResponse$(req: RequestInfo, body?: any, headers?: HttpHeaders) {
  return createResponse$(req, STATUS.BAD_REQUEST, 'Bad Request', body, headers);
}

/**
 * Custom response of any kind
 * @param requestInfo
 * @param status Http status code. See STATUS.
 * @param statusText Http status text for that code.
 * @param [body] The body, if any
 * @param [headers] Headers, if any. Creates empty headers object if none supplied.
 * @returns A cold response Observable from a factory for ResponseOptions
 */
export function createResponse$(requestInfo: RequestInfo, status: number, statusText: string, body?: any, headers?: HttpHeaders) {
  const options: ResponseOptions = {
    url: requestInfo.url,
    status,
    statusText,
    body,
    headers: headers || new HttpHeaders()
  };
  return requestInfo.utils.createResponse$(() => options);
}

/** Add an Etag response header from the company's id and a version number */
export function addCompanyEtagResponseHeader(db: Db, companyId: string, headers?: HttpHeaders): HttpHeaders {
  const version = db.CompanyVersion[companyId];
  const etag = createCompanyEtag(companyId, version);
  if (etag) {
    const weakEtag = `W/"${etag}"`;
    return headers ? headers.set('Etag', weakEtag) : new HttpHeaders({'Etag': weakEtag});
  } else {
    return headers || new HttpHeaders();
  }
}

/** Create a company Etag from the company's id and a version number */
export function createCompanyEtag(companyId: string, version: number) {
  return companyId && !isNaN(version) ? companyId + '/' + version : null;
}

/** Mimic server-side API logic for deriving current user id from the request */
export function getCurrentUserIdFromRequest(req: RequestInfo, db: Db) {
    // try to get userId from request via the AccessToken
    let userId: string;
    const sub = getSubFromRequest(req);
    const subEnding = sub.split(':').pop(); // get the part after the colon

    const authUser = db.User.find(
      // match exactly, match the digits, match the username ... because mock-world is weird.
      u =>
        u.oidcSub === sub ||
        u.oidcSub.split(':').pop() === subEnding ||
        u.username === subEnding
    );

    if (authUser && authUser.companyId) {
      userId = authUser.id;
    } else {
      // Admin? Look for current user id in header
      userId = (req.req as any).headers.get('X-Sp-Current-User-Id');
    }
    return userId;
}

/** Extract the oidcSub from the request. Ex: 'NewCustUser:1234' */
export function getSubFromRequest(req: RequestInfo): string {
  try {
    let authHeader = (<any> req.headers).get('authorization');
    if (!authHeader) {
      // bug in AngularInMemoryWebApi v6.0.1 wipes out the original header.
      // but it's still there in the original req, located at `req.req.headers`.
      authHeader = (<any> req.req).headers.get('authorization');
    }
    const token = authHeader.split(' ')[1]; // strip off the leading "Bearer "
    const parsedToken = parseJwt(token);
    return parsedToken.sub || parsedToken.profile.sub;
  } catch (error) {
    // Check out both req.headers.normalizedNames and req.req.headers.normalizedNames
    console.error('Failed to find sub in profile of token of request', req);
    return '';
  }
}

/**
 * Ensure that a change to a Company satisfies constraints.
 * Call it in one of the intercepted  HTTP methods.
 * @returns null if no problems detected and normal in-memory processing should handle it.
 * Returns error response if it caught something.
 */
export function guardCompanyChange(req: RequestInfo, db: Db) {
  const company: Company = (req.req as any).body;
  if (company) {
    const existingCompany = db.Company.find((c: Company) => c.id === company.id);
    const status = company.status;
    if (status) {
      if (existingCompany && existingCompany.status !== status) {
        const body = `Cannot change Company status from ${existingCompany.status} to ${status}`;
        return createBadRequestResponse$(req, body);
      }
    } else {
      const body = 'Cannot save a company with a null/empty status';
      return createBadRequestResponse$(req, body);
    }
  }
}

/** Parse a company Etag into the companyId and the version */
export function parseCompanyEtag(etag: string) {
  const match = (etag || '').match(/(W\/"|")?(.*)\/(\d*)["]?$/);
  if (match) {
    const companyId = match[2];
    const version = Number.parseInt(match[3], 10);
    return { companyId, version };
  } else {
    return { companyId: null, version: NaN };
  }
}


