import { Component, ElementRef, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { from, Observable, of, ReplaySubject } from 'rxjs';
import { catchError, concatMap, first, map, mergeMap } from 'rxjs/operators';

import { Address } from '@model';
import { AddressService } from '@entity';
import { areDifferent } from '@utils';
import { TaxCollectorService } from '@app/core/services/tax-services/tax-collector.service';

@Component({
  selector: 'sp-address-modal',
  templateUrl: './address-modal.component.html'
})
/**
 * Modal Address Editor
 */
export class AddressModalComponent {

  @ViewChild('editAddressContent') editAddressContent: ElementRef;
  @ViewChild('removeAddressContent') removeAddressContent: ElementRef;

  /** Modal title */
  modalTitle = 'Address';

  /** The Address to edit with modal address editor. Internal to component  */
  address: Address;

  /** User's delete confirmation. Internal to component */
  deleteConfirmation: '';
  get isDeleteConfirmed() {
    return /delete/i.test(this.deleteConfirmation);
  }

  constructor(private addressService: AddressService, private taxCollectorService: TaxCollectorService, private modalService: NgbModal) { }

  /**
   * Open the location editor modal for an existing address and save the address if user changes it.
   * Does nothing if read-only.
   * @param existingAddress Address to edit
   * @param [title] Title to display in modal editor
   * @returns Cold, terminating observable of the saved, new-or-updated Address or null if no change.
   * Must subscribe but no need to unsubscribe.
   */
  addOrEditAddress(existingAddress: Address, title?: string): Observable<Address> {
    this.address = Object.assign({}, existingAddress); // remove prototype while cloning.
    const modalTitle = title || this.address.id == null ? 'Add Address' : 'Edit Address';

    return this.addressService.readOnly$.pipe(
      first(),
      concatMap(isReadOnly => {
        return isReadOnly
          ? of(null)
          : this.openAddressModal(modalTitle).pipe(
            concatMap(newAddr => this.save(newAddr))
          );
      })
    );
  }

  /**
   * Open a delete confirmation dialog and
   * save a "removed" address if user confirms "delete".
   * Does nothing if read-only.
   * @param existingAddress Address to delete/remove
   * @returns Cold, terminating observable of the saved Address with isRemoved:true or null if no change.
   * Must subscribe but no need to unsubscribe.
   */
  removeAddress(existingAddress: Address): Observable<Address> {
    this.deleteConfirmation = '';
    this.address = { ...existingAddress };

    return this.addressService.readOnly$.pipe(
      first(),
      concatMap(isReadOnly => {
        return isReadOnly
          ? of(null)
          : from(this.modalService.open(this.removeAddressContent).result).pipe(
          map(result => /delete/i.test(result) ? { ...existingAddress, isRemoved: true } : null),
          catchError(error => of(null)), // dismissed
          concatMap(newAddr => this.save(newAddr))
        );
      })
    );
  }

  /** Internal to component */
  canSave(): boolean {
    const canSave = this.hasSomeValue(this.address.city) &&
      this.hasSomeValue(this.address.street1) &&
      this.hasSomeValue(this.address.zip) &&
      this.hasSomeValue(this.address.state);
    return canSave;
  }

  /**
   * Open the address modal to add or edit an address
   * @param [title] Title to display in modal editor
   * @returns Address if added/edited or null canceled dismissed.
   */
  private openAddressModal(title?: string): Observable<Address> {
    this.modalTitle = title || 'Address';
    return from(this.modalService.open(this.editAddressContent, { backdrop: 'static' }).result).pipe(
      map(result => result === 'save' ? this.address : null),
      catchError(error => of(null)) // dismissed
    );
  }

  private save = (newAddr: Address): Observable<Address> => {
    if (!newAddr) {
      return of(null); // nothing to save
    }

    return this.addressService.entityMap$.pipe(
      first(),
      concatMap(addrMap => {
        const existing = addrMap[newAddr.id];
        if (existing && !areDifferent(newAddr, existing)) {
          return of(null); // no change
        }

        const saveObservable = existing
          ? this.addressService.update(newAddr)
          : this.addressService.add(newAddr);

        return saveObservable.pipe(
          // After saving address, re-compute taxes and save them
          // then pass the saved address along
          concatMap(savedAddr =>
            this.taxCollectorService.computeTaxes().pipe(map(_ => savedAddr))
          )
        );
      }),
    );
  }

  private hasSomeValue(val: string | null) {
    return val != null && val.length > 0;
  }
}
